import {
    BaseElement,
    booleanConverter,
    cache,
    createClassName,
    defineElement,
    html,
    nothing,
    property,
    type PropertyValues,
    query,
    state,
    styleMap,
} from '@umetrics/sartorius-ui-base';
import { version } from '../metadata';
import { namespacedTag } from '../tag';
import { windowScrollStyles as styles } from './windowScrollStyles';
import { ResizeAwareMixin } from '@umetrics/sartorius-ui-mixins';
import { dateFormatter, perfDebounce } from '@umetrics/sartorius-ui-utils';

import { Domain2D } from '../data/domain';
import { WithContext } from '../data/plotContext';
import { defaultZoomBox } from '../data/scale';
import { WithDataContext } from '../data/dataContext';
import { findDataPointByValue, findInterpolatedDomainValue } from '../utils';
import { handleLeftRulerMove, handleRightRulerMove, MIN_THUMBS_GAP, viewBoxToPx } from './utils';
import resizeVertical from '@umetrics/sartorius-ui-icon/dist/icons/resizeVertical';
import clock from '@umetrics/sartorius-ui-icon/dist/icons/clock';
import { Datum } from '@umetrics/sartorius-ui-plot-utils';

export enum SlotIds {
    LeftDisabledRuler = 'left-disabled-ruler',
    LeftRuler = 'left-ruler',
    RightDisabledRuler = 'right-disabled-ruler',
    RightRuler = 'right-ruler',
    Bar = 'bar',
}

export interface WindowScrollOnChangeEventDetail {
    event: Event;
    target: HTMLElement;
    mouse: [x: number, y: number];
    isVisible: boolean;
    dataPoint: number | undefined;
    currentInterpolatedDataPoint: number | undefined;
    leftRulerPosition: number;
    rightRulerPosition: number;
    leftRulerDataPoint: Datum | undefined;
    rightRulerDataPoint: Datum | undefined;
    zoomedDomain?: { start: number | undefined; end: number | undefined };
    barActive: boolean;
}

export interface WindowScrollProperties {
    height?: number;
    rulerWidth?: number;
    disabled?: boolean /* Disable drag/resize */;
    tooltips?: boolean /* Enable tooltips on drag/resize */;
    bgColor?: string;
    leftLabel?: 'auto' | string /* auto mean lower domain bound passed though to the dataFormatter */;
    rightLabel?: 'auto' | string /* auto mean upper domain bound passed though to the dataFormatter */;
    dataFormatter?: (
        data: Datum | number | string
    ) => string /* Tooltip and label formatter, default dates YY:MM:dd hh:mm*/;

    onZoom?: (e: CustomEvent) => void;
    onPan?: (e: CustomEvent) => void;
    onChange?: (e: CustomEvent<WindowScrollOnChangeEventDetail>) => void;
    onZoomLimitChange?: (e: CustomEvent<number>) => void;
}

@defineElement({ tagName: namespacedTag('window-scroll'), styles, version })
export class WindowScroll
    extends WithDataContext(WithContext(ResizeAwareMixin(BaseElement)))
    implements WindowScrollProperties
{
    private static Icon;

    @property({ type: Object, attribute: false })
    domainBounds: Domain2D | undefined;

    @property({ type: Number })
    height = 50;

    @property({ type: Number, attribute: 'ruler-width' })
    rulerWidth?: number;

    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    disabled = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    tooltips?: boolean;

    @property({ type: String, attribute: 'bg-color' })
    bgColor?: string;

    @property({ type: String, attribute: 'left-label' })
    leftLabel?: string;
    @property({ type: String, attribute: 'right-label' })
    rightLabel?: string;

    @property({ type: Object, reflect: false, attribute: false })
    set dataFormatter(val: undefined | ((data: Datum | number | string) => string)) {
        this._dataFormatter = val ? val : dateFormatter();
    }

    get dataFormatter() {
        return this._dataFormatter;
    }

    onZoom;
    onPan;
    onChange;

    @state()
    private _isBarDragging = false;
    @state()
    private _isRulerDragging = false;
    @state()
    private _distanceBetweenRulers?: number;

    private _bounds: Domain2D = defaultZoomBox;
    private _startPoint = 0;
    private _hasLeftRulerSlot = false;
    private _hasLeftDisabledRulerSlot = false;
    private _hasRightRulerSlot = false;
    private _hasRightDisabledRulerSlot = false;
    private _hasBarSlot = false;
    private _dataFormatter = dateFormatter();
    private _leftLabel;
    private _rightLabel;

    private _trackingState: Partial<WindowScrollOnChangeEventDetail> = {
        barActive: false,
        mouse: [0, 0],
        isVisible: false,
        dataPoint: undefined,
        leftRulerPosition: 0,
        rightRulerPosition: 0,
        leftRulerDataPoint: undefined,
        rightRulerDataPoint: undefined,
    };

    private _mouse;
    private _firstRulerPosition = 0;
    private _lastRulerPosition = 0;
    private _tooltipRightWidth = 0;
    private _tooltipLeftWidth = 0;

    // value that determine the smallest diff of viewbox ranges
    // is defined only after the first rendering
    private zoomLimit!: number;

    @query('[class*="-container"]')
    private _container!: HTMLElement;
    @query('[class*="-tooltip-left"]')
    private _tooltipLeft!: HTMLElement;
    @query('[class*="-tooltip-right"]')
    private _tooltipRight!: HTMLElement;

    @state()
    private _vertical?: boolean;

    private onContainerResize = perfDebounce(() => {
        this.resolveZoomLimit();
    }, 10);

    constructor() {
        super();
        this.handleMouseMove = this.handleMouseMove.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);

        this.barDragStart = this.barDragStart.bind(this);
        this.barMousemove = this.barMousemove.bind(this);
        this.barMouseup = this.barMouseup.bind(this);

        this.leftDragStart = this.leftDragStart.bind(this);
        this.leftMousemove = this.leftMousemove.bind(this);
        this.rightDragStart = this.rightDragStart.bind(this);
        this.rightMouseMove = this.rightMouseMove.bind(this);
    }

    public connectedCallback() {
        WindowScroll.dynSubRegister([{ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] }], () => {
            super.connectedCallback();
        });
        this._vertical = this.slot === 'vertical-scrollbar';
    }

    public disconnectedCallback(): void {
        this._uninstallResizeObserver(this._container);
        super.disconnectedCallback();
    }

    protected updated(changedProperties) {
        super.updated(changedProperties);
        if (changedProperties.has('viewbox')) {
            this.updateTrackingState({});
        }
    }

    protected firstUpdated(_changedProperties: PropertyValues) {
        if (!this._trackingState.leftRulerDataPoint) {
            this._trackingState.leftRulerDataPoint = this._verticalOr(
                () =>
                    findDataPointByValue(
                        this._firstRulerPosition,
                        this.allDataElements!,
                        this._container.clientHeight,
                        this.scaleY,
                        this.scaleMap?.[this.scaleY!]?.scale
                    )?.y,
                () =>
                    findDataPointByValue(
                        this._firstRulerPosition,
                        this.allDataElements!,
                        this._container.clientWidth,
                        this.scaleX,
                        this.scaleMap?.[this.scaleX!]?.scale
                    )?.x
            );
        }
        if (!this._trackingState.rightRulerDataPoint) {
            this._trackingState.rightRulerDataPoint = this._verticalOr(
                () =>
                    findDataPointByValue(
                        this._lastRulerPosition,
                        this.allDataElements!,
                        this._container.clientHeight,
                        this.scaleY,
                        this.scaleMap?.[this.scaleY!]?.scale
                    )?.y,
                () =>
                    findDataPointByValue(
                        this._lastRulerPosition,
                        this.allDataElements!,
                        this._container.clientWidth,
                        this.scaleX,
                        this.scaleMap?.[this.scaleX!]?.scale
                    )?.x
            );
        }
        this._uninstallResizeObserver(this);
        this._installResizeObserver(this._container);
        super.firstUpdated(_changedProperties);
    }

    protected willUpdate(_changedProperties: PropertyValues<this>): void {
        super.willUpdate(_changedProperties);
        if (_changedProperties.has('domainBounds') || _changedProperties.has('fittedDomain')) {
            this._bounds = this.domainBounds ?? defaultZoomBox;
        }

        if (_changedProperties.has('rulerWidth') || _changedProperties.has('domainBounds')) {
            this.resolveZoomLimit();
        }
        if (
            this.allDataElements &&
            this.dataFormatter &&
            (_changedProperties.has('allDataElements') ||
                _changedProperties.has('dataFormatter') ||
                _changedProperties.has('leftLabel') ||
                _changedProperties.has('rightLabel'))
        ) {
            this._leftLabel = this._updateAutoLabel(this.leftLabel, 0);
            this._rightLabel = this._updateAutoLabel(this.rightLabel);
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            disabled: this.disabled,
            dragging: this._isRulerDragging || this._isBarDragging,
            high:
                this._distanceBetweenRulers! <
                (this._tooltipLeft?.clientWidth ?? 0) + (this._tooltipRight?.clientWidth ?? 0),
            vertical: !!this._vertical,
        });
        let containerWidth, viewbox, bounds;
        this._verticalOr(
            () => {
                containerWidth = this._container?.clientHeight ?? 0;
                viewbox = this.viewbox?.y;
                bounds = this._bounds.y;
            },
            () => {
                containerWidth = this._container?.clientWidth ?? 0;
                viewbox = this.viewbox?.x;
                bounds = this._bounds.x;
            }
        );
        const { left, width } = viewBoxToPx(viewbox ?? [0, 1], containerWidth, bounds);

        const rulerWidth = this.rulerWidth ?? 10;
        const minRulerDistance = rulerWidth ?? 10;
        const effectiveWidth = Math.max(width, minRulerDistance);

        // Calculate positions to avoid overlap
        this._firstRulerPosition = left;
        this._lastRulerPosition = Math.max(left + effectiveWidth, this._firstRulerPosition + minRulerDistance);

        const halfRulerSize = rulerWidth / 2;

        const rootStyle = {
            '--windowscroll-root-padding': `${halfRulerSize}px`,
            '--windowscroll-content-margin': `-${halfRulerSize}px`,
            '--windowscroll-container-bg-color': this.bgColor,
            '--windowscroll-container-height': `${this.height}px`,
            '--windowscroll-ruler-width': `${rulerWidth}px`,
            '--windowscroll-left-ruler-pos': `${this._firstRulerPosition - halfRulerSize}px`,
            '--windowscroll-right-ruler-pos': `${this._lastRulerPosition - halfRulerSize}px`,
            '--windowscroll-bar-width': `${effectiveWidth}px`,
            '--windowscroll-bar-pos': `${left}px`,
            '--windowscroll-left-tooltip-pos': `${this._adjustedLeftPosition(containerWidth, this._tooltipLeftWidth, this._firstRulerPosition - halfRulerSize)}px`,
            '--windowscroll-right-tooltip-pos': `${this._adjustedRightPosition(rulerWidth, this._tooltipRightWidth, this._lastRulerPosition - halfRulerSize) - rulerWidth}px`,
        };

        const labelHtml =
            this.leftLabel || this.rightLabel
                ? html`
                      <div class="${baseClassName}-labels">
                          <div class="${baseClassName}-left-label">${this._leftLabel ?? this.leftLabel}</div>
                          <div class="${baseClassName}-right-label">${this._rightLabel ?? this.rightLabel}</div>
                      </div>
                  `
                : null;

        return html`
            <div class="${rootClasses}" style=${styleMap(rootStyle)}>
                ${labelHtml}
                <div
                    class="${baseClassName}-container"
                    @mousemove=${this.handleMouseMove}
                    @mouseleave=${this.handleMouseLeave}
                >
                    <div class="${baseClassName}-content">
                        <slot></slot>
                    </div>
                    <div class="${baseClassName}-placeholder">
                        <slot name="placeholder"></slot>
                    </div>

                    <div class="${baseClassName}-ruler ${baseClassName}-left-ruler" @mousedown=${this.leftDragStart}>
                        ${this.getLeftRuler(baseClassName)} ${this._renderTooltip(baseClassName, 'left')}
                    </div>

                    <div class="${baseClassName}-bar" @mousedown=${this.barDragStart}>
                        <slot @slotchange="${this.handleSlotChange}" name="${SlotIds.Bar}"></slot>
                        ${!this._hasBarSlot ? html` <div class="${baseClassName}-default-bar"></div>` : nothing}
                    </div>

                    <div
                        class="${baseClassName}-ruler ${baseClassName}-right-ruler __right-ruler"
                        @mousedown=${this.rightDragStart}
                    >
                        ${this.getRightRuler(baseClassName)} ${this._renderTooltip(baseClassName, 'right')}
                    </div>
                </div>
            </div>
        `;
    }

    protected _resizeObserverCallback(): void {
        this.onContainerResize();
    }

    private _verticalOr = <T>(v: () => T, h: () => T): T => (this._vertical ? v() : h());

    private _adjustedLeftPosition = (containerWidth, tooltipWidth, desiredLeftPos) => {
        const tooltipRightEdge = desiredLeftPos + tooltipWidth;
        return tooltipRightEdge > containerWidth ? Math.round(containerWidth - tooltipRightEdge) : 0;
    };
    private _adjustedRightPosition = (rulerWidth, tooltipWidth, desiredRightPos) => {
        const leftEdge = desiredRightPos + rulerWidth - tooltipWidth;
        return leftEdge > 0 ? 0 : leftEdge;
    };

    private resolveZoomLimit(): void {
        if (!this._container || !this.rulerWidth) {
            return;
        }

        const containerWidth = this._container.offsetWidth;
        const [min, max] = this._bounds.x ?? defaultZoomBox.x;

        this.zoomLimit = ((max - min) / containerWidth) * (this.rulerWidth + MIN_THUMBS_GAP);

        this.dispatchEvent(
            new CustomEvent('zoom-limit-change', {
                detail: this.zoomLimit,
                bubbles: true,
                composed: true,
                cancelable: true,
            })
        );
    }

    private _renderTooltip = (baseClassName, id: string) => {
        if (!this.tooltips) {
            return null;
        }
        const d = id === 'left' ? 'M0 30H8L0 37V30Z' : 'M0 30H8V37 L0 30Z';
        const data = id === 'left' ? this._trackingState.leftRulerDataPoint : this._trackingState.rightRulerDataPoint;
        return html` <div class="${baseClassName}-tooltip ${baseClassName}-tooltip-${id}">
            <div class="${baseClassName}-tooltip-content">
                ${this._renderClock(baseClassName)}
                <span class="${baseClassName}-tooltip-data">${this._dataFormatter(data ?? 0)}</span>
            </div>
            <div class="${baseClassName}-tooltip-effect">
                <svg width="8" height="37" viewBox="0 0 8 37" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="${d}" fill="#252525" />
                    <rect width="8" height="30" fill="#252525" />
                </svg>
            </div>
        </div>`;
    };

    private _updateAutoLabel(control, offset = -1) {
        if (control === 'auto') {
            const len = this.allDataElements!.length;
            const point = offset > -1 ? offset : len;
            const scaleName = this._vertical ? this.scaleY : this.scaleX;
            const scale = this.scaleMap?.[scaleName!];
            const p = findDataPointByValue(point, this.allDataElements!, len, scaleName, scale?.scale);
            if (p) {
                const data = this._vertical ? p.y : p.x;
                return this.dataFormatter!(data);
            }
            return '';
        }
        return undefined;
    }

    private async barDragStart(event: MouseEvent) {
        event.preventDefault();
        event.stopPropagation();

        if (this.disabled || this._isBarDragging) {
            return;
        }
        this._isBarDragging = true;

        const { firstRulerDataPoint, lastRulerDataPoint } = this._verticalOr(
            () => {
                this._startPoint = event.clientY;
                const scale = this.scaleMap?.[this.scaleY!].scale;
                const first = findDataPointByValue(
                    this._firstRulerPosition,
                    this.allDataElements!,
                    this._container.clientHeight,
                    this.scaleY,
                    scale
                );
                const last = findDataPointByValue(
                    this._lastRulerPosition,
                    this.allDataElements!,
                    this._container.clientHeight,
                    this.scaleY,
                    scale
                );
                return { firstRulerDataPoint: first?.y, lastRulerDataPoint: last?.y };
            },
            () => {
                this._startPoint = event.clientX;
                const scale = this.scaleMap?.[this.scaleX!].scale;
                const first = findDataPointByValue(
                    this._firstRulerPosition,
                    this.allDataElements!,
                    this._container.clientWidth,
                    this.scaleX,
                    scale
                );
                const last = findDataPointByValue(
                    this._lastRulerPosition,
                    this.allDataElements!,
                    this._container.clientWidth,
                    this.scaleX,
                    scale
                );
                return { firstRulerDataPoint: first?.x, lastRulerDataPoint: last?.x };
            }
        );

        this.updateTrackingState({
            event,
            barActive: true,
            leftRulerDataPoint: firstRulerDataPoint,
            rightRulerDataPoint: lastRulerDataPoint,
        });

        document.addEventListener('mousemove', this.barMousemove);
        document.addEventListener('mouseup', this.barMouseup);

        await this.updateComplete;
        this._updateDistanceBetween();
    }

    private barMousemove(event: MouseEvent) {
        event.preventDefault();
        event.stopPropagation();

        if (!this._isBarDragging) return;

        const { firstRulerDataPoint, lastRulerDataPoint, currentPoint, domainDelta } = this._verticalOr(
            () => {
                const scale = this.scaleMap?.[this.scaleY!].scale;
                const first = findDataPointByValue(
                    this._firstRulerPosition,
                    this.allDataElements!,
                    this._container.clientHeight,
                    this.scaleY,
                    scale
                );
                const last = findDataPointByValue(
                    this._lastRulerPosition,
                    this.allDataElements!,
                    this._container.clientHeight,
                    this.scaleY,
                    scale
                );
                const _currentPoint = event.clientY;
                const _domainDelta =
                    ((this._startPoint - _currentPoint) / this._container.clientHeight) *
                    (this._bounds.y[1] - this._bounds.y[0]);

                return {
                    firstRulerDataPoint: first?.y,
                    lastRulerDataPoint: last?.y,
                    currentPoint: _currentPoint,
                    domainDelta: _domainDelta,
                };
            },
            () => {
                const scale = this.scaleMap?.[this.scaleX!]?.scale;

                const first = findDataPointByValue(
                    this._firstRulerPosition,
                    this.allDataElements!,
                    this._container.clientWidth,
                    this.scaleX,
                    scale
                );
                const last = findDataPointByValue(
                    this._lastRulerPosition,
                    this.allDataElements!,
                    this._container.clientWidth,
                    this.scaleX,
                    scale
                );
                const _currentPoint = event.clientX;
                const _domainDelta =
                    ((this._startPoint - _currentPoint) / this._container.clientWidth) *
                    (this._bounds.x[1] - this._bounds.x[0]);

                return {
                    firstRulerDataPoint: first?.x,
                    lastRulerDataPoint: last?.x,
                    currentPoint: _currentPoint,
                    domainDelta: _domainDelta,
                };
            }
        );
        this.panByDomainDelta(domainDelta);

        this.updateTrackingState({
            event,
            barActive: true,
            leftRulerDataPoint: firstRulerDataPoint,
            rightRulerDataPoint: lastRulerDataPoint,
        });

        this._startPoint = currentPoint;
        this.requestUpdate();
    }

    private barMouseup(e: MouseEvent) {
        e.preventDefault();
        e.stopPropagation();
        document.removeEventListener('mousemove', this.barMousemove);
        document.removeEventListener('mouseup', this.barMouseup);
        this._isBarDragging = false;
        this._distanceBetweenRulers = undefined;

        this.updateTrackingState({ barActive: false });
    }

    private async leftDragStart(e: MouseEvent) {
        e.preventDefault();
        if (this.disabled) {
            return;
        }

        this._startPoint = this._vertical ? e.clientY : e.clientX;
        this._isRulerDragging = true;
        this.leftMousemove(e); // update ruler datapoint, for tooltip

        document.addEventListener('mousemove', this.leftMousemove);
        document.addEventListener(
            'mouseup',
            () => {
                this._isRulerDragging = false;
                this._distanceBetweenRulers = undefined;
                this.updateTrackingState({ barActive: false });
                document.removeEventListener('mousemove', this.leftMousemove);
            },
            { once: true }
        );
        await this.updateComplete;
        this._updateDistanceBetween();
        const _leftToolTip = this.shadowRoot!.querySelector('[class*="-tooltip-left"]');
        const _rightToolTip = this.shadowRoot!.querySelector('[class*="-tooltip-right"]');
        this._tooltipLeftWidth = _leftToolTip?.getBoundingClientRect().width ?? 0;
        this._tooltipRightWidth = _rightToolTip?.getBoundingClientRect().width ?? 0;
    }

    private async rightDragStart(e: MouseEvent) {
        e.preventDefault();
        if (this.disabled) {
            return;
        }
        this._startPoint = this._vertical ? e.clientY : e.clientX;
        this._isRulerDragging = true;
        this.rightMouseMove(e); // update ruler datapoint, for tooltip

        document.addEventListener('mousemove', this.rightMouseMove);
        document.addEventListener(
            'mouseup',
            () => {
                this._isRulerDragging = false;
                this._distanceBetweenRulers = undefined;
                this.updateTrackingState({ barActive: false });
                document.removeEventListener('mousemove', this.rightMouseMove);
                this._distanceBetweenRulers = 0;
            },
            { once: true }
        );
        await this.updateComplete;
        this._updateDistanceBetween();
        const _leftToolTip = this.shadowRoot!.querySelector('[class*="-tooltip-left"]');
        const _rightToolTip = this.shadowRoot!.querySelector('[class*="-tooltip-right"]');
        this._tooltipLeftWidth = _leftToolTip?.getBoundingClientRect().width ?? 0;
        this._tooltipRightWidth = _rightToolTip?.getBoundingClientRect().width ?? 0;
    }

    private leftMousemove(e: MouseEvent) {
        e.preventDefault();
        const options = this._vertical
            ? {
                  startX: this._startPoint,
                  currentX: e.clientY,
                  viewbox: this.viewbox as Domain2D,
                  containerWidth: this._container.clientHeight,
                  bounds: this._bounds.y,
                  rulerWidth: this.rulerWidth ?? 10,
              }
            : {
                  startX: this._startPoint,
                  currentX: e.clientX,
                  viewbox: this.viewbox as Domain2D,
                  containerWidth: this._container.clientWidth,
                  bounds: this._bounds.x,
                  rulerWidth: this.rulerWidth ?? 10,
              };
        const newViewbox = handleLeftRulerMove(!!this._vertical, options);
        this.dispatchEvent(
            new CustomEvent('zoom', {
                bubbles: true,
                detail: {
                    ...this.scaleZoomMap,
                    ...(!this._vertical && { x: newViewbox.x }),
                    ...(this._vertical && { y: newViewbox.y }),
                    viewbox: newViewbox,
                },
            })
        );

        this.updateTrackingState({
            event: e,
            barActive: true,
        });

        this._updateDistanceBetween();
        this._startPoint = this._vertical ? e.clientY : e.clientX;
    }

    private rightMouseMove(e: MouseEvent) {
        e.preventDefault();
        const options = {
            startX: this._startPoint,
            currentX: this._vertical ? e.clientY : e.clientX,
            viewbox: this.viewbox as Domain2D,
            containerWidth: this._vertical ? this._container.clientHeight : this._container.clientWidth,
            bounds: this._vertical ? this._bounds.y : this._bounds.x,
            rulerWidth: this.rulerWidth ?? 10,
        };
        const newViewbox = handleRightRulerMove(!!this._vertical, options);
        this.dispatchEvent(
            new CustomEvent('zoom', {
                bubbles: true,
                detail: {
                    ...this.scaleZoomMap,
                    ...(!this._vertical && { x: newViewbox.x }),
                    ...(this._vertical && { y: newViewbox.y }),
                    viewbox: newViewbox,
                },
            })
        );

        const { firstRulerDataPoint, lastRulerDataPoint, zoomedDomain } = this._getRulerDataPoints(e);

        this.updateTrackingState({
            event: e,
            barActive: true,
            leftRulerDataPoint: firstRulerDataPoint,
            rightRulerDataPoint: lastRulerDataPoint,
            zoomedDomain,
        });
        this._updateDistanceBetween();
    }

    private _updateDistanceBetween = () => {
        this._distanceBetweenRulers = Math.abs(this._lastRulerPosition - this._firstRulerPosition);
    };

    private handleSlotChange(e: Event) {
        const slot = e.target as HTMLSlotElement;
        const nodes = slot.assignedElements({ flatten: true });

        switch (slot.name) {
            case 'left-ruler': {
                this._hasLeftRulerSlot = nodes.length > 0;
                break;
            }
            case 'left-disabled-ruler': {
                this._hasLeftDisabledRulerSlot = nodes.length > 0;
                break;
            }
            case 'right-ruler': {
                this._hasRightRulerSlot = nodes.length > 0;
                break;
            }
            case 'right-disabled-ruler': {
                this._hasRightDisabledRulerSlot = nodes.length > 0;
                break;
            }
            case 'bar': {
                this._hasBarSlot = nodes.length > 0;
            }
        }
        this.requestUpdate();
    }

    private handleMouseMove(event: MouseEvent) {
        if (!this._container) return;

        const rect = this._container.getBoundingClientRect();
        const mouseX = event.clientX - rect.left;
        const mouseY = event.clientY - rect.top;

        const scale = this.scaleMap?.[this.scaleX!]?.scale;
        const currentDataPoint = findDataPointByValue(
            mouseX,
            this.allDataElements!,
            this._container.getBoundingClientRect().width,
            this.scaleX,
            scale
        );

        const currentInterpolatedDataPoint = findInterpolatedDomainValue(
            Math.max(0, Math.min(mouseX, rect.width)),
            this.allDataElements!,
            this._container.getBoundingClientRect().width,
            this.scaleX,
            scale
        );

        this._mouse = [mouseX, mouseY];

        this.updateTrackingState({
            event,
            isVisible: true,
            dataPoint: currentDataPoint?.x,
            currentInterpolatedDataPoint,
            leftRulerPosition: this._firstRulerPosition,
            rightRulerPosition: this._lastRulerPosition,
        });
    }

    private handleMouseLeave(event: MouseEvent) {
        this.updateTrackingState({
            event,
            isVisible: false,
        });
    }

    private getLeftRuler = (baseClassName) =>
        this.getRuler(
            baseClassName,
            this._hasLeftRulerSlot || this._hasLeftDisabledRulerSlot,
            SlotIds.LeftRuler,
            SlotIds.LeftDisabledRuler
        );

    private getRightRuler = (baseClassName) =>
        this.getRuler(
            baseClassName,
            this._hasRightRulerSlot || this._hasRightDisabledRulerSlot,
            SlotIds.RightRuler,
            SlotIds.RightDisabledRuler
        );

    private getRuler(baseClassName, hasSlot, slot, disabledSlot) {
        const handleIcon = this._renderRulerHandle(baseClassName);
        const ruler = !hasSlot ? html` <div class="${baseClassName}-default-ruler">${handleIcon}</div>` : nothing;

        return html`${cache(
            this.disabled
                ? html` <slot @slotchange="${this.handleSlotChange}" name="${disabledSlot}"></slot> `
                : html` <slot @slotchange="${this.handleSlotChange}" name="${slot}"></slot>`
        )}${ruler}`;
    }

    private _renderRulerHandle = (baseClassName) => this._renderIcon(baseClassName, resizeVertical, 'ruler-handle');
    private _renderClock = (baseClassName) => this._renderIcon(baseClassName, clock, 'tooltip-icon');
    private _renderIcon = (baseClassName, icon, id) =>
        html` <${WindowScroll.Icon.tag} icon=${icon} class="${baseClassName}-${id}" />`;

    private panByDomainDelta(domainDeltaX: number) {
        if (this._isRulerDragging) {
            return;
        }

        const viewbox = this.viewbox as Domain2D;
        const domain = this._vertical ? viewbox.y : viewbox.x;

        // Invert delta for vertical mode
        const adjustedDelta = this._vertical ? -domainDeltaX : domainDeltaX;

        // Skip interaction if fully zoomed out
        if (domain[0] === 0 && domain[1] === 1) {
            return;
        }

        let newViewboxXmin = domain[0] - adjustedDelta;
        let newViewboxXmax = domain[1] - adjustedDelta;
        const bound = this._verticalOr(
            () => this._bounds.y,
            () => this._bounds.x
        );

        if (newViewboxXmin < bound[0]) {
            newViewboxXmin = bound[0];
            newViewboxXmax = newViewboxXmin + (domain[1] - domain[0]);
        } else if (newViewboxXmax > bound[1]) {
            newViewboxXmax = bound[1];
            newViewboxXmin = newViewboxXmax - (domain[1] - domain[0]);
        }
        const newViewBox = [newViewboxXmin, newViewboxXmax];
        this.dispatchEvent(
            new CustomEvent('pan', {
                bubbles: true,
                detail: {
                    ...this.scaleZoomMap,
                    ...(!this._vertical && { x: newViewBox }),
                    ...(this._vertical && { y: newViewBox }),
                    viewbox: this._vertical ? { ...viewbox, y: newViewBox } : { ...viewbox, x: newViewBox },
                },
            })
        );
    }

    private updateTrackingState(newState: Partial<WindowScrollOnChangeEventDetail>) {
        const dataPoints = this._getRulerDataPoints();

        this._trackingState = {
            ...this._trackingState,
            target: this,
            mouse: this._mouse,
            leftRulerPosition: this._firstRulerPosition,
            rightRulerPosition: this._lastRulerPosition,
            leftRulerDataPoint: dataPoints.firstRulerDataPoint,
            rightRulerDataPoint: dataPoints.lastRulerDataPoint,
            zoomedDomain: dataPoints.zoomedDomain,
            ...newState,
        };

        this.dispatchEvent(
            new CustomEvent('change', {
                bubbles: true,
                detail: this._trackingState,
            })
        );
    }

    private _getRulerDataPoints(e?: MouseEvent) {
        return this._verticalOr(
            () => {
                if (e) {
                    this._startPoint = e.clientY;
                }

                const scale = this.scaleMap?.[this.scaleY!]?.scale;
                const first = findDataPointByValue(
                    this._firstRulerPosition,
                    this.allDataElements!,
                    this._container?.clientHeight ?? 0,
                    this.scaleY,
                    scale
                );

                const last = findDataPointByValue(
                    this._lastRulerPosition,
                    this.allDataElements!,
                    this._container?.clientHeight ?? 0,
                    this.scaleY,
                    scale
                );

                const firstInterpolated = findInterpolatedDomainValue(
                    this._firstRulerPosition,
                    this.allDataElements!,
                    this._container?.clientHeight ?? 0,
                    this.scaleY,
                    scale
                );

                const lastInterpolated = findInterpolatedDomainValue(
                    this._lastRulerPosition,
                    this.allDataElements!,
                    this._container?.clientHeight ?? 0,
                    this.scaleY,
                    scale
                );

                return {
                    firstRulerDataPoint: first?.y,
                    lastRulerDataPoint: last?.y,
                    zoomedDomain: {
                        start: firstInterpolated,
                        end: lastInterpolated,
                    },
                };
            },
            () => {
                if (e) {
                    this._startPoint = e.clientX;
                }
                const scale = this.scaleMap?.[this.scaleX!]?.scale;
                const first = findDataPointByValue(
                    this._firstRulerPosition,
                    this.allDataElements!,
                    this._container?.clientWidth ?? 0,
                    this.scaleX,
                    scale
                );

                const last = findDataPointByValue(
                    this._lastRulerPosition,
                    this.allDataElements!,
                    this._container?.clientWidth ?? 0,
                    this.scaleX,
                    scale
                );

                const firstInterpolated = findInterpolatedDomainValue(
                    this._firstRulerPosition,
                    this.allDataElements!,
                    this._container?.clientWidth ?? 0,
                    this.scaleX,
                    scale
                );

                const lastInterpolated = findInterpolatedDomainValue(
                    this._lastRulerPosition,
                    this.allDataElements!,
                    this._container?.clientWidth ?? 0,
                    this.scaleX,
                    scale
                );

                return {
                    firstRulerDataPoint: first?.x,
                    lastRulerDataPoint: last?.x,
                    zoomedDomain: {
                        start: firstInterpolated,
                        end: lastInterpolated,
                    },
                };
            }
        );
    }
}
