import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const composablePlotStyles: GenerateStyles = (tagName) => {
    const { root } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                width: 100%;
                height: 100%;
            }

            ${root} {
                --area-r1: 'heading heading heading . pane';
                --area-r2: '. x2 . verscrollbar pane';
                --area-r3: 'y main y2 verscrollbar pane';
                --area-r4: '. x . . pane';
                --area-r5: '. horscrollbar . . .';
                --grid-main: 1fr;
                --grid: auto auto var(--grid-main) auto auto / auto 1fr auto auto auto;

                width: 100%;
                height: 100%;
                display: grid;
                position: relative;
                padding: var(--plot-paddings, 0);
                grid-template: var(--grid);
                grid-template-areas:
                    var(--area-r1)
                    var(--area-r2)
                    var(--area-r3)
                    var(--area-r4)
                    var(--area-r5);
                -webkit-font-smoothing: antialiased;
                font-synthesis: none;
                text-rendering: optimizeLegibility;

                container-type: inline-size;
            }

            .heading {
                grid-area: heading;
                display: grid;
                height: 30px;
                font-size: 14px;
                margin: 0;
                text-align: center;
                place-items: center;
            }

            .main-area {
                grid-area: var(--grid-area, main);
                overflow: visible;
                position: relative;
                user-select: none;
                background-color: color-mix(
                    in srgb,
                    var(--main-background) var(--main-background-opacity),
                    transparent
                );
            }

            .main-area slot::slotted(*) {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                user-select: none;
            }

            .axis-x,
            .axis-x2 {
                width: 100%;
                display: flex;
                flex-direction: column;
            }

            .axis-x {
                grid-area: var(--grid-area, x);
            }

            .axis-x2 {
                grid-area: var(--grid-area, x2);
            }

            .axis-y,
            .axis-y2 {
                height: 100%;
                display: flex;
                flex-direction: row;
            }

            .axis-y {
                grid-area: var(--grid-area, y);
            }

            .axis-y2 {
                grid-area: var(--grid-area, y2);
            }

            .pane {
                grid-area: pane;
                overflow: hidden;
            }

            .horscrollbar {
                grid-area: horscrollbar;
            }

            .verscrollbar {
                grid-area: verscrollbar;
            }

            .watermark {
                position: absolute;
                right: 16px;
                bottom: 2px;
                font-size: 8px;
                white-space: nowrap;
                color: var(--color-secondary-4);
            }
        `,
    ];
};
