import { nothing, svg, unsafeSVG } from '@umetrics/sartorius-ui-base';

export const shapeRawRectangle = '<path d="M -5 -5 L 5 -5 L 5 5 L -5 5 Z" />';
export const shapeRawDiamond = '<path d="M 0 -6 L 4.5 0 L 0 6 L -4.5 0 Z" />';
export const shapeRawTriangle = '<path d="M -5 3.75 L 0 -6.25 L 5 3.75 Z" />';
export const shapeRawCircle = '<circle cx="0" cy="0" r="5" />';
export const shapeRawPentagon = '<path d="M -5 -1 L 0 -5 L 5 -1 L 3 4 L -3 4 Z" />';
export const shapeRawStar =
    '<path d="M 0 -5.8322 L 1.7303 -2.3232 L 5.6023 -1.7545 L 2.8072 0.9801 L 3.4727 4.84 L 0.0121 3.025 L -3.4485 4.84 L -2.783 0.9801 L -5.5781 -1.7545 L -1.7061 -2.3232 Z" />';
export const shapeRawCross =
    '<path d="M -4.466 2.706 L -1.826 0 L -4.51 -2.64 L -2.607 -4.543 L 0 -1.859 L 2.728 -4.499 L 4.554 -2.673 L 1.914 0 L 4.598 2.662 L 2.695 4.565 L 0.011 1.881 L -2.64 4.521 Z" />';
export const shapeRawReversedTriangle = '<path d="M -5 -3.75 L 0 6.25 L 5 -3.75 Z" />';

export enum PlotShape {
    rectangle = 'rectangle',
    diamond = 'diamond',
    triangle = 'triangle',
    circle = 'circle',
    pentagon = 'pentagon',
    star = 'star',
    cross = 'cross',
    reversedTriangle = 'reversedTriangle',

    custom = 'custom', // Provide custom shape via customShape prop
}

export const rawShapeMap = {
    [PlotShape.rectangle]: shapeRawRectangle,
    [PlotShape.diamond]: shapeRawDiamond,
    [PlotShape.triangle]: shapeRawTriangle,
    [PlotShape.circle]: shapeRawCircle,
    [PlotShape.pentagon]: shapeRawPentagon,
    [PlotShape.star]: shapeRawStar,
    [PlotShape.cross]: shapeRawCross,
    [PlotShape.reversedTriangle]: shapeRawReversedTriangle,
};

export const shapeMap = {
    [PlotShape.rectangle]: svg`${unsafeSVG(shapeRawRectangle)}`,
    [PlotShape.diamond]: svg`${unsafeSVG(shapeRawDiamond)}`,
    [PlotShape.triangle]: svg`${unsafeSVG(shapeRawTriangle)}`,
    [PlotShape.circle]: svg`${unsafeSVG(shapeRawCircle)}`,
    [PlotShape.pentagon]: svg`${unsafeSVG(shapeRawPentagon)}`,
    [PlotShape.star]: svg`${unsafeSVG(shapeRawStar)}`,
    [PlotShape.cross]: svg`${unsafeSVG(shapeRawCross)}`,
    [PlotShape.reversedTriangle]: svg`${unsafeSVG(shapeRawReversedTriangle)}`,
};

export const DEFAULT_SHAPE_SIZE = 5;

export function renderShape(
    shape: PlotShape,
    customShape?: { d: string; transform?: string },
    size: number = DEFAULT_SHAPE_SIZE
) {
    //Shapes have an inherent size that need to be used as baseline
    const normalizedSize = size / DEFAULT_SHAPE_SIZE;
    const theShape =
        shape === PlotShape.custom
            ? svg`<path d="${customShape?.d}" transform=${customShape?.transform || nothing}>`
            : shapeMap[shape];
    return svg`
        <g stroke-width=${1 / normalizedSize} transform="scale(${normalizedSize} ${normalizedSize})">
            ${theShape}
        </g>
    `;
}

export const rawShape = (shape: PlotShape) => rawShapeMap[shape];
