import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    type PropertyValueMap,
    svg,
} from '@umetrics/sartorius-ui-base';
import { version } from '../metadata';
import { WithContext } from '../data/plotContext';
import { delaunayStyles } from './delaunayStyles';
import { namespacedTag } from '../tag';
import { WithInteractionContext } from './interactionContext';
import type { Point2D } from '@umetrics/sartorius-ui-plot-utils';
import { LinearScale } from '../data';

interface Rect {
    left: number;
    top: number;
    right: number;
    bottom: number;
}

const makeRect = (p0: Point2D, p1: Point2D): Rect => {
    const { x: x0, y: y0 } = p0;
    const { x: x1, y: y1 } = p1;

    const x = Math.min(x0, x1);
    const y = Math.min(y0, y1);

    const w = Math.abs(x1 - x0);
    const h = Math.abs(y1 - y0);

    return {
        left: x,
        top: y,
        right: x + w,
        bottom: y + h,
    };
};

const clampRect = (rect: Rect, bounds: Rect): Rect => {
    const left = Math.max(rect.left, bounds.left);
    const top = Math.max(rect.top, bounds.top);
    const right = Math.min(rect.right, bounds.right);
    const bottom = Math.min(rect.bottom, bounds.bottom);
    return {
        left,
        top,
        right,
        bottom,
    };
};

export interface ZoomProperties {
    onZoom?;
}

@defineElement({ tagName: namespacedTag('zoom'), styles: delaunayStyles, version })
export class Zoom extends WithInteractionContext(WithContext(BaseElement)) implements ZoomProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    onZoom?;

    _clicking = false;
    _initialPoint?: Point2D = undefined;
    _currentPoint?: Point2D = undefined;
    _currentRect?: Rect = undefined;
    scaleX = 'zoomX';
    scaleY = 'zoomY';

    constructor() {
        super();
        this.onMouseDown = this.onMouseDown.bind(this);
        this.onMouseMove = this.onMouseMove.bind(this);
        this.onMouseUp = this.onMouseUp.bind(this);
        this.sendZoomClear = this.sendZoomClear.bind(this);
    }

    disconnectedCallback(): void {
        super.disconnectedCallback();
        this.interactionTarget?.removeEventListener('mousedown', this.onMouseDown);
        this.interactionTarget?.removeEventListener('dblclick', this.sendZoomClear);
    }

    public outerEvent(e: MouseEvent) {
        if (e.type === 'mousedown') {
            this.onMouseDown(e);
        } else if (e.type === 'dblclick') {
            this.sendZoomClear();
        }
    }

    protected firstUpdated(_changedProperties: PropertyValueMap<any> | Map<PropertyKey, unknown>): void {
        this.interactionTarget?.addEventListener('mousedown', this.onMouseDown);
        this.interactionTarget?.addEventListener('dblclick', this.sendZoomClear);
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        return html`<div class="${rootClasses}">${this.renderRect()}</div> `;
    }

    private renderRect() {
        if (!this._currentRect || !this.interactionTarget) return null;

        const { left, top, right, bottom } = this._currentRect;
        return svg`
          <svg style="width: 100%; height: 100%">
            <rect stroke="black" fill="#888" opacity="0.2" x="${left}" y="${top}" width="${right - left}" height="${
                bottom - top
            }">
          </svg>
      `;
    }

    private onMouseDown(e) {
        if (!this.interactionTarget) return;
        e.preventDefault();

        this._clicking = true;
        const { left, top } = this.interactionTarget.getBoundingClientRect();

        this._initialPoint = { x: e.clientX - left, y: e.clientY - top };
        window.addEventListener('mousemove', this.onMouseMove);
        window.addEventListener('mouseup', this.onMouseUp);
    }
    private onMouseMove(e) {
        if (!this._clicking || !this.interactionTarget || !this._initialPoint) return;
        const r = this.interactionTarget.getBoundingClientRect();
        const bounds = {
            left: 0,
            top: 0,
            right: r.width,
            bottom: r.height,
        };
        this._currentPoint = { x: e.clientX - r.left, y: e.clientY - r.top };
        this._currentRect = clampRect(makeRect(this._initialPoint, this._currentPoint), bounds);
        this.requestUpdate();
    }
    private onMouseUp() {
        if (this._clicking && this._currentRect) {
            const [scaleX, scaleY] = this.getScales() as [LinearScale, LinearScale];

            const { left, right, top, bottom } = this._currentRect;
            const viewbox = {
                x: [scaleX.invert(left), scaleX.invert(right)],
                y: [scaleY.invert(bottom), scaleY.invert(top)],
            };
            if (viewbox.x[1] - viewbox.x[0] > 0 && viewbox.y[1] - viewbox.y[0] > 0) {
                const zoom = {
                    x: 1 / Math.abs(viewbox.x[1] - viewbox.x[0]),
                    y: 1 / Math.abs(viewbox.y[1] - viewbox.y[0]),
                };
                this.dispatchEvent(
                    new CustomEvent('zoom', {
                        detail: {
                            viewbox,
                            zoom,
                        },
                        bubbles: true,
                    })
                );
            }
        }

        this._clicking = false;
        this._initialPoint = undefined;
        this._currentPoint = undefined;
        this._currentRect = undefined;
        this.requestUpdate();
        window.removeEventListener('mousemove', this.onMouseMove);
        window.removeEventListener('mouseup', this.onMouseUp);
    }
    private sendZoomClear() {
        this.dispatchEvent(new CustomEvent('zoom-clear', { bubbles: true }));
    }
}
