import { css, CSSResultArray, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';
import { svgShapeStyles } from './svgShapeStyles';

export const svgWhiskersStyles: GenerateStyles = (tagName) => {
    const { child } = cssSelector(tagName);
    //
    return [
        svgShapeStyles(tagName),
        css`
            ${child('target')} {
                pointer-events: none;
            }

            ${child('hover-target')} {
                opacity: 0;
                pointer-events: painted;
                cursor: var(--plot-cursor, pointer);
            }

            ${child('hover-target')}:hover {
                opacity: initial;
            }
            .highlight {
                opacity: initial;
            }

            .interactable {
                pointer-events: painted;
            }

            ${child('target')}:has(+ ${child('hover-target')}:hover) {
                fill: var(--plot-hover-fill);
                fill-opacity: var(--plot-hover-opacity);

                stroke-width: var(--plot-hover-stroke-width);
                stroke: var(--plot-hover-stroke);
                stroke-opacity: var(--plot-hover-stroke-opacity);
                stroke-dasharray: var(--plot-hover-stroke-dasharray);
            }
        `,
    ] as CSSResultArray;
};
