    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                width: 100%;
                height: 100%;
                overflow: hidden;
                z-index: 9999;
            }

            ${root} {
                width: 100%;
                height: 100%;
            }

            ::slotted(*) {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }

            .container {
                position: relative;
            }

            .hover-cursor {
                pointer-events: none;
                position: absolute;
            }

            .tracker-item {
                cursor: grab;
                position: absolute;
                z-index: 11;
                height: 100%;
            }

            .data-point {
                position: absolute;
                left: -5px;
                bottom: -5px;
                z-index: 12;
                border-radius: 50%;
                transform: translate(var(--data-point-x, 0), var(--data-point-y, 0));
                width: 10px;
                height: 10px;
                background-color: var(--data-point-color, black);
                display: flex;
                opacity: 0.75;
            }

            .tooltip {
                position: absolute;
                font-family:
                    Roboto,
                    Arial,
                    Open Sans,
                    Helvetica,
                    sans-serif;

                left: 16px;
                transition: 0s left 0.25s ease;

                bottom: -12px;
                transform: translate(var(--data-point-x, 0), var(--data-point-y, 0));
                display: flex;
                background-color: #252525;
                color: white;
                align-items: center;
                justify-content: flex-start;
                width: 110px;
                z-index: 20;
                height: 24px;
            }

            .tooltip-flip {
                left: calc(-110px - 16px);
                justify-content: flex-end;
            }

            .tooltip-legend {
                width: 12px;
                height: 100%;
                background-color: var(--data-point-color, black);
                flex: none;
            }

            .tooltip-flip .tooltip-legend {
                order: 2;
            }

            .tooltip-data {
                display: flex;
                margin-left: 8px;
                margin-right: 8px;
                gap: 4px;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
            }

            .tooltip-value {
                font-size: 16px;
                font-weight: 600;
                flex: 1;
            }

            .tooltip-unit {
                font-size: 12px;
                font-weight: 400;
                line-height: 13.8px;
                flex: 1;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
            }

            .ruler-tracker-text {
                height: 20px;
                padding: 2px 8px;
                margin-top: auto;
                width: max-content;
            }

            .ruler-cursor-text {
                height: 20px;
                padding: 2px 8px;
                margin-top: auto;
                width: max-content;
            }
        `,
    ];
