import { css, cssSelector, cssVariableAsVar, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const zoomAndPanStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: flex;
                width: 100%;
                height: 100%;
                overflow: visible;
            }

            ${root} {
                width: 100%;
                height: 100%;
                overflow: visible;
            }

            ::slotted(*) {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                user-select: none;
            }

            .scroll-slider:hover,
            .scroll-slider-edge:hover {
                filter: brightness(50%);
            }

            :host(:hover) .horizontal-scroll,
            .horizontal-scroll.active :host(:hover) .vertical-scroll,
            .horizontal-scroll.active,
            .vertical-scroll.active,
            .scroll-active {
                opacity: 0.75 !important;
                transition: opacity 0.4s;
            }

            .horizontal-scroll,
            .vertical-scroll {
                opacity: 0;
                transition: opacity 0.4s;
            }

            .scroll-slider {
                fill: ${cssVariableAsVar(`color_secondary_4`)};
                cursor: all-scroll;
            }

            .scroll-slider-edge {
                fill: ${cssVariableAsVar(`color_secondary_4`)};
            }

            .scroll-slider-edge.left,
            ${child('cursor-overlay')}.left {
                cursor: ew-resize;
            }

            .scroll-slider-edge.right,
            ${child('cursor-overlay')}.right {
                cursor: ew-resize;
            }

            .scroll-slider-edge.up,
            ${child('cursor-overlay')}.up {
                cursor: ns-resize;
            }

            .scroll-slider-edge.down,
            ${child('cursor-overlay')}.down {
                cursor: ns-resize;
            }

            ${child('cursor-overlay')} {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                cursor: grab;
            }

            ${child('cursor-overlay')}:has(+ ${is('normal')}) {
                cursor: default !important;
            }

            ${child('cursor-overlay')}:has(+ ${is('grabbing')}) {
                cursor: grabbing !important;
            }

            ${child('cursor-overlay')}:has(+ ${is('dragging')}) {
                cursor: all-scroll !important;
            }
        `,
    ];
};
