import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    property,
    styleMap,
    nothing,
} from '@umetrics/sartorius-ui-base';
import { namespacedTag } from '../../tag';
import { nodeStyles } from './nodeStyles';
import { version } from '../../metadata';
import { WithContext } from '../../data/plotContext';
import { LinearScale } from '../../data';

export interface NodeProperties {
    x?: number;
    y?: number;
    dx?: number;
    dy?: number;
}

@defineElement({ tagName: namespacedTag('node'), styles: nodeStyles, version })
export class Node extends WithContext(BaseElement) implements NodeProperties {
    @property()
    x: number | undefined;

    @property()
    y: number | undefined;

    @property()
    dx: number | undefined;

    @property()
    dy: number | undefined;

    render() {
        const [rootClasses, baseClassName] = createClassName(this.tagName.toLowerCase(), {});

        if (!this.size || !this.getScales()) return nothing;
        const [scaleX, scaleY] = this.getScales() as [LinearScale, LinearScale];

        const hasXY = this.x != null && this.y != null;
        const hasDXDY = this.dx != null && this.dy != null;

        if (!hasXY && !hasDXDY) {
            console.warn(
                `<${namespacedTag('node')}> requires either (x,y) or (dx,dy) to be defined. Nothing will be rendered.`
            );
            return nothing;
        }

        const translateX = hasXY ? (this.x as number) : scaleX(this.dx as number);
        const translateY = hasXY ? (this.y as number) : scaleY(this.dy as number);

        const nodeStyleMap = {
            position: 'absolute',
            transform: `translate(${translateX}px, ${translateY}px)`,
        };

        return html`
            <div class="${rootClasses} ${baseClassName} node" style=${styleMap(nodeStyleMap)}>
                <slot></slot>
            </div>
        `;
    }
}
