const defaultSignificantDigits = 6;
const defaultSciFormatLowerLimitDigits = 4;
const defaultSciFormatUpperLimitDigits = 6;

export type ValueDigitsFormatter = (
    significantDigits?: number,
    sciFormatLowerLimitDigits?: number,
    sciFormaUpperLimitDigits?: number
) => (value: any) => string;

const getSignificantDigitCount = (value) => {
    value = value.toExponential();
    let res = 0;
    for (let i = 0, len = value.length; i < len; i++) {
        if (value[i] === 'e') break;
        if (+value[i] >= 0) res++;
    }
    return res;
};

const getValueWithAtMostSignificantDigits = (value: number, significantDigits: number) =>
    parseFloat(value.toPrecision(significantDigits)).toString();

const shouldUseScientificNotation = (sciFormatLowerLimitDigits, sciFormaUpperLimitDigits, value) => {
    const sciFormatUpperLimit = Math.pow(10, sciFormaUpperLimitDigits);
    const sciFormatLowerLimit = Math.pow(10, -sciFormatLowerLimitDigits);
    return Math.abs(value) <= sciFormatLowerLimit || Math.abs(value) >= sciFormatUpperLimit;
};

export const numberFormatter =
    (
        significantDigits = defaultSignificantDigits,
        sciFormatLowerLimitDigits = defaultSciFormatLowerLimitDigits,
        sciFormaUpperLimitDigits = defaultSciFormatUpperLimitDigits
    ) =>
    (value: any): string => {
        if (value === null || value === undefined || isNaN(value)) {
            return '';
        }
        if (value === 0) return '0';
        if (shouldUseScientificNotation(sciFormatLowerLimitDigits, sciFormaUpperLimitDigits, value)) {
            const currentSignificant = getSignificantDigitCount(value);
            if (currentSignificant <= significantDigits) {
                return value.toExponential(currentSignificant - 1);
            } else {
                return value.toExponential(significantDigits - 1);
            }
        }
        return getValueWithAtMostSignificantDigits(value, significantDigits);
    };

export const stringFormatter = (value: any): string => {
    if (value === null || value === undefined) {
        return '';
    }
    return String(value);
};

export const autoFormatter =
    (
        significantDigits = defaultSignificantDigits,
        sciFormatLowerLimitDigits = defaultSciFormatLowerLimitDigits,
        sciFormaUpperLimitDigits = defaultSciFormatUpperLimitDigits
    ) =>
    (value): string => {
        if (typeof value === 'number')
            return numberFormatter(significantDigits, sciFormatLowerLimitDigits, sciFormaUpperLimitDigits)(value);
        return stringFormatter(value);
    };

export const valueDigitsFormatter: ValueDigitsFormatter =
    (
        significantDigits = defaultSignificantDigits,
        sciFormatLowerLimitDigits = defaultSciFormatLowerLimitDigits,
        sciFormaUpperLimitDigits = defaultSciFormatUpperLimitDigits
    ) =>
    (value): string =>
        autoFormatter(significantDigits, sciFormatLowerLimitDigits, sciFormaUpperLimitDigits)(value);
