import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    nothing,
    property,
    type PropertyValueMap,
    type PropertyValues,
    state,
    styleMap,
} from '@umetrics/sartorius-ui-base';
import { timeTrackerCursorStyles as styles } from './timeTrackerCursorStyles';
import { namespacedTag } from '../tag';
import { WithContext } from '../data/plotContext';
import { Plottable } from '../plottable/plottableMixin';
import { WithInteractionContext } from '../interaction';
import { Domain, LinearScale } from '../data';
import { version } from '../metadata';
import { WithDataContext } from '../data/dataContext';
import { calculateMetadataYSnapshot, MetadataDetail, MetaDataYSliceInterpolated } from './metadataUtils';
import { emptyBox } from '../utils';
import { SVGShape } from '../plottable/svg/svgShape';

export interface OnTimeTrackerCursorChangeEvent {
    target: TimeTrackerCursor;
    mouse: [x: number, y: number];
    positionX: number | null;
}

export interface TimeTrackerMetadata extends MetadataDetail<number[], MetaDataYSliceInterpolated> {}

export interface TimeTrackerCursorConfig {
    showRulerText?: boolean;
    rulerTextFormatter?: (data) => string;
    showTooltips?: boolean;
    showDatapoints?: boolean;
    showTooltipLegend?: boolean;
    dataSerie?: {
        color?: string;
        unit?: string;
        dataFormatter?: (data) => string;
    }[];
}

export interface HTMLTimeTrackerCursorProperties {
    data?: number[];
    color?: string;
    width?: number;
    hidden?: boolean;
    disabled?: boolean;
    emitMetadata?: boolean;
    cursorPendingOpacity?: string;
    pixelProximityThreshold?: number;
    config?: TimeTrackerCursorConfig /* Enable & config internal visuals like label, datapoints and tooltips */;

    /**
     * Show the next Y value once the mouse position crosses the midpoint
     * between two consecutive X data points.
     */
    midpoint?: boolean;

    onChange: (e: CustomEvent<Domain>) => void;
    onCursorMove: (e: CustomEvent<{ mouseX: number; dataX: unknown }>) => void;
    onDragStart?: (e: CustomEvent<OnTimeTrackerCursorChangeEvent>) => void;
    onMetadata: (e: CustomEvent<TimeTrackerMetadata>) => void;
    onDrag?: (e: CustomEvent<OnTimeTrackerCursorChangeEvent>) => void;
    onEnd?: (e: CustomEvent<OnTimeTrackerCursorChangeEvent>) => void;
}

@defineElement({ tagName: namespacedTag('time-tracker-cursor'), styles, version })
export class TimeTrackerCursor
    extends Plottable(WithInteractionContext(WithDataContext(WithContext(BaseElement))))
    implements HTMLTimeTrackerCursorProperties
{
    @property({ type: String }) color = 'black';
    @property({ type: Number }) width = 2;
    @property({ type: Boolean }) hidden = false;
    @property({ type: Boolean }) disabled = false;
    @property({ type: Boolean }) emitMetadata = false;
    @property({ type: Number }) pixelProximityThreshold = 10;
    @property({ type: String }) filterDataByTag = 'SVG-LINE';
    @property({ type: String }) cursorPendingOpacity = '0.4';
    @property({ type: Boolean }) midpoint = false;

    @property({ type: Object }) config?: TimeTrackerCursorConfig;

    onChange;
    onCursorMove;
    onMetadata;
    onDragStart;
    onDrag;
    onEnd;

    @state() private dragState: number | null = 0;
    @state() private startDragPosition: number | null = null;
    @state() private _mouse: [number, number] = [0, 0];
    @state() private _currentX?: number;
    @state() private _metadata?: TimeTrackerMetadata;

    private _lastMouseMoveEvent: MouseEvent | null = null;
    private _animationFrameRequest: number | null = null;
    private _isRootElementInterationStarted = false;
    // during pan we should lock tracker placement
    private _lockPlacement = false;

    private _outMouseX;
    private _outMouseY;

    private get allFilteredData() {
        return (
            this.allDataElements
                ?.filter((el) => el.tagName.endsWith(this.filterDataByTag))
                .map((el) => {
                    return {
                        scales: (el as SVGShape).getScales() as [LinearScale, LinearScale],
                        data: el.plotData,
                    };
                }) ?? []
        );
    }

    private get isCreated() {
        return this.data.length === 1;
    }

    constructor() {
        super();
        this._handleMouseDown = this._handleMouseDown.bind(this);
        this._handleMouseMove = this._handleMouseMove.bind(this);
        this._handleMouseUp = this._handleMouseUp.bind(this);
        this._handlePlaceTracker = this._handlePlaceTracker.bind(this);
        this._handleMouseLeave = this._handleMouseLeave.bind(this);
        this._handleCursorMouseMove = this._handleCursorMouseMove.bind(this);
    }

    render() {
        if (!this.size || !this.getScales() || this.hidden) return nothing;

        const [width, height] = this.size;
        const [scaleX] = this.getScales() as [LinearScale, LinearScale];

        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        const bulletsHtml = this.config?.showDatapoints ? this._renderDatapoints() : nothing;
        const tooltipsHtml = this.config?.showTooltips ? this._renderTooltips() : nothing;
        return html` <div
            @mousedown=${this._handleRootMouseDown}
            @mouseup=${this._handleRootMouseUp}
            width=${width}
            height=${height}
            class="${rootClasses} container"
        >
            ${this._renderCursor(width, height)} ${this._renderTracker(baseClassName, scaleX, height)}
            ${bulletsHtml}${tooltipsHtml}
            <slot></slot>
        </div>`;
    }

    updated(changedProperties) {
        if (changedProperties.has('viewbox') && this.isCreated) {
            this._dispatchMetadata();
        }
    }

    disconnectedCallback() {
        this.interactionTarget?.removeEventListener('click', this._handlePlaceTracker);
        this.interactionTarget?.removeEventListener('mouseleave', this._handleMouseLeave);
        this.interactionTarget?.removeEventListener('mousemove', this._handleCursorMouseMove);
        super.disconnectedCallback();
    }

    protected firstUpdated(_changedProperties: PropertyValueMap<any> | Map<PropertyKey, unknown>): void {
        if (this.interactionTarget) {
            this.interactionTarget.addEventListener('click', this._handlePlaceTracker);
            this.interactionTarget.addEventListener('mouseleave', this._handleMouseLeave);
            this.interactionTarget.addEventListener('mousemove', this._handleCursorMouseMove);
        }
    }

    protected willUpdate(_changedProperties: PropertyValues<this>): void {
        super.willUpdate(_changedProperties);
        if (
            _changedProperties.has('allDataElements') ||
            _changedProperties.has('scaleMap') ||
            _changedProperties.has('data')
        ) {
            if (this.allDataElements && this.getScales() && this.viewbox && this.data && this.isCreated) {
                // When data changes externally or scale changes, re-dispatch metadata !IMPORTANT PART
                this._dispatchMetadata();
            }
        }
    }

    private _renderDatapoints() {
        if (this.data[0] == null) {
            return nothing;
        }

        const bullets = this._metadata?.ySlice?.positions?.map((pos, i) => {
            const { x, y } = pos;
            return html`
                <div
                    class="data-point"
                    style="--data-point-x: ${x}px;
                        --data-point-y: ${y}px;
                        --data-point-color: ${this.config?.dataSerie?.[i]?.color ?? 'black'};"
                ></div>
            `;
        });
        return html`${bullets}`;
    }

    private _renderTooltips() {
        if (this.data[0] == null) {
            return nothing;
        }

        const sortedPositions = this._getSortedPositions();
        let lastY = Number.POSITIVE_INFINITY;

        return sortedPositions.map(({ x, y, index }) => {
            const tooltipData = this._getTooltipData(index);
            if (!tooltipData) {
                return nothing;
            }

            const adjustedY = this._calculateAdjustedY(y!, lastY);
            lastY = adjustedY;

            return this._renderTooltipElement({
                x,
                y: adjustedY,
                ...tooltipData,
            });
        });
    }

    private _getSortedPositions() {
        return [...(this._metadata?.ySlice?.positions || [])]
            .map((pos, i) => ({ ...pos, index: i }))
            .sort((a, b) => a.y! - b.y!);
    }

    private _getTooltipData(index: number) {
        const value = this._metadata?.ySlice.exact[index];
        if (!value?.y) {
            return null;
        }

        const legend = this.config?.showTooltipLegend ? html` <div class="tooltip-legend"></div>` : nothing;
        const formatter = this.config?.dataSerie?.[index]?.dataFormatter || ((val: any) => val);
        const color = this.config?.dataSerie?.[index]?.color || 'black';
        const unit = this.config?.dataSerie?.[index]?.unit || '';

        return { value: formatter(value.y), legend, color, unit };
    }

    private _calculateAdjustedY(currentY: number, lastY: number) {
        const verticalSpacing = 26;
        return lastY === Number.POSITIVE_INFINITY || currentY > lastY + verticalSpacing
            ? currentY
            : lastY + verticalSpacing;
    }

    private _renderTooltipElement({ x, y, value, legend, color, unit }: any) {
        const flip = this.size && x ? x + 110 + 16 > this.size[0] : false;
        const left = flip ? 'calc(-110px - 16px)' : '16px';
        const classes = `tooltip${flip ? ' tooltip-flip' : ''}`;

        return html`
            <div
                class="${classes}"
                style="--data-point-x: ${x}px;
                   --data-point-y: ${y}px;
                   --data-point-color: ${color};
                   --tooltip-left: ${left};"
            >
                ${legend}
                <div class="tooltip-data">
                    <div class="tooltip-value">${value}</div>
                    <div class="tooltip-unit">${unit}</div>
                </div>
            </div>
        `;
    }

    private _renderCursor(width: number, height: number) {
        if (this.isCreated) return nothing;

        const [x] = this._mouse;
        const crosshairPath = `M${x} 0 V${height}`;
        const opacity = this.isCreated ? 1 : parseFloat(this.cursorPendingOpacity);

        const cursorHandlerStyleMap = styleMap({
            display: 'flex',
            flexDirection: 'column',
            opacity: String(opacity),
        });

        const slotStyleMapVars = styleMap({
            position: 'absolute',
            display: 'flex',
            flexDirection: 'column',
            height: `${height}px`,
            transform: `translateX(${x}px)`,
            width: `${width}px`,
            pointerEvents: 'none',
            opacity: String(opacity),
        });

        const rulerHtml = this.config?.showRulerText
            ? html` <div class="ruler-cursor-text">${this._renderRulerText(this._currentX)}</div> `
            : nothing;
        return html`
            <svg width=${width} height=${height} class="hover-cursor" style=${cursorHandlerStyleMap}>
                <path stroke=${this.color} stroke-width=${this.width} d=${crosshairPath} />
            </svg>
            <div style=${slotStyleMapVars}>
                ${rulerHtml}
                <slot name="time-tracker-cursor"></slot>
            </div>
        `;
    }

    private _renderRulerText = (text) => (text ? (this.config?.rulerTextFormatter?.(text) ?? text) : ''); // Assume being a function

    private _renderTracker(baseClassName: string, scaleX: LinearScale, containerHeight: number) {
        if (this.data[0] == null) return nothing;

        const xValue = this.data[0];
        const xPos = scaleX(xValue);
        const isCreated = this.isCreated;

        const trackerStyleMap = styleMap({
            transform: `translateX(${xPos - this.width / 2}px)`,
            width: `${this.width}px`,
            backgroundColor: this.color,
        });

        const slotStyleMapVars = styleMap({
            position: 'absolute',
            display: 'flex',
            flexDirection: 'column',
            height: `${containerHeight}px`,
            width: '0px',
            pointerEvents: 'none',
        });
        const rulerHtml = this.config?.showRulerText
            ? html` <div class="ruler-tracker-text">${this._renderRulerText(xValue)}</div> `
            : nothing;

        return html` <div
            class="tracker-item ${baseClassName}"
            style=${trackerStyleMap}
            @mousedown="${(e: MouseEvent) => this._handleMouseDown(e, xValue)}"
        >
            ${isCreated
                ? html` <div style=${slotStyleMapVars}>
                      ${rulerHtml}
                      <slot name="time-tracker-ruler"></slot>
                  </div>`
                : nothing}
        </div>`;
    }

    private _updateDataX(newValue: number) {
        this.data[0] = newValue;
        this._dispatchChange();
        this._dispatchMove();
        this._dispatchMetadata();
    }

    private _handleCursorMouseMove(e: MouseEvent) {
        if (!this.interactionTarget || !this.getScales()) return;

        const rect = this.interactionTarget.getBoundingClientRect() ?? emptyBox;
        this._mouse = [e.clientX - rect.left, 0];
        // Cursor moves do not necessarily mean data changes, so just dispatch move
        this._dispatchMove();
    }

    private _handleRootMouseDown(event: MouseEvent) {
        if (!this.isCreated) {
            return;
        }

        const path = event.composedPath();
        const specificElement = path.find((el) => (el as HTMLElement)?.classList?.contains('tracker-item'));

        if (specificElement == null) {
            this._isRootElementInterationStarted = true;
            this._outMouseX = event.clientX;
            this._outMouseY = event.clientY;
            return;
        }

        this._lockPlacement = false;
        this._isRootElementInterationStarted = false;
    }

    private _handleRootMouseUp(event: MouseEvent) {
        if (!this.isCreated || !this._isRootElementInterationStarted) {
            return;
        }

        if (this._outMouseX !== event.clientX || this._outMouseY !== event.clientY) {
            this._lockPlacement = true;
            return;
        }

        this._placeTracker(event);
        this._lockPlacement = false;
        this._isRootElementInterationStarted = false;
    }

    private _placeTracker(event: MouseEvent) {
        if (!this.interactionTarget || this.disabled) return;

        const rect = this.interactionTarget.getBoundingClientRect();
        const [scaleX, scaleY] = (this.getScales() ?? []) as [LinearScale, LinearScale];
        if (!scaleX || !scaleY) return;

        const newValue = scaleX.invert(event.clientX - rect.left);
        this.startDragPosition = newValue;
        this.dragState = newValue;
        this._updateDataX(newValue);
    }

    private _handlePlaceTracker(event: MouseEvent) {
        if (this._lockPlacement) {
            return;
        }
        this._placeTracker(event);
    }

    private _handleMouseDown(event: MouseEvent, posX: number) {
        event.stopPropagation();

        if (this.disabled) return;

        const [scaleX, scaleY] = (this.getScales() ?? []) as [LinearScale, LinearScale];
        if (!scaleX || !scaleY) return;

        this.dragState = posX;
        this.startDragPosition = scaleX.invert(event.clientX);

        this._dispatchDrag('drag-start');
        this.interactionTarget?.addEventListener('mousemove', this._handleMouseMove);
        this.interactionTarget?.addEventListener('mouseup', this._handleMouseUp, { once: true });
    }

    private _handleMouseMove(event: MouseEvent) {
        event.stopPropagation();
        this._lastMouseMoveEvent = event;
        this._scheduleMouseMoveUpdate();
    }

    private _handleMouseMoveDeferred(event: MouseEvent) {
        if (this.dragState == null || this.startDragPosition == null || !this.interactionTarget) return;
        const [scaleX] = this.getScales() as [LinearScale, LinearScale];
        if (!scaleX) return;

        const invertedX = scaleX.invert(event.clientX);
        const dx = invertedX - this.startDragPosition;
        const newValue = this.dragState + dx;

        this.dragState = newValue;
        this.startDragPosition = invertedX;

        this._updateDataX(newValue);

        this._dispatchDrag('drag');
    }

    private _handleMouseUp() {
        this._dispatchDrag('drag-end');
        this.dragState = null;
        this.startDragPosition = null;

        this.interactionTarget?.removeEventListener('mousemove', this._handleMouseMove);
        this.interactionTarget?.removeEventListener('mouseup', this._handleMouseUp);
    }

    private _handleMouseLeave() {
        this._dispatchDrag('drag-end');
        this.interactionTarget?.removeEventListener('mousemove', this._handleMouseMove);
        this.interactionTarget?.removeEventListener('mouseup', this._handleMouseUp);

        this._dispatchMetadata();
    }

    private _dispatchDrag(eventName: 'drag-start' | 'drag' | 'drag-end') {
        this.dispatchEvent(
            new CustomEvent<OnTimeTrackerCursorChangeEvent>(eventName, {
                bubbles: true,
                detail: {
                    target: this,
                    mouse: this._mouse,
                    positionX: this.dragState,
                },
            })
        );
    }

    private _dispatchMove() {
        const scales = this.getScales() as [LinearScale, LinearScale];
        if (!scales) return;
        const [scaleX] = scales;
        this._currentX = scaleX.invert(this._mouse[0]);

        this.dispatchEvent(
            new CustomEvent('cursor-move', {
                detail: {
                    mouseX: this._mouse[0],
                    dataX: this._currentX,
                },
            })
        );
    }

    private _dispatchChange() {
        this.dispatchEvent(new CustomEvent('change', { detail: this.data }));
        this.requestUpdate('data');
    }

    private _dispatchMetadata() {
        if (!this.getScales() || !this.interactionTarget) return;

        const rect = this.interactionTarget.getBoundingClientRect() ?? emptyBox;
        const height = rect.height;

        const [scaleX] = this.getScales() as [LinearScale, LinearScale];
        if (!scaleX) return;

        const currentValue = this.data[0] ?? 0;
        const { interpolated, exact } = calculateMetadataYSnapshot({
            xPosition: scaleX(currentValue),
            pixelProximityThreshold: this.pixelProximityThreshold,
            plotData: this.allFilteredData,
            midpoint: this.midpoint,
        });

        const positions = interpolated.map((d) => {
            const [_scaleX, _scaleY] = d.scales;
            return {
                x: d.x != null ? _scaleX(d.x as number) : null,
                y: d.y != null ? _scaleY(d.y as number) - height : null,
            };
        });

        this._metadata = {
            positionX: scaleX(currentValue),
            dataX: currentValue,
            ySlice: { positions, exact, interpolated },
        };

        this.dispatchEvent(
            new CustomEvent('metadata', {
                detail: this._metadata,
            })
        );
    }

    private _scheduleMouseMoveUpdate() {
        if (this._animationFrameRequest) return;

        this._animationFrameRequest = requestAnimationFrame(() => {
            if (this._lastMouseMoveEvent) {
                this._handleMouseMoveDeferred(this._lastMouseMoveEvent);
                this._lastMouseMoveEvent = null;
            }
            this._animationFrameRequest = null;
        });
    }
}
