import { GeneratorType, LabelGenerator, PlotLabelData, RawLabelPoint } from './labelGenerators';
import { Point, SVGBar } from '../../plottable';
import { LabelSettings } from './labels';
import { QuadTree } from '@umetrics/sartorius-ui-plot-utils';

export class BarLabelGenerator implements LabelGenerator<SVGBar, { points: Point[]; vertical: boolean }> {
    generatorType = GeneratorType.BAR;

    calculateLabels(
        bar: SVGBar,
        _labelSettings: LabelSettings,
        _accumulated: QuadTree,
        transform:
            | ((data: { points: Point[]; vertical: boolean }) => { points: Point[]; vertical: boolean })
            | undefined
    ): PlotLabelData {
        const transformed = transform && transform({ points: bar.data, vertical: bar.vertical });
        const candidatePoints = transformed?.points || bar.data;
        const scales = bar.getScales();
        if (!scales) {
            return { items: [], plotSurface: [] };
        }
        const [scaleX, scaleY] = scales;
        const domainX = scaleX.domain();
        const domainY = scaleY.domain();
        const numericX = typeof domainX[0] === 'number';
        const numericY = typeof domainY[0] === 'number';
        const invertedY = numericY && (domainY[0] as number) > (domainY[1] as number);

        const labelPoints: RawLabelPoint[] =
            candidatePoints
                ?.flatMap((point: Point, idx: number) => {
                    const id = `bar-plot-label-${bar.id}-${idx}`;
                    const x0 = point.x;
                    const userData = (point as Point & { userData?: any }).userData;
                    if (point.x1 || point.y1) {
                        const x1 = point.x1 || (numericX && bar.vertical ? 0 : x0);
                        const minX = Math.min(x0, x1);
                        const maxX = Math.max(x0, x1);
                        const midX = (x0 + x1) / 2;
                        const y0 = point.y;
                        const y1 = point.y1 || (numericY && !bar.vertical ? 0 : y0);
                        const minY = Math.min(y0, y1);
                        const maxY = Math.max(y0, y1);
                        const midY = (y0 + y1) / 2;
                        const low = bar.vertical
                            ? {
                                  point: { x: minX, y: midY },
                                  originalPoint: point,
                                  id,
                                  userData,
                                  pointIdx: idx,
                                  seriesIdx: 0,
                                  forceAlignment: 'left',
                                  angle: Math.PI / 2,
                              }
                            : {
                                  point: { x: midX, y: invertedY ? maxY : minY },
                                  originalPoint: point,
                                  id,
                                  userData,
                                  pointIdx: idx,
                                  seriesIdx: 0,
                                  forceAlignment: 'bottom',
                                  angle: 0,
                              };
                        const high = bar.vertical
                            ? {
                                  point: { x: maxX, y: midY },
                                  originalPoint: point,
                                  id,
                                  userData,
                                  pointIdx: idx,
                                  seriesIdx: 0,
                                  forceAlignment: 'right',
                                  angle: Math.PI / 2,
                              }
                            : {
                                  point: { x: midX, y: invertedY ? minY : maxY },
                                  originalPoint: point,
                                  id,
                                  userData,
                                  pointIdx: idx,
                                  seriesIdx: 0,
                                  forceAlignment: 'top',
                                  angle: 0,
                              };
                        return [low, high];
                    }

                    return bar.vertical
                        ? {
                              point,
                              originalPoint: point,
                              userData,
                              id,
                              pointIdx: idx,
                              seriesIdx: 0,
                              forceAlignment: point.x > 0 ? 'right' : 'left',
                              angle: Math.PI / 2,
                          }
                        : invertedY
                          ? {
                                point,
                                originalPoint: point,
                                userData,
                                id,
                                pointIdx: idx,
                                seriesIdx: 0,
                                forceAlignment: point.y > 0 ? 'bottom' : 'top',
                                angle: 0,
                            }
                          : {
                                point,
                                originalPoint: point,
                                userData,
                                id,
                                pointIdx: idx,
                                seriesIdx: 0,
                                forceAlignment: point.y > 0 ? 'top' : 'bottom',
                                angle: 0,
                            };
                })
                .map((p) => ({ ...p, value: bar.vertical ? p.point.x : p.point.y })) || [];

        return { items: labelPoints, plotSurface: [] };
    }

    extremes(data: { points: Point[]; vertical: boolean }): { points: Point[]; vertical: boolean } {
        if ((data?.points?.length || 0) < 2) {
            return data;
        }
        const points: any = data.points.reduce((current: Point[], next: Point) => {
            if (!current.length) {
                return [next, next];
            }
            if (current[0].y > next.y) {
                return [next, current[1]];
            }
            if (current[1].y < next.y) {
                return [current[0], next];
            }
            return current;
        }, []);

        return { points, vertical: data.vertical };
    }
}
