import { betweenOrAlmostEqual, DEFAULT_EPSILON } from './tolerant';
import { Point2D } from './data';

export interface AABB {
    // axis aligned boundary box
    minX: number;
    maxX: number;
    minY: number;
    maxY: number;
}

export const intersects = (a: AABB, b: AABB, epsilon = DEFAULT_EPSILON) =>
    (betweenOrAlmostEqual(b.minX, a.minX, a.maxX, epsilon) || betweenOrAlmostEqual(a.minX, b.minX, b.maxX, epsilon)) &&
    (betweenOrAlmostEqual(b.minY, a.minY, a.maxY, epsilon) || betweenOrAlmostEqual(a.minY, b.minY, b.maxY, epsilon));

export interface RectangleBounds {
    topLeft: Point2D;
    bottomRight: Point2D;
}

export interface RectangleCorners {
    topLeft: Point2D;
    topRight: Point2D;
    bottomRight: Point2D;
    bottomLeft: Point2D;
}

export interface RectangleData {
    x: number;
    y: number;
    width: number;
    height: number;
}

export const pointDistance = (p1: Point2D, p2: Point2D) => {
    return Math.sqrt(Math.pow(p1.x - p2.x, 2) + Math.pow(p1.y - p2.y, 2));
};

export const getEdgeNormal = (p1: Point2D, p2: Point2D) => {
    return { x: p1.y - p2.y, y: p2.x - p1.x }; // Perpendicular vector
};

export const isRangesOverlapping = (range1: { min: number; max: number }, range2: { min: number; max: number }) => {
    const epsilon = 1e-6 * Math.max(Math.abs(range1.max), Math.abs(range2.max));
    // Check if the projections overlap on the axis
    return range1.max >= range2.min - epsilon && range2.max >= range1.min - epsilon;
};

export const dotProduct = (point: Point2D, axis: { x: number; y: number }) => {
    return point.x * axis.x + point.y * axis.y;
};

export const projectRectangle = (r: RectangleCorners, normal: { x: number; y: number }) => {
    // Project all corners of the rectangle onto the axis
    const coords = [r.topLeft, r.topRight, r.bottomRight, r.bottomLeft];
    let min = dotProduct(coords[0], normal);
    let max = min;

    for (let i = 1; i < 4; i++) {
        const projection = dotProduct(coords[i], normal);
        if (projection < min) min = projection;
        if (projection > max) max = projection;
    }

    return { min, max };
};

// check using SAT (Separating Axes Theorem), this is generalizable to convex polygons
export const rectanglesOverlap = (r1: RectangleCorners, r2: RectangleCorners): boolean => {
    const axes = [
        getEdgeNormal(r1.topLeft, r1.topRight),
        getEdgeNormal(r1.topRight, r1.bottomRight),
        getEdgeNormal(r2.topLeft, r2.topRight),
        getEdgeNormal(r2.topRight, r2.bottomRight),
    ];

    return !axes.some((normal) => {
        const projection1 = projectRectangle(r1, normal);
        const projection2 = projectRectangle(r2, normal);

        return !isRangesOverlapping(projection1, projection2);
    });
};

const isInside = (p: Point2D, bounds: RectangleBounds) => {
    return (
        bounds.topLeft.x <= p.x && bounds.topLeft.y <= p.y && bounds.bottomRight.x >= p.x && bounds.bottomRight.y >= p.y
    );
};

export const isCompletelyInside = (candidate: RectangleCorners, domainBounds: RectangleBounds) => {
    return (
        isInside(candidate.topLeft, domainBounds) &&
        isInside(candidate.topRight, domainBounds) &&
        isInside(candidate.bottomRight, domainBounds) &&
        isInside(candidate.bottomLeft, domainBounds)
    );
};
