import { cssVariables, svg } from '@umetrics/sartorius-ui-base';

export const renderWatermarkSVG = (text: string) => {
    return svg`
      <text
          font-size='8px'
          fill=${cssVariables.color_secondary_4}
          text-anchor='start'
          dominant-baseline="hanging"
      >
          ${text}
      </text>
  `;
};
