import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const svgStyles: GenerateStyles = (tagName) => {
    const { root, is } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
                pointer-events: none;
                transition:
                    fill 0s,
                    stroke-width 0.2s,
                    opacity 0.2s,
                    r 0.2s;
            }

            :host {
                display: block;
                width: 100%;
                height: 100%;
            }

            ${root} {
                width: 100%;
                height: 100%;
                overflow: hidden;
            }

            ${is('dashed')} .line {
                stroke-dasharray: 3 3;
            }

            svg {
                display: block;
                width: 100%;
                height: 100%;
            }

            ${is('overflow')} {
                overflow: visible;
            }

            .hover-target {
                opacity: 0;
                pointer-events: initial;
                cursor: default;
            }
            .hover-target:hover {
                opacity: initial;
            }
        `,
    ];
};
