import { BaseElement, property, PropertyValues, state } from '@umetrics/sartorius-ui-base';
import { Domain2D } from '../data/domain';
import { WithContextInterface } from '../data/plotContext';
import { Constructor } from '../data/types';
import { anyChanged } from '../litUtils';
import { PlottableInterface } from './plottableMixin';
import type { Point2D } from '@umetrics/sartorius-ui-plot-utils';

export interface FilterProperties {
    filter?: (_data: Point2D[] | Point2D[][], _viewbox: Domain2D) => Point2D[] | Point2D[][];
}

export declare abstract class WithFilterInterface extends BaseElement {
    filteredData: Point2D[] | Point2D[][];
    getFilteredData(): Point2D[] | Point2D[][];
}

type FilterRequirements = BaseElement & WithContextInterface & PlottableInterface;

export const WithFilter = <T extends Constructor<FilterRequirements>>(superClass: T) => {
    abstract class WithFilterElement extends superClass {
        //TODO: Remove this state?
        @state()
        filteredData;
        @property()
        filter = (_data: Point2D[] | Point2D[][], _viewbox?: Domain2D) => _data;

        public getFilteredData() {
            if (this.plotData && this.plotData.length > 0 && Array.isArray(this.plotData[0])) {
                const v: any[] = [];
                this.plotData.forEach((p) => {
                    if (Array.isArray(p)) {
                        v.push(this.filter(p, this.viewbox));
                    }
                });
                return v;
            }
            return this.filter(this.plotData, this.viewbox);
        }

        protected willUpdate(props: PropertyValues<this>): void {
            super.willUpdate(props);
            if (anyChanged(props, ['plotData', 'viewbox', 'filter'])) {
                if (this.plotData && this.viewbox) {
                    this.filteredData = this.getFilteredData();
                }
            }
        }
    }
    return WithFilterElement as Constructor<WithFilterInterface> & T;
};
