import { css, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const legacyTooltipStyles: GenerateStyles = (tagName) => {
    const { root, is } = cssSelector(tagName);

    const white = cssVariableAsVar('color_primary_2');
    const gray = cssVariableAsVar('color_secondary_2');

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
                pointer-events: none;
            }

            :host {
                display: contents;
            }

            ${root} {
                position: absolute;
            }

            ${root} {
                pointer-events: none;
                box-sizing: border-box;
                position: absolute;
                top: var(--plot-tooltip-y);
                left: var(--plot-tooltip-x);
                transform: translate(5px, -50%);
                border: 1px solid ${gray};
                z-index: 1000;
            }

            ${is('left')} {
                pointer-events: none;
                box-sizing: border-box;
                position: absolute;
                top: var(--plot-tooltip-y);
                left: var(--plot-tooltip-x);
                transform: translate(calc(-100% - 5px), -50%);
                border: 1px solid ${gray};
                z-index: 1000;
            }

            .tooltip-content {
                background: ${white};
                z-index: 1;
                padding: 4px;
            }
            .tooltip-content,
            .tooltip-content p {
                text-align: start;
                margin: 0;
                white-space: nowrap;
                font-size: 12px;
            }

            .tooltip-target {
                position: absolute;
                display: initial;
                top: var(--plot-tooltip-y);
                left: var(--plot-tooltip-x);
                pointer-events: none;
            }

            .tooltip-arrow {
                border: 1px solid ${gray};
                width: 8px;
                height: 8px;
                background: ${white};
                top: 50%;
                left: -8px;
                transform: translate3d(4px, -50%, -1px) rotate(45deg);
                position: absolute;
                z-index: -1;
            }

            ${is('left')} .tooltip-arrow {
                border: 1px solid ${gray};
                width: 8px;
                height: 8px;
                background: ${white};
                top: 50%;
                right: 0px;
                left: auto;
                transform: translate3d(4px, -50%, -1px) rotate(45deg);
                position: absolute;
                z-index: -1;
            }
        `,
    ];
};
