import { BaseElement, booleanConverter, property, query } from '@umetrics/sartorius-ui-base';
import { Plottable, PlottableProperties } from '../plottableMixin';
import { WithContext } from '../../data/plotContext';
import { Datum } from '@umetrics/sartorius-ui-plot-utils';
import { varToStyle } from '../../litUtils';
import { functionChangeDetection } from '../dataChangeDetection';
import { SceneGroup } from '../../rendering/types';

export type ShapeStyles =
    | 'fillColor'
    | 'fillOpacity'
    | 'strokeColor'
    | 'strokeWidth'
    | 'strokeDashArray'
    | 'strokeLinecap'
    | 'strokeOpacity';

export interface ShapeStyle {
    fillColor?: string;
    fillOpacity?: number;
    filter?: string;

    strokeColor?: string;
    strokeWidth?: number;
    strokeDashArray?: string;
    strokeLinecap?: string;
    strokeOpacity?: number;
}

export interface ShapeStyleArgs {
    x?: Datum;
    y?: Datum;
    index: number;
    subIndex?: number;
}

export type ShapeStyleFunctionType = (args: ShapeStyleArgs) => ShapeStyle;

export interface ShapeAdjustArgs {
    v: number;
    v1?: number;
    bandwidth: number;
    index: number;
}

export type ShapeAdjustFunctionType = (args: ShapeAdjustArgs) => [number, number];

export type IndexToBoolFunctionType =
    | boolean
    | number
    | number[]
    | undefined
    | ((index: number, subIndex?: number) => boolean);

export interface SVGShapeProperties extends PlottableProperties {
    mode?: 'svg' | 'canvas' | 'webgl' /* Render engine, svg & canvas full support. webgl ongoing... */;
    clipping?: boolean;
    simplify?: boolean;

    defaultStyle?: ShapeStyle | ShapeStyleFunctionType /* Apply style for normal states */;
    hoverStyle?: ShapeStyle | ShapeStyleFunctionType /* Apply style for hover state */;

    xAdjust?: ShapeAdjustFunctionType /* Adjust x value */;
    yAdjust?: ShapeAdjustFunctionType /* Adjust x value */;

    hide?: IndexToBoolFunctionType;
    hideLabel?: IndexToBoolFunctionType;
    highlight?: IndexToBoolFunctionType;

    interactable?: IndexToBoolFunctionType;

    metadata?: any /* Event pass back */;
}

export abstract class SVGShape extends Plottable(WithContext(BaseElement)) implements SVGShapeProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    @property({ type: String, reflect: false })
    mode: 'svg' | 'canvas' | 'webgl' = 'svg';
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    clipping = true;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    simplify = false;

    @property({
        type: Function,
        reflect: false,
        attribute: false,
        hasChanged: functionChangeDetection<ShapeStyle | ShapeStyleFunctionType | undefined>,
    })
    defaultStyle?: ShapeStyle | ShapeStyleFunctionType;
    @property({
        type: Function,
        reflect: false,
        attribute: false,
        hasChanged: functionChangeDetection<ShapeStyle | ShapeStyleFunctionType | undefined>,
    })
    hoverStyle?: ShapeStyle | ShapeStyleFunctionType;

    @property({
        type: Function,
        reflect: false,
        attribute: false,
        hasChanged: functionChangeDetection<ShapeAdjustFunctionType | undefined>,
    })
    xAdjust?: ShapeAdjustFunctionType;
    @property({
        type: Function,
        reflect: false,
        attribute: false,
        hasChanged: functionChangeDetection<ShapeAdjustFunctionType | undefined>,
    })
    yAdjust?: ShapeAdjustFunctionType;

    @property({
        type: Object,
        reflect: false,
        converter: booleanConverter,
        hasChanged: functionChangeDetection<IndexToBoolFunctionType | undefined>,
    })
    interactable: IndexToBoolFunctionType = true;

    @property({
        type: Object,
        reflect: false,
        hasChanged: functionChangeDetection<IndexToBoolFunctionType | undefined>,
    })
    hide?: IndexToBoolFunctionType = false;
    @property({
        type: Object,
        reflect: false,
        hasChanged: functionChangeDetection<IndexToBoolFunctionType | undefined>,
    })
    highlight?: IndexToBoolFunctionType = false;
    @property({
        type: Object,
        reflect: false,
        hasChanged: functionChangeDetection<IndexToBoolFunctionType | undefined>,
    })
    hideLabel?: IndexToBoolFunctionType = false;

    @property({ type: Object, reflect: false, attribute: false })
    metadata?;

    @query('canvas')
    protected _canvasRef?: HTMLCanvasElement;
    protected _dpi; // devicePixelRatio

    public sceneGroup(): SceneGroup | Promise<SceneGroup> | undefined {
        return undefined;
    }

    public abstract exportSVG();

    protected async updated(_changedProperties) {
        super.updated(_changedProperties);

        const { mode } = this;

        if (this._propsImpactCanvasRendering(_changedProperties)) {
            if (mode === 'canvas' || mode === 'webgl') {
                await this.updateComplete;
                this.dispatchEvent(new CustomEvent('request-rerender', { bubbles: true, detail: { target: this } }));
            }
        }
    }

    protected _propsImpactCanvasRendering = (_changedProperties) =>
        _changedProperties.has('viewbox') ||
        _changedProperties.has('size') ||
        _changedProperties.has('data') ||
        _changedProperties.has('mode') ||
        _changedProperties.has('scaleMap');

    protected _isVisibilityHidden = (index, subIndex = -1) => this._varFunc(this.hide, index, subIndex);
    protected _isLabelHidden = (index, subIndex = -1) => this._varFunc(this.hideLabel, index, subIndex);
    protected _isHighlight = (index, subIndex = -1) => this._varFunc(this.highlight, index, subIndex);
    protected _isInteractable = (index, subIndex = -1) => this._varFunc(this.interactable, index, subIndex);

    protected _isFunc = (f) => !!f && 'function' === typeof f;
    protected _getDefaultStyle = (args: ShapeStyleArgs, def: ShapeStyle = {}): ShapeStyle => ({
        ...def,
        ...this._style(this.defaultStyle ?? {}, args),
    });
    protected _getHoverStyle = (args: ShapeStyleArgs): ShapeStyle => this._style(this.hoverStyle ?? {}, args);
    protected _getStyle = (obj: ShapeStyle | undefined, v: ShapeStyles, def: string | number): string | number =>
        obj && obj[v] ? obj[v]! : def;

    protected _varFunc = (prop: IndexToBoolFunctionType, index = -1, subIndex = -1) => {
        if (typeof prop === 'boolean') {
            return prop;
        } else if (typeof prop === 'number') {
            return prop === index;
        } else if (typeof prop === 'function') {
            return (prop as (i: number, j: number) => boolean)(index, subIndex) ?? false;
        } else if (Array.isArray(prop) && prop.length > 0) {
            return prop.indexOf(index) > -1;
        }
        return false;
    };

    protected _generateStyle(
        args: ShapeStyleArgs,
        { fillColor, fillOpacity, strokeColor, strokeWidth, strokeOpacity, strokeDashArray },
        extra = ''
    ) {
        const hoverStyle = this._getHoverStyle(args);

        return `${varToStyle({
            '--plot-hover-fill': this._getStyle(hoverStyle, 'fillColor', fillColor),
            '--plot-hover-opacity': this._getStyle(hoverStyle, 'fillOpacity', fillOpacity),
            '--plot-hover-stroke': this._getStyle(hoverStyle, 'strokeColor', strokeColor),
            '--plot-hover-stroke-width': this._getStyle(hoverStyle, 'strokeWidth', strokeWidth),
            '--plot-hover-stroke-opacity': this._getStyle(hoverStyle, 'strokeOpacity', strokeOpacity),
            '--plot-hover-stroke-dasharray': this._getStyle(hoverStyle, 'strokeDashArray', strokeDashArray),
        })}${extra}`;
    }

    /**
     * Handler for slot-change at slot name="defs". Moves defs
     * to : <defs id="custom-defs"></defs>.
     * Install using : <slot name="defs" @slotchange=${this._defsSlotchange}></slot>
     *
     * @param e
     * @protected
     */
    protected _defsSlotchange(e) {
        const slot = e.target;
        const assignedNodes = slot.assignedNodes();
        if (assignedNodes.length > 0) {
            const passedDefs = assignedNodes[0];
            const customDefsChildren = passedDefs.children;
            const shadowDefs = this.shadowRoot!.querySelector('#custom-defs') as Element;
            Array.from(customDefsChildren).forEach((child) => {
                shadowDefs.appendChild((child as Element).cloneNode(true));
            });
        }
    }

    protected abstract _invalidateCanvas(ctx: CanvasRenderingContext2D);

    protected abstract _invalidateGl(gl: WebGLRenderingContext);

    private _style = (p: ShapeStyleFunctionType | ShapeStyle, args: ShapeStyleArgs): ShapeStyle =>
        Object.fromEntries(
            Object.entries(
                this._isFunc(p) ? ((p as ShapeStyleFunctionType)(args) ?? {}) : ((p as ShapeStyle) ?? {})
            ).filter(([_, v]) => v !== null && v !== undefined && v !== '')
        );
}
