import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    svg,
} from '@umetrics/sartorius-ui-base';
import { version } from '../../metadata';
import { PlottableProperties } from '../plottableMixin';
import { WithContext } from '../../data/plotContext';
import { namespacedTag } from '../../tag';
import { svgStyles } from './svgStyles';
import { FilterProperties } from '../filterMixin';
import { Datum, Point2D, roundToPrecision2 } from '@umetrics/sartorius-ui-plot-utils';

export type AreaDirection = 'below' | 'above';

export interface SVGCrosshairProperties extends PlottableProperties, FilterProperties {
    position?: Point2D;
}

@defineElement({ tagName: namespacedTag('svg-crosshair'), styles: svgStyles, version })
export class SVGCrosshair extends WithContext(BaseElement) implements SVGCrosshairProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    @property()
    position?: Point2D = undefined;
    @property({ type: Boolean, converter: booleanConverter })
    full? = false;

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        if (!this.size || !this.getScales() || !this.position) return;
        const [width, height] = this.size;
        const [scaleX, scaleY] = this.getScales();
        const sX = (d: Datum) => roundToPrecision2(scaleX(d as number | Date) as number);
        const sY = (d: Datum) => roundToPrecision2(scaleY(d as number | Date) as number);

        const x = sX(this.position.x);
        const y = sY(this.position.y);

        const upperRight = this.full ? this._renderPath(`M ${x} 0 V ${y} H ${width}`) : null;
        return html`
            <svg width=${width} height=${height} class="${rootClasses}">
                ${upperRight} ${this._renderPath(`M 0 ${y} H ${x} V ${height}`)}
            </svg>
        `;
    }

    private _renderPath = (d) => svg`
                    <path
                    d="${d}"
                    fill="none"
                    stroke="var(--plot-stroke, red)"
                    stroke-dasharray="var(--plot-stroke-dasharray, 3 3)"
                />
    `;
}
