export const ResolutionOptions = ['year', 'month', 'day', 'minute', 'second', 'millisecond'] as const;
export const DateResolutionOptions = ResolutionOptions.slice(0, 3);
export const TimeResolutionOptions = ResolutionOptions.slice(3);
export type DateTimeResolution = (typeof ResolutionOptions)[number];

//Offsets in regard to sv-SE locale, e.g. 2023-10-05 14:48:00 GMT+02:00
const offsets = {
    year: 4,
    month: 7,
    day: 10,
    minute: 16,
    second: 19,
    timezone: 19,
    timezoneEnd: 26,
};

export interface DateFormatOptions {
    resolution?: DateTimeResolution;
    timeZone?: Intl.DateTimeFormatOptions['timeZone'];
    showTimezoneOffset?: boolean;
}

export const isValidDate = (d: unknown) => d instanceof Date && !isNaN(d.valueOf());

const makeDateIfNeeded = (date: Date | string | number): Date => {
    if (date === null || date === undefined) {
        return new Date(NaN);
    }
    if (date instanceof Date) {
        return date;
    }
    if (typeof date === 'number') {
        return new Date(date);
    }
    return new Date(date);
};

//Makes a formatter that outputs datetime strings conforming to ISO 8601.
export const dateFormatter = (options?: DateFormatOptions) => {
    const resolution = options?.resolution || 'minute';
    const showTimezoneOffset = TimeResolutionOptions.includes(resolution) && options?.showTimezoneOffset;
    const fmt = new Intl.DateTimeFormat('sv-SE', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit',
        hour12: false,
        // fractionalSecondDigits: 3, // <--- Needs update to tsconfig target?
        timeZone: options?.timeZone,
        timeZoneName: showTimezoneOffset ? 'longOffset' : undefined,
    });
    const formatDate = (value: Date | string | number) => {
        const date = makeDateIfNeeded(value);
        if (!isValidDate(date)) {
            return '';
        }
        const fullStr = fmt.format(date);
        const datetimeStr = fullStr.substring(0, offsets[resolution]);
        const offsetStr = fullStr.substring(offsets.timezone, offsets.timezoneEnd).replace('GMT', 'UTC');
        return `${datetimeStr}${offsetStr}`;
    };
    return formatDate;
};
