import {
    booleanConverter,
    createClassName,
    defineElement,
    html,
    ifDefined,
    property,
    svg,
} from '@umetrics/sartorius-ui-base';
import { version } from '../../metadata';
import { namespacedTag } from '../../tag';
import { SVGShape, SVGShapeProperties } from './svgShape';
import { svgMultiPolygonStyles } from './svgMultiPolygonStyles';
import { findDataPosition } from './lineUtils';
import { roundToPrecision2 } from '@umetrics/sartorius-ui-plot-utils';

export interface MultiPolygonDataType {
    value: number;
    coordinates: number[][][][];
}

export interface SVGMultiPolygonProperties extends SVGShapeProperties {
    data?: MultiPolygonDataType[];

    width: number /* width of grid, that is x len */;
    height: number /* height of grid, that is y len */;
    inset?: string /* inset clip rect around, default 0.5/width% 0.5/height% */;
    ring?: boolean;
    useRawScales?: boolean;

    onPolygonHover?: any;
    onPolygonHoverEnd?: any;
    onPolygonClick?: any;
    onPolygonMove?: any;
}

@defineElement({ tagName: namespacedTag('svg-multi-polygon'), styles: svgMultiPolygonStyles, version })
export class SVGMultiPolygon extends SVGShape implements SVGMultiPolygonProperties {
    // Static members

    // Public members
    @property({ type: Number, reflect: true, attribute: 'width' })
    width;
    @property({ type: Number, reflect: true, attribute: 'height' })
    height;
    @property({ type: String, reflect: true, attribute: 'inset' })
    inset;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    ring;
    @property({ type: Boolean, reflect: true, attribute: 'use-raw-scales' })
    useRawScales = false;

    // Protected members

    // Private members

    public yVals = () =>
        (this.data ?? []).flatMap((p) => p.coordinates.flatMap((q) => q.flatMap((r) => r.flatMap((s) => s[1]))));
    public xVals = () =>
        (this.data ?? []).flatMap((p) => p.coordinates.flatMap((q) => q.flatMap((r) => r.flatMap((s) => s[0]))));

    public exportSVG() {
        const [baseClassName] = createClassName(this.tagName.toLowerCase(), {});
        return this._renderSVG(baseClassName);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            canvas: this.mode !== 'svg',
        });

        if (!this.size || !this.getScales()) return;
        const [width, height] = this.size;

        if (this.mode !== 'svg') {
            return html`
                <div class="${rootClasses}">
                    <canvas class="${baseClassName}-target"></canvas>
                </div>
            `;
        }

        return html`
            <svg width=${width} height=${height} class="${rootClasses}">
                <defs id="custom-defs"></defs>
                ${this._renderSVG(baseClassName)}
            </svg>
            <slot name="defs" @slotchange=${this._defsSlotchange}></slot>
        `;
    }

    protected _invalidateCanvas(ctx) {
        throw new Error('Arc 2d canvas not implemented' + ctx);
    }

    protected _invalidateGl(gl) {
        throw new Error('Arc gl canvas not implemented' + gl);
    }

    private _renderSVG(baseClassName) {
        const [scaleX, scaleY] = this.getScales();

        const domainX = scaleX.domain();
        const domainY = scaleY.domain();
        const flipY = domainY[0] > domainY[1];
        const scaleFactorX = this._calcScaleFactor(domainX, this.width);
        const scaleFactorY = this._calcScaleFactor(domainY, this.height);
        const shiftX = domainX[0] as number;
        const shiftY = (flipY ? domainY[1] : domainY[0]) as number;

        // Custom or calc inset 100%*(0.5/size). Needed to clip artifacts from contour algorithm.
        // Especially for small datasets, like 16x16. For larger the artifacts.
        // See https://github.com/d3/d3-contour/issues/72
        const inset = this.inset ?? `${50 / this.width}% ${50 / this.height}%`;

        return svg`
                <g clip-path='inset(${inset})'>
                    ${
                        this.data
                            ? this.data.map((contour, index) => {
                                  return this._renderContour(
                                      baseClassName,
                                      contour,
                                      index,
                                      (x) => (this.useRawScales ? scaleX(x) : scaleX(x * scaleFactorX + shiftX)),
                                      (y) => (this.useRawScales ? scaleY(y) : scaleY(y * scaleFactorY + shiftY))
                                  );
                              })
                            : null
                    }
                </g>`;
    }

    /** Render a contour, iterate every coordinate. 2nd depth **/
    private _renderContour = (baseClassName, contour, index, scaleX, scaleY) => {
        const style = this._getDefaultStyle(
            { index },
            {
                fillColor: 'black',
                fillOpacity: 1,
                filter: '',
                strokeOpacity: 1,
                strokeColor: '',
                strokeWidth: 1,
                strokeDashArray: '',
            }
        );
        const hightlighted = this._isHighlight(index);
        const hidden = this._isVisibilityHidden(index);
        const interactable = this._isInteractable(index);

        return svg`<g 
                    class='${baseClassName}-target${hightlighted ? ' highlight' : ''}${
                        interactable ? ' interactable' : ''
                    }'
                    fill=${style.fillColor}
                    fill-opacity=${style.fillOpacity}
                    filter=${style.filter}                    
                    stroke=${style.strokeColor}
                    stroke-width=${style.strokeWidth}
                    stroke-opacity=${style.strokeOpacity}
                    stroke-dasharray=${style.strokeDashArray}
                    visibility=${ifDefined(hidden ? 'hidden' : undefined)}
                    style=${this._generateStyle({ index }, style as any)} data-dbg-index='${index}' data-dbg-length='${
                        contour.coordinates.length
                    }' data-dbg-value='${contour.value}'>${contour.coordinates.map((coordinate) =>
                        this._renderCoordinate(baseClassName, coordinate, scaleX, scaleY, index, contour.value)
                    )}</g>`;
    };

    /** Render a coordinate, iterate every polygon. 3rd depth **/
    private _renderCoordinate = (baseClassName, coordinate, scaleX, scaleY, index, zValue) => {
        return svg`
            <path class='${baseClassName}-path' 
                d='M${coordinate.map((polygon) => this._renderPolygon(polygon, scaleX, scaleY)).join('M')}'
                                    
                @click=${(e) => this._handleSVGPolygonClick(e, index, 0)} 
                @mouseenter=${(e) => this._handleSVGPolygonMouseEnter(e, index, 0)} 
                @mouseleave=${(e) => this._handleSVGPolygonMouseLeave(e, index, 0)}
                @mousemove=${(e) => this._handleSVGPolygonMouseMove(e, index, 0, zValue)}
                />`;
    };

    /** Render a polygon as a path, iterate every point. 4th depth **/
    private _renderPolygon = (polygon, scaleX, scaleY) => {
        return polygon.map((point) => this._renderPoint(point, scaleX, scaleY)).join('L');
    };

    /** Render a point as MZ in the path, deepest depth **/
    private _renderPoint = (point, scaleX, scaleY) => {
        return `${roundToPrecision2(scaleX(point[0]))},
        ${roundToPrecision2(scaleY(point[1]))}`;
    };

    private _calcScaleFactor = (domain, dimension) => this._domainLen(domain) / dimension;

    /** Calc domain bandwidth. **/
    private _domainLen = (domain) => Math.abs(domain[1] - domain[0]);

    private _handleSVGPolygonClick(evt, index, subIndex) {
        this._dispatchClick(evt, index, subIndex);
    }

    private _handleSVGPolygonMouseEnter(evt, index, subIndex) {
        this._dispatchHover(evt, index, subIndex);
    }

    private _handleSVGPolygonMouseLeave(evt, index, subIndex) {
        this._dispatchHoverEnd(evt, index, subIndex);
    }

    private _handleSVGPolygonMouseMove(evt, index, subIndex, zValue) {
        this._dispatchMouseMove(evt, index, subIndex, zValue);
    }

    private _dispatchClick(evt, index, subIndex) {
        this.dispatchEvent(
            new CustomEvent('polygon-click', {
                bubbles: true,
                detail: {
                    evt,
                    metadata: this.metadata,
                    target: this,
                    index,
                    subIndex,
                },
            })
        );
    }

    private _dispatchHover(evt, index, subIndex) {
        this.dispatchEvent(
            new CustomEvent('polygon-hover', {
                bubbles: true,
                detail: {
                    evt,
                    metadata: this.metadata,
                    target: this,
                    index,
                    subIndex,
                },
            })
        );
    }

    private _dispatchHoverEnd(evt, index, subIndex) {
        this.dispatchEvent(
            new CustomEvent('polygon-hover-end', {
                bubbles: true,
                detail: {
                    evt,
                    metadata: this.metadata,
                    target: this,
                    index,
                    subIndex,
                },
            })
        );
    }

    private _dispatchMouseMove(evt, index, subIndex, zValue) {
        this.dispatchEvent(
            new CustomEvent('polygon-move', {
                bubbles: true,
                detail: {
                    evt,
                    metadata: this.metadata,
                    target: this,
                    index,
                    subIndex,
                    point: { ...findDataPosition(this, this.getScalesWithMeta(), evt), z: zValue },
                },
            })
        );
    }
}
