import { createContext, consume } from '@lit-labs/context';
import { BaseElement, property } from '@umetrics/sartorius-ui-base';
import { Constructor } from '../data/types';
import { Settings } from '@umetrics/sartorius-ui-plot-utils';

export const settingsContext = createContext<Settings | undefined>('settings-context');

export declare abstract class WithSettingsContextInterface extends BaseElement {
    settings?: Settings;
}

export const WithSettingsContext = <T extends Constructor<BaseElement>>(superClass: T) => {
    abstract class PlottableElement extends superClass {
        @consume({ context: settingsContext, subscribe: true })
        @property({ attribute: false })
        settings?: Settings;
    }
    return PlottableElement as Constructor<WithSettingsContextInterface> & T;
};
