import { calculateRotatedCoords, GeneratorType, LabelGenerator, PlotLabelData, RawLabelPoint } from './labelGenerators';
import { SVGMultiPolygon } from '../../plottable';
import { LabelSettings } from './labels';
import { Datum, Point2D, QuadTree, StoredBox } from '@umetrics/sartorius-ui-plot-utils';
import { customFontSize, TextMeasurer } from '../../axis/textMeasurer';
import {
    isCompletelyInside,
    pointDistance,
    RectangleBounds,
    RectangleCorners,
} from '@umetrics/sartorius-ui-plot-utils/dist/geometrics';
import { D3Scale } from '../scale';
import { ScaleBand, ScaleLinear, ScaleLogarithmic, ScaleTime } from 'd3-scale';

type ScreenProps = Point2D & {
    offset: Point2D;
    horizontalLabels: boolean;
};

type PointCandidatePosition = Point2D & {
    screen: ScreenProps;
    pointIdx: number;
    seriesIdx: number;
    userData?: any;
    value: number;
    type: string;
    label: string;
};

type PointCandidatePositionWithAngle = PointCandidatePosition & { angleInRadians: number };

type PointCandidatePositionWithAngleAndCoords = PointCandidatePositionWithAngle &
    RectangleCorners & { noAngle: boolean };

type PointScore = PointCandidatePositionWithAngleAndCoords & {
    score: number;
};

const minDistanceFrom = (
    candidate: PointCandidatePositionWithAngle,
    previouslySelected: PointCandidatePositionWithAngle[]
) => {
    let min = Infinity;
    for (const selected of previouslySelected) {
        const dist = pointDistance(candidate.screen, selected.screen);
        if (dist < min) {
            min = dist;
            if (min === 0) break; // Early exit for minimum possible distance
        }
    }
    return min;
};

export class MultiPolygonLabelGenerator implements LabelGenerator<SVGMultiPolygon, any[]> {
    // Static members
    private static _textMeasurer = new TextMeasurer();

    generatorType = GeneratorType.MULTI_POLY;

    calculateLabels = (
        multiPoly: SVGMultiPolygon,
        labelSettings: LabelSettings,
        accumulated: QuadTree,
        transform: ((data: any[]) => any[]) | undefined
    ): PlotLabelData => {
        if (!multiPoly.id) {
            console.warn(
                'Please provide the multi polygon with a unique ID in order to properly track generated labels'
            );
        }
        const scales = multiPoly.getScales();
        if (!scales) {
            return { items: [], plotSurface: [] };
        }
        const [scaleX, scaleY] = scales;
        const domain = multiPoly.fittedDomain || multiPoly.getDomain2D();
        if (!domain) {
            return { items: [], plotSurface: [] };
        }
        const domainBounds: RectangleBounds = {
            topLeft: {
                x: Math.min(scaleX(domain.x[0]) as number, scaleX(domain.x[1]) as number),
                y: Math.min(scaleY(domain.y[0]) as number, scaleY(domain.y[1]) as number),
            },
            bottomRight: {
                x: Math.max(scaleX(domain.x[0]) as number, scaleX(domain.x[1]) as number),
                y: Math.max(scaleY(domain.y[0]) as number, scaleY(domain.y[1]) as number),
            },
        };
        const { width, height } = multiPoly.getBoundingClientRect();
        const screenBounds = {
            topLeft: { x: 0, y: 0 },
            bottomRight: { x: width, y: height },
        };

        const domainX = scaleX.domain();
        const domainY = scaleY.domain();
        const flipY = domainY[0] > domainY[1];
        const scaleFactorX =
            (multiPoly.width && Math.abs((domainX[1] as number) - (domainX[0] as number)) / multiPoly.width) || 1;
        const scaleFactorY =
            (multiPoly.height && Math.abs((domainY[1] as number) - (domainY[0] as number)) / multiPoly.height) || 1;
        const shiftX = domainX[0] as number;
        const shiftY = (flipY ? domainY[1] : domainY[0]) as number;

        const data = multiPoly.data;
        const transformed = (transform && transform(data)) || data;

        const polyPoints = transformed.flatMap((contour, index) => {
            const threshold = contour.value;
            const userData = contour.userData;

            const settings = multiPoly.getLabelSettings(
                { index: index, metadata: multiPoly.metadata, plotId: multiPoly.id },
                labelSettings
            );
            const label = settings.labelFormatter!(threshold, userData);
            const textX = MultiPolygonLabelGenerator._textMeasurer.measureTextLength(label, customFontSize(12)) + 4;
            const textY = 12 + 4; // height and padding
            const offsetY = textY / 2;
            const offsetX = textX / 2;

            return (contour.lineCoordinates || contour.coordinates).flatMap((coordinate) => {
                return coordinate.map((poly) => {
                    return poly
                        .map((point: [number, number]) => {
                            return {
                                x: multiPoly.useRawScales ? point[0] : point[0] * scaleFactorX + shiftX,
                                y: multiPoly.useRawScales ? point[1] : point[1] * scaleFactorY + shiftY,
                            };
                        })
                        .map(
                            (point: Point2D) =>
                                ({
                                    ...point,
                                    screen: {
                                        x: (scaleX(point.x) as number) + settings.offsetHorizontal!,
                                        y: (scaleY(point.y) as number) + settings.offsetVertical!,
                                        offset: { x: offsetX, y: offsetY },
                                        horizontalLabels: settings.horizontalLabels,
                                    },
                                    label,
                                    pointIdx: 0,
                                    seriesIdx: index,
                                    value: threshold,
                                    userData,
                                    type: 'multiPolyPoint',
                                }) as PointCandidatePosition
                        )
                        .map(MultiPolygonLabelGenerator.calculateTangentAngle)
                        .map((p) => ({
                            ...p,
                            ...calculateRotatedCoords(
                                p.screen,
                                settings.noAngle! ? 0 : p.angleInRadians,
                                p.screen.offset.x,
                                p.screen.offset.y
                            ),
                            noAngle: settings.noAngle,
                        }));
                });
            });
        });

        const selectedPolyPoints = polyPoints.reduce(
            (result: PointCandidatePositionWithAngleAndCoords[], next: PointCandidatePositionWithAngleAndCoords[]) => {
                if (!next.length) {
                    return result;
                }
                const chosen = this.choseFrom(next, result, domainBounds, screenBounds, accumulated, scaleX, scaleY);
                if (!chosen) {
                    return result;
                }
                const box: StoredBox = {
                    minX: Math.min(chosen.topLeft.x, chosen.topRight.x, chosen.bottomLeft.x, chosen.bottomRight.x),
                    maxX: Math.max(chosen.topLeft.x, chosen.topRight.x, chosen.bottomLeft.x, chosen.bottomRight.x),
                    minY: Math.min(chosen.topLeft.y, chosen.topRight.y, chosen.bottomLeft.y, chosen.bottomRight.y),
                    maxY: Math.max(chosen.topLeft.y, chosen.topRight.y, chosen.bottomLeft.y, chosen.bottomRight.y),
                };
                accumulated.insert(box);
                return [...result, chosen];
            },
            []
        );

        const rawPoints = selectedPolyPoints.map(
            (point: PointCandidatePositionWithAngleAndCoords, idx) =>
                ({
                    point: { x: point.x, y: point.y },
                    originalPoint: { x: point.x, y: point.y },
                    value: point.value,
                    id: 'contour-' + multiPoly.id + '-' + idx,
                    userData: point.userData,
                    pointIdx: point.pointIdx,
                    seriesIdx: point.seriesIdx,
                    angle: point.angleInRadians,
                    type: point.type,
                }) as RawLabelPoint
        );

        return { items: rawPoints, plotSurface: [] };
    };

    extremes = (data: any[]): any[] => {
        if (!data?.length) {
            return [];
        }
        if (data.length === 1) {
            return data;
        }
        return data.reduce((result, next) => {
            if (!result.length) {
                return [next, next];
            }
            if (next.value < result[0].value) {
                return [next, result[1]];
            }
            if (next.value > result[1].value) {
                return [result[0], next];
            }
            return result;
        }, []);
    };

    private static calculateTangentAngle = (
        screenPoint: PointCandidatePosition,
        idx: number,
        screenPoints: PointCandidatePosition[]
    ): PointCandidatePositionWithAngle => {
        const numPoints = screenPoints.length;
        let tangentSlope: number;

        if (numPoints === 1) {
            tangentSlope = 0;
        } else if (idx === 0) {
            const nextPoint = screenPoints[idx + 1];
            tangentSlope = (nextPoint.screen.y - screenPoint.screen.y) / (nextPoint.screen.x - screenPoint.screen.x);
        } else if (idx === numPoints - 1) {
            const prevPoint = screenPoints[idx - 1];
            tangentSlope = (screenPoint.screen.y - prevPoint.screen.y) / (screenPoint.screen.x - prevPoint.screen.x);
        } else {
            const prevPoint = screenPoints[idx - 1];
            const nextPoint = screenPoints[idx + 1];

            const slopePrev = (screenPoint.screen.y - prevPoint.screen.y) / (screenPoint.screen.x - prevPoint.screen.x);
            const slopeNext = (nextPoint.screen.y - screenPoint.screen.y) / (nextPoint.screen.x - screenPoint.screen.x);

            tangentSlope = (slopePrev + slopeNext) / 2;
        }

        return { ...screenPoint, angleInRadians: Math.atan(tangentSlope) };
    };

    private choseFrom(
        candidates: PointCandidatePositionWithAngleAndCoords[],
        previouslySelected: PointCandidatePositionWithAngleAndCoords[],
        domainBounds: RectangleBounds,
        screenBounds: RectangleBounds,
        accumulated: QuadTree,
        scaleX: D3Scale,
        scaleY: D3Scale
    ): PointCandidatePositionWithAngleAndCoords | undefined {
        let inside = candidates
            .filter((candidate) => {
                return isCompletelyInside(candidate, domainBounds);
            })
            .filter((candidate) => {
                return isCompletelyInside(candidate, screenBounds);
            });

        if (!inside?.length) {
            inside = this._findSegmentMidpoints(candidates, scaleX, scaleY, screenBounds);
        }

        if (!inside?.length) {
            return undefined;
        }

        return inside.reduce((chosen: PointScore | undefined, next: PointCandidatePositionWithAngleAndCoords) => {
            let score = previouslySelected?.length ? minDistanceFrom(next, previouslySelected) : 500;
            const box: StoredBox = {
                minX: Math.min(next.topLeft.x, next.topRight.x, next.bottomLeft.x, next.bottomRight.x),
                maxX: Math.max(next.topLeft.x, next.topRight.x, next.bottomLeft.x, next.bottomRight.x),
                minY: Math.min(next.topLeft.y, next.topRight.y, next.bottomLeft.y, next.bottomRight.y),
                maxY: Math.max(next.topLeft.y, next.topRight.y, next.bottomLeft.y, next.bottomRight.y),
            };
            const hasColliding = accumulated.hasIntersectingBoxesWith(box);
            if (!hasColliding) {
                score += 100000;
            }
            if (next.screen.horizontalLabels) {
                score += Math.PI - Math.abs(next.angleInRadians) * 10000;
            }
            if (!chosen || score > chosen.score) {
                return { ...next, score };
            }

            return chosen;
        }, undefined);
    }

    private _findSegmentMidpoints(
        candidates: PointCandidatePositionWithAngleAndCoords[],
        scaleX:
            | ScaleBand<Datum>
            | ScaleLinear<number, number>
            | ScaleLogarithmic<number, number>
            | ScaleTime<number, number | Date>,
        scaleY:
            | ScaleBand<Datum>
            | ScaleLinear<number, number>
            | ScaleLogarithmic<number, number>
            | ScaleTime<number, number | Date>,
        screenBounds: RectangleBounds
    ) {
        return candidates.reduce((lines: PointCandidatePositionWithAngleAndCoords[], candidate, idx) => {
            if (idx === 0) {
                return lines;
            }
            const l = this._findLineMidpoint([candidates[idx - 1], candidate], scaleX, scaleY, screenBounds);
            if (l) {
                lines.push(l);
            }
            return lines;
        }, []);
    }

    private _findLineMidpoint(
        candidates: PointCandidatePositionWithAngleAndCoords[],
        scaleX:
            | ScaleBand<Datum>
            | ScaleLinear<number, number>
            | ScaleLogarithmic<number, number>
            | ScaleTime<number, number | Date>,
        scaleY:
            | ScaleBand<Datum>
            | ScaleLinear<number, number>
            | ScaleLogarithmic<number, number>
            | ScaleTime<number, number | Date>,
        screenBounds: RectangleBounds
    ) {
        let inside: PointCandidatePositionWithAngleAndCoords | undefined;
        // interpolate to get it on screen if possible
        const first = candidates[0];
        const last = candidates[1];
        const mid = {
            x: (first.x + last.x) / 2,
            y: (first.y + last.y) / 2,
        };
        const midScreen: Point2D = { x: scaleX(mid.x) as number, y: scaleY(mid.y) as number };
        const between = {
            ...first,
            ...mid,
            screen: {
                ...first.screen,
                ...midScreen,
            },
            ...calculateRotatedCoords(midScreen, first.angleInRadians, first.screen.offset.x, first.screen.offset.y),
        } as PointCandidatePositionWithAngleAndCoords;
        if (isCompletelyInside(between, screenBounds)) {
            inside = between;
        }
        return inside;
    }
}
