/**
 * Deeply merges two settings objects. (This is kinda generic and could be used for other...)
 * @param defaultObj The default object containing base settings.
 * @param inputObj The object containing overrides.
 * @returns A new object with merged properties.
 */
export function settingsMerge<T extends object>(defaultObj: T, inputObj: Partial<T>): T {
    const result = { ...defaultObj };
    for (const key in inputObj) {
        if (Object.prototype.hasOwnProperty.call(inputObj, key)) {
            const inputValue = inputObj[key];
            const defaultValue = defaultObj[key];

            if (
                inputValue &&
                typeof inputValue === 'object' &&
                !Array.isArray(inputValue) &&
                defaultValue &&
                typeof defaultValue === 'object' &&
                !Array.isArray(defaultValue)
            ) {
                result[key as keyof T] = settingsMerge(defaultValue, inputValue as Partial<T[keyof T]>) as T[keyof T];
            } else if (inputValue !== undefined) {
                result[key as keyof T] = inputValue as T[keyof T];
            }
        }
    }
    return result;
}
