import { calculatePlacement, GeneratorType, LabelGenerator, PlotLabelData, RawLabelPoint } from './labelGenerators';
import { isSingleValueX, isSingleValueY, SVGLine } from '../../plottable';
import { isFullySpecified, isNumber, Point2D, QuadTree } from '@umetrics/sartorius-ui-plot-utils';
import { LabelSettings } from './labels';
import { customFontSize, TextMeasurer } from '../../axis/textMeasurer';
import { ScaleContinuousNumeric } from 'd3-scale';
import { DateScale } from '../scale';

const EMPTY: PlotLabelData = { items: [], plotSurface: [] };

export class LineLabelGenerator implements LabelGenerator<SVGLine, Point2D[]> {
    // Static members
    private static _textMeasurer = new TextMeasurer();

    generatorType = GeneratorType.LINE;

    calculateLabels(
        line: SVGLine,
        defaultSettings: LabelSettings,
        _accumulated: QuadTree,
        transform: ((data: Point2D[]) => Point2D[]) | undefined
    ): PlotLabelData {
        if (!line.data?.length) {
            return EMPTY;
        }

        const { width, height } = line.getBoundingClientRect();

        const domain = line.fittedDomain || line.getDomain2D();
        if (!domain) {
            return EMPTY;
        }
        if (!domain.x?.length || !domain.y?.length) {
            return EMPTY;
        }
        const extent = {
            x: [
                Math.min(domain.x[0] as number, domain.x[1] as number),
                Math.max(domain.x[0] as number, domain.x[1] as number),
            ],
            y: [
                Math.min(domain.y[0] as number, domain.y[1] as number),
                Math.max(domain.y[0] as number, domain.y[1] as number),
            ],
        };

        let data = line.data;
        if (!data?.length) {
            return EMPTY;
        }
        if (!(data[0] instanceof Array)) {
            data = [data];
        }

        const scalesWithMeta = line.getScalesWithMeta();
        if (!scalesWithMeta) {
            return EMPTY;
        }
        const [scaleX, scaleY] = scalesWithMeta;

        const rawLabelPoints: RawLabelPoint[] = data
            .map(
                (series: Point2D[]) =>
                    (((series.length === 1 && isNumber(series[0].x)) || isNumber(series[0].y)) && series) ||
                    series.filter((point: Point2D) => isFullySpecified(point))
            )
            .map(transform)
            .flatMap((series: Point2D[], idx: number) => {
                let settings: LabelSettings;

                if (isSingleValueX(series)) {
                    const userData = (series[0] as Point2D & { userData?: any }).userData;
                    const value = series[0].x;
                    settings = line.getLabelSettings(
                        { index: idx, pointIndex: 0, point: series[0], metadata: line.metadata, plotId: line.id },
                        defaultSettings
                    );
                    const label = settings.labelFormatter!(value, userData);
                    const textX = LineLabelGenerator._textMeasurer.measureTextLength(label, customFontSize(12)) + 4;
                    const placement = calculatePlacement(settings.placement!, height, textX);
                    let placementY = (extent.y[1] - extent.y[0]) * (1 - placement) + extent.y[0];
                    if (scaleY.scaleType !== 'band') {
                        placementY = (scaleY.scale as ScaleContinuousNumeric<any, any> | DateScale).invert(
                            (height - 1) * (1 - placement) + 1
                        ) as number;
                    }
                    return [
                        {
                            value,
                            userData,
                            id: `line-plot-label-${line.id}-${idx}-${0}`,
                            point: {
                                x: value,
                                y: placementY,
                            },
                            originalPoint: series[0],
                            pointIdx: 0,
                            seriesIdx: idx,
                            angle: Math.PI / 2,
                            type: 'lineLimitX',
                        },
                    ];
                }

                if (isSingleValueY(series)) {
                    const userData = (series[0] as Point2D & { userData?: any }).userData;
                    const value = series[0].y;
                    settings = line.getLabelSettings(
                        { index: idx, pointIndex: 0, point: series[0], metadata: line.metadata, plotId: line.id },
                        defaultSettings
                    );
                    const label = settings.labelFormatter!(value, userData);
                    const textX = LineLabelGenerator._textMeasurer.measureTextLength(label, customFontSize(12)) + 4;
                    const placement = calculatePlacement(settings.placement!, width, textX);
                    let placementX = (extent.x[1] - extent.x[0]) * placement + extent.x[0];
                    if (scaleX.scaleType !== 'band') {
                        placementX = (scaleX.scale as ScaleContinuousNumeric<any, any> | DateScale).invert(
                            (width - 1) * placement + 1
                        ) as number;
                    }
                    return [
                        {
                            value,
                            userData,
                            id: `line-plot-label-${line.id}-${idx}-${0}`,
                            pointIdx: 0,
                            seriesIdx: idx,
                            point: {
                                x: placementX,
                                y: series[0].y,
                            },
                            originalPoint: series[0],
                            type: 'lineLimitY',
                        },
                    ];
                }

                return series.map((point, pointIndex) => {
                    const userData = (point as Point2D & { userData?: any }).userData;
                    return {
                        point,
                        originalPoint: point,
                        userData,
                        value: point,
                        id: `line-plot-label-${line.id}-${idx}-${pointIndex}`,
                        pointIdx: pointIndex,
                        seriesIdx: idx,
                        type: 'linePoint',
                    };
                });
            });

        return { items: rawLabelPoints, plotSurface: [] };
    }
    extremes(data: Point2D[]): Point2D[] {
        if (!data?.length) {
            return [];
        }
        if (data.length <= 2) {
            return data;
        }
        return data.reduce((result: Point2D[], next) => {
            if (!result.length) {
                return [next, next];
            }
            if (next.y < result[0].y) {
                return [next, result[1]];
            }
            if (next.y > result[1].y) {
                return [result[0], next];
            }
            return result;
        }, []);
    }
}
