import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const windowScrollStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: flex;
                width: 100%;
                height: 100%;
            }

            ${root} {
                width: 100%;
                height: 100%;
            }

            ${is('!vertical')} {
                padding: 0 var(--windowscroll-root-padding, 0) 0 var(--windowscroll-root-padding, 0);
            }

            ${is('vertical')} {
                padding: var(--windowscroll-root-padding, 0) 0 var(--windowscroll-root-padding, 0) 0;
            }

            ${child('container')} {
                position: relative;
                background-color: var(--windowscroll-container-bg-color, var(--color-secondary-1, #ddd));
                user-select: none;
            }

            ${is('!vertical')} ${child('container')} {
                width: 100%;
                height: var(--windowscroll-container-height, 100%);
            }

            ${is('vertical')} ${child('container')} {
                width: var(--windowscroll-container-height, 100%);
                height: 100%;
            }

            ${child('content')} {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }

            ${is('!vertical')} ${child('content')} {
                margin-left: var(--windowscroll-content-margin, 0);
                margin-right: var(--windowscroll-content-margin, 0);
            }

            ${is('vertical')} ${child('content')} {
                margin-top: var(--windowscroll-content-margin, 0);
                margin-bottom: var(--windowscroll-content-margin, 0);
            }

            ${child('placeholder')} {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                z-index: 1;
            }

            ${is('!vertical')} ${child('placeholder')} {
                margin-left: var(--windowscroll-content-margin, 0);
                margin-right: var(--windowscroll-content-margin, 0);
            }

            ${is('vertical')} ${child('placeholder')} {
                margin-top: var(--windowscroll-content-margin, 0);
                margin-bottom: var(--windowscroll-content-margin, 0);
            }

            ${child('bar')} {
                position: absolute;
                cursor: move;
                z-index: 2;
            }

            ${is('!vertical')} ${child('bar')} {
                height: 100%;
                width: var(--windowscroll-bar-width, 100%);
                transform: translate(var(--windowscroll-bar-pos, 0), 0);
            }

            ${is('vertical')} ${child('bar')} {
                height: var(--windowscroll-bar-width, 100%);
                width: 100%;
                transform: translate(0, var(--windowscroll-bar-pos, 0));
            }

            ${child('ruler')} {
                position: absolute;
                z-index: 3;
            }

            ${is('!vertical')} ${child('ruler')} {
                height: 100%;
                width: var(--windowscroll-ruler-width, 10px);
                cursor: ew-resize;
            }

            ${is('vertical')} ${child('ruler')} {
                height: var(--windowscroll-ruler-width, 10px);
                width: 100%;
                cursor: ns-resize;
            }

            ${is('!vertical')} ${child('left-ruler')} {
                transform: translate(var(--windowscroll-left-ruler-pos, 0), 0);
            }

            ${is('vertical')} ${child('left-ruler')} {
                transform: translate(0, var(--windowscroll-left-ruler-pos, 0));
            }

            ${is('!vertical')} ${child('right-ruler')} {
                transform: translate(var(--windowscroll-right-ruler-pos, 0), 0);
            }

            ${is('vertical')} ${child('right-ruler')} {
                transform: translate(0, var(--windowscroll-right-ruler-pos, 0));
            }

            ${child('default-bar')} {
                background-color: var(--color-tertiary-8);
                opacity: 0.1;
                width: 100%;
                height: 100%;
            }

            ${is('!disabled')} ${child('default-bar')}:hover {
                opacity: 0.2;
            }

            ${is('disabled')} ${child('default-bar')} {
                opacity: 0.2;
                cursor: default;
            }

            ${child('default-ruler')} {
                display: flex;
                flex-direction: column;
                justify-content: center;
                font-size: var(--windowscroll-ruler-width, 10px);
                height: 100%;
                color: var(--color-primary-2);
                background-color: var(--color-tertiary-8);
            }

            ${is('!disabled')} ${child('default-ruler')}:hover {
                box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.1);
            }

            ${is('disabled')} ${child('default-ruler')} {
                height: 100%;
                opacity: 0.7;
                background-color: var(--color-secondary-4);
                border: none;
                cursor: default;
            }

            ${child('labels')} {
                display: flex;
                flex-direction: row;
                font-size: 12px;
                color: var(--color-primary-3);
                line-height: 12px;
                padding-bottom: 4px;
            }

            ${child('left-label')} {
                flex: 1;
            }

            ${child('right-label')} {
                flex: 1;
                text-align: end;
            }

            ${child('tooltip')} {
                position: absolute;
                top: -40px;
                z-index: 4;

                width: fit-content;
                height: auto;
                display: none;
                flex-direction: column;
                overflow: hidden;
                transition: top 0.125s ease-in-out;
            }

            ${child('tooltip-left')} {
                left: var(--windowscroll-left-tooltip-pos, 0);
            }

            ${child('tooltip-right')} {
                left: unset;
                right: calc(100% + var(--windowscroll-right-tooltip-pos, 10px));
            }

            ${is('dragging')} ${child('tooltip')} {
                display: flex;
            }

            ${is('high')} ${child('tooltip-left')} {
                top: -70px;
            }

            ${child('tooltip')} ${child('tooltip-content')} {
                position: relative;
                display: flex;
                align-items: center;
                padding-left: 4px;
                background-color: black;
                height: 24px;
                color: white;
                font-size: smaller;
            }

            ${child('tooltip-icon')} {
                margin-right: 10px;
                font-size: 16px;
            }

            ${child('tooltip-data')} {
                font-size: 14px;
                white-space: nowrap;
                margin-right: 8px;
            }

            ${child('tooltip-effect')} {
                position: relative;
                height: 37px;
                width: auto;
                z-index: -1;
                top: -30px;
                transition: top 0.125s ease-in-out;
            }

            ${child('tooltip-left')} ${child('tooltip-effect')} {
                left: calc(-1 * var(--windowscroll-left-tooltip-pos, 0));
            }

            ${child('tooltip-right')} ${child('tooltip-effect')} {
                left: calc(100% + var(--windowscroll-right-tooltip-pos, -8));
            }

            ${is('high')} ${child('tooltip-left')} ${child('tooltip-effect')} {
                top: 0;
            }
        `,
    ];
};
