export type Datum = number | string | Date;

export interface Point2D<T = number> {
    x: T;
    y: T;
}

export function isNumber(value) {
    return typeof value === 'number' && !isNaN(value);
}

export function isFiniteNumber(value) {
    return isNumber(value) && value !== Infinity && value !== -Infinity;
}

export function isValidString(value) {
    return typeof value === 'string' && Boolean(value.length);
}

export function isValidDate(value) {
    return typeof value === 'object' && value instanceof Date;
}

export function isValidDatum(value) {
    return isFiniteNumber(value) || isValidString(value) || isValidDate(value);
}

export const isFullySpecified = (point: Point2D<Datum | undefined>) => isValidDatum(point.x) && isValidDatum(point.y);
