import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    nothing,
    property,
    PropertyValues,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { namespacedTag } from '../tag';
import { LinearScale } from '../data';
import { WithContext } from '../data/plotContext';
import { version } from '../metadata';
import { dataCursorStyles as styles } from './dataCursorStyles';
import { WithDataContext } from '../data/dataContext';
import { calculateMetadataYSnapshot, MetadataValue, MetadataYSnapshot } from './metadataUtils';
import { SVGShape } from '../plottable/svg/svgShape';
import { dataChangeDetection } from '../plottable/dataChangeDetection';

export interface OnDataCursorHoverEvent {
    isVisible: boolean;
    event: MouseEvent | null;
    target: DataCursor;
    mouse: [x: number, y: number];
    data: MetadataYSnapshot;
    xValue: MetadataValue;
}

export interface DataCursorConfig {
    showDatapoints?: boolean;
    showTooltipMonitor?: boolean;
    tooltipMonitorHeadFormatter?: (x) => [string, string] /* Deprecated, use tooltipMonitorHead */;
    tooltipMonitorPosition?: 'fixed' | 'absolute'; // fixed default
    tooltipMonitorHead?: {
        group?: string | number;
        row: {
            color?: string;
            label?: ((x) => string) | string;
            value?: ((x) => string) | string;
        }[];
    }[];
    dataSerie?: {
        forId?: string;
        forGroup?: string | number;
        color?: string;
        value?: ((interpolated, exact) => string) | string;
        label?: ((interpolated, exact) => string) | string;

        unit?: string /* Deprecated, use label */;
        dataFormatter?: (interpolated, exact) => string /* Deprecated, use value */;
    }[];
}

export interface HTMLDataCursorProperties {
    color: string;
    width: number;

    /**
     * Show the next Y value once the mouse position crosses the midpoint
     * between two consecutive X data points.
     */
    midpoint?: boolean;
    pixelProximityThreshold?: number;
    config?: DataCursorConfig; // Config object that enable internal visuals
    outerVisibility?: number; // Force cursor and datapoints visibility for this mouseX
    onData?: (e: CustomEvent<OnDataCursorHoverEvent>) => void;
}

type XValue = number | string | Date | null;

@defineElement({ tagName: namespacedTag('data-cursor'), styles, version })
export class DataCursor extends WithDataContext(WithContext(BaseElement)) implements HTMLDataCursorProperties {
    @property({ type: String })
    color = 'grey';

    @property({ type: Number })
    width = 1;

    @property({ type: Number })
    pixelProximityThreshold = 10;

    @property({ type: String })
    filterDataByTag = 'SVG-LINE';

    @property({ type: Object, hasChanged: dataChangeDetection, attribute: false }) config?: DataCursorConfig;

    @property({ type: Number, attribute: 'outer-visibility', reflect: false })
    outerVisibility = -1;

    @property({ type: Boolean })
    midpoint = false;

    onData;

    @state()
    private state = { position: { x: 0, y: 0 }, isVisible: false };
    private _mouse: [x: number, y: number] = [0, 0];
    private _lastEvent: MouseEvent | null = null;
    private _isCursorInContainerArea = false;

    @query('.container')
    private _container;

    private _config; // re-mapping deprecations in config.

    private get allFilteredData() {
        return (
            this.allDataElements
                ?.filter((el) => el.tagName.endsWith(this.filterDataByTag))
                .map((el) => {
                    return {
                        scales: (el as SVGShape).getScales() as [LinearScale, LinearScale],
                        data: el.plotData,
                        slot: el.slot ?? '',
                        id: el.id ?? '',
                    };
                }) ?? []
        );
    }

    handleDocumentMouseMove = (event: MouseEvent) => {
        if (this._container && this.isMouseOverContainer(event.clientX, event.clientY)) {
            this._handleMouseMove(event);
            this._isCursorInContainerArea = true;
        } else {
            if (this._isCursorInContainerArea) {
                this._handleMouseLeave(event);
            }
            this._isCursorInContainerArea = false;
        }
    };

    connectedCallback(): void {
        super.connectedCallback();
        document.addEventListener('mousemove', this.handleDocumentMouseMove);
    }

    disconnectedCallback(): void {
        document.removeEventListener('mousemove', this.handleDocumentMouseMove);
        super.disconnectedCallback();
    }

    isMouseOverContainer(mouseX: number, mouseY: number): boolean {
        const rect = this._container.getBoundingClientRect();
        return mouseX >= rect.left && mouseX <= rect.right && mouseY >= rect.top && mouseY <= rect.bottom;
    }

    render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        return html`
            <div class="${rootClasses} ${baseClassName} container">
                ${this._renderCursor()} ${this._renderInternalVisuals()}
                <slot></slot>
            </div>
        `;
    }

    emitData() {
        const [mouseX] = this._mouse;
        const data = this._getDataPoints(mouseX);

        this.dispatch(this._lastEvent, data, this._getXValue(mouseX));
    }

    // TODO : Remove this update handling of deprecated stuff // NOSONAR
    // prettier-ignore
    protected update(_changedProperties: PropertyValues<this>): void { // NOSONAR

        super.update(_changedProperties);

        if (_changedProperties.has('config') && this.config) {
            this._config = { ...this.config };
            this._config.dataSerie = this.config.dataSerie ? [...this.config.dataSerie] : [];

            let deprecations = false;
            if (this.config.tooltipMonitorHeadFormatter && !this.config.tooltipMonitorHead) {
                this._config.tooltipMonitorHead = [
                    {
                        group: 0,
                        row: [
                            {
                                color: 'none',
                                label: (x) => this.config?.tooltipMonitorHeadFormatter!(x)[0],
                                value: (x) => this.config?.tooltipMonitorHeadFormatter!(x)[1],
                            },
                        ],
                    },
                ];
                delete this._config.tooltipMonitorHeadFormatter;
            }

            this.config.dataSerie?.forEach((d, i) => {
                if (d != null) {
                    if (d.unit) {
                        this._config.dataSerie[i].label = d.unit;
                        delete this._config.dataSerie[i].unit;
                        deprecations = true;
                    }
                    if (d.dataFormatter) {
                        this._config.dataSerie[i].value = d.dataFormatter;
                        delete this._config.dataSerie[i].dataFormatter;
                        deprecations = true;
                    }
                }
            });
            if (deprecations) {
                console.warn(
                    'DataCursor config with deprecated format:',
                    this.config,
                    'Should be changed to:',
                    this._config,
                );
            }
        }
    }

    protected updated(_changedProperties: PropertyValues<this>): void {
        super.updated(_changedProperties);

        // Is this really needed? Please check this!
        if (
            _changedProperties.has('allDataElements') ||
            (_changedProperties.has('scaleMap') &&
                dataChangeDetection(this.scaleMap, _changedProperties.get('scaleMap')))
        ) {
            if (this.allDataElements && this.getScales()) {
                this.emitData();
            }
        }
    }

    private _renderCursor() {
        const [width, height] = this.size ?? [0, 0];
        const x = this.outerVisibility > -1 ? this.outerVisibility : this._mouse[0];
        const crosshairPath = `M${x} 0 V${height}`;
        return this.state.isVisible || this.outerVisibility > -1
            ? html` <svg width=${width} height=${height}>
                  <path stroke=${this.color} stroke-width=${this.width} d=${crosshairPath} />
              </svg>`
            : nothing;
    }

    // q: data cursor should not be responsible to render this, it can be controlled from outside via event?
    // a: In our case, the events causing virtual dom updates will be too slow with flickers. Having internal
    // render solve that (lit is 2-4x faster than react). And we also want to keep styling of the tooltip etc
    // internally, controlled by branding.
    private _renderInternalVisuals() {
        if (this.config && (this.state.isVisible || this.outerVisibility > -1)) {
            const config = this.config;
            const x = this.outerVisibility > -1 ? this.outerVisibility : this._mouse[0];
            const xValue = this._getXValue(x);
            const datapoints = this._getDataPoints(x);

            const monitorHtml =
                config.showTooltipMonitor && this.state.isVisible
                    ? this._renderMonitor(datapoints, config, xValue)
                    : nothing;
            const datapointsHtml = this.config.showDatapoints ? this._renderDataPoints(datapoints, config) : nothing;

            return html`${datapointsHtml}${monitorHtml}`;
        }
        return nothing;
    }

    private _renderMonitor(
        datapoints: MetadataYSnapshot & { plotData: any[] },
        config: DataCursorConfig,
        xValue: XValue
    ) {
        const { dataSerieCounter, headerCounter, innerHtml } = this._renderInnerMonitor(xValue, config, datapoints);

        // Some hardcoded sizes here below, Do something better...
        const { position, left, top } = this._getMonitorPosition(this.state.position, dataSerieCounter + headerCounter);

        const wrappedHtml = headerCounter === 1 ? innerHtml : html` <div class="item-container">${innerHtml}</div>`;
        return html` <div
            class="monitor"
            style=" --data-cursor-monitor-x: ${left}px;
                    --data-cursor-monitor-y: ${top}px;
                    --data-cursor-monitor-position: ${position};"
        >
            ${wrappedHtml}
        </div>`;
    }

    private _renderInnerMonitor(
        xValue: XValue,
        config: DataCursorConfig,
        datapoints: MetadataYSnapshot & {
            plotData: any[];
        }
    ) {
        let dataSerieCounter = 0;
        let headerCounter = 0;
        const single =
            this._config?.tooltipMonitorHead.length === 1 && this._config?.tooltipMonitorHead[0].row.length === 1;
        const innerHtml = this._config?.tooltipMonitorHead.map((head, headIndex) => {
            const group = head.group ?? headIndex;
            const headHtml = head.row.map((headRow) => {
                ++headerCounter;
                return this._renderMonitorHead(headRow, xValue, dataSerieCounter, single);
            });

            const seriesHtml = config.dataSerie
                ?.filter((d) => (d?.forGroup ?? 0) === group)
                .map((dataSerie) => {
                    const dataPointsPosition = dataSerieCounter;
                    ++dataSerieCounter;
                    return this._renderMonitorItem(dataSerie, datapoints, dataPointsPosition);
                });

            return single
                ? html` <div class="single-group">${headHtml}</div>
                      <div class="item-container">${seriesHtml}</div>`
                : html` ${headHtml}${seriesHtml}`;
        });
        return { dataSerieCounter, headerCounter, innerHtml };
    }

    private _renderMonitorItem(
        dataSerie,
        datapoints: MetadataYSnapshot & { plotData: any[] },
        dataPointsPosition: number
    ) {
        if (dataSerie != null) {
            if (dataSerie.forId) {
                const positionByMatchingId = datapoints.plotData?.findIndex((p) => p.id === dataSerie.forId);
                if (positionByMatchingId > -1) {
                    dataPointsPosition = positionByMatchingId;
                }
            }

            const di = datapoints.interpolated?.[dataPointsPosition];
            const de = datapoints.exact?.[dataPointsPosition];

            if (di?.y == null) {
                return nothing;
            }
            return html`
                <div class="item-legend" style="--monitor-item-color:${dataSerie.color ?? 'none'};"></div>
                <div class="item-value${di.highlighted ? ' highlight' : ''}">
                    <div>${this._callOrFallback(dataSerie.value, () => (di.y as number)?.toFixed(2), di.y, de.y)}</div>
                </div>
                <div class="item-label">
                    <span style="direction: ltr;unicode-bidi: plaintext;">
                        ${this._callOrFallback(dataSerie.label, () => dataSerie.label, di.y, de.y) ?? ''}
                    </span>
                </div>
            `;
        } else {
            return nothing;
        }
    }

    private _callOrFallback = <T, R>(value: T | ((...args: any[]) => R), fallback: () => R, ...args: any[]): R =>
        typeof value === 'function' ? (value as (...args: any[]) => R)(...args) : fallback();

    private _renderMonitorHead(headRow, xValue: XValue, dataSerieCounter, single) {
        const newGroup = dataSerieCounter > 0 ? ' head-group' : '';
        const singleHtml = single
            ? html` <div class="head-legend${newGroup}" style="--monitor-item-color:${'none'};"></div>`
            : nothing;
        return html` <div class="head-legend${newGroup}" style="--monitor-item-color:${headRow.color ?? 'none'};"></div>
            <div class="head-label${newGroup}">
                ${typeof headRow.label === 'function' ? headRow.label(xValue) : headRow.label}
            </div>
            ${singleHtml}
            <div class="head-value${newGroup}">
                <span style="direction: ltr;unicode-bidi: plaintext;">
                    ${typeof headRow.value === 'function' ? headRow.value(xValue) : headRow.value}
                </span>
            </div>`;
    }

    private _getMonitorPosition = ({ x, y }, actualCount: number) => {
        if (this.config?.tooltipMonitorPosition === 'absolute') {
            const expectedHeight = 42 + 24 * actualCount; //42->header height, 24->serie height
            const deltaY = y + expectedHeight - this.size![1];
            const deltaX = x + 180 - this.size![0]; // 180 -> width
            const left = deltaX > 0 ? x - deltaX : x;
            const top = deltaY > 0 ? y - deltaY : y;
            return { position: 'absolute', left, top };
        }
        const rect = this._container.getBoundingClientRect();
        return { position: 'fixed', left: rect.left + x, top: rect.top + y };
    };

    private _renderDataPoints = (datapoints: MetadataYSnapshot & { plotData: any[] }, config: DataCursorConfig) =>
        datapoints.interpolated.map((d, di) => {
            const [_scaleX, _scaleY] = d.scales;
            const posX = d.x != null ? _scaleX(d.x as number) : null;
            const posY = d.y != null ? _scaleY(d.y as number) - this.size![1] : null;

            const positionById = config.dataSerie
                ? config.dataSerie
                      .filter((s) => s != null)
                      .findIndex((s) => s.forId && s.forId === datapoints.plotData[di].id)
                : -1;
            const position = positionById > -1 ? positionById : di;

            const dataSerie = config.dataSerie?.[position];
            if (dataSerie && posX && posY) {
                return datapoints.plotData[di].slot === this.slot
                    ? html`
                          <div
                              class="data-point"
                              style=" --data-point-x: ${posX}px;
                                        --data-point-y: ${posY}px;
                                        --data-point-color: ${dataSerie.color ?? 'black'};"
                          ></div>
                      `
                    : nothing;
            }
            return nothing;
        });

    private _handleMouseMove(event: MouseEvent) {
        if (!this._container || !this.getScales()) return;

        const rect = this._container.getBoundingClientRect();
        const mouseX = event.clientX - rect.left;
        const mouseY = event.clientY - rect.top;
        this._mouse = [mouseX, mouseY];
        this._lastEvent = event;

        this.state = {
            position: { x: mouseX, y: mouseY },
            isVisible: true,
        };

        this.emitData();
    }

    private _handleMouseLeave(event: MouseEvent) {
        this.state = {
            ...this.state,
            isVisible: false,
        };
        this.dispatch(event, { interpolated: [], exact: [] }, -1);
    }

    private dispatch(event: MouseEvent | null, data: OnDataCursorHoverEvent['data'], xValue) {
        this.dispatchEvent(
            new CustomEvent<OnDataCursorHoverEvent>('data', {
                bubbles: true,
                detail: {
                    event,
                    target: this,
                    mouse: this._mouse,
                    data,
                    xValue,
                    isVisible: this.state.isVisible,
                },
            })
        );
    }

    private _getDataPoints(mouseX: number): MetadataYSnapshot & { plotData: any[] } {
        const allData = this.allFilteredData ?? [];

        const result = calculateMetadataYSnapshot({
            xPosition: mouseX,
            pixelProximityThreshold: this.pixelProximityThreshold,
            plotData: allData,
            midpoint: this.midpoint,
        });

        return { ...result, plotData: allData };
    }

    private _getXValue(mouseX: number): MetadataValue {
        const [scaleX] = this.getScales() as [LinearScale, LinearScale];
        return scaleX.invert(mouseX);
    }
}
