import { Line } from 'd3-shape';
import { Point2D } from '@umetrics/sartorius-ui-plot-utils';

export type EventHandlingFunction<T extends Event> = (args: {
    event: T;
    eventType: SupportedEventType;
    target: Shape;
    updateShapes: (...shapes: Shape[]) => void;
}) => {};

export enum SupportedEventType {
    CLICK,
    GLOBAL_MOUSE_MOVE,
    GLOBAL_POINTER_MOVE,
    GLOBAL_TOUCH_MOVE,
    MOUSE_DOWN,
    MOUSE_ENTER,
    MOUSE_LEAVE,
    MOUSE_MOVE,
    MOUSE_OUT,
    MOUSE_OVER,
    MOUSE_UP,
    MOUSE_UP_OUTSIDE,
    POINTER_CANCEL,
    POINTER_DOWN,
    POINTER_ENTER,
    POINTER_LEAVE,
    POINTER_MOVE,
    POINTER_OUT,
    POINTER_OVER,
    POINTER_TAP,
    POINTER_UP,
    POINTER_UP_OUTSIDE,
    RIGHT_CLICK,
    RIGHT_DOWN,
    RIGHT_UP,
    RIGHT_UP_OUTSIDE,
    TAP,
    TOUCH_CANCEL,
    TOUCH_END,
    TOUCH_END_OUTSIDE,
    TOUCH_MOVE,
    TOUCH_START,
    WHEEL,
}

export enum ShapeKind {
    PATH,
    CIRCLE,
    RECTANGLE,
    SVG,
    SPRITE,
    REF,
}

export abstract class RenderNode {
    zIndex?: number;
    readonly id: string;

    protected constructor(id: string) {
        this.id = id;
    }
}

export interface SceneGroup extends RenderNode {
    shared?: Shape[];
    textures?: SceneTexture[];
    children?: (SceneGroup | Shape | SharedRef)[];
}

export interface PathInstructions {
    line: Line<Point2D>;
    points: Point2D[];
}

export interface PathOptions {
    svgPath: string;
}

export interface CircleOptions {
    x: number;
    y: number;
    radius: number;
}

export interface RectangleOptions {
    x: number;
    y: number;
    width: number;
    height: number;
}

export interface SVGOptions {
    svg: string;
}

export const TransformTypeOptions = ['translate', 'scale'];
export type TransformType = (typeof TransformTypeOptions)[number];
export interface TransformTypeOptions {
    type: TransformType;
}

export interface TranslateOptions extends TransformTypeOptions {
    x: number;
    y: number;
}

export interface ScaleOptions extends TransformTypeOptions {
    scaleX: number;
    scaleY: number;
}

export type TransformOptions = TransformTypeOptions[];

export interface FillOptions {
    color?: string;
    opacity?: number;
}

export interface StrokeOptions {
    color?: string;
    opacity?: number;
    width?: number;
    dashArray?: number[];
}

export interface CachedOptions {
    cachedCircleOptions?: CircleOptions;
    cachedPathOptions?: PathOptions;
    cachedRectangleOptions?: RectangleOptions;
    cachedSVGOptions?: SVGOptions;
    cachedSharedRefOptions?: SharedRefOptions;
    cachedTransformOptions?: TransformOptions;
    cachedFillOptions?: FillOptions;
    cachedStrokeOptions?: StrokeOptions;
    cachedZIndex?: number;
}

export interface GraphicsOptions {
    on?: Partial<Record<SupportedEventType, EventHandlingFunction<any>>>;
    transform?: TransformOptions;
    fill?: FillOptions;
    stroke?: StrokeOptions;
}

export interface ShapeOptions extends GraphicsOptions {
    path?: PathOptions;
    circle?: CircleOptions;
    rectangle?: RectangleOptions;
    svg?: SVGOptions;
    ref?: SharedRefOptions;
}

export interface SharedRefOptions {
    sharedId: string;
}

export interface Shape extends RenderNode, ShapeOptions {
    readonly kind: ShapeKind;
}

export interface SharedRef extends RenderNode, SharedRefOptions {}

export interface SceneTextureSourceOptions {
    svg: string;
}

export interface SceneTexture {
    id: string;
    options: SceneTextureSourceOptions;
}
