import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const labelsStyles: GenerateStyles = (tagName) => {
    const { root } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
                --text-size: 0.75rem;
                --label-background-color: transparent;
                --label-hover-background-color: var(--color-primary-2);
            }

            :host {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                pointer-events: none;
            }

            ${root} {
                position: relative;
                overflow: hidden;
                width: 100%;
                height: 100%;
            }

            .label-wrapper {
                position: absolute;
                display: inline-block;
                white-space: nowrap;
                overflow: visible;
                background-color: transparent;
                width: 100px;
                height: 20px;
            }

            .label-wrapper:hover {
                z-index: 2;
            }

            .label-wrapper:has(.label-input) {
                z-index: 3;
            }

            .label-wrapper > .label-text,
            .label-wrapper > .label-input {
                font-size: var(--text-size);
                vertical-align: top;
                display: inline-block;
                pointer-events: all;
            }

            .label-wrapper > .label-text {
                padding: 2px;
                transition: background-color 0.5s ease-out;
                background-color: var(--label-background-color);
            }

            .label-wrapper > .label-text.read-only {
                pointer-events: none;
            }

            .label-wrapper > .label-text.editable:hover {
                padding: 1px;
                cursor: pointer;
                border: 1px solid var(--color-border-action-hover);
                background-color: var(--label-hover-background-color);
            }

            .label-wrapper > .label-input {
                position: absolute;
                top: 0;
                left: 0;
                padding-inline: 1px;
                width: 100%;
                color: inherit;
                border: 1px solid var(--color-border-action-hover);
                background-color: var(--label-hover-background-color);
                outline: none;
            }

            .label-wrapper > .label-input:focus {
                outline: none;
            }
        `,
    ];
};
