/**
 * Calculates the squared distance between two points in a 2D space.
 *
 * @template T - The type of the points, which should support access to their x and y coordinates
 * @param {T} p1 - The first point.
 * @param {T} p2 - The second point.
 * @param {(p: T) => number} accessorX - A function that extracts the x-coordinate from a point of type T.
 * @param {(p: T) => number} accessorY - A function that extracts the y-coordinate from a point of type T.
 * @param {number} p1Idx - Index of the point p1 in the points array
 * @param {number} p2Idx - Index of the point p2 in the points array
 * @param {T[]} points - The points array
 *
 * @returns {number} The squared distance between the two points.
 *
 */
function getSquareDistance<T>(
    p1: T,
    p2: T,
    accessorX: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number),
    accessorY: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number),
    p1Idx: number,
    p2Idx: number,
    points: T[]
): number {
    const dx = accessorX(p1, p1Idx, points) - accessorX(p2, p2Idx, points);
    const dy = accessorY(p1, p1Idx, points) - accessorY(p2, p2Idx, points);
    return dx * dx + dy * dy;
}

/**
 * Computes the squared distance from a point to a line segment.
 *
 * The function first determines the projection of the point onto the line defined by the segment.
 * If this projection falls outside the segment, it adjusts to the nearest endpoint of the segment.
 *
 * @template T - The type of the points, which should support access to their x and y coordinates.
 * @param {T} p - The point for which the distance to the segment is being calculated.
 * @param {T} p1 - The first endpoint of the line segment.
 * @param {T} p2 - The second endpoint of the line segment.
 * @param {(p: T) => number} accessorX - A function that extracts the x-coordinate from a point of type T.
 * @param {(p: T) => number} accessorY - A function that extracts the y-coordinate from a point of type T.
 * @param {number} pIdx - Index of the point p in the points array
 * @param {number} p1Idx - Index of the point p1 in the points array
 * @param {number} p2Idx - Index of the point p2 in the points array
 * @param {T[]} points - The array of points
 *
 * @returns {number} The squared distance from the point to the line segment.
 *
 * @example
 */
function getSquareSegmentDistance<T>(
    p: T,
    p1: T,
    p2: T,
    accessorX: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number),
    accessorY: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number),
    pIdx: number,
    p1Idx: number,
    p2Idx: number,
    points: T[]
): number {
    let x = accessorX(p1, p1Idx, points);
    let y = accessorY(p1, p1Idx, points);
    const dx = accessorX(p2, p2Idx, points) - x;
    const dy = accessorY(p2, p2Idx, points) - y;

    if (dx !== 0 || dy !== 0) {
        const t = ((accessorX(p, pIdx, points) - x) * dx + (accessorY(p, pIdx, points) - y) * dy) / (dx * dx + dy * dy);

        if (t > 1) {
            x = accessorX(p2, p2Idx, points);
            y = accessorY(p2, p2Idx, points);
        } else if (t > 0) {
            x += dx * t;
            y += dy * t;
        }
    }

    const dpx = accessorX(p, pIdx, points) - x;
    const dpy = accessorY(p, pIdx, points) - y;

    return dpx * dpx + dpy * dpy;
}

/**
 * Simplifies a list of points by removing points that are within a specified squared distance from the previous point.
 *
 * This function performs radial distance-based simplification by iterating through the points and only
 * keeping points that are further than a given squared tolerance from the last kept point. This is useful
 * for reducing the number of points in a dataset while preserving the overall shape.
 *
 * @template T - The type of the points, which should support access to their x and y coordinates.
 * @param {T[]} points - The array of points to be simplified.
 * @param {number} sqTolerance - The squared distance threshold for simplification. Points closer than this distance
 *                                to the previous point are removed.
 * @param {(p: T) => number} accessorX - A function that extracts the x-coordinate from a point of type T.
 * @param {(p: T) => number} accessorY - A function that extracts the y-coordinate from a point of type T.
 *
 * @returns {T[]} A new array of points with simplification applied. Only points that are further than the squared
 *                 tolerance from the previous point are included.
 *
 * @example
 */
function simplificationRadialDist<T>(
    points: T[],
    sqTolerance: number,
    accessorX: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number),
    accessorY: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number)
): T[] {
    let prevPoint = points[0];
    let prevPointIdx = 0;
    const newPoints = [prevPoint];
    let point;

    for (let i = 1, len = points.length; i < len; i++) {
        point = points[i];
        if (getSquareDistance(point, prevPoint, accessorX, accessorY, i, prevPointIdx, points) > sqTolerance) {
            newPoints.push(point);
            prevPoint = point;
            prevPointIdx = i;
        }
    }

    if (prevPoint !== point) newPoints.push(point);

    return newPoints;
}

/**
 * Recursively simplifies a portion of a list of points using the Ramer-Douglas-Peucker algorithm.
 *
 * This function is a step in the Ramer-Douglas-Peucker (RDP) algorithm, which recursively simplifies a
 * polyline by removing points that are within a specified tolerance from the line segment connecting
 * two end points. The goal is to reduce the number of points while preserving the overall shape of the
 * polyline. This function works on a segment of the polyline defined by indices `first` and `last`.
 *
 * @template T - The type of the points, which should support access to their x and y coordinates.
 * @param {T[]} points - The array of points to be simplified.
 * @param {number} first - The index of the first point in the current segment.
 * @param {number} last - The index of the last point in the current segment.
 * @param {number} sqTolerance - The squared distance tolerance for deciding whether to keep a point.
 * @param {T[]} simplified - The array of simplified points that will be updated by this function.
 * @param {(p: T) => number} accessorX - A function that extracts the x-coordinate from a point of type T.
 * @param {(p: T) => number} accessorY - A function that extracts the y-coordinate from a point of type T.
 *
 * @returns {void} This function does not return a value; it modifies the `simplified` array in place.
 *
 * @example
 * see test
 */
function simplifyDPStep<T>(
    points: T[],
    first: number,
    last: number,
    sqTolerance: number,
    simplified: T[],
    accessorX: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number),
    accessorY: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number)
) {
    let maxSqDist = sqTolerance;
    let index: number | undefined;

    for (let i = first + 1; i < last; i++) {
        const sqDist = getSquareSegmentDistance(
            points[i],
            points[first],
            points[last],
            accessorX,
            accessorY,
            i,
            first,
            last,
            points
        );
        if (sqDist > maxSqDist) {
            index = i;
            maxSqDist = sqDist;
        }
    }

    if (index !== undefined && maxSqDist > sqTolerance) {
        if (index - first > 1) simplifyDPStep(points, first, index, sqTolerance, simplified, accessorX, accessorY);
        simplified.push(points[index]);
        if (last - index > 1) simplifyDPStep(points, index, last, sqTolerance, simplified, accessorX, accessorY);
    }
}

/**
 * Simplifies a list of points using the Ramer-Douglas-Peucker (RDP) algorithm.
 *
 * This function applies the Ramer-Douglas-Peucker (RDP) algorithm to simplify a polyline by reducing the number
 * of points while preserving the overall shape of the polyline. The RDP algorithm works by recursively removing
 * points that are within a specified tolerance from the line segment connecting two end points.
 *
 * @template T - The type of the points, which should support access to their x and y coordinates.
 * @param {T[]} points - The array of points to be simplified.
 * @param {number} sqTolerance - The squared distance tolerance for deciding whether to keep a point. Points with
 *                                a distance greater than this from the line segment connecting two endpoints are kept.
 * @param {(p: T) => number} accessorX - A function that extracts the x-coordinate from a point of type T.
 * @param {(p: T) => number} accessorY - A function that extracts the y-coordinate from a point of type T.
 *
 * @returns {T[]} A new array of points representing the simplified polyline. Only points that are further than the
 *                 squared tolerance from the line segments are included.
 *
 * @example
 * see test
 */
function simplificationDouglasPeucker<T>(
    points: T[],
    sqTolerance: number,
    accessorX: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number),
    accessorY: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number)
): T[] {
    const last = points.length - 1;
    const simplified = [points[0]];

    simplifyDPStep(points, 0, last, sqTolerance, simplified, accessorX, accessorY);
    simplified.push(points[last]);

    return simplified;
}

/**
 * Lossy Simplifies a list of points using a combination of radial distance and Ramer-Douglas-Peucker (RDP) algorithms.
 *
 * This function performs simplification of a polyline by first applying a radial distance-based simplification
 * (if `highestQuality` is `false`), and then applying the Ramer-Douglas-Peucker (RDP) algorithm to further
 * simplify the resulting points. This two-step approach helps to balance performance and quality.
 *
 * @template T - The type of the points, which should support access to their x and y coordinates.
 * @param {T[]} points - The array of points to be simplified.
 * @param {number} [tolerance=1] - The distance tolerance for simplification. Points with a distance greater than this
 *                                  threshold will be kept. The tolerance is squared for the internal calculations.
 * @param {boolean} [highestQuality=false] - If `true`, only the RDP algorithm is applied without radial distance
 *                                            simplification. If `false`, radial distance simplification is applied
 *                                            before RDP to improve performance.
 * @param {(p: T) => number} [accessorX=(p: T) => (p as { x: number }).x] - A function to extract the x-coordinate from a point
 *                                                                            of type T. Defaults to accessing `x` property.
 * @param {(p: T) => number} [accessorY=(p: T) => (p as { y: number }).y] - A function to extract the y-coordinate from a point
 *                                                                            of type T. Defaults to accessing `y` property.
 *
 * @returns {T[]} A new array of points representing the simplified polyline. The result is a reduced set of points
 *                 based on the tolerance and quality settings.
 *
 * @example
 * see test
 */
export function simplification<T>(
    points: T[],
    tolerance = 1,
    highestQuality = false,
    accessorX: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number) = (p: T) =>
        (p as { x: number; y: number }).x,
    accessorY: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number) = (p: T) =>
        (p as { x: number; y: number }).y
): T[] {
    if (points.length <= 2) return points;

    const sqTolerance = tolerance * tolerance;

    points = highestQuality ? points : simplificationRadialDist(points, sqTolerance, accessorX, accessorY);
    return simplificationDouglasPeucker(points, sqTolerance, accessorX, accessorY);
}

const calc_slope = (x0: number, y0: number, xi: number, yi: number, epsilon: number) => {
    const dx = xi - x0;
    const dy = yi - y0;

    if (Math.abs(dx) < 1e-9) {
        return { angleTop: Infinity, angleBottom: -Infinity };
    }

    const angle = Math.atan2(dy, dx);
    return { angleTop: angle + epsilon, angleBottom: angle - epsilon };
};

const split_iteration = <T>(
    points: T[],
    offset: number,
    epsilon: number,
    accessorX: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number),
    accessorY: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number)
) => {
    if (offset >= points.length - 1) {
        return undefined;
    }

    const x0 = accessorX(points[offset], offset, points);
    const y0 = accessorY(points[offset], offset, points);
    let xLast = accessorX(points[offset + 1], offset + 1, points);
    let yLast = accessorY(points[offset + 1], offset + 1, points);

    let { angleTop, angleBottom } = calc_slope(x0, y0, epsilon, xLast, yLast);

    for (let i = offset + 2; i < points.length; ++i) {
        const xi = accessorX(points[i], i, points);
        const yi = accessorY(points[i], i, points);
        const { angleTop: currentTop, angleBottom: currentBottom } = calc_slope(x0, y0, xi, yi, epsilon);
        const { angleTop: lastTop, angleBottom: lastBottom } = calc_slope(xLast, yLast, xi, yi, epsilon);
        angleTop = Math.min(angleTop, currentTop, lastTop);
        angleBottom = Math.max(angleBottom, currentBottom, lastBottom);
        if (angleTop < angleBottom) {
            return i - 1;
        }
        xLast = xi;
        yLast = yi;
    }
    return points.length - 1;
};

export const simplifyFunneling = <T>(
    points: T[],
    epsilonRad: number,
    accessorX: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number) = (p: T) =>
        (p as { x: number; y: number }).x,
    accessorY: ((p: T) => number) | ((p: T, idx: number, ps: T[]) => number) = (p: T) =>
        (p as { x: number; y: number }).y
) => {
    const keep: number[] = [];
    let split: number | undefined = 0;
    while (split !== undefined) {
        keep.push(split);
        split = split_iteration(points, split, epsilonRad, accessorX, accessorY);
    }

    return keep.map((i) => points[i]);
};
