import { consume, createContext } from '@lit-labs/context';
import { BaseElement, PropertyValues, state } from '@umetrics/sartorius-ui-base';
import { Constructor } from '../data/types';

export const renderUpdateContext = createContext<string[] | undefined>('render-update-context');

export declare abstract class WithRenderUpdateTriggerInterface extends BaseElement {
    rerender?: string[];

    wasUpdated(updated: string[]): void;

    renderUpdate(toUpdate: string[]): Promise<string[]> | string[];
}

const _uniqueString = (s: string, idx: number, sa: string[]) => sa.indexOf(s) === idx;

export const WithRenderUpdateTrigger = <T extends Constructor<BaseElement>>(superClass: T) => {
    abstract class RenderUpdateAware extends superClass {
        @consume({ context: renderUpdateContext, subscribe: true })
        @state()
        rerender?: string[];

        wasUpdated(updated: string[]) {
            this.dispatchEvent(new CustomEvent('rerender-scheduled', { bubbles: true, detail: { updated } }));
        }

        async willUpdate(_changedProperties: PropertyValues) {
            super.willUpdate(_changedProperties);
            if (_changedProperties.has('rerender')) {
                if (this.rerender?.length) {
                    const toUpdate = this.rerender.filter(_uniqueString);
                    const updated = await this.renderUpdate(toUpdate);
                    this.wasUpdated(updated);
                }
            }
        }

        abstract renderUpdate(toUpdate: string[]): Promise<string[]> | string[];
    }

    return RenderUpdateAware as Constructor<WithRenderUpdateTriggerInterface> & T;
};
