import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    nothing,
    property,
    query,
    state,
    styleMap,
} from '@umetrics/sartorius-ui-base';
import { Point2D } from '@umetrics/sartorius-ui-plot-utils';
import { Delaunay } from 'd3-delaunay';
import { namespacedTag } from '../../tag';
import { nodesStyles } from './nodesStyles';
import { version } from '../../metadata';
import { Plottable } from '../../plottable/plottableMixin';
import { WithDataContext } from '../../data/dataContext';
import { WithContext } from '../../data/plotContext';
import { LinearScale } from '../../data';
import { emptyBox, findDataAllPointsByX, joinData } from '../../utils';
import { SVGShape } from '../../plottable/svg/svgShape';

interface ClosestPointResult {
    x: number;
    y: number;
    dx: number;
    dy: number;
    scaleX: LinearScale;
    scaleY: LinearScale;
    lineIndex: number;
}

export interface NodesProperties {
    activeLineIndex?: number;
    disableCursor?: boolean;
    pixelProximityThreshold?: number;
    onAction?: (e: CustomEvent<NodesEvent>) => void;
    onMove?: (e: CustomEvent<NodesEvent>) => void;
    onLeave?: () => void;
}

export interface NodeMetadata {
    data: Point2D<number | null | undefined>[];
    positions: Point2D<number | null>[];
}

export interface NodesEvent extends Point2D<number>, ClosestPointResult {
    metadata: NodeMetadata;
}

@defineElement({ tagName: namespacedTag('nodes'), styles: nodesStyles, version })
export class Nodes extends Plottable(WithDataContext(WithContext(BaseElement))) implements NodesProperties {
    @property()
    activeLineIndex = -1;

    @property()
    disableCursor = false;

    @property({ type: String })
    filterDataByTag = 'SVG-LINE';

    @property({ type: Number })
    pixelProximityThreshold = 10;

    @property({ type: Number })
    searchRadius = 20;

    onAction;
    onMove;
    onLeave;

    @state()
    private cursor: ClosestPointResult | null = null;

    @query('.container')
    private _container: HTMLDivElement | undefined;

    private get containerBox() {
        return this._container?.getBoundingClientRect() ?? emptyBox;
    }

    private get allFilteredData() {
        return (
            this.allDataElements
                ?.filter((el) => el.tagName.endsWith(this.filterDataByTag))
                .map((el) => {
                    return {
                        scales: (el as SVGShape).getScales() as [LinearScale, LinearScale],
                        data: el.plotData,
                    };
                }) ?? []
        );
    }

    render() {
        const [rootClasses, baseClassName] = createClassName(this.tagName.toLowerCase(), {});
        if (!this.size || !this.getScales()) return;

        const [width, height] = this.size ?? [0, 0];
        return html`
            <div
                @mousemove=${this._handleContainerMouseOver}
                @mouseleave=${this._handleContainerMouseOut}
                width=${width}
                height=${height}
                class="container ${rootClasses} ${baseClassName}"
            >
                <slot></slot>
                ${this._renderHoverCursor()}
            </div>
        `;
    }

    private _renderHoverCursor() {
        if (!this.cursor) return nothing;

        const { x, y } = this.cursor;

        const cursorStyle = {
            position: 'absolute',
            transform: `translate(${x}px, ${y}px)`,
        };

        return this.disableCursor
            ? nothing
            : html`
                  <div id="hover" @click=${(e) => this._placeNoteAtClosestPoint(e)} style=${styleMap(cursorStyle)}>
                      <slot name="cursor"></slot>
                  </div>
              `;
    }

    private _getMetadata(x: number) {
        const { height } = this.containerBox;
        const [scaleX] = this.getScales() as [LinearScale, LinearScale];
        const data = findDataAllPointsByX(scaleX(x), this.allFilteredData, {
            pixelProximityThreshold: this.pixelProximityThreshold,
        });

        const positions = data.map((d) => {
            const [_scaleX, _scaleY] = d.scales;
            return {
                x: d.x != null ? _scaleX(d.x) : null,
                y: d.y != null ? _scaleY(d.y) - height : null,
            };
        });

        return {
            data,
            positions,
        };
    }

    private _handleContainerMouseOver(event: MouseEvent) {
        const detail = this._buildPayload(event);

        if (detail == null) {
            if (this.cursor != null) {
                this.dispatchEvent(new CustomEvent<NodesEvent>('leave'));
            }

            this.cursor = null;
            return;
        }

        this.cursor = {
            x: detail.x,
            y: detail.y,
            dx: detail.scaleX(detail.x),
            dy: detail.scaleY(detail.y),
            scaleX: detail.scaleX,
            scaleY: detail.scaleY,
            lineIndex: detail.lineIndex,
        };
        this.dispatchEvent(new CustomEvent<NodesEvent>('move', { detail }));
    }

    private _handleContainerMouseOut() {
        this.cursor = null;
    }

    private _placeNoteAtClosestPoint(event: MouseEvent) {
        if (!this._container) return;
        const detail = this._buildPayload(event);
        if (detail != null) {
            this.dispatchEvent(new CustomEvent<NodesEvent>('action', { detail }));
        }
    }

    private _buildPayload(event: MouseEvent) {
        const closest = this._getClosestPoint(event);
        if (!closest) return null;

        const metadata = this._getMetadata(closest.x);

        return {
            ...closest,
            metadata,
        };
    }

    private _getClosestPoint(event: MouseEvent): ClosestPointResult | null {
        const [offsetX, offsetY] = this._getOffset(event);
        const allData = this.allFilteredData;

        // active index needed to select specific line
        const linesToConsider =
            this.activeLineIndex !== -1 && allData.length > this.activeLineIndex
                ? [allData[this.activeLineIndex]]
                : allData;

        let bestDistance = Infinity;
        let bestCandidate: ClosestPointResult | null = null;

        linesToConsider.forEach(({ scales, data }, lineIndex) => {
            const [lineScaleX, lineScaleY] = scales;
            const { flatData } = joinData([data.flat()]);
            if (!flatData.length) return;

            const delaunay = Delaunay.from(
                flatData,
                (d) => lineScaleX(d.x),
                (d) => lineScaleY(d.y)
            );

            const idx = delaunay.find(offsetX, offsetY);
            if (idx === -1) return;

            const candidate = flatData[idx];
            const px = lineScaleX(candidate.x);
            const py = lineScaleY(candidate.y);

            const dx = offsetX - px;
            const dy = offsetY - py;
            const dist = Math.sqrt(dx * dx + dy * dy);

            if (dist < bestDistance) {
                bestDistance = dist;
                bestCandidate = {
                    x: lineScaleX(candidate.x),
                    y: lineScaleY(candidate.y),
                    dx: candidate.x,
                    dy: candidate.y,
                    scaleX: lineScaleX,
                    scaleY: lineScaleY,
                    lineIndex,
                };
            }
        });

        // important!!! if the best distance is beyond the users threshold, return null
        if (!bestCandidate || bestDistance > this.searchRadius) {
            return null;
        }

        return bestCandidate;
    }

    private _getOffset(event: MouseEvent) {
        if (!this._container) return [0, 0];
        const { left, top } = this.containerBox;
        const { clientX, clientY } = event;
        const offsetX = clientX - left;
        const offsetY = clientY - top;
        return [offsetX, offsetY];
    }
}
