import { Point2D } from '@umetrics/sartorius-ui-plot-utils';
import { Point } from '../../plottable';

export const LabelAlignmentOptions = ['top', 'left', 'bottom', 'right', 'center'] as const;
export type LabelAlignment = (typeof LabelAlignmentOptions)[number];

export interface PlacementOption {
    type: 'pixel' | 'percent';
    amount: number;
}

export interface LabelSettings {
    noAngle?: boolean;
    horizontalLabels?: boolean;
    labelFormatter?: (data: any, userData?: any) => string;
    backgroundColor?: string;
    hoverBackgroundColor?: string;
    offsetHorizontal?: number;
    offsetVertical?: number;
    textColor?: string;
    textOpacity?: number;
    placement?: PlacementOption;
    alignment?: LabelAlignment;
    editable?: boolean;
    hideCollisions?: boolean;
    collisionAvoidance?: boolean;
}

export interface LabelItem {
    id: string;
    point: Point2D<number>;
    originalPoint?: Point;
    angleInRadians?: number;
    visible?: boolean;
    textColor?: string;
    textOpacity?: number;
    backgroundColor?: string;
    hoverBackgroundColor?: string;
    offsetHorizontal: number;
    offsetVertical: number;
    originOffsetX: number;
    originOffsetY: number;
    editable: boolean;
    label: string;
}

export interface LabelSettingsArgs {
    index: number;
    pointIndex?: number;
    point?: Point2D;
    metadata?: any;
    plotId?: string;
}

export type LabelSettingsFunction = (args: LabelSettingsArgs) => LabelSettings;
