export const roundToSignificantDigits = (value: number, digits: number) => {
    if (value === 0) {
        return value;
    }

    const scale = Math.pow(10, digits - Math.ceil(Math.log10(Math.abs(value))));
    const rounded = Math.round(value * scale) / scale;
    return parseFloat(rounded.toPrecision(digits));
};

export const roundToPrecision2 = (value: number) => {
    // hardcoded instead of something more general to perform better
    return Math.round(value * 100) / 100;
};
