import { css, cssSelector, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const htmlRangeStyles: GenerateStyles = (tagName) => {
    const { root } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                width: 100%;
                height: 100%;
                overflow: hidden;
                z-index: 9999;
            }

            ${root} {
                width: 100%;
                height: 100%;
            }

            ::slotted(*) {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }

            .container {
                position: relative;
            }

            .hover-cursor {
                pointer-events: none;
                position: absolute;
            }

            .ruler-item {
                position: absolute;
                display: flex;
                flex-direction: column;
                height: 100%;
                z-index: 2;
                cursor: ew-resize;
            }

            .bar {
                position: absolute;
                height: 100%;
                z-index: 1;
                cursor: pointer;
            }
        `,
    ];
};
