import { BaseElement, createClassName, defineElement, html, type PropertyValueMap } from '@umetrics/sartorius-ui-base';
import { version } from '../metadata';
import { WithContext } from '../data/plotContext';
import { WithDataContext } from '../data/dataContext';
import { namespacedTag } from '../tag';
import { WithInteractionContext } from './interactionContext';
import { delaunayStyles } from './delaunayStyles';

export interface GestureInteractionProperties {
    onStationaryClick?;
}

@defineElement({ tagName: namespacedTag('gesture-interaction'), styles: delaunayStyles, version })
export class GestureInteraction
    extends WithInteractionContext(WithDataContext(WithContext(BaseElement)))
    implements GestureInteractionProperties
{
    // Static members

    // Private members

    // Protected members

    // Public members
    onStationaryClick;

    _clickState;

    constructor() {
        super();
        this._handleMouseDown = this._handleMouseDown.bind(this);
        this._handleMouseUp = this._handleMouseUp.bind(this);
    }

    disconnectedCallback(): void {
        super.disconnectedCallback();
        this.interactionTarget?.removeEventListener('mousedown', this._handleMouseDown);
        this.interactionTarget?.removeEventListener('mouseup', this._handleMouseUp);
    }

    protected firstUpdated(_changedProperties: PropertyValueMap<any> | Map<PropertyKey, unknown>): void {
        this.interactionTarget?.addEventListener('mousedown', this._handleMouseDown);
        this.interactionTarget?.addEventListener('mouseup', this._handleMouseUp);
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        return html`<div class="${rootClasses}"></div> `;
    }

    private _handleMouseDown(e) {
        this._clickState = {
            x: e.clientX,
            y: e.clientY,
        };
    }

    private _handleMouseUp(e) {
        if (!this._clickState) return;
        const { x: prevX, y: prevY } = this._clickState;
        const deltaX = e.clientX - prevX;
        const deltaY = e.clientY - prevY;
        const deltaSum = Math.abs(deltaX + deltaY);
        if (deltaSum < 0.01) this._dispatchStationaryClick();
        this._clickState = undefined;
    }

    private _dispatchStationaryClick() {
        this.dispatchEvent(new CustomEvent('stationary-click'));
    }
}
