import { css, CSSResultArray, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';
import { svgStyles } from './svgStyles';

/**
 * Base shape style
 *
 * @param tagName
 */
export const svgShapeStyles: GenerateStyles = (tagName) => {
    const { is } = cssSelector(tagName);
    //
    return [
        svgStyles(tagName),
        css`
            ${is('canvas', 'hover')} {
                cursor: var(--plot-cursor, pointer);
            }
        `,
    ] as CSSResultArray;
};
