import { calculateCoords, GeneratorType, LabelGenerator, PlotLabelData, RawLabelPoint } from './labelGenerators';
import { SVGScatter } from '../../plottable';
import { isFullySpecified, Point2D, QuadTree } from '@umetrics/sartorius-ui-plot-utils';
import { LabelSettings } from './labels';
import { RectangleCorners } from '@umetrics/sartorius-ui-plot-utils/dist/geometrics';

export class ScatterLabelGenerator implements LabelGenerator<SVGScatter, Point2D[]> {
    // Static members
    // private static _textMeasurer = new TextMeasurer();

    generatorType = GeneratorType.SCATTER;

    calculateLabels(
        scatter: SVGScatter,
        _defaultSettings: LabelSettings,
        _accumlated: QuadTree,
        transform: (data: Point2D[]) => Point2D[] = (data) => data
    ): PlotLabelData {
        let scatterDataSeries = scatter.data;
        if (!scatterDataSeries?.length) {
            return { items: [], plotSurface: [] };
        }
        if (!(scatterDataSeries[0] instanceof Array)) {
            scatterDataSeries = [scatterDataSeries];
        }
        const scales = scatter.getScales();
        if (!scales) {
            return { items: [], plotSurface: [] };
        }
        const [scaleX, scaleY] = scales;

        const domain = scatter.fittedDomain || scatter.getDomain2D();
        if (!domain) {
            return { items: [], plotSurface: [] };
        }

        const rawLabelPoints: (RawLabelPoint & { pointSize: number })[] = scatterDataSeries
            .map((series: Point2D[]) => series.filter((point: Point2D) => isFullySpecified(point)))
            .map(transform)
            .flatMap((series: Point2D[], idx: number) => {
                return series
                    .map((point: Point2D, pointIndex) => {
                        const userData = (point as Point2D & { userData?: any }).userData;
                        const pointSize = scatter.pointSize(point, pointIndex, idx);
                        return {
                            point,
                            originalPoint: point,
                            id: `scatter-plot-label-${scatter.id}-${pointIndex}`,
                            pointIdx: pointIndex,
                            seriesIdx: idx,
                            value: point,
                            userData,
                            type: 'scatterPoint',
                            pointSize,
                        };
                    })
                    .filter((l) => l.point);
            });

        const pointRects: RectangleCorners[] = rawLabelPoints.map((s) => {
            const screen: Point2D = { x: scaleX(s.point!.x) as number, y: scaleY(s.point!.y) as number };
            return calculateCoords({ x: 0, y: 0 }, screen, s.pointSize, s.pointSize);
        });

        return {
            items: rawLabelPoints.map((d) => {
                const rlp: any = { ...d };
                delete rlp.pointSize;
                return rlp as RawLabelPoint;
            }),
            plotSurface: pointRects,
        };
    }

    extremes(data: Point2D[]): Point2D[] {
        return data.reduce((result: Point2D[], next) => {
            if (!result.length) {
                return [next, next];
            }
            if (next.y < result[0].y) {
                return [next, result[1]];
            }
            if (next.y > result[1].y) {
                return [result[0], next];
            }
            return result;
        }, []);
    }
}
