import { BaseElement, cssVariables, property } from '@umetrics/sartorius-ui-base';
import { pipe } from '../genericUtils';

export type Constructor<T extends BaseElement> = abstract new (...args: any[]) => T;

export const remapCssVarColor = (color: string) => {
    const [match] = Array.from(color.matchAll(/var\(--(.+)\)/g), (m) => m[1]);
    return match ? cssVariables[match.replace(/-/g, '_')] : color;
};

export const remapRgbColor = (color: string) => {
    const [match] = Array.from(color.matchAll(/rgb\((.+)\)/g), (m) => m[1]);
    if (match) {
        return (
            '#' +
            match
                .split(',')
                .map((v) => Number(v))
                .map((v) => v.toString(16))
                .map((v) => (v.length < 2 ? `0${v}` : v))
                .join('')
        );
    }
    return color;
};
export const remapRgbaColor = (color: string) => {
    const [match] = Array.from(color.matchAll(/rgba\((.+)\)/g), (m) => m[1]);
    if (match) {
        const [r, g, b, a] = match.split(',').map((v) => Number(v));
        return (
            '#' +
            [r, g, b, Math.floor(a * 255)]
                .map((v) => v.toString(16))
                .map((v) => (v.length < 2 ? `0${v}` : v))
                .join('')
        );
    }
    return color;
};

export const extractAlphaFromHex = (color: string): [string, number] => {
    const alphaIndex = 1 + 2 * 3; // # + rgb
    if (color.startsWith('#') && color.length > alphaIndex) {
        const alphaHex = color.slice(alphaIndex);
        return [color.slice(0, alphaIndex), Number.parseInt(alphaHex, 16) / 255];
    }
    return [color, 1];
};

export const colorRemapper = pipe(remapCssVarColor, remapRgbColor, remapRgbaColor);

export interface WithColorProperties {
    color?: string;
    colorFunc?: (x, y, index?: number) => `#${string}` | string;
    opacityFunc?: (x, y, index?: number) => number;
}
export declare abstract class WithColorInterface extends BaseElement {
    color: string;
    opacity: number;
    exportOpacity: number;
    colorFunc?: (x, y, index?: number) => `#${string}` | string;
    opacityFunc?: (x, y, index?: number) => number;
}

export const WithColor = <T extends Constructor<BaseElement>>(superClass: T) => {
    abstract class PlottableElement extends superClass {
        @property()
        colorFunc?: (x, y, index?: number) => `#${string}` | string;
        @property()
        opacityFunc?: (x, y, index?: number) => number;

        private _color = 'black';
        private _opacity = 1.0;

        @property()
        set color(color: string) {
            const prev = this._color;
            if (prev !== color) {
                const remappedColor = colorRemapper(color);
                const [colorNoAlpha, opacity] = extractAlphaFromHex(remappedColor);
                this._color = colorNoAlpha;
                this._opacity = opacity;
                this.requestUpdate('color', prev);
            }
        }

        get color() {
            return this._color;
        }
        get opacity() {
            return this._opacity;
        }
        get exportOpacity() {
            const styleOpacity = this.style.opacity ? Number(this.style.opacity) : 1;
            return styleOpacity * this._opacity;
        }
    }
    return PlottableElement as Constructor<WithColorInterface> & T;
};
