import { WithDataContext } from '../dataContext';
import { WithContext } from '../plotContext';
import {
    BaseElement,
    createClassName,
    createRef,
    defineElement,
    html,
    nothing,
    property,
    PropertyValues,
    ref,
    Ref,
    state,
    styleMap,
    svg,
} from '@umetrics/sartorius-ui-base';
import { namespacedTag } from '../../tag';
import { labelsStyles } from './labelsStyles';
import { version } from '../../metadata';
import { Plottable, PlottableProperties } from '../../plottable/plottableMixin';
import { generateAll, generateExtremes } from './labelGenerators';
import { isFullySpecified, Point2D, roundToSignificantDigits } from '@umetrics/sartorius-ui-plot-utils';
import { LabelItem, LabelSettings } from './labels';
import { isFiniteNumber } from '../../interaction';
import { UUID } from '@umetrics/sartorius-ui-utils';
import { consume, createContext } from '@lit-labs/context';
import { Point } from '../../plottable';

export const labelSettingChangeContext = createContext<string>('label-setting-change');

export interface DataLabelsProperties extends PlottableProperties {
    editable: boolean;
    hide: boolean;
    defaultLabelSettings?: LabelSettings;
    data?: LabelItem[];
    offsetHorizontal?: number;
    offsetVertical?: number;
    showAll?: () => void;
    showExtremes?: () => void;
    remove?: () => void;
}

const round = (value: number, decimals: number) => {
    const num = Number(value + 'e' + decimals).valueOf();
    return Number(Math.round(num) + 'e-' + decimals);
};

const formatDataValue = (v: any) => {
    if (typeof v === 'number') {
        return round(v, 2);
    }
    return v.toString();
};

const defaultLabelSettings: LabelSettings = {
    horizontalLabels: true,
    noAngle: false,
    labelFormatter: (data: any, userData?: any) => {
        if (userData?.label !== undefined && userData.label !== null) {
            return userData.label;
        }
        if (data === undefined || data === null) {
            return '';
        }
        if (isFullySpecified(data)) {
            const p = data as Point2D;
            const x = formatDataValue(p.x);
            const y = formatDataValue(p.y);
            return `(${x}, ${y})`;
        }
        if (isFiniteNumber(data)) {
            return `${data.toFixed(2)}`;
        }
        return data.toString();
    },
    offsetVertical: 0,
    offsetHorizontal: 0,
    textColor: 'var(--color-text-default)',
    textOpacity: 1,
    backgroundColor: 'transparent',
    hoverBackgroundColor: 'var(--color-primary-2)',
    alignment: 'center',
    placement: {
        type: 'percent',
        amount: 50,
    },
    hideCollisions: false,
    collisionAvoidance: true,
    editable: false,
};

@defineElement({ tagName: namespacedTag('data-labels'), styles: labelsStyles, version })
export class DataLabels extends Plottable(WithDataContext(WithContext(BaseElement))) implements DataLabelsProperties {
    @property()
    editable = false;

    @property()
    hide = false;

    @property({
        hasChanged(value: LabelItem[], oldValue: LabelItem[]): boolean {
            return (
                value?.length !== oldValue?.length ||
                value?.some(
                    (item) => !oldValue.some((oldItem) => oldItem.id === item.id && oldItem.label === item.label)
                )
            );
        },
    })
    data: LabelItem[] = [];

    @property()
    defaultLabelSettings: LabelSettings = {};

    @property()
    offsetHorizontal = 0;

    @property()
    offsetVertical = 0;

    @state()
    changing = '';

    @state()
    plotLabelData: Record<string, LabelItem[]> = {};

    @consume({ context: labelSettingChangeContext, subscribe: true })
    @state()
    labelSettingChange?: string;

    includeInDomain = false;

    private labelData: LabelItem[] = [];

    private inputRef: Ref<HTMLInputElement> = createRef();

    public showExtremes() {
        const { allDataElements } = this;
        if (!allDataElements) {
            this.plotLabelData = {};
            this.labelData = this._mergeLabels();
        }

        const displayLabelsFrom = allDataElements?.filter((plottable) => plottable.displayLabels) || [];
        const generated = generateExtremes(displayLabelsFrom, this._getLabelSettings());
        this.plotLabelData =
            generated.reduce((labelItems: Record<string, LabelItem[]>, next) => {
                labelItems[next.id] = next.labels;
                return labelItems;
            }, {}) || {};

        this.labelData = this._mergeLabels();
    }

    public showAll() {
        const { allDataElements } = this;
        if (!allDataElements) {
            this.plotLabelData = {};
            this.labelData = this._mergeLabels();
        }

        const displayLabelsFrom = allDataElements?.filter((plottable) => plottable.displayLabels) || [];
        const generated = generateAll(displayLabelsFrom, this._getLabelSettings());
        this.plotLabelData =
            generated.reduce((labelItems: Record<string, LabelItem[]>, next) => {
                labelItems[next.id] = next.labels;
                return labelItems;
            }, {}) || {};

        this.labelData = this._mergeLabels();
    }

    public remove() {
        this.plotLabelData = {};
        this.data = [];
        this.labelData = this._mergeLabels();
    }

    /**
     * SVG export
     * First draft. Doesnt not handle background-color nor styling set by css variables.
     **/
    public exportSVG() {
        const scales = this.getScales();
        if (!scales) {
            return nothing;
        }
        const [scaleX, scaleY] = scales;
        const labelsSvg = this.labelData.map(
            ({
                point: p,
                label,
                visible,
                textColor,
                textOpacity,
                backgroundColor,
                originOffsetX,
                originOffsetY,
                offsetHorizontal,
                offsetVertical,
                angleInRadians,
            }) => {
                if (visible === false || !label) {
                    return nothing;
                }
                if (!scaleX(p.x) || !scaleY(p.y)) {
                    return nothing;
                }

                const screenX = (scaleX(p.x) as number) + offsetHorizontal + this.offsetHorizontal;
                const screenY = (scaleY(p.y) as number) + offsetVertical + this.offsetVertical;

                const angleInDegree = (angleInRadians || 0) * (180 / Math.PI);
                return svg`<g transform="translate(${screenX - originOffsetX}, ${screenY - originOffsetY}) rotate(${angleInDegree})"
                    style="background-color:${backgroundColor};transform-origin: ${originOffsetX}px ${originOffsetY}px;">
                    <text x="0" y="0" style="color: ${textColor};opacity: ${textOpacity || 1};">${label}</text></g>
                `;
            }
        );
        return svg`<g style="font-size: 0.75rem;dominant-baseline: hanging;">${labelsSvg}</g>`;
    }

    updated(changedProperties: Map<string, any>) {
        if (changedProperties.has('allDataElements')) {
            this.allDataElements?.forEach((dataElement) => {
                if (!dataElement) {
                    return;
                }
                if (!dataElement.id) {
                    dataElement.id = `plottable-${UUID()}`;
                }
            });
            this.showAll();
        }
        if (changedProperties.has('scaleMap')) {
            this.showAll();
        }
        if (changedProperties.has('changing') && this.changing) {
            this._focusInput();
        }
    }

    protected willUpdate(_changedProperties: PropertyValues) {
        if (_changedProperties.has('editable') || _changedProperties.has('labelSettingChange')) {
            this.defaultLabelSettings.editable = this.editable;
            this.showAll();
        }

        if (_changedProperties.has('data') || _changedProperties.has('plotLabelData')) {
            this.labelData = this._mergeLabels();
            this.dispatchEvent(new CustomEvent('labels-updated', { detail: { data: this.labelData } }));
        }
    }

    protected render() {
        if (this.hide) {
            return;
        }

        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {});
        const scales = this.getScales();
        if (!scales) {
            return null;
        }
        const [scaleX, scaleY] = scales;

        return html` <div class="${rootClasses}">
            ${this.labelData.map(
                ({
                    point: p,
                    originalPoint,
                    label,
                    id,
                    visible,
                    textColor,
                    textOpacity,
                    hoverBackgroundColor,
                    backgroundColor,
                    originOffsetX,
                    originOffsetY,
                    offsetHorizontal,
                    offsetVertical,
                    angleInRadians,
                    editable,
                }) => {
                    if (visible === false || !label) {
                        return null;
                    }
                    if (!scaleX(p.x) || !scaleY(p.y)) {
                        return null;
                    }

                    const screenX = (scaleX(p.x) as number) + offsetHorizontal + this.offsetHorizontal;
                    const screenY = (scaleY(p.y) as number) + offsetVertical + this.offsetVertical;

                    const labelStyles = {
                        transformOrigin: `${originOffsetX}px ${originOffsetY}px`,
                        transform: `translate(${screenX - originOffsetX}px, ${screenY - originOffsetY}px) rotate(${
                            angleInRadians || 0
                        }rad)`,
                        color: textColor,
                        opacity: textOpacity || 1,
                    };

                    const displayStyles = {
                        '--label-background-color': backgroundColor,
                        '--label-hover-background-color': hoverBackgroundColor,
                    };
                    return html`
                        <div style=${styleMap(labelStyles)} class="${baseClassName} label-wrapper">
                            ${this.changing === id
                                ? html`<input
                                      data-testid=${this._dataTestId(originalPoint)}
                                      style=${styleMap(displayStyles)}
                                      ${ref(this.inputRef)}
                                      value=${label}
                                      class="${baseClassName} label-input"
                                      type="text"
                                      @blur=${(e: InputEvent) => this._onInputBlur(e, p, id)}
                                      @keydown=${(e: KeyboardEvent) => this._onInputKeydown(e, id, p)}
                                  />`
                                : html` <div
                                      data-testid=${this._dataTestId(originalPoint)}
                                      class="${baseClassName} label-text${editable ? ' editable' : ' read-only'}"
                                      style=${styleMap(displayStyles)}
                                      @click=${editable ? () => this._onLabelClick(id) : undefined}
                                  >
                                      ${label}
                                  </div>`}
                        </div>
                    `;
                }
            )}
        </div>`;
    }

    private _dataTestId = (point: Point<any> | undefined) => {
        if (!point) {
            return undefined;
        }
        const values = [
            { lab: 'x', val: point.x },
            { lab: 'y', val: point.y },
            { lab: 'x1', val: point.x1 },
            { lab: 'y1', val: point.y1 },
        ]
            .filter((d) => d.val !== undefined && d.val !== null)
            .map(({ lab, val }) => ({ lab, val: val! }));

        const content = values
            .map(({ lab, val }) => ({ lab, val: typeof val === 'number' ? roundToSignificantDigits(val, 5) : val }))
            .map((d) => `${d.lab}: ${d.val}`)
            .join(',');

        if (!content) {
            return undefined;
        }

        return `(${content})`;
    };

    private _mergeLabels() {
        const plotPoints = Object.keys(this.plotLabelData)
            .flatMap((k) => this.plotLabelData[k])
            .filter(
                (l) => !this.data.some((dl) => dl.id === l.id || (dl.point.x === l.point.x && dl.point.y === l.point.y))
            )
            .filter((p, idx, points) => this._unique(p, idx, points));
        return [...plotPoints, ...this.data];
    }

    private _unique = ({ point }: LabelItem, idx: number, points: LabelItem[]) => {
        return points.findIndex((d) => d.point.x === point.x && d.point.y === point.y) === idx;
    };

    private _onInputBlur = (e: InputEvent, point: Point2D<number>, identifier: string) => {
        const target = e.target as HTMLInputElement;
        if (!target) {
            return;
        }
        const newLabel = target.value;

        this._updateLabel(identifier, point, newLabel);
    };

    private _updateLabel(identifier: string, p: Point2D<number>, newLabel: string) {
        if (!this.changing) {
            return;
        }
        const dataPoint = this.labelData.find(({ id }) => identifier === id);
        if (!dataPoint) {
            this.data = [
                ...this.data,
                {
                    point: p,
                    originalPoint: p,
                    label: newLabel,
                    id: identifier,
                    textColor: 'black',
                    originOffsetX: 0,
                    originOffsetY: 0,
                    offsetHorizontal: 0,
                    offsetVertical: 0,
                    editable: this.editable,
                },
            ];
        } else {
            const inData = this.data.findIndex((dp) => dp.id === identifier) !== -1;
            if (inData) {
                this.data = [...this.data.map((d) => (identifier === d.id ? { ...dataPoint, label: newLabel } : d))];
            } else {
                this.data = [...this.data, { ...dataPoint, label: newLabel }];
            }
        }

        if (this.changing === identifier) {
            this.changing = '';
        }
    }

    private _onLabelClick(id: string) {
        if (!this.editable) {
            return;
        }
        this.changing = id;
    }

    private _focusInput() {
        this.inputRef.value?.select();
    }

    private _onInputKeydown(e: KeyboardEvent, id: string, point: Point2D<number>) {
        if (e.key === 'Enter') {
            e.preventDefault();
            const label = this.inputRef.value?.value || '';
            this._updateLabel(id, point, label);
        } else if (e.key === 'Escape') {
            e.preventDefault();
            this.changing = '';
        }
    }

    private _getLabelSettings(): LabelSettings {
        return { ...defaultLabelSettings, ...this.defaultLabelSettings };
    }
}
