import { Domain, Domain2D } from '../data';

export interface RulerMoveOptions {
    startX: number; // Not only X, rename to startValue.
    currentX: number; // Not only X, rename to currentValue.
    viewbox: Domain2D;
    containerWidth: number; // Not only width, rename to containerSize.
    bounds: Domain;
    rulerWidth: number; // No only width, rename to rulerSize.
}

export function viewBoxToPx(viewbox: Domain, containerWidth: number, domainBounds: Domain) {
    const totalDomainWidth = domainBounds[1] - domainBounds[0];
    const viewboxWidth = viewbox[1] - viewbox[0];

    const left = ((viewbox[0] - domainBounds[0]) / totalDomainWidth) * containerWidth;
    const width = (viewboxWidth / totalDomainWidth) * containerWidth;

    return { left, width };
}

export function pxToViewbox(px: { left: number; width: number }, containerWidth: number, domainBounds: Domain) {
    const totalDomainWidth = domainBounds[1] - domainBounds[0];

    const viewboxMin = (px.left / containerWidth) * totalDomainWidth + domainBounds[0];
    const viewboxMax = ((px.left + px.width) / containerWidth) * totalDomainWidth + domainBounds[0];

    return [viewboxMin, viewboxMax];
}

// defines how close might be two thumbs (rulers)
export const MIN_THUMBS_GAP = 1;

export function handleLeftRulerMove(vertical: boolean, options: RulerMoveOptions): Domain2D {
    const { startX, currentX, viewbox, containerWidth, bounds, rulerWidth } = options;
    const diffX = currentX - startX;
    const viewboxDim = vertical ? viewbox.y : viewbox.x;
    const pxViewbox = viewBoxToPx([viewboxDim[0], viewboxDim[1]], containerWidth, bounds);
    let newLeftPx = pxViewbox.left + diffX;
    // Ensure the left ruler does not move past its current end position minus the ruler width
    const maxLeftPx = pxViewbox.left + pxViewbox.width - rulerWidth - MIN_THUMBS_GAP;
    newLeftPx = Math.min(newLeftPx, maxLeftPx);
    const newViewboxMin = pxToViewbox({ left: newLeftPx, width: pxViewbox.width }, containerWidth, bounds)[0];
    const boundedNewViewboxMin = Math.max(bounds[0], newViewboxMin);
    return vertical
        ? { ...viewbox, y: [boundedNewViewboxMin, viewboxDim[1]] }
        : { ...viewbox, x: [boundedNewViewboxMin, viewboxDim[1]] };
}

export function handleRightRulerMove(vertical: boolean, options: RulerMoveOptions): Domain2D {
    const { startX, currentX, viewbox, containerWidth, bounds, rulerWidth } = options;
    const diffX = currentX - startX;
    const viewboxDim = vertical ? viewbox.y : viewbox.x;
    const pxViewbox = viewBoxToPx([viewboxDim[0], viewboxDim[1]], containerWidth, bounds);
    let newRightPx = pxViewbox.left + pxViewbox.width + diffX;
    const minRightPx = pxViewbox.left + rulerWidth + MIN_THUMBS_GAP;
    newRightPx = Math.max(newRightPx, minRightPx);
    const newViewboxMax = pxToViewbox(
        { left: pxViewbox.left, width: newRightPx - pxViewbox.left },
        containerWidth,
        bounds
    )[1];
    const boundedNewViewboxMax = Math.min(bounds[1], newViewboxMax);
    return vertical
        ? { ...viewbox, y: [viewboxDim[0], boundedNewViewboxMax] }
        : { ...viewbox, x: [viewbox.x[0], boundedNewViewboxMax] };
}
