export const DEFAULT_EPSILON = 1e-9;

export const almostEqual = (a: number, b: number, epsilon: number = DEFAULT_EPSILON): boolean =>
    Math.abs(a - b) < epsilon;

export const greaterThanOrAlmostEqual = (a: number, b: number, epsilon: number = DEFAULT_EPSILON): boolean =>
    a > b || almostEqual(a, b, epsilon);

export const lessThanOrAlmostEqual = (a: number, b: number, epsilon: number = DEFAULT_EPSILON): boolean =>
    a < b || almostEqual(a, b, epsilon);

export const betweenOrAlmostEqual = (a: number, min: number, max: number, epsilon: number = DEFAULT_EPSILON) =>
    greaterThanOrAlmostEqual(a, min, epsilon) && lessThanOrAlmostEqual(a, max, epsilon);

export const definitelyLessThan = (a: number, b: number, epsilon: number = DEFAULT_EPSILON): boolean => a < b - epsilon;

export const definitelyLessThanOrEqual = (a: number, b: number, epsilon: number = DEFAULT_EPSILON): boolean =>
    a <= b - epsilon;

export const definitelyGreaterThan = (a: number, b: number, epsilon: number = DEFAULT_EPSILON): boolean =>
    a > b + epsilon;

export const definitelyGreaterThanOrEqual = (a: number, b: number, epsilon: number = DEFAULT_EPSILON): boolean =>
    a >= b + epsilon;

export const definitelyBetween = (a: number, min: number, max: number, epsilon: number = DEFAULT_EPSILON) =>
    definitelyGreaterThan(a, min, epsilon) && definitelyLessThan(a, max, epsilon);

export const definitelyBetweenOrEqual = (a: number, min: number, max: number, epsilon: number = DEFAULT_EPSILON) =>
    definitelyGreaterThanOrEqual(a, min, epsilon) && definitelyLessThanOrEqual(a, max, epsilon);
