import {
    BaseElement,
    createClassName,
    cssVariables,
    defineElement,
    html,
    property,
    svg,
} from '@umetrics/sartorius-ui-base';
import { version } from '../../metadata';
import { Plottable, PlottableProperties } from '../plottableMixin';
import { Dimension, WithContext } from '../../data/plotContext';
import { namespacedTag } from '../../tag';
import { svgStyles } from './svgStyles';
import { WithSettingsContext } from '../../settings/settingsContext';
import { getTimescaleXTicks, getXTicks, getYTicks } from '../../scaleUtil';
import { DateScale, ScaleWithMeta } from '../../data';
import { Settings } from '@umetrics/sartorius-ui-plot-utils';

const densityFilter = <T>(array: T[], percentage: number): T[] => {
    const result: T[] = [];

    if (percentage <= 0) {
        return result;
    }

    const step = Math.ceil(1 / percentage);

    for (let i = 0; i < array.length; i++) {
        if (i % step === 0) {
            result.push(array[i]);
        }
    }

    return result;
};

export interface SVGGridProperties extends PlottableProperties {
    grid?: GridMode;
    origin?: GridMode;
    density?: number;
    color?: string;
    originColor?: string;
}
export type GridMode = 'default' | 'vertical' | 'horizontal' | 'off';

@defineElement({ tagName: namespacedTag('svg-grid'), styles: svgStyles, version })
export class SVGGrid extends WithSettingsContext(Plottable(WithContext(BaseElement))) implements SVGGridProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    @property({ type: String, reflect: false, attribute: 'grid' })
    grid: GridMode = 'default';
    @property({ type: String, reflect: false, attribute: 'origin' })
    origin: GridMode = 'default';
    @property({ type: Number, reflect: false })
    densityX: number | undefined = undefined;
    @property({ type: Number, reflect: false })
    densityY: number | undefined = undefined;

    @property()
    color = cssVariables.color_secondary_2;
    @property()
    originColor = cssVariables.color_secondary_5;

    public exportSVG() {
        return this.renderGrid();
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});
        if (!this.size || !this.getScales() || !this.settings) return;
        const [width, height] = this.size;

        return html` <svg width=${width} height=${height} class="${rootClasses}">${this.renderGrid()}</svg> `;
    }

    protected renderGrid() {
        if (!this.size || !this.getScales() || !this.settings) return;
        const [width, height] = this.size;
        const [scaleXMeta, scaleYMeta] = this.getScalesWithMeta();
        const scaleX = scaleXMeta.scale;
        const scaleY = scaleYMeta.scale;

        const [xTicks, yTicks] = this.getGridTicks(this.size, scaleXMeta, scaleYMeta, this.settings);

        const verticalPath = xTicks.map((v) => `M${scaleX(v)},0 V${height}`).join(' ');
        const horizontalPath = yTicks.map((v) => `M0,${scaleY(v)} H${width}`).join(' ');

        const originX = scaleX(0);
        const originY = scaleY(0);
        const originPath =
            (originX && this.origin !== 'vertical' ? `M${originX},0 V${height} ` : '') +
            (originY && this.origin !== 'horizontal' ? `M0,${originY} H${width}` : '');
        const originHtml =
            originPath && this.origin !== 'off'
                ? svg`<path d="${originPath}" stroke="${this.originColor}" class="origin-grid" />`
                : null;

        const verticalHtml =
            this.grid === 'vertical' || this.grid === 'default' ? svg`<path d="${verticalPath}" />` : null;
        const horizontalHtml =
            this.grid === 'horizontal' || this.grid === 'default' ? svg`<path d="${horizontalPath}" />` : null;

        return svg`
            <g stroke="${this.color}">
                ${verticalHtml}
                ${horizontalHtml}
                ${originHtml}
            </g>
        `;
    }

    private getGridTicks(
        viewportSize: Dimension,
        scaleXMeta: ScaleWithMeta,
        scaleYMeta: ScaleWithMeta,
        settings: Settings
    ): [number[], number[]] {
        const [width, height] = viewportSize;

        const rangeW = scaleXMeta.scale.range();
        const scaleWidth = Math.abs(rangeW[1] - rangeW[0]);
        let xTicks: number[] =
            scaleXMeta.scaleType === 'timescale'
                ? getTimescaleXTicks(settings.axis.x, scaleXMeta.scale as DateScale, width)
                : getXTicks(settings.axis.x, scaleXMeta, width, width);

        if (this.densityX != null) {
            xTicks = densityFilter(getXTicks(settings.axis.x, scaleXMeta, scaleWidth, width), this.densityX);
        }

        let yTicks: number[] = [];
        if (this.densityY != null) {
            const rangeH = scaleYMeta.scale.range();
            const scaleHeight = Math.abs(rangeH[0] - rangeH[1]);

            yTicks = densityFilter(getYTicks(settings.axis.y, scaleYMeta, scaleHeight, height), this.densityY);
        } else {
            yTicks = getYTicks(settings.axis.y, scaleYMeta, height, height);
        }

        return [xTicks, yTicks];
    }
}
