import { css, cssSelector, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const sceneRendererStyle: GenerateStyles = (tagName) => {
    const { root } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                width: 100%;
                height: 100%;
            }

            ${root} {
                width: 100%;
                height: 100%;
                display: block;
            }
        `,
    ];
};
