export interface AxisSettings {
    tickFontSize: number;
    titleFontSize: number;
    labelWidth?: 'auto' | number;
    /**
     * Whether to apply css `word-break: pre-line` rule to break label that contains newline characters
     */
    breakNewLine?: boolean;
    /**
     * Minimum expected gap in pixels between labels, default is 5
     */
    labelGapMin?: number;
    /**
     * An approximate number of ticks to be rendered on an axis. Default is `undefined`.
     * For a `log` scale an additional `minor` ticks appear between an integer ticks, makes sense
     * in case of significant zoom
     */
    estimatedTickCount?: number;
    /**
     * Whether to render axis as a `brace` with an additional pseudo ticks on each edge, default is `true`
     */
    includeEdgePseudoTicks?: boolean;
}

export interface XAxisSettings extends AxisSettings {
    suppressLabelTilt?: boolean;
}

export interface YAxisSettings extends AxisSettings {
    labelOrientation?: 'horizontal' | 'vertical';
}

export type AxisStyle = 'default' | 'tilt45' | 'hidden';

export interface Settings {
    heading: {
        fontSize: number;
    };
    axis: {
        x: XAxisSettings;
        y: YAxisSettings;
    };
    traceSize: {
        line: number;
        point: number;
    };
    axisStyle?: AxisStyle;
}

export const defaultSettings: Settings = {
    heading: {
        fontSize: 14,
    },
    axis: {
        x: {
            tickFontSize: 12,
            titleFontSize: 14,
        },
        y: {
            tickFontSize: 12,
            titleFontSize: 14,
        },
    },
    traceSize: {
        line: 1,
        point: 1,
    },
};
