import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    property,
    type PropertyValueMap,
    type PropertyValues,
} from '@umetrics/sartorius-ui-base';
import { version } from '../metadata';
import { WithContext } from '../data/plotContext';
import { delaunayStyles } from './delaunayStyles';
import { WithDataContext } from '../data/dataContext';
import { Delaunay } from 'd3-delaunay';
import { namespacedTag } from '../tag';
import { WithInteractionContext } from './interactionContext';
import type { Point2D } from '@umetrics/sartorius-ui-plot-utils';
import { PlottableInterface } from '../plottable/plottableMixin';

const joinData = (allData: Point2D[][]) => {
    const dataIndex = allData.reduce<number[]>((acc, d) => {
        // if (!d?.length) return acc;
        const lastVal = acc.slice(-1)[0] ?? 0;
        return [...acc, lastVal + d.length];
    }, []);
    const flatData = allData.flat(1);
    return {
        dataIndex,
        flatData,
    };
};

export interface DelaunayProperties {
    distanceThreshold?: number;
    crosshair?: boolean;

    onPointHover?: any;
    onPointHoverEnd?: any;
}

@defineElement({ tagName: namespacedTag('delaunay'), styles: delaunayStyles, version })
export class DelaunayDiagram
    extends WithInteractionContext(WithDataContext(WithContext(BaseElement)))
    implements DelaunayProperties
{
    // Static members

    // Private members

    // Protected members

    // Public members
    @property()
    distanceThreshold = 5;
    @property()
    crosshair = false;

    private _delaunay;
    private _dataIndex;
    private _flatData;
    private _allScales;

    private _activePoint;

    private _mouse;

    constructor() {
        super();
        this._onMouseMove = this._onMouseMove.bind(this);
        this._onMouseLeave = this._onMouseLeave.bind(this);
        this._onClick = this._onClick.bind(this);
        this._shouldIncludeElement = this._shouldIncludeElement.bind(this);
    }

    disconnectedCallback(): void {
        super.disconnectedCallback();
        this.interactionTarget?.removeEventListener('mousemove', this._onMouseMove);
        this.interactionTarget?.removeEventListener('mouseleave', this._onMouseLeave);
        this.interactionTarget?.removeEventListener('click', this._onClick);
    }

    protected firstUpdated(_changedProperties: PropertyValueMap<any> | Map<PropertyKey, unknown>): void {
        this.interactionTarget?.addEventListener('mousemove', this._onMouseMove);
        this.interactionTarget?.addEventListener('mouseleave', this._onMouseLeave);
        this.interactionTarget?.addEventListener('click', this._onClick);
    }

    protected willUpdate(_changedProperties: PropertyValues<this>): void {
        super.willUpdate(_changedProperties);
        if (_changedProperties.has('allDataElements') || _changedProperties.has('scaleMap')) {
            if (this.allDataElements && this.viewbox) {
                const allData = this.allDataElements.filter(this._shouldIncludeElement).map((el) => el.plotData);
                //We need to use the scales for each element in case they are different
                const allScales = this.allDataElements
                    .filter(this._shouldIncludeElement)
                    .map((el: any) => el.getScales());
                const hasScales = allScales.some((s) => s);

                if (hasScales) {
                    const { dataIndex, flatData } = joinData(allData);
                    this._dataIndex = dataIndex;
                    this._flatData = flatData;
                    this._allScales = allScales;

                    //We need to remap undefined to null to avoid hanging on edge cases
                    this._delaunay = Delaunay.from(
                        flatData,
                        (d, i) => {
                            const scaleX = this._findScalesForPoint(i)?.[0];
                            return scaleX?.(d.x) ?? null;
                        },
                        (d, i) => {
                            const scaleY = this._findScalesForPoint(i)?.[1];
                            return scaleY?.(d.y) ?? null;
                        }
                    );
                }
            }
        }
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        return html`<div class="${rootClasses}">${this.renderCrosshair()}</div> `;
    }

    private renderCrosshair() {
        if (!this.crosshair || !this._allScales || !this.size || !this._mouse) return null;
        const [width, height] = this.size;
        const [x, y] = this._mouse;
        const crosshairPath = `M0 ${y} H${width} M${x} 0 V${height}`;
        return html` <svg width=${width} height=${height}>
            <path stroke-dasharray="4 4" stroke="black" d=${crosshairPath} opacity="0.2" />
            <circle cx=${x} cy=${y} r=${this.distanceThreshold} opacity="0.1" />
        </svg>`;
    }

    private _shouldIncludeElement(el: PlottableInterface) {
        return el.includeInDomain && el.slot === this.interactionTargetSlot;
    }

    private _findScalesForPoint(index: number) {
        if (!this._allScales || !this._dataIndex) return null;
        const plotIndex = this._dataIndex.findIndex((v) => v > index);
        return this._allScales[plotIndex];
    }

    private _onClick = (e) => {
        if (!this._activePoint) return;

        this.dispatchEvent(
            new CustomEvent('point-click', {
                bubbles: true,
                detail: {
                    evt: e,
                    ...this._getEventMetadata(this._activePoint),
                },
            })
        );
    };

    private _onMouseMove = (e) => {
        if (!this._delaunay || !this._allScales || !this._flatData || !this.interactionTarget || !this.allDataElements)
            return;
        const { left, top } = this.interactionTarget.getBoundingClientRect();
        const { clientX, clientY } = e;
        const offsetX = clientX - left;
        const offsetY = clientY - top;
        const closestPointIndex = this._delaunay.find(offsetX, offsetY);
        const closestPoint = this._flatData[closestPointIndex];
        if (!closestPoint) return;
        const [scaleX, scaleY] = this._findScalesForPoint(closestPointIndex);

        const { x, y } = closestPoint;
        const screenX = scaleX(x)! as number;
        const screenY = scaleY(y)! as number;
        const distX = screenX - offsetX;
        const distY = screenY - offsetY;
        const pixelDistance = Math.sqrt(distX ** 2 + distY ** 2);

        if (pixelDistance < this.distanceThreshold) {
            if (this._activePoint !== closestPointIndex) {
                this._activePoint = closestPointIndex;
                this.dispatchEvent(
                    new CustomEvent('point-hover', {
                        detail: {
                            ...this._getEventMetadata(closestPointIndex),
                            clientX,
                            clientY,
                        },
                        bubbles: true,
                    })
                );
            }
        } else {
            if (this._activePoint !== undefined) {
                this.dispatchEvent(
                    new CustomEvent('point-hover-end', {
                        bubbles: true,
                    })
                );
            }

            this._activePoint = undefined;
        }
        if (this.crosshair) {
            this._mouse = [offsetX, offsetY];
            this.requestUpdate();
        }
    };

    private _onMouseLeave = () => {
        if (this._activePoint !== undefined) {
            this.dispatchEvent(
                new CustomEvent('point-hover-end', {
                    bubbles: true,
                })
            );
        }
        this._activePoint = undefined;
    };

    private _getEventMetadata(closestPointIndex) {
        if (!this.allDataElements) return null;

        const plotIndex = this._dataIndex.findIndex((v) => v > closestPointIndex);
        const offset = this._dataIndex[plotIndex] - closestPointIndex;
        const path = [plotIndex, offset];
        const closestPoint = this._flatData[closestPointIndex];
        return {
            closestPoint,
            path,
            target: this.allDataElements.filter(this._shouldIncludeElement)[plotIndex],
        };
    }
}
