import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    ifDefined,
    property,
    PropertyValues,
    query,
    repeat,
    state,
    svg,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { composablePlotStyles } from './composablePlotStyle';
import { provide } from '@lit-labs/context';
import {
    Dimension,
    domainContext,
    plotRootElementContext,
    scaleContext,
    scaleZoomMapContext,
    sizeContext,
    WithContextInterface,
} from './data/plotContext';
import { PlottableInterface } from './plottable/plottableMixin';
import { Domain2D, domainMapEquals, isValidDomain, joinDomains, labelSettingChangeContext } from './data';
import { ResizeAwareMixin } from '@umetrics/sartorius-ui-mixins';
import { perfDebounce, UUID } from '@umetrics/sartorius-ui-utils';
import { dataContext } from './data/dataContext';
import { namespacedTag } from './tag';
import { defaultSettings, Settings } from '@umetrics/sartorius-ui-plot-utils';
import { settingsContext, settingsMerge } from './settings';
import { filterEntries, getRectOffset, groupBy, isDefined, mapEntries, mapValues } from './genericUtils';
import {
    applyScaleSettings,
    DomainMap,
    mergeScaleSettingsWithDefault,
    PartialScaleSettingsMap,
    ScaleSettingsMap,
    ScaleZoomMap,
} from './data/scale';
import {
    copySVGFileToClipboard,
    downloadPNGFile,
    downloadSVGFile,
    exportElement,
    renderSVGToElement,
    renderSVGToSVGImg,
} from './svgExport';
import { renderWatermarkSVG } from './watermark';
import { mouseEventContext, REGISTER_ZOOM_STRATEGY, ZoomStrategy } from './interaction';
import { defaultZoomLens } from './data/constants';
import { varToStyle } from './litUtils';
import { colorRemapper } from './plottable/colorMixin';
import type { Axis } from './axis';
import { renderUpdateContext } from './rendering/renderUpdateTrigger';

export interface ComposablePlotProperties {
    viewbox?: Domain2D;
    settings?: Settings;
    heading?: string;
    scaleSettings?: PartialScaleSettingsMap;
    zoomState?: ScaleZoomMap;
    watermark?: string;
    backgroundOpacity?: number /* 1 */;
    backgroundColor?: string /* none */;
    paddings?: [string, string?, string?, string?];
    slotMap?: SlotMap /* Override default slot names, if you dont like them... */;

    //Events
    onLineClick?;
    onLineHover?;
    onLineHoverEnd?;
    onDomainFitted?;
    onPointClick?;
    onPointHover?;
    onPointHoverEnd?;
}

interface SlotName {
    slotName: string;
}

interface SlotWithCardinality extends SlotName {
    cardinalityPrefix: string;
}

interface DefaultSlotWithCardinality {
    cardinalityPrefix: string;
}

interface SlotMap {
    main: DefaultSlotWithCardinality /* For plottables, interactions, grid etc */;
    xAxisBottom: SlotName /* Only for bottom x axis */;
    xAxisTop: SlotName /* Only for flipped x axis  */;
    yAxisLeft: SlotWithCardinality /* Only for left y axis */;
    yAxisRight: SlotWithCardinality /* Only for right y axis */;
    pane: SlotName /* Only for legend and such */;
    scrollbar: SlotName /* Only for window scrollbar element */;
    verticalScrollbar: SlotName /* Only for window scrollbar element */;
}

export type PlotElement = PlottableInterface & WithContextInterface;

const scaleSettingsChanged = (value: PartialScaleSettingsMap, oldValue: PartialScaleSettingsMap) => {
    if ((!value && oldValue) || (!oldValue && value)) {
        return true;
    }
    if (!(value || oldValue)) {
        return false;
    }
    const updatedSettings = filterEntries(value!, ([key, settings]) => {
        const oldKeySettings = oldValue![key];
        return Object.entries(settings).reduce((changed, [k, v]) => {
            const oldSetting = oldKeySettings && oldKeySettings[k];
            return changed || oldSetting !== v;
        }, false);
    });
    const removed = filterEntries(oldValue!, ([key]) => {
        return !isDefined(value![key]);
    });
    return isDefined(updatedSettings) || isDefined(removed);
};

@defineElement({ tagName: namespacedTag('plot'), styles: composablePlotStyles, version })
export class ComposablePlot extends ResizeAwareMixin(BaseElement) implements ComposablePlotProperties {
    // Static members
    private static SLOTMAP_DEFAULT: SlotMap = {
        main: { cardinalityPrefix: 'main-y' },
        xAxisBottom: { slotName: 'axis-x' },
        xAxisTop: { slotName: 'axis-x2' },
        yAxisLeft: { slotName: 'axis-y', cardinalityPrefix: '-y' },
        yAxisRight: { slotName: 'axis-y2', cardinalityPrefix: '-y' },
        pane: { slotName: 'pane' },
        scrollbar: { slotName: 'scrollbar' },
        verticalScrollbar: { slotName: 'vertical-scrollbar' },
    };
    // Private members

    // Protected members

    // Public members

    // Plot root element to host composable tree
    @provide({ context: plotRootElementContext })
    plotRootElement!: HTMLElement;
    @provide({ context: domainContext })
    @state()
    fittedDomain: Domain2D | undefined = undefined;
    @state()
    fittedDomainMap: DomainMap | undefined = undefined;
    @provide({ context: sizeContext })
    @state()
    size: Dimension | undefined = undefined;
    @provide({ context: dataContext })
    @state()
    allDataElements: PlotElement[] | undefined = undefined;
    @provide({ context: mouseEventContext })
    mouseEvent: MouseEvent | undefined = undefined;

    @property({
        hasChanged: scaleSettingsChanged,
    })
    scaleSettings: PartialScaleSettingsMap | undefined = undefined;
    @property()
    watermark = '';
    @property()
    heading: string | undefined = undefined;
    @property()
    viewbox: Domain2D | undefined = undefined;

    @property()
    zoomState: ScaleZoomMap | undefined = undefined;

    @provide({ context: scaleZoomMapContext })
    @state()
    scaleZoomMap: ScaleZoomMap | undefined = undefined;

    @property()
    settings: Settings | undefined = undefined;

    @property()
    backgroundColor; // transparent is not a valid svg value

    @property()
    backgroundOpacity = 1;

    // TODO remove default paddings
    @property()
    paddings?: [string, string?, string?, string?] = ['8px', '8px', '12px', '8px'];

    @property({ type: Object, reflect: false, attribute: false })
    set slotMap(val: SlotMap) {
        this._slotMap = val
            ? {
                  ...ComposablePlot.SLOTMAP_DEFAULT,
                  ...val,
              }
            : ComposablePlot.SLOTMAP_DEFAULT;
    }

    get slotMap() {
        return this._slotMap;
    }

    @provide({ context: settingsContext })
    providedSettings = defaultSettings;

    @provide({ context: scaleContext })
    @state()
    scaleMap;

    @provide({ context: labelSettingChangeContext })
    @state()
    labelSettingChange;

    @provide({ context: renderUpdateContext })
    @state()
    rerender: string[] | undefined = undefined;

    @state()
    mergedSettings: ScaleSettingsMap = mergeScaleSettingsWithDefault(this.scaleSettings);

    // This query only one main-area in multislot scenario. Will be ok for resize observer since
    // the grid make all main-areas the same size. But needs to be reworked in the export case.
    @query('#main-area')
    mainSlotRef;

    @state()
    _multiSlots: number[] = [2];

    // TODO : @state should only be used for private props. This one is exposed as a @property
    @state()
    zoomStrategy: ZoomStrategy | undefined = undefined;

    private _debouncedCallback;

    private _slotMap: SlotMap = ComposablePlot.SLOTMAP_DEFAULT;

    constructor() {
        super();
        this._wheelEventHandler = this._wheelEventHandler.bind(this);
        this.addEventListener(
            REGISTER_ZOOM_STRATEGY,
            (e) => (this.zoomStrategy = (e as CustomEvent<ZoomStrategy | undefined>).detail)
        );
    }

    public connectedCallback(): void {
        this.addEventListener('wheel', this._wheelEventHandler);
        this.addEventListener('mousedown', this._wheelEventHandler);
        this.addEventListener('scene-canvas-event-dispatch', this._wheelEventHandler);
        this.addEventListener('dblclick', this._wheelEventHandler);

        super.connectedCallback();
    }

    public disconnectedCallback(): void {
        this._uninstallResizeObserver(this.mainSlotRef);
        this.removeEventListener('wheel', this._wheelEventHandler);
        this.removeEventListener('mousedown', this._wheelEventHandler);
        this.removeEventListener('scene-canvas-event-dispatch', this._wheelEventHandler);
        this.removeEventListener('dblclick', this._wheelEventHandler);

        super.disconnectedCallback();
    }

    public async renderSVGPreview(target) {
        const { contents, dimensions } = await this.getSVGContents();
        renderSVGToElement(target, contents, dimensions);
    }

    public async renderImagePreview(target) {
        const { contents, dimensions } = await this.getSVGContents();
        renderSVGToSVGImg(target, contents, dimensions);
    }

    public async exportSVG(filename = 'export.svg') {
        const { contents, dimensions } = await this.getSVGContents();
        downloadSVGFile(filename, contents, dimensions);
    }

    public async exportPNG(filename = 'export.png') {
        const { contents, dimensions } = await this.getSVGContents();
        await downloadPNGFile(filename, contents, dimensions);
    }

    public async copyToClipboard() {
        const { contents, dimensions } = await this.getSVGContents();
        await copySVGFileToClipboard(contents, dimensions);
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        const heading = this.heading ? html` <h2 class="heading">${this.heading}</h2> ` : null;
        const variables = {
            '--main-background': colorRemapper(this.backgroundColor || 'none'),
            '--main-background-opacity': `${Math.min((this.backgroundOpacity || 1) * 100, 100)}%`,
            '--plot-paddings': this.paddings?.filter((p) => p?.length)?.join(' '),
        };

        const gridRow = (area = 0) => {
            // Prepare one temporary slot which is always the last index, that could be used for multiSlot scenario.
            const multiSlot = area
                ? null
                : html`
                      <slot
                          name="${this._slotName(this._slotMap.main, this._multiSlots[this._multiSlots.length - 1])}"
                          @slotchange=${this._handleMultiSlotchange}
                      ></slot>
                  `;

            const gridAreaPostfix = area ? `-y${area}` : '';
            const mainSlot = this._slotName(this._slotMap.main, area);
            return html`
                <div id="axis-y" class="axis-y" style="${varToStyle({ 'grid-area': 'y' + gridAreaPostfix })}">
                    <slot name="${this._slotName(this._slotMap.yAxisLeft, area)}"></slot>
                </div>
                <div id="axis-y2" class="axis-y2" style="${varToStyle({ 'grid-area': 'y2' + gridAreaPostfix })}">
                    <slot name="${this._slotName(this._slotMap.yAxisRight, area)}"></slot>
                </div>

                <div
                    id="${area ? mainSlot : 'main-area'}"
                    class="main-area"
                    @mouseenter=${this._wheelEventHandler}
                    @mouseleave=${this._wheelEventHandler}
                    style="${varToStyle({ 'grid-area': 'main' + gridAreaPostfix })}"
                >
                    <slot
                        name=${ifDefined(mainSlot)}
                        @datachange=${this.updateFittedDomain}
                        @slotchange=${this._handleSlotchange}
                        @labelsettingchange=${this.signalLabelSettingChange}
                        @request-rerender="${this._renderUpdate}"
                        @rerender-scheduled="${this._rerenderScheduled}"
                    ></slot>
                    ${multiSlot}
                </div>
            `;
        };

        const gridRowHtml =
            this._multiSlots.length > 1
                ? repeat(this._multiSlots.slice(0, -1).reverse(), (item) => item, gridRow)
                : null;

        return html`
            <div class="${rootClasses}" id="root" style=${varToStyle(variables)}>
                ${heading} ${gridRowHtml} ${gridRow()}
                <div id="axis-x" class="axis-x">
                    <slot name="${this._slotName(this._slotMap.xAxisBottom)}"></slot>
                </div>
                <div id="axis-x2" class="axis-x2">
                    <slot name="${this._slotName(this._slotMap.xAxisTop)}"></slot>
                </div>

                <div class="verscrollbar">
                    <slot name="${this._slotName(this._slotMap.verticalScrollbar)}"></slot>
                </div>

                <div class="pane">
                    <slot name="${this._slotName(this._slotMap.pane)}"></slot>
                </div>

                <div class="horscrollbar">
                    <slot name="${this._slotName(this._slotMap.scrollbar)}"></slot>
                </div>

                <div class="watermark">${this.watermark}</div>
            </div>
        `;
    }

    protected _resizeObserverCallback(entries: ResizeObserverEntry[]) {
        const [{ borderBoxSize }] = entries;
        const [{ blockSize: height, inlineSize: width }] = borderBoxSize;
        if (!this._debouncedCallback) {
            this._debouncedCallback = perfDebounce((w, h) => this.updateSize([w, h]), 10);
        }
        this._debouncedCallback(width, height);
    }

    protected _resizeObserverElem() {
        return this.mainSlotRef;
    }

    protected firstUpdated() {
        const rootEl: HTMLElement | null = this.renderRoot.querySelector<HTMLElement>('#root');

        this.plotRootElement = rootEl!;

        this._uninstallResizeObserver(this);
        this._installResizeObserver(this.mainSlotRef);
    }

    protected willUpdate(_changedProperties: PropertyValues<this>): void {
        super.willUpdate(_changedProperties);

        if (_changedProperties.has('settings')) {
            this.providedSettings = this.settings ? settingsMerge(defaultSettings, this.settings) : defaultSettings;
        }

        if (_changedProperties.has('scaleSettings')) {
            this.mergedSettings = mergeScaleSettingsWithDefault(this.scaleSettings);
            if (!this.updateFittedDomain()) {
                // fitted domain is updated it will already initiate a scale update, so only update if fitted domain wasn't updated
                this.scaleZoomMap = this.updateScaleZoomMap();
            }
        }

        if (
            _changedProperties.has('fittedDomain') ||
            _changedProperties.has('viewbox') ||
            _changedProperties.has('zoomState') ||
            _changedProperties.has('zoomStrategy')
        ) {
            this.scaleZoomMap = this.updateScaleZoomMap();
        }

        if (
            _changedProperties.has('scaleZoomMap') ||
            _changedProperties.has('fittedDomain') ||
            _changedProperties.has('size')
        ) {
            this.updateScales();
        }
    }

    protected update(_changedProperties: PropertyValues<this>): void {
        super.update(_changedProperties);

        if (_changedProperties.has('_multiSlots')) {
            const rootEl: HTMLElement | null = this.renderRoot.querySelector<HTMLElement>('#root');
            if (this._multiSlots.length > 1) {
                // No support for verscrollbar!
                const newS1 =
                    this._multiSlots
                        .slice(0, -1)
                        .reverse()
                        .map((a) => {
                            return `'y-y${a} main-y${a} y2-y${a} . pane'` + `'. . . . pane'`;
                        })
                        .join('') + `'y main y2 . pane'`;
                rootEl?.style.setProperty('--area-r3', newS1);

                const span = 1.0 / this._multiSlots.length;

                const newS =
                    this._multiSlots
                        .slice(0, -1)
                        .reverse()
                        .map((_) => {
                            return span + 'fr 16px';
                        })
                        .join(' ') + ` ${span}fr`;
                rootEl?.style.setProperty('--grid-main', newS);
            } else {
                rootEl?.style.removeProperty('--area-r3');
                rootEl?.style.removeProperty('--grid-main');
            }
        }
    }

    private _slotName(p: SlotName | DefaultSlotWithCardinality): string;
    private _slotName(p: SlotWithCardinality | DefaultSlotWithCardinality, cardinality: number): string;
    private _slotName(
        p: SlotWithCardinality | SlotName | DefaultSlotWithCardinality,
        cardinality?: number
    ): string | undefined {
        if (!!cardinality && 'cardinalityPrefix' in p) {
            return 'slotName' in p
                ? `${p.slotName}${p.cardinalityPrefix}${cardinality}`
                : `${p.cardinalityPrefix}${cardinality}`;
        }
        return 'slotName' in p ? p.slotName : undefined;
    }

    private _wheelEventHandler(e) {
        if (e.type === 'dblclick' && e.shiftKey) {
            this.exportSVG();
            return;
        }
        if (e.composedPath().find((p) => (p as HTMLElement).classList?.contains('main-area'))) {
            this.mouseEvent = e;
        }
    }

    // sync fittedDomainMap and scaleZoomMap
    private updateScaleZoomMap() {
        if (!isDefined(this.fittedDomainMap)) {
            return;
        }

        switch (this.zoomStrategy) {
            case ZoomStrategy.MultiAxis:
                return mapEntries(this.fittedDomainMap!, ([scaleKey]) => this.zoomState?.[scaleKey] ?? defaultZoomLens);

            case ZoomStrategy.Viewport:
            default:
                return {
                    ...this.viewbox,
                    ...mapEntries(this.fittedDomainMap!, ([scaleKey]) =>
                        this.mergedSettings[scaleKey].orientation === 'horizontal'
                            ? (this.viewbox?.x ?? defaultZoomLens)
                            : (this.viewbox?.y ?? defaultZoomLens)
                    ),
                };
        }
    }

    private updateScales() {
        if (!isDefined(this.fittedDomainMap) || this.hasInvalidSize() || !this.scaleZoomMap) return;

        const domainMap = this.fittedDomainMap!;
        const filteredSettings = filterEntries(this.mergedSettings, ([key]) => !!domainMap[key]);

        this.scaleMap = applyScaleSettings(filteredSettings, domainMap, this.size!, this.scaleZoomMap);
    }

    private updateSize(newSize: Dimension) {
        this.size = newSize;
    }

    private signalLabelSettingChange() {
        this.labelSettingChange = UUID();
    }

    private _handleSlotchange(e) {
        const slotName = e.target.name;
        const slotNumber = Number(slotName.split('-y')[1]);
        if (e.target.assignedNodes().length === 0) {
            this._multiSlots = this._multiSlots.filter((n) => n !== slotNumber);
            if (slotNumber < this._multiSlots[this._multiSlots.length - 1]) {
                this._multiSlots = this._multiSlots.slice(0, -1);
                this._multiSlots.push(slotNumber);
            }
        }
        this.updateFittedDomain();
    }

    private _handleMultiSlotchange(e) {
        const slotName = e.target.name;
        const last = this._multiSlots[this._multiSlots.length - 1];
        if (e.target.assignedNodes().length > 0 && slotName === 'main-y' + last) {
            this._multiSlots.sort((a, b) => a - b);
            let availableSlot = last + 1;
            while (this._multiSlots.includes(availableSlot)) {
                availableSlot++;
            }
            this._multiSlots = [...this._multiSlots, availableSlot];
        }
    }

    private _plottableElements() {
        return this._mainElements().filter((el) => !!el.plotData);
    }

    private _updateAllDataElements = (plottableElements) => {
        if (
            !this.allDataElements ||
            this.allDataElements.length !== plottableElements.length ||
            !this.allDataElements.every((el, index) => el === plottableElements[index])
        ) {
            this.allDataElements = plottableElements;
        }
    };

    private updateFittedDomain() {
        const plottableElements = this._plottableElements();
        this._updateAllDataElements(plottableElements);

        const plottableElementsInDomain = plottableElements.filter((el) => el.includeInDomain);
        const elemsByScaleX = groupBy(plottableElementsInDomain, (el) => el.scaleX ?? 'x');
        const elemsByScaleY = groupBy(plottableElementsInDomain, (el) => el.scaleY ?? 'y');

        // filter plotted elements by scale settings
        const filteredScaleElements = filterEntries(
            { ...elemsByScaleX, ...elemsByScaleY },
            ([key]) => this.mergedSettings[key] != null
        );

        const requestedDomains = mapEntries(filteredScaleElements, ([key, elems]) => {
            const { orientation, scaleType, domain: forcedDomain, symmetricDomain } = this.mergedSettings[key];
            if (forcedDomain) return forcedDomain;
            let computedDomain = joinDomains(
                elems.map((el) => {
                    return orientation === 'horizontal' ? el.getDomainX(scaleType) : el.getDomainY(scaleType);
                })
            );
            // Apply symmetric domain adjustment if enabled. N/A for band scale and log scale. Not sure about time scale, will need some other math here.
            if (symmetricDomain && scaleType !== 'band' && scaleType !== 'log' && computedDomain.length === 2) {
                const [min, max] = computedDomain as [number, number];
                const bound = Math.max(Math.abs(min), Math.abs(max));
                computedDomain = [-bound, bound];
            }

            return computedDomain;
        });

        //There may be domains that are in settings but not currently requested by an element
        const remainingSettings = filterEntries(
            this.mergedSettings,
            ([key, setting]) => !Object.keys(requestedDomains).includes(key) && Boolean(setting.domain)
        );
        const remainingDomains = mapValues(remainingSettings, (setting) => setting.domain!);
        const domains = filterEntries({ ...requestedDomains, ...remainingDomains }, ([, domain]) =>
            isValidDomain(domain)
        );
        if (!isDefined(domains) || domainMapEquals(domains, this.fittedDomainMap)) {
            return false;
        }

        this.fittedDomainMap = domains;
        //Keep this to avoid changing the event API.
        const defaultAxisDomain = {
            x: domains['x'],
            y: domains['y'],
        } as Domain2D;
        this.fittedDomain = defaultAxisDomain;

        this.dispatchEvent(
            new CustomEvent('domain-fitted', {
                detail: {
                    domain: defaultAxisDomain,
                    domainMap: domains,
                },
            })
        );
        return true;
    }

    private _renderUpdate = (e: CustomEvent) => {
        if (!this.rerender?.includes(e.detail.target.plotUniqueId)) {
            this.rerender = [...(this.rerender || []), e.detail.target.plotUniqueId];
        }
    };

    private _rerenderScheduled = (e: CustomEvent) => {
        const done = e.detail.updated;
        this.rerender = (this.rerender || []).filter((r) => !done.includes(r));
    };

    private hasInvalidSize() {
        return !this.size || this.size.some((d) => d === 0);
    }

    private async getSVGContents() {
        // Hide toolbar and await resize observer and the updates triggered by it
        await this._exportHideScroller();
        await this.updateComplete;

        const rootRect = this.getBoundingClientRect();
        const { width: rootWidth, height: rootHeight } = rootRect;

        const pane = this._paneElements().map(exportElement());
        const paneRect = this.shadowRoot!.querySelector('.pane')!.getBoundingClientRect();
        const [paneOffsetX, paneOffsetY] = getRectOffset(paneRect, rootRect); //[paneRect.left - rootRect.left, paneRect.top - rootRect.top];

        const watermarkRect = this.shadowRoot!.querySelector('.watermark')!.getBoundingClientRect();
        const [watermarkOffsetX, watermarkOffsetY] = getRectOffset(watermarkRect, rootRect); //[paneRect.left - rootRect.left, paneRect.top - rootRect.top];

        // In multislot scenario only x-axis supported placed on the bottom default slot
        const axisx1 = this._exportAxisElements(
            this._axisXElements(),
            'axis-x',
            (x, y) => [x, y], // No special offsets for X1
            rootRect
        );
        const axisx2 = this._exportAxisElements(
            this._axisX2Elements(),
            'axis-x2',
            (x, y, rect) => [x, y + rect.height], // Render X2 below its position
            rootRect
        );

        const [mainWidth, mainHeight] = this.size!;
        // One or many main areas. Many in multiSlot plots
        const mainsSvg = [0, ...this._multiSlots, 0].map((cardinality, index, array) => {
            if (index < array.length - 1) {
                const mainSlot = this._slotName(this._slotMap.main, cardinality);
                const yAxisLeftSlot = this._slotName(this._slotMap.yAxisLeft, cardinality);
                const yAxisRightSlot = this._slotName(this._slotMap.yAxisRight, cardinality);
                const mainElements = this._queryAssignedElements(mainSlot);
                const axisYLeftElements = this._queryAssignedElements(yAxisLeftSlot);
                const axisYRightElements = this._queryAssignedElements(yAxisRightSlot);

                const mainRect = (
                    index === 0
                        ? this.mainSlotRef
                        : this.shadowRoot!.querySelector(`slot[name="${mainSlot}"]`)!.parentElement
                ).getBoundingClientRect();
                const [mainOffsetX, mainOffsetY] = getRectOffset(mainRect, rootRect);
                const mainElementsSvg = mainElements.map(exportElement());
                const mainSvg =
                    mainElementsSvg.length < 1
                        ? null
                        : svg`
                    <clipPath id="main-box">
                        <rect x="0" y="0" width="${mainWidth}" height="${mainHeight}" />
                    </clipPath>
                    <g class="main" clip-path="url(#main-box)" transform="translate(${mainOffsetX} ${mainOffsetY})">
                        <rect 
                            x="0" 
                            y="0" 
                            width="${mainWidth}" 
                            height="${mainHeight}" 
                            fill="${colorRemapper(this.backgroundColor || 'none')}" 
                            fill-opacity="${this.backgroundOpacity || 1}"/>
                        ${mainElementsSvg}
                    </g>`;

                // Left y-axis
                const axisy1 = this._exportAxisElements(
                    axisYLeftElements,
                    'axis-y',
                    (x, y, rect) => [x + rect.width, y], // Render Y Axis to the left
                    rootRect
                );

                // Right y-axis (flipped)
                const axisy2 = this._exportAxisElements(
                    axisYRightElements,
                    'axis-y2',
                    (x, y) => [x, y], // No special offsets for Y2
                    rootRect
                );
                const xAxisSvg = cardinality === 0 ? svg`${axisx1}${axisx2}` : null;

                return svg`
            ${mainSvg}
            ${axisy1}${axisy2}
            ${xAxisSvg}`;
            }
            return null;
        });
        this._exportRestoreScroller();

        return {
            dimensions: [rootWidth, rootHeight] as Dimension,
            contents: svg`
            ${mainsSvg}
            <g class="pane" transform="translate(${paneOffsetX} ${paneOffsetY})">${pane}</g>
            <g class="watermark" transform="translate(${watermarkOffsetX} ${watermarkOffsetY})">${renderWatermarkSVG(
                this.watermark
            )}</g>`,
        };
    }

    private _exportAxisElements(elements, className, offsetFn, rootRect) {
        return elements.map((e) => {
            const axisRect = e.getBoundingClientRect();
            const [axisOffsetX, axisOffsetY] = getRectOffset(axisRect, rootRect);
            const [finalOffsetX, finalOffsetY] = offsetFn(axisOffsetX, axisOffsetY, axisRect);
            return svg`
        <g class="${className}" transform="translate(${finalOffsetX} ${finalOffsetY})">
        ${exportElement({ useElementSize: true })(e)}
        </g>
        `;
        });
    }

    private _getScroller = (): HTMLDivElement | null => this.shadowRoot!.querySelector('div[class="scrollbar"]');
    private _exportHideScroller = async () => {
        const scrollbar = this._getScroller();

        // Hide toolbar and await resize observer and the updates triggered by it
        if (scrollbar && scrollbar.getBoundingClientRect().height > 0) {
            scrollbar.style.setProperty('display', 'none');
            await new Promise((resolve) => setTimeout(resolve, 20)); // Debounce is 10
        }
    };
    private _exportRestoreScroller = () => {
        const scrollbar = this._getScroller();
        if (scrollbar) {
            scrollbar.style.removeProperty('display');
        }
    };

    private _queryAssignedElements(slotName: string): Element[] {
        const slot = this.shadowRoot?.querySelector(
            slotName ? `slot[name="${slotName}"]` : 'slot:not([name])'
        ) as HTMLSlotElement;
        return slot ? slot.assignedElements({ flatten: true }) : [];
    }

    private _refElements = (ref, c = 0) => this._queryAssignedElements(this._slotName(ref, c));
    private _mainElements = () => {
        const l = (this._refElements(this._slotMap.main) as PlotElement[]) ?? [];
        this._multiSlots.forEach((s, index, array) => {
            if (index < array.length - 1) {
                const slotted = this._refElements(this._slotMap.main, s);
                l.push(...(slotted as PlotElement[]));
            }
        });
        return l;
    };
    private _axisXElements = () => this._refElements(this._slotMap.xAxisBottom) as Axis[];
    private _axisX2Elements = () => this._refElements(this._slotMap.xAxisTop) as Axis[];
    private _paneElements = () => this._refElements(this._slotMap.pane) as HTMLElement[];
}
