import { Datum, isNumber, isValidDate, isValidString, type Point2D } from '@umetrics/sartorius-ui-plot-utils';
import { isFiniteNumber } from '../interaction/zoomAndPan/zoomAndPan';
import { DomainMap } from './scale';
import { isDefined } from '../genericUtils';

export type Domain<P = number> = [P, P];

export interface Domain2D<X = number, Y = number> {
    x: Domain<X>;
    y: Domain<Y>;
}

export const isValidDomain = (domain: Domain | Datum[]) =>
    domain?.every(isFiniteNumber) || domain?.every(isValidString) || domain?.every(isValidDate);
export const isValidDomain2D = ({ x, y }: Domain2D) => isValidDomain(x) && isValidDomain(y);

//Hacky way to check but good enough for now
export const isCategoricalDomain = (domain: Domain | Datum[]) =>
    domain && (domain.length > 2 || domain.some((v) => typeof v === 'string'));

export const isDateDomain = (domain: Domain | Datum[]) =>
    domain && (domain.length > 2 || domain.every((v) => typeof v === 'object'));

export function getDomain2D(dataPairs: any): Domain2D {
    let minX = Infinity;
    let maxX = -Infinity;
    let minY = Infinity;
    let maxY = -Infinity;
    dataPairs.forEach(({ x, y }) => {
        if (isNumber(x) && x < minX) minX = x;
        if (isNumber(x) && x > maxX) maxX = x;
        if (isNumber(y) && y < minY) minY = y;
        if (isNumber(y) && y > maxY) maxY = y;
    });

    return {
        x: [minX, maxX],
        y: [minY, maxY],
    };
}

export function getDateDomain(data: Date[]): [Date, Date] {
    return data.reduce(
        (dom: [Date, Date], next: Date) => {
            if (next < dom[0]) {
                dom[0] = next;
            }
            if (next > dom[1]) {
                dom[1] = next;
            }
            return dom;
        },
        [new Date('3000-01-01'), new Date('1000-01-01')]
    );
}

export function getDomain(data: number[]): Domain {
    let min = Infinity;
    let max = -Infinity;
    data.forEach((x) => {
        if (isNumber(x) && x < min) min = x;
        if (isNumber(x) && x > max) max = x;
    });

    return [min, max];
}

export function joinContinuousDomains(domains: Domain<number>[]): Domain<number> {
    const data = domains.filter(isValidDomain).flat(1);
    let min = Infinity;
    let max = -Infinity;

    data.forEach((x) => {
        if (x < min) min = x;
        if (x > max) max = x;
    });

    return [min, max];
}

export function joinDateDomains(domains: Date[][]): [Date, Date] {
    const data = domains.filter(isValidDomain).flat(1);
    let min = new Date('3000-01-01');
    let max = new Date('1000-01-01');

    data.forEach((x) => {
        if (x < min) min = x;
        if (x > max) max = x;
    });

    return [min, max];
}

export function joinCategoricalDomains(domains: Datum[][]): Datum[] {
    return [...new Set(domains.flat(1))];
}

export function joinDomains(domains: Domain[] | Datum[][]) {
    //Hacky way to check for continuous domains but good enough for now
    if (domains.some(isCategoricalDomain)) {
        return joinCategoricalDomains(domains as Datum[][]);
    } else if (domains.some(isDateDomain)) {
        return joinDateDomains(domains as Date[][]);
    } else {
        return joinContinuousDomains(domains as Domain<number>[]);
    }
}

export const insideDomain =
    ({ x: [xmin, xmax], y: [ymin, ymax] }: Domain2D) =>
    (p: Point2D) =>
        p.x >= xmin && p.x <= xmax && p.y >= ymin && p.y <= ymax;

export const domainEquals = (a?: Domain2D, b?: Domain2D) => {
    if (!a || !b) return false;
    const {
        x: [xa0, xa1],
        y: [ya0, ya1],
    } = a;
    const {
        x: [xb0, xb1],
        y: [yb0, yb1],
    } = b;
    return xa0 === xb0 && xa1 === xb1 && ya0 === yb0 && ya1 === yb1;
};

export const domainMapEquals = (a?: DomainMap, b?: DomainMap) => {
    if (!isDefined(a) || !isDefined(b)) return false;
    const aKeys = Object.keys(a!);
    const bKeys = Object.keys(b!);
    if (aKeys.length !== bKeys.length || !aKeys.every((k) => bKeys.includes(k))) return false;
    return aKeys.every((ak) => a![ak].every((v) => b![ak].includes(v as number)));
};

export const getDomainRange = (domain?: Domain2D): Domain => {
    if (!domain) return [NaN, NaN];
    const {
        x: [x0, x1],
        y: [y0, y1],
    } = domain;

    return [x1 - x0, y1 - y0];
};

export const getDomainZoom = (view?: Domain2D, bounds?: Domain2D): Domain => {
    if (!view || !bounds) return [NaN, NaN];
    const {
        x: [viewx0, viewx1],
        y: [viewy0, viewy1],
    } = view;
    const {
        x: [boundsx0, boundsx1],
        y: [boundsy0, boundsy1],
    } = bounds;
    const viewxrange = viewx1 - viewx0;
    const viewyrange = viewy1 - viewy0;
    const boundsxrange = boundsx1 - boundsx0;
    const boundsyrange = boundsy1 - boundsy0;
    return [boundsxrange / viewxrange, boundsyrange / viewyrange];
};

export const isInvertedDomain = (domain) => typeof domain[0] === 'number' && domain[0] > domain[1];
export const isInvertedDomainForScale = (scale) => isInvertedDomain(scale.domain());

export const scaleDomainToMinimumRange = (domain, minRange) => {
    const originalRange = Math.abs(domain[1] - domain[0]);
    if (originalRange < minRange) {
        const center = (domain[0] + domain[1]) / 2;
        const halfNewRange = minRange / 2;
        const s = center - halfNewRange;
        const e = center + halfNewRange;
        return isInvertedDomain(domain) ? [e, s] : [s, e];
    }
    return domain;
};
