import parse, { Command } from 'parse-svg-path';
import { DashedPathHandler } from './dashedPathHandler';

export interface PathCommandHandler {
    beginPath(): void;
    moveTo(x: number, y: number): void;
    closePath(): void;
    lineTo(x: number, y: number): void;
    bezierCurveTo(cp1x: number, cp1y: number, cp2x: number, cp2y: number, x: number, y: number): void;
    quadraticCurveTo(cpx: number, cpy: number, x: number, y: number): void;
    arcTo(
        rx: number,
        ry: number,
        xAxisRotation: number,
        largeArcFlag: number,
        sweepFlag: number,
        x: number,
        y: number
    ): void;
}

const CUBIC_COMMANDS = ['C', 'c', 'S', 's'];
const QUADRATIC_COMMANDS = ['Q', 'q', 'T', 't'];

const calculateControlPoint = (commands: Command[], index: number, state: PathState, validCommands: string[]) => {
    if (!state.lastControlPosition || index - 1 < 0 || !validCommands.includes(commands[index - 1][0])) {
        return { x: state.currentPosition.x, y: state.currentPosition.y };
    }

    return {
        x: 2 * state.currentPosition.x - state.lastControlPosition.x,
        y: 2 * state.currentPosition.y - state.lastControlPosition.y,
    };
};

const calculateControlPointForCubicCurves = (commands: Command[], index: number, state: PathState) =>
    calculateControlPoint(commands, index, state, CUBIC_COMMANDS);

const calculateControlPointForQuadraticCurves = (commands: Command[], index: number, state: PathState) =>
    calculateControlPoint(commands, index, state, QUADRATIC_COMMANDS);

interface Point {
    x: number;
    y: number;
}

interface PathState {
    startPosition: Point;
    currentPosition: Point;
    lastControlPosition?: Point;
}

const updateState = (state: PathState, x: number, y: number, startPosition: boolean, lastControlPosition?: Point) => {
    return {
        currentPosition: { x, y },
        startPosition: startPosition ? { x, y } : state.startPosition,
        lastControlPosition,
    };
};

const handleMoveTo = (x: number, y: number, state: PathState, handler: PathCommandHandler) => {
    handler.moveTo(x, y);
    return updateState(state, x, y, true);
};

const handleClosePath = (state: PathState, handler: PathCommandHandler) => {
    handler.closePath();
    return updateState(state, state.startPosition.x, state.startPosition.y, false);
};

const handleLineTo = (x: number, y: number, state: PathState, handler: PathCommandHandler) => {
    handler.lineTo(x, y);
    return updateState(state, x, y, false);
};

const handleCubicBezier = (
    cp1x: number,
    cp1y: number,
    cp2x: number,
    cp2y: number,
    x: number,
    y: number,
    state: PathState,
    handler: PathCommandHandler
) => {
    handler.bezierCurveTo(cp1x, cp1y, cp2x, cp2y, x, y);
    return updateState(state, x, y, false, { x: cp2x, y: cp2y });
};

const handleQuadraticCurve = (
    cpx: number,
    cpy: number,
    x: number,
    y: number,
    state: PathState,
    handler: PathCommandHandler
) => {
    handler.quadraticCurveTo(cpx, cpy, x, y);
    return updateState(state, x, y, false, { x: cpx, y: cpy });
};

const handleArc = (
    rx: number,
    ry: number,
    xAxisRotation: number,
    largeArcFlag: number,
    sweepFlag: number,
    x: number,
    y: number,
    state: PathState,
    handler: PathCommandHandler
) => {
    handler.arcTo(rx, ry, xAxisRotation, largeArcFlag, sweepFlag, x, y);
    return updateState(state, x, y, false);
};

const processPathCommands = (commands: Command[], handler: PathCommandHandler) => {
    handler.beginPath();
    let state: PathState = {
        startPosition: { x: 0, y: 0 },
        currentPosition: { x: 0, y: 0 },
    };
    commands.forEach((command, idx) => {
        switch (command[0]) {
            case 'M': {
                state = handleMoveTo(command[1], command[2], state, handler);
                break;
            }
            case 'm': {
                state = handleMoveTo(
                    command[1] + state.currentPosition.x,
                    command[2] + state.currentPosition.y,
                    state,
                    handler
                );
                break;
            }
            case 'Z':
            case 'z':
                state = handleClosePath(state, handler);
                break;
            case 'L': {
                state = handleLineTo(command[1], command[2], state, handler);
                break;
            }
            case 'l': {
                state = handleLineTo(
                    command[1] + state.currentPosition.x,
                    command[2] + state.currentPosition.y,
                    state,
                    handler
                );
                break;
            }
            case 'H': {
                state = handleLineTo(command[1], state.currentPosition.y, state, handler);
                break;
            }
            case 'h': {
                state = handleLineTo(command[1] + state.currentPosition.x, state.currentPosition.y, state, handler);
                break;
            }
            case 'V': {
                state = handleLineTo(state.currentPosition.x, command[1], state, handler);
                break;
            }
            case 'v': {
                state = handleLineTo(state.currentPosition.x, command[1] + state.currentPosition.y, state, handler);
                break;
            }
            case 'C': {
                state = handleCubicBezier(
                    command[1],
                    command[2],
                    command[3],
                    command[4],
                    command[5],
                    command[6],
                    state,
                    handler
                );
                break;
            }
            case 'c': {
                state = handleCubicBezier(
                    command[1] + state.currentPosition.x,
                    command[2] + state.currentPosition.y,
                    command[3] + state.currentPosition.x,
                    command[4] + state.currentPosition.y,
                    command[5] + state.currentPosition.x,
                    command[6] + state.currentPosition.y,
                    state,
                    handler
                );
                break;
            }
            case 'S': {
                const { x: cp1x, y: cp1y } = calculateControlPointForCubicCurves(commands, idx, state);
                state = handleCubicBezier(cp1x, cp1y, command[1], command[2], command[3], command[4], state, handler);
                break;
            }
            case 's': {
                const { x: cp1x, y: cp1y } = calculateControlPointForCubicCurves(commands, idx, state);
                state = handleCubicBezier(
                    cp1x,
                    cp1y,
                    command[1] + state.currentPosition.x,
                    command[2] + state.currentPosition.y,
                    command[3] + state.currentPosition.x,
                    command[4] + state.currentPosition.y,
                    state,
                    handler
                );
                break;
            }
            case 'Q': {
                state = handleQuadraticCurve(command[1], command[2], command[3], command[4], state, handler);
                break;
            }
            case 'q': {
                state = handleQuadraticCurve(
                    command[1] + state.currentPosition.x,
                    command[2] + state.currentPosition.y,
                    command[3] + state.currentPosition.x,
                    command[4] + state.currentPosition.y,
                    state,
                    handler
                );
                break;
            }
            case 'T': {
                const { x: cpx, y: cpy } = calculateControlPointForQuadraticCurves(commands, idx, state);
                state = handleQuadraticCurve(cpx, cpy, command[1], command[2], state, handler);
                break;
            }
            case 't': {
                const { x: cpx, y: cpy } = calculateControlPointForQuadraticCurves(commands, idx, state);
                state = handleQuadraticCurve(
                    cpx,
                    cpy,
                    command[1] + state.currentPosition.x,
                    command[2] + state.currentPosition.y,
                    state,
                    handler
                );
                break;
            }
            case 'A': {
                state = handleArc(
                    command[1],
                    command[2],
                    command[3],
                    command[4],
                    command[5],
                    command[6],
                    command[7],
                    state,
                    handler
                );
                break;
            }
            case 'a': {
                state = handleArc(
                    command[1],
                    command[2],
                    command[3],
                    command[4],
                    command[5],
                    command[6] + state.currentPosition.x,
                    command[7] + state.currentPosition.y,
                    state,
                    handler
                );
                break;
            }
            default:
                throw new Error(`Unsupported command: ${command[0]}`);
        }
    });
};

export const segmentsFromSVGPath = (svgPath: string, handler: PathCommandHandler, dashArray: number[] = []) => {
    const commands = parse(svgPath);
    let finalHandler: PathCommandHandler = handler;
    if (dashArray.length) {
        finalHandler = new DashedPathHandler(handler, dashArray);
    }
    processPathCommands(commands, finalHandler);
};
