import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    nothing,
    property,
    PropertyValues,
    query,
    state,
    styleMap,
} from '@umetrics/sartorius-ui-base';
import { namespacedTag } from '../../tag';
import { Plottable } from '../../plottable/plottableMixin';
import { WithContext } from '../../data/plotContext';
import { htmlRangeStyles as styles } from './rangeStyles';
import { version } from '../../metadata';
import { Domain, LinearScale } from '../../data';
import { WithDataContext } from '../../data/dataContext';
import { emptyBox, findDataAllPointsByX, getYInRange, GetYInRangeInvalidInputStrategy } from '../../utils';
import { interpolateNumber } from 'd3-interpolate';
import { MetadataDetail, MetaDataYSliceDefault } from '../metadataUtils';
import { SVGShape } from '../../plottable/svg/svgShape';

export interface RangeCursorProperties {
    data?: number[];
    disabled: boolean;
    cursorColor: string;
    rulerColor: string;
    barBg: string;
    barPendingBg: string;
    width: number;
    interpolatedMetadata: boolean;
    filterDataByTag: string;
    rangeYInRangeInvalidStrategy: GetYInRangeInvalidInputStrategy;
    pixelProximityThreshold?: number;
    onChange: (e: CustomEvent<Domain>) => void;
    onMetadata: (e: CustomEvent<RangeSelectorMetadata>) => void;
    onCursorMove: (e: CustomEvent<{ mouseX: number; dataX: unknown }>) => void;
    onCursorLeave: (e: CustomEvent) => void;
}

export interface RangeSelectorMetadata<T = any> {
    selected: T[][];
    cursors: MetadataDetail<T, MetaDataYSliceDefault<T>>[];
}

const RANGE_RULERS_AMOUNT = 2;

@defineElement({ tagName: namespacedTag('range-cursor'), styles, version })
export class RangeCursor extends Plottable(WithDataContext(WithContext(BaseElement))) implements RangeCursorProperties {
    @property({ type: String })
    cursorColor = 'grey';

    @property({ type: String })
    rulerColor = 'black';

    @property({ type: String })
    barBg = 'rgba(0, 0, 0, 0.1)';

    @property({ type: String })
    barPendingBg = 'rgba(0, 0, 0, 0.04)';

    @property({ type: String })
    cursorPendingOpacity = 0.4;

    @property({ type: Number })
    width = 2;

    @property({ type: Boolean })
    disabled = false;

    @property({ type: Boolean })
    interpolatedMetadata = false;

    @property({ type: String })
    filterDataByTag = 'SVG-LINE';

    @property({ type: Number })
    pixelProximityThreshold = 10;

    @property({ type: String })
    rangeYInRangeInvalidStrategy = GetYInRangeInvalidInputStrategy.RETURN_EMPTY;

    onChange;
    onMetadata;
    onCursorMove;
    onCursorLeave;

    @query('.container')
    private _container: HTMLDivElement | undefined;

    @state()
    private state = { position: { x: 0, y: 0 }, isVisible: false };

    private _mouse: [x: number, y: number] = [0, 0];
    private _draggingIndex: number | null = null;
    private _draggingBar = false;
    private _initialBarMouseOffset: number | null = null;
    private _lastMouseMoveEvent: MouseEvent | null = null;
    private _animationFrameRequest: number | null = null;

    // track if cursor has entered in the container area
    private _isCursorInContainerArea = false;
    private _isCursorDirty = false;

    private get isRangeCreated() {
        return this.data.length === RANGE_RULERS_AMOUNT;
    }

    private get allFilteredData() {
        return (
            this.allDataElements
                ?.filter((el) => el.tagName.endsWith(this.filterDataByTag))
                .map((el) => {
                    return {
                        scales: (el as SVGShape).getScales() as [LinearScale, LinearScale],
                        data: el.plotData,
                    };
                }) ?? []
        );
    }

    private get containerBox() {
        return this._container?.getBoundingClientRect() ?? emptyBox;
    }

    constructor() {
        super();
        this._handleMouseClick = this._handleMouseClick.bind(this);
    }

    handleDocumentMouseMove = (event: MouseEvent) => {
        if (this.disabled) return;
        if (this._container && this.isMouseOverContainer(event.clientX, event.clientY)) {
            this._lastMouseMoveEvent = event;
            this._isCursorInContainerArea = true;
            this._isCursorDirty = true;
            this._scheduleMouseMoveUpdate();
        } else {
            this._lastMouseMoveEvent = event;
            this._scheduleMouseMoveUpdate();
            this._isCursorInContainerArea = false;
        }

        if (!this._isCursorInContainerArea) {
            this._handleMouseLeave();
        }
    };

    handleDocumentMouseUp = () => {
        if (this.disabled) return;
        this._draggingIndex = null;
        this._draggingBar = false;
        this._initialBarMouseOffset = null;
    };

    connectedCallback() {
        super.connectedCallback();
        document.addEventListener('mousemove', this.handleDocumentMouseMove);
        document.addEventListener('mouseup', this.handleDocumentMouseUp);
        document.addEventListener('click', this._handleMouseClick);
    }

    disconnectedCallback() {
        document.removeEventListener('mousemove', this.handleDocumentMouseMove);
        document.removeEventListener('mouseup', this.handleDocumentMouseUp);
        document.removeEventListener('click', this._handleMouseClick);
        super.disconnectedCallback();
    }

    willUpdate(_changedProperties: PropertyValues<this>): void {
        super.willUpdate(_changedProperties);
        if (
            _changedProperties.has('allDataElements') ||
            _changedProperties.has('scaleMap') ||
            _changedProperties.has('data')
        ) {
            if (this.allDataElements && this.getScales() && this.viewbox && this.data) {
                this._emitChangeWithMetadata();
            }
        }
    }

    isMouseOverContainer(mouseX: number, mouseY: number): boolean {
        const { left, right, top, bottom } = this.containerBox;
        return mouseX >= left && mouseX <= right && mouseY >= top && mouseY <= bottom;
    }

    render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {});
        const [width, height] = this.size ?? [0, 0];

        return html`
            <div class="${rootClasses} ${baseClassName} container">
                ${this._renderCursor(width, height)} ${this._renderRulers()} ${this._renderBar()}
            </div>
        `;
    }

    private _renderBar() {
        if (!this.getScales() || this.data.length === 0) {
            return nothing;
        }

        const [scaleX] = this.getScales() as LinearScale[];
        const positions = this.data.map((posX) => scaleX(posX));
        let [start, end] = [Math.min(...positions), Math.max(...positions)];

        if (this.data.length === 1) {
            const cursorX = this._mouse[0];
            [start, end] = [Math.min(start, cursorX), Math.max(start, cursorX)];
        }

        const barWidth = end - start;

        const barStyles = {
            transform: `translateX(${start}px)`,
            width: `${barWidth}px`,
            backgroundColor: this.isRangeCreated ? this.barBg : this.barPendingBg,
        };

        return html`
            <div class="bar" style=${styleMap(barStyles)} @mousedown="${this._handleBarMouseDown}">
                <slot name="bar"></slot>
            </div>
        `;
    }

    private _renderCursor(width, height) {
        const [x] = this._mouse;
        const crosshairPath = `M${x} 0 V${height}`;
        const opacity = this.isRangeCreated ? 1 : this.cursorPendingOpacity;

        const slotStyleMap = styleMap({
            position: 'absolute',
            display: this._isCursorDirty ? 'flex' : 'none',
            flexDirection: 'column',
            height: `${height}px`,
            transform: `translateX(${x}px)`,
            width: `${width}px`,
            pointerEvents: 'none',
            opacity,
        });

        const cursorHandlerStyleMap = styleMap({
            display: this._isCursorDirty ? 'flex' : 'none',
            flexDirection: 'column',
            opacity,
        });

        return this.isRangeCreated
            ? nothing
            : html`<svg width=${width} height=${height} class="hover-cursor" style=${cursorHandlerStyleMap}>
                      <path stroke=${this.cursorColor} stroke-width=${this.width} d=${crosshairPath} />
                  </svg>
                  <div style=${slotStyleMap}><slot name="range-cursor"></slot></div>`;
    }

    private _renderRulers() {
        if (!this.getScales()) return;
        const [scaleX] = this.getScales();

        return this.data.map((posX, index) => {
            const rulerStyleMap = styleMap({
                transform: `translateX(${Number(scaleX(posX)) - this.width / 2}px)`,
                width: `${this.width}px`,
                backgroundColor: this.rulerColor,
            });

            return html`<div
                class="ruler-item"
                style=${rulerStyleMap}
                @mousedown="${(event: MouseEvent) => this._handleMouseDown(event, index)}"
            >
                <slot name=${'ruler-' + index}></slot>
            </div>`;
        });
    }

    private _scheduleMouseMoveUpdate() {
        if (this._animationFrameRequest) return;

        this._animationFrameRequest = requestAnimationFrame(() => {
            if (this._lastMouseMoveEvent) {
                this._handleMouseMove(this._lastMouseMoveEvent);
                this._lastMouseMoveEvent = null;
            }
            this._animationFrameRequest = null;
        });
    }

    private _handleMouseMove(event: MouseEvent) {
        if (!this.getScales() || this.disabled) return;

        let mouseX = event.clientX - this.containerBox.left;
        const mouseY = event.clientY - this.containerBox.top;

        // Constrain the mouseX within container boundaries
        const { left, right } = this.containerBox;
        if (mouseX < 0) mouseX = 0;
        if (mouseX > right - left) mouseX = right - left;

        this.state = {
            position: { x: mouseX, y: mouseY },
            isVisible: true,
        };

        const [scaleX] = this.getScales() as LinearScale[];

        this._mouse = [mouseX, mouseY];

        const range = scaleX.range();

        if (this._draggingIndex !== null) {
            const newValue = scaleX.invert(Math.min(mouseX, range[1]));
            this.data[this._draggingIndex] = newValue;
            this.dispatchEvent(new CustomEvent('change', { detail: this.data }));
            this._emitChangeWithMetadata();
            this.requestUpdate();
        }

        if (this._draggingBar && this._initialBarMouseOffset !== null) {
            let offsetX = mouseX - this._initialBarMouseOffset;

            // need to sort range selector data before utilizing it as `start` or `end`
            const [start, end] = this.data.map((posX) => scaleX(posX)).sort((a: number, b: number) => a - b);

            // user agent might render viewbox which pixel metrics are fractional
            // and that value might exceed scale range
            const rightBoundary = Math.min(range[1], right - left);

            // Constrain the offsetX within container boundaries
            if (start + offsetX < 0) offsetX = -start;
            if (end + offsetX > rightBoundary) offsetX = rightBoundary - end;

            this.data = [scaleX.invert(start + offsetX), scaleX.invert(end + offsetX)];
            this.dispatchEvent(new CustomEvent('change', { detail: this.data }));
            this._initialBarMouseOffset = mouseX;
            this._emitChangeWithMetadata();
            this.requestUpdate();
        }

        this.dispatchEvent(new CustomEvent('cursor-move', { detail: { mouseX, dataX: scaleX.invert(mouseX) } }));
    }

    private _handleMouseLeave() {
        if (this.disabled) return;
        this.state = {
            ...this.state,
            isVisible: false,
        };
        this.dispatchEvent(new CustomEvent('cursor-leave'));
    }

    private _handleMouseDown(event: MouseEvent, index: number) {
        event.stopPropagation();
        this._draggingIndex = index;
    }

    private _handleMouseClick(event: MouseEvent) {
        if (this.disabled) return;
        event.stopPropagation();

        if (this.isRangeCreated || !Array.isArray(this.data)) {
            return;
        }

        if (this._container && this.isMouseOverContainer(event.clientX, event.clientY)) {
            const mouseX = event.clientX - this.containerBox.left;
            const [scaleX] = this.getScales() as LinearScale[];
            const newValue = scaleX.invert(mouseX);

            this.data.push(newValue);

            this.dispatchEvent(new CustomEvent('change', { detail: this.data }));
            this._emitChangeWithMetadata();
            this.requestUpdate();
        }
    }

    private _handleBarMouseDown(event: MouseEvent) {
        if (!this._container || this.disabled || this.data.length < 2) {
            return;
        }

        event.stopPropagation();
        this._draggingBar = true;
        this._initialBarMouseOffset = event.clientX - this.containerBox.left;
    }

    private _emitChangeWithMetadata() {
        const allLinesData = this.allFilteredData;
        const [scaleX, scaleY] = this.getScales() as [LinearScale, LinearScale];
        const [firstRuler, secondRuler] = this.data ?? [0, 0];

        const firstRulerData = this._getRulerData(firstRuler, scaleX, allLinesData);
        const secondRulerData = this._getRulerData(secondRuler, scaleX, allLinesData);

        const firstRulerPositions = this._calculateRulerPositions(
            firstRulerData,
            scaleX,
            scaleY,
            this.containerBox.height
        );
        const secondRulerPositions = this._calculateRulerPositions(
            secondRulerData,
            scaleX,
            scaleY,
            this.containerBox.height
        );

        this.dispatchEvent(
            new CustomEvent('metadata', {
                detail: {
                    selected: getYInRange(
                        allLinesData.map((x) => x.data.flat()),
                        this.data,
                        this.rangeYInRangeInvalidStrategy
                    ),
                    cursors: [
                        {
                            positionX: scaleX(firstRuler),
                            dataX: firstRuler,
                            ySlice: {
                                data: firstRulerData,
                                positions: firstRulerPositions,
                            },
                        },
                        {
                            positionX: scaleX(secondRuler),
                            dataX: secondRuler,
                            ySlice: {
                                data: secondRulerData,
                                positions: secondRulerPositions,
                            },
                        },
                    ],
                },
            })
        );
    }

    private _getRulerData(ruler: number, scaleX: LinearScale, allLinesData: any) {
        return this.interpolatedMetadata
            ? findDataAllPointsByX(scaleX(ruler), allLinesData, {
                  interpolateFn: interpolateNumber,
                  pixelProximityThreshold: this.pixelProximityThreshold,
              })
            : findDataAllPointsByX(scaleX(ruler), allLinesData, {
                  pixelProximityThreshold: this.pixelProximityThreshold,
              });
    }

    private _calculateRulerPositions(
        rulerData: any,
        scaleX: LinearScale,
        scaleY: LinearScale,
        containerHeight: number
    ) {
        return rulerData.map((d: any) => ({
            x: d.x != null ? scaleX(d.x) : null,
            y: d.y != null ? scaleY(d.y) - containerHeight : null,
        }));
    }
}
