import { AABB, intersects } from './geometrics';
import { definitelyGreaterThanOrEqual, definitelyLessThanOrEqual } from './tolerant';

export type StoredBox = AABB & { data?: any };

export class QuadTree {
    private _boundary: AABB;
    private _capacity: number;
    private _boxes: StoredBox[];
    private _divided: boolean;

    private _northeast?: QuadTree;
    private _northwest?: QuadTree;
    private _southeast?: QuadTree;
    private _southwest?: QuadTree;

    constructor(boundary: AABB, capacity = 10) {
        this._boundary = boundary;
        this._capacity = capacity;
        this._boxes = [];
        this._divided = false;
    }

    size() {
        return (
            this._boxes.length +
            (this._divided
                ? this._northeast!.size() + this._northwest!.size() + this._southeast!.size() + this._southwest!.size()
                : 0)
        );
    }

    insert(box: StoredBox) {
        if (!intersects(box, this._boundary)) {
            return false;
        }

        if (!this._divided && this._boxes.length < this._capacity) {
            this._boxes.push({ ...box });
            return true;
        }

        if (!this._divided) {
            this._subdivide();
        }

        // relying on the lazy evaluation of Array.some
        const insertedInChild = [this._northwest!, this._northeast!, this._southeast!, this._southwest!]
            .filter((child) => child?._surrounds(box))
            .some((child) => child?.insert(box));

        if (!insertedInChild) {
            this._boxes.push({ ...box });
        }

        return true;
    }

    hasIntersectingBoxesWith(box: AABB, filter: (stored: StoredBox) => boolean = () => true): boolean {
        if (!intersects(this._boundary, box)) {
            return false;
        }

        return (
            this._boxes.some((b) => intersects(b, box) && filter(b)) ||
            (this._divided &&
                (this._northeast!.hasIntersectingBoxesWith(box, filter) ||
                    this._northwest!.hasIntersectingBoxesWith(box, filter) ||
                    this._southeast!.hasIntersectingBoxesWith(box, filter) ||
                    this._southwest!.hasIntersectingBoxesWith(box, filter)))
        );
    }

    query(box: AABB, filter: (stored: StoredBox) => boolean = () => true): StoredBox[] {
        if (!intersects(this._boundary, box)) {
            return [];
        }

        const local = this._boxes.filter((b) => intersects(b, box)).filter(filter);
        if (!this._divided) {
            return local;
        }

        return [
            ...local,
            ...this._northwest!.query(box, filter),
            ...this._northeast!.query(box, filter),
            ...this._southeast!.query(box, filter),
            ...this._southwest!.query(box, filter),
        ];
    }

    private _subdivide() {
        const epsilon = 1e-9;
        const { minX, maxX, minY, maxY } = this._boundary;

        const xSplit = (maxX + minX) / 2 + epsilon;
        const ySplit = (maxY + minY) / 2 + epsilon;

        const northEastBoundary = { minX: xSplit, maxX, minY, maxY: ySplit };
        const northWestBoundary = { minX, maxX: xSplit, minY, maxY: ySplit };
        const southEastBoundary = { minX: xSplit, maxX, minY: ySplit, maxY };
        const southWestBoundary = { minX, maxX: xSplit, minY: ySplit, maxY };

        this._northeast = new QuadTree(northEastBoundary, this._capacity);
        this._northwest = new QuadTree(northWestBoundary, this._capacity);
        this._southeast = new QuadTree(southEastBoundary, this._capacity);
        this._southwest = new QuadTree(southWestBoundary, this._capacity);

        const quads = [this._northeast, this._northwest, this._southeast, this._southwest];
        this._boxes = this._boxes.reduce((remaining: StoredBox[], box) => {
            const surrounding = quads.find((q) => q._surrounds(box));
            if (surrounding && surrounding.insert(box)) {
                return remaining;
            }
            remaining.push(box);
            return remaining;
        }, []);

        this._divided = true;
    }

    private _surrounds(box: AABB) {
        const { minX, maxX, minY, maxY } = this._boundary;
        return (
            definitelyGreaterThanOrEqual(box.minX, minX) &&
            definitelyLessThanOrEqual(box.maxX, maxX) &&
            definitelyGreaterThanOrEqual(box.minY, minY) &&
            definitelyLessThanOrEqual(box.maxY, maxY)
        );
    }
}
