import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const axisStyles: GenerateStyles = (tagName) => {
    const { root, is } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: flex;
                width: 100%;
                height: 100%;
                user-select: none;
            }

            :host(:not([flip])) {
                align-items: flex-start;
            }

            :host([flip]) {
                align-items: flex-end;
            }

            :host([orientation='vertical'][flip]) {
                justify-content: left;
                align-items: initial;
            }

            :host([orientation='vertical']:not([flip])) {
                justify-content: right;
                align-items: initial;
            }

            ${root} {
                width: 100%;
                height: 100%;
                display: flex;
                color: var(--label-color);
                overflow: visible;
            }

            svg {
                width: 100%;
                overflow: visible;
            }

            ${is('vertical')} {
                width: auto;
                height: 100%;
                flex-direction: row;
            }

            ${is('vertical')} > * {
                flex-shrink: 0;
            }

            ${is('vertical')} svg {
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
            }

            ${is('horizontal')} {
                height: auto;
                width: 100%;
                flex-direction: column;
            }

            .axis-label {
                display: inline-block;
                font-size: var(--label-font-size);
                line-height: var(--label-font-size);
                text-align: center;
                position: relative;
            }

            .axis-label-inner {
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                position: absolute;
                inset: 0;
            }

            ${is('vertical')} .axis-label {
                order: 1;
                min-width: calc(var(--label-font-size, 16px) + 2px);

                writing-mode: vertical-rl;
                transform: rotate(180deg);
            }

            ${is('vertical', 'flip')} .axis-label {
                order: 3;
            }

            ${is('horizontal')} .axis-label {
                order: 3;
                min-height: calc(var(--label-font-size, 16px) + 2px);
            }

            ${is('horizontal', 'flip')} .axis-label {
                order: 1;
            }

            .tick-wrapper {
                height: 100%;
            }

            .ticks {
                color: var(--tick-color);
            }

            .axis > path {
                stroke: var(--axis-color);
            }

            ${is('vertical')} .tick-wrapper {
                position: relative;
                width: 6px;
                margin-right: 0.5px;
                order: 3;
            }

            ${is('vertical', 'flip')} .tick-wrapper {
                order: 1;
            }

            ${is('horizontal')} .ticks {
                margin-top: 0.5px;
            }

            ${is('horizontal', 'flip')} .ticks {
                order: 3;
            }

            ${is('vertical')} .axis-numbers {
                height: 100%;
                display: grid;
                grid-template-rows: 1fr;
                grid-template-columns: 1fr;
                margin-right: 2px;
                order: 2;
                transition: width 0.2s;
            }

            .number {
                font-size: var(--tick-font-size);
                white-space: var(--label-white-space);
                text-align: var(--label-text-align);
            }

            ${is('vertical')} .number {
                position: relative;
                top: calc(var(--tick-font-size) / -2);
                grid-area: 1 / 1;
                height: fit-content;
                text-align: right;
            }

            ${is('vertical', 'flip')} .number {
                text-align: left;
            }

            ${is('vertical', 'vertical-labels')} .number {
                top: 0;
                writing-mode: vertical-lr;
            }

            ${is('horizontal')} .axis-numbers {
                width: 100%;
                display: grid;
                grid-template-rows: 1fr;
                grid-template-columns: 1fr;
                transition: height 0.2s;
            }

            ${is('horizontal', 'flip')} .axis-numbers {
                order: 2;
            }

            ${is('horizontal')} .number {
                width: fit-content;
            }

            ${is('horizontal')} .number-wrapper {
                grid-area: 1 / 1;
                transform: translateX(-50%);
                width: var(--label-width, fit-content);
                height: fit-content;
                text-align: center;
                position: relative;
                transition: transform 0.2s;
            }

            ${is('tilt45')} .number-wrapper {
                transform: translateX(10px) translateY(0px) rotate(45deg);
                transform-origin: top left;
            }

            ${is('tilt45', 'flip')} .number-wrapper {
                top: 100%;
                transform: translateX(0px) translateY(-10px) rotate(-45deg);
            }

            ${is('tilt45')} .axis-numbers {
                height: calc(12px + var(--label-height, 0));
            }

            ${is('hidden', 'horizontal')} .axis-numbers {
                height: 0;
                overflow: hidden;
            }

            ${is('hidden', 'vertical')} .axis-numbers {
                width: 0;
                overflow: hidden;
            }
        `,
    ];
};
