import { Datum, isNumber } from '@umetrics/sartorius-ui-plot-utils';
import { Domain, Domain2D, isCategoricalDomain, isDateDomain } from './domain';
import { mapEntries } from '../genericUtils';
import {
    scaleBand,
    ScaleBand,
    scaleLinear,
    ScaleLinear,
    scaleLog,
    ScaleLogarithmic,
    ScaleTime,
    scaleTime,
} from 'd3-scale';
import { Dimension } from './plotContext';
import { defaultZoomLens } from './constants';

export type DomainMap = Record<string, Domain | Datum[]>;
export type ScaleType = 'auto' | 'band' | 'linear' | 'log' | 'timescale';
export type Orientation = 'horizontal' | 'vertical';
export type BandScale = ScaleBand<Datum>;
export type LinearScale = ScaleLinear<number, number>;
export type LogScale = ScaleLogarithmic<number, number>;
export type DateScale = ScaleTime<number, number | Date>;

export type D3Scale = BandScale | LinearScale | LogScale | DateScale;

export interface ScaleWithMeta {
    scaleType: Omit<ScaleType, 'auto'>;
    domain: Domain;
    scale: D3Scale;
    orientation: Orientation;
    ticks?: ScaleCustomTick[] | number;
}

export type ScaleMap = Record<string, ScaleWithMeta>;

export type ScaleZoomMap = Record<string, Domain>;

interface PaddingOptions {
    type: 'percent' | 'pixel' | 'data';
    start?: number;
    end?: number;
}

interface ScaleCustomTick {
    value: Datum;
    label: Datum;
}

interface ScaleSettings {
    scaleType: ScaleType;
    domain?: Domain | Datum[];
    symmetricDomain?: boolean;
    includeValue?: number;
    padding?: PaddingOptions;
    orientation: Orientation;
    ticks?: ScaleCustomTick[] | number;
}

interface ScaleGeneratorOptions<D extends Domain | Datum[] = Domain | Datum[]> {
    domain: D;
    range: [number, number];
    orientation: Orientation;
}

type LinearScaleGeneratorOptions = ScaleGeneratorOptions<Domain> & {
    ticks?: ScaleCustomTick[] | number;
};

export type ScaleSettingsMap = Record<string, ScaleSettings>;
export type PartialScaleSettingsMap = Record<string, Partial<ScaleSettings>> | undefined;

export const defaultZoomBox: Domain2D = {
    x: defaultZoomLens,
    y: defaultZoomLens,
};

const defaultPaddingOptions: PaddingOptions = {
    type: 'percent',
    start: 0,
    end: 0,
};

const defaultScaleSettings: PartialScaleSettingsMap = {
    x: {
        scaleType: 'auto',
        orientation: 'horizontal',
    },
    y: {
        scaleType: 'auto',
        orientation: 'vertical',
    },
    zoomX: {
        scaleType: 'auto',
        domain: defaultZoomLens,
        orientation: 'horizontal',
    },
    zoomY: {
        scaleType: 'auto',
        domain: defaultZoomLens,
        orientation: 'vertical',
    },
};

export const mergeScaleSettingsWithDefault = (
    scaleSettings: PartialScaleSettingsMap,
    defaultSettings: PartialScaleSettingsMap = defaultScaleSettings
): ScaleSettingsMap => {
    if (!scaleSettings) return defaultSettings as ScaleSettingsMap;
    return {
        ...(defaultSettings as ScaleSettingsMap),
        ...mapEntries(scaleSettings, ([key, settings]) => ({
            orientation: 'vertical',
            scaleType: 'auto',
            ...defaultSettings[key],
            ...settings,
        })),
    };
};

const makeBandScale = ({ domain, range, orientation }): ScaleWithMeta => {
    return {
        scaleType: 'band',
        domain,
        scale: scaleBand<Datum>().domain(domain).range(range).paddingInner(0.05).paddingOuter(0.5).align(1.0),
        orientation,
        // padding should be configurable....
    };
};
const makeLinearScale = ({ domain, range, orientation, ticks }: LinearScaleGeneratorOptions): ScaleWithMeta => {
    return {
        scaleType: 'linear',
        domain,
        scale: scaleLinear().domain(domain).range(range),
        ticks,
        orientation,
    };
};

/**
 * base 10 log only. scaleLog support custom base, so it can be a setting if needed
 *
 * @param domain
 * @param range
 */
const makeLogScale = ({ domain, range, orientation, ticks }): ScaleWithMeta => {
    if (
        (domain.length === 2 && domain[0] <= 0 && domain[1] >= 0) ||
        (domain.length === 3 && domain[0] <= 0 && domain[2] >= 0)
    ) {
        throw Error('Log domain must not contain 0 (log(0)=-)');
    }
    const r = domain.length === 3 ? [range[0], range[0] / 2, range[1]] : range;

    return {
        scaleType: 'log',
        domain,
        scale: scaleLog(domain, r) as LogScale,
        ticks,
        orientation,
    };
};

const makeTimeScale = ({ domain, range, orientation }): ScaleWithMeta => {
    return {
        scaleType: 'timescale',
        domain,
        scale: scaleTime().domain(domain).range(range),
        orientation,
    };
};

const makeAutoScale = ({ domain, range, orientation }): ScaleWithMeta => {
    if (domain.length === 2 && domain.every((d) => typeof d === 'number')) {
        return makeLinearScale({ domain, range, orientation } as LinearScaleGeneratorOptions);
    } else if (domain.length === 2 && domain.every((d) => d instanceof Date)) {
        return makeTimeScale({ domain, range, orientation });
    } else {
        return makeBandScale({ domain, range, orientation });
    }
};

const applyDomainOptionsHelper = (domain: Domain | Datum[], options?: Domain | Datum[], include?: number) => {
    if (isCategoricalDomain(domain)) {
        return options ?? domain;
    }

    if (isDateDomain(domain)) {
        const [dateMin, dateMax] = (domain as Date[]) ?? [undefined, undefined];
        const [optMin, optMax] = (options as Date[]) ?? [undefined, undefined];
        return [optMin ?? dateMin, optMax ?? dateMax];
    }

    const [dMin, dMax] = (domain as Domain) ?? [undefined, undefined];
    const [oMin, oMax] = (options as Domain) ?? [undefined, undefined];
    const minVals = [oMin ?? dMin, include].filter(isNumber) as number[];
    const maxVals = [oMax ?? dMax, include].filter(isNumber) as number[];

    const min = Math.min.apply(null, minVals);
    const max = Math.max.apply(null, maxVals);
    return [min, max];
};

const applyPaddingOptions = (domain: Domain | Datum[], options: PaddingOptions | undefined, pixelRange: Domain) => {
    if (isCategoricalDomain(domain) || isDateDomain(domain)) {
        return domain;
    }
    const [min, max] = domain as Domain;
    const { start, end, type } = options ?? defaultPaddingOptions;
    if (type === 'percent') {
        const range = Math.abs(max - min);
        return [min - range * start!, max + range * end!];
    } else if (type === 'data') {
        return [min - start!, max + end!];
    } else {
        const [rmin, rmax] = pixelRange;
        const tempScale = scaleLinear()
            .domain(domain as Domain)
            .range(pixelRange);
        return [tempScale.invert(rmin - start!), tempScale.invert(rmax + end!)];
    }
};

//Zoom should be in percentage, [0,1] means show the whole range, [0, 0.5] is the first half, [0.5,1] the second.
//Showing half the data results in 2x zoom
export const applyZoom = (zoom: Domain, range: Domain): Domain => {
    const rangeDelta = range[1] - range[0];
    const zoomLevel = 1 / Math.abs(zoom[1] - zoom[0]);
    const zoomedDelta = rangeDelta * zoomLevel;
    const start = -zoomedDelta * zoom[0];
    const end = start + zoomedDelta;

    return [start, end];
};

export const applyScaleSettings = (
    scaleSettings: ScaleSettingsMap,
    fittedDomainMap: DomainMap,
    [width, height]: Dimension,
    scaleZoomMap: ScaleZoomMap
) => {
    return mapEntries(scaleSettings, ([key, setting]) => {
        const domain = applyDomainOptionsHelper(fittedDomainMap[key], setting.domain, setting.includeValue);
        const pixelRange: Domain = setting.orientation === 'horizontal' ? [0, width] : [0, height];
        const paddedDomain = applyPaddingOptions(domain, setting.padding, pixelRange);
        const scaleZoom: Domain = scaleZoomMap[key];

        const range =
            setting.orientation === 'horizontal'
                ? applyZoom(scaleZoom, [0, width])
                : applyZoom([scaleZoom[1], scaleZoom[0]], [height, 0]); //Inverted since pixel coordinates start from top

        switch (setting.scaleType) {
            case 'band':
                return makeBandScale({ domain: paddedDomain, range, orientation: setting.orientation });
            case 'linear':
                return makeLinearScale({
                    domain: paddedDomain as Domain,
                    range,
                    orientation: setting.orientation,
                    ticks: setting.ticks,
                });
            case 'log':
                return makeLogScale({
                    domain: paddedDomain,
                    range,
                    orientation: setting.orientation,
                    ticks: setting.ticks,
                });
            case 'auto':
                return makeAutoScale({ domain: paddedDomain, range, orientation: setting.orientation });
            case 'timescale':
                return makeTimeScale({ domain: paddedDomain, range, orientation: setting.orientation });
        }
    });
};
