type ThrottledFunction<T extends (...args: any) => any> = (...args: Parameters<T>) => ReturnType<T>;

export function perfThrottle<T extends (...args: any) => any>(func: T, limit: number): ThrottledFunction<T> {
    let inThrottle: boolean;
    let lastResult: ReturnType<T>;

    return function (this: any): ReturnType<T> {
        if (!inThrottle) {
            inThrottle = true;
            setTimeout(() => (inThrottle = false), limit);
            // eslint-disable-next-line prefer-rest-params
            lastResult = func(...arguments);
        }
        return lastResult;
    };
}

export function perfThrottleLatestSubmitted<T extends (...args: any) => any>(
    func: T,
    limit: number
): ThrottledFunction<T> {
    let timeout: NodeJS.Timeout | null;
    let lastResult: ReturnType<T>;
    let lastRun = Date.now() - limit;

    return function (this: any): ReturnType<T> {
        // ensure that the latest submitted function always run
        if (timeout) {
            clearTimeout(timeout);
        }
        const now = Date.now();
        const wait = Math.max(0, lastRun - now + limit);
        timeout = setTimeout(() => {
            // eslint-disable-next-line prefer-rest-params
            lastResult = func(...arguments);
            lastRun = Date.now();
            timeout = null;
        }, wait);
        return lastResult;
    };
}
