import {
    BaseElement,
    booleanConverter,
    createClassName,
    cssVariables,
    defineElement,
    html,
    property,
    state,
    svg,
} from '@umetrics/sartorius-ui-base';
import { version } from '../../metadata';
import { WithContext } from '../../data/plotContext';
import { namespacedTag } from '../../tag';
import { WithInteractionContext } from '../interactionContext';
import { Dir2 } from './utilityTypes';
import { zoomAndPanStyles } from './zoomAndPanStyles';
import { Domain, Domain2D, getDomainZoom } from '../../data/domain';
import { isNumber } from '@umetrics/sartorius-ui-plot-utils';
import { LinearScale, ScaleZoomMap } from '../../data/scale';
import { WithZoomBaseContext, ZoomAndPanProperties, ZoomStrategy } from '../zoomBaseContext';
import { defaultZoomLens } from '../../data/constants';

export function isFiniteNumber(value) {
    // Move to utils
    return isNumber(value) && value !== Infinity && value !== -Infinity;
}

interface ScrollDragState {
    prevX: number;
    prevY: number;
    dir: Dir2;
}

interface ZoomPanEventDetail {
    viewbox?: Domain2D;
    zoom?: {
        x: number;
        y: number;
    };
    zoomState?: ScaleZoomMap;
}

@defineElement({ tagName: namespacedTag('zoom-and-pan'), styles: zoomAndPanStyles, version })
export class ZoomAndPan
    extends WithInteractionContext(WithContext(WithZoomBaseContext(BaseElement)))
    implements ZoomAndPanProperties
{
    // Static members

    // Private members

    // Protected members

    // Public members

    @property({ type: Boolean, attribute: 'hide-scrollbar-x', reflect: false, converter: booleanConverter })
    hideScrollbarX?: boolean;
    @property({ type: Boolean, attribute: 'hide-scrollbar-y', reflect: false, converter: booleanConverter })
    hideScrollbarY?: boolean;

    @state()
    _mouseOver = false;

    scaleX = 'zoomX';
    scaleY = 'zoomY';

    zoomStrategy: ZoomStrategy = ZoomStrategy.Viewport; // Deprecated. Set MultiAxis

    private _scrollDragState: ScrollDragState | null = null;

    constructor() {
        super();

        this._handleScrollDrag = this._handleScrollDrag.bind(this);
        this._handleScrollDragMove = this._handleScrollDragMove.bind(this);
        this._handleScrollDragUp = this._handleScrollDragUp.bind(this);
    }

    public outerEvent(e: MouseEvent | WheelEvent) {
        if (e.type === 'wheel') {
            this.wheelHandler(e as WheelEvent);
            e.preventDefault();
            e.stopPropagation();
        } else if (e.type === 'mousedown') {
            this.mouseDownHandler(e);
        } else if (e.type === 'mouseenter') {
            this._mouseOver = true;
        } else if (e.type === 'mouseleave') {
            this._mouseOver = false;
        }
    }

    protected render() {
        if (!this.size) return;
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            [`${this._cursorOverlay}`]: !!this._cursorOverlay,
            ['normal']:
                !!this.viewbox &&
                this.viewbox.x[0] === 0 &&
                this.viewbox.x[1] === 1 &&
                this.viewbox.y[0] === 0 &&
                this.viewbox.y[1] === 1,
        });
        const [width, height] = this.size;

        return html`
            <div class=${baseClassName + '-cursor-overlay'}></div>
            <svg width=${width} height=${height} class="${rootClasses}">${this.renderScrollbar()}</svg>
        `;
    }

    protected renderScrollbar() {
        if (!this.size || !this.viewbox || (this.hideScrollbarX && this.hideScrollbarY)) {
            return;
        }

        const [width, height] = this.size;
        const thickness = 6;
        const bounds = this.customDomainBounds ?? { x: this.boundsX, y: this.boundsY };

        const viewbox = this._getViewbox();

        const [zoomX, zoomY] = getDomainZoom(viewbox, bounds);

        // No defined min size for the ruler, but use "thickness" for now
        const sliderWidth = Math.max(width / zoomX, thickness);
        const sliderHeight = Math.max(height / zoomY, thickness);

        const {
            x: [bx0, bx1],
            y: [by0, by1],
        } = bounds;
        const {
            x: [vx0],
            y: [, vy1],
        } = viewbox;

        const sliderX = ((vx0 - bx0) / (bx1 - bx0)) * width;
        const sliderY = height - ((vy1 - by0) / (by1 - by0)) * height;

        const shouldRenderHorizontalScroll = !this.disableX && !this.hideScrollbarX && isFiniteNumber(sliderWidth);
        const shouldRenderVerticalScroll = !this.disableY && !this.hideScrollbarY && isFiniteNumber(sliderHeight);

        const panActive = this._mouseOver || this._panState || this._scrollDragState;
        return [
            shouldRenderHorizontalScroll &&
                svg`<g class=${'horizontal-scroll' + (panActive ? ' active' : '')}
                transform='translate(0,${height})'>
                <rect x='0' y='0' height='${thickness}' fill='${cssVariables.color_secondary_2}' width='${width}'/>
                <rect @mousedown='${this._handleScrollDrag(Dir2.Horizontal)}'
                      class='scroll-slider horizontal'
                      x='${sliderX}'
                      y='0'
                      height='${thickness}'
                      width='${sliderWidth}' />
            </g>`,
            shouldRenderVerticalScroll &&
                svg`<g class=${'vertical-scroll' + (panActive ? ' active' : '')} transform='translate(${-thickness},0)'>
                <rect x='0' y='0' height='${height}' fill='${cssVariables.color_secondary_2}' width='${thickness}'/>
                <rect @mousedown='${this._handleScrollDrag(
                    Dir2.Vertical
                )}' class='scroll-slider vertical' x='0' y='${sliderY}' height='${sliderHeight}' width='${thickness}'/>
            </g>`,
        ];
    }

    private _getViewbox = (): Domain2D => {
        if (this.zoomStrategy === 'Viewport') {
            return this.viewbox!;
        }
        const other: Domain = defaultZoomLens;
        if (this._hoverAxis) {
            const domain = this.scaleZoomMap?.[this._hoverAxis.scaleKey] ?? other;
            if (this._hoverAxis.orientation === 'horizontal') {
                return { x: domain, y: other };
            } else {
                return { x: other, y: domain };
            }
        }
        return { x: this.scaleZoomMap?.[this.scaleX] ?? other, y: this.scaleZoomMap?.[this.scaleY] ?? other };
    };

    private _handleScrollDrag = (dir: Dir2) => (e) => {
        e.stopPropagation();
        this._scrollDragState = {
            prevX: e.clientX,
            prevY: e.clientY,
            dir,
        };
        this._panState = {
            prevX: e.clientX,
            prevY: e.clientY,
            scaleKey: null,
            slot: '',
        };

        this._cursorOverlay = 'dragging';

        window.addEventListener('mousemove', this._handleScrollDragMove);
        window.addEventListener('mouseup', this._handleScrollDragUp);
    };

    private _handleScrollDragMove = (e) => {
        e.preventDefault();
        const viewbox = this._getViewbox();
        if (this._scrollDragState && viewbox) {
            const [scaleX, scaleY] = this.getScales() as [LinearScale, LinearScale];
            const { clientX, clientY } = e;
            const dxScreen = clientX - this._scrollDragState.prevX;
            const dyScreen = clientY - this._scrollDragState.prevY;

            const {
                x: [xmin, xmax],
                y: [ymin, ymax],
            } = viewbox;

            const xExtent = Math.max(1e-9, xmax - xmin);
            const yExtent = Math.max(1e-9, ymax - ymin);

            const newXmin = scaleX.invert(scaleX(xmin) + dxScreen / xExtent);
            const newXmax = scaleX.invert(scaleX(xmax) + dxScreen / xExtent);
            const newYmin = scaleY.invert(scaleY(ymin) + dyScreen / yExtent);
            const newYmax = scaleY.invert(scaleY(ymax) + dyScreen / yExtent);

            //Can only pan inside outer bounds
            const [xBoundsMin, xBoundsMax] = this.boundsX;
            const [yBoundsMin, yBoundsMax] = this.boundsY;
            let x: Domain, y: Domain;

            if (this._scrollDragState.dir === Dir2.Horizontal) {
                x = this._getBoundValue(newXmin, xBoundsMin, newXmax, xBoundsMax, viewbox.x);
                y = viewbox.y;
            } else {
                x = viewbox.x;
                y = this._getBoundValue(newYmin, yBoundsMin, newYmax, yBoundsMax, viewbox.y);
            }
            const newDomain: Domain2D = {
                x,
                y,
            };
            const updatedZoomState = this.panOnViewport(clientX, clientY, this._panState!);

            this._panState = {
                ...this._panState!,
                prevX: clientX,
                prevY: clientY,
            };

            this._scrollDragState = {
                ...this._scrollDragState,
                prevX: e.clientX,
                prevY: e.clientY,
            };
            this._dispatchPanEvent<ZoomPanEventDetail>({
                zoomState: updatedZoomState,
                viewbox: newDomain,
            });
        }
    };

    private _getBoundValue(
        newMin: number,
        boundsMin: number,
        newMax: number,
        boundsMax: number,
        currentView: Domain
    ): Domain {
        if (newMin >= boundsMin && newMax <= boundsMax) {
            return [newMin, newMax];
        }
        if (newMin >= boundsMin) {
            return [currentView[0], boundsMax];
        }
        if (newMin <= boundsMax) {
            return [boundsMin, currentView[1]];
        }
        return currentView;
    }

    private _handleScrollDragUp = () => {
        this._scrollDragState = null;
        this._panState = null;
        window.removeEventListener('mousemove', this._handleScrollDragMove);
        window.removeEventListener('mouseup', this._handleScrollDragUp);
        this._cursorOverlay = undefined;
        this._panState = null;
    };
}
