import { AxisSettings, YAxisSettings } from '@umetrics/sartorius-ui-plot-utils';
import { defaultTickSize } from './axis/renderAxis';
import { DateScale, LogScale } from './data';
import { timeTicks } from 'd3-time';
import { customFontSize, TextMeasurer } from './axis/textMeasurer';
import { dateFormatter, DateTimeResolution, ResolutionOptions } from '@umetrics/sartorius-ui-utils';

const _textMeasurer = new TextMeasurer();

// an odd function, generating a 'len' number string of 'H' characters to try using as much width as possible
const _hTemplate = (len: number) =>
    new Array(Math.floor(len))
        .fill(0)
        .map(() => 'H')
        .join('');

const _dateTimeResolution = (d: Date): DateTimeResolution => {
    if (d.getMilliseconds() !== 0) {
        return 'millisecond';
    }
    if (d.getSeconds() !== 0) {
        return 'second';
    }
    if (d.getMinutes() !== 0 || d.getHours() !== 0) {
        return 'minute';
    }
    if (d.getDate() !== 1) {
        return 'day';
    }
    if (d.getMonth() !== 0) {
        return 'month';
    }
    return 'year';
};

export const getFinestDateTimeResolution = (dates: Date[]) => {
    const order = dates.reduce((finest, date) => {
        const res = _dateTimeResolution(date);
        const resOrder = ResolutionOptions.indexOf(res);
        return resOrder > finest ? resOrder : finest;
    }, 0);
    return ResolutionOptions[order];
};

export const scaleDateFormatter = (resolution: DateTimeResolution) =>
    dateFormatter({ resolution, showTimezoneOffset: false });

export const getTimescaleXTicks = (axisSettings: AxisSettings, timeScale: DateScale, viewportWidth: number) => {
    const yearTemplateLength = _textMeasurer.measureTextLength('HH', customFontSize(axisSettings.tickFontSize ?? 12));

    // get candidate ticks, pretending to need only year format length
    const startDate = timeScale.invert(0);
    const endDate = timeScale.invert(viewportWidth);
    const candidateTicks = timeTicks(startDate, endDate, Math.floor(viewportWidth / yearTemplateLength));
    const resolution = getFinestDateTimeResolution(candidateTicks);
    const formatter = scaleDateFormatter(resolution);
    const representationLength = Math.max(formatter(startDate).length, formatter(endDate).length);
    const representationTemplate = _hTemplate(representationLength);
    const representationTemplateLength =
        _textMeasurer.measureTextLength(representationTemplate, customFontSize(axisSettings.tickFontSize ?? 12)) +
        (axisSettings.labelGapMin || 10);
    let gaps: number[];
    let minGap: number;
    let maxGap: number;
    let est = Math.floor(viewportWidth / representationTemplateLength);
    let ticks: Date[];
    do {
        est -= 2;
        ticks = timeTicks(startDate, endDate, est);
        if (ticks.length <= 1) {
            return ticks;
        }
        gaps = ticks.slice(1).map((t, idx) => (timeScale(t) as number) - (timeScale(ticks[idx]) as number));
        maxGap = Math.max(...gaps);
        minGap = Math.min(...gaps);
    } while (maxGap < representationTemplateLength && est > 0);
    const hasSmallerGap = maxGap - minGap > 20;
    if (!hasSmallerGap) {
        return ticks;
    }

    const even = [ticks[0]];
    let next = (timeScale(ticks[0]) as number) + maxGap;
    while (next < viewportWidth) {
        even.push(timeScale.invert(next));
        next += maxGap;
    }
    return even;
};

const getTickCount = (dimension, pixelsPerTick, min = 2) => Math.max(min, Math.round(dimension / pixelsPerTick));

// add `minor` ticks between `intermediate` ticks, is useful for a significant zoom
const getLogScaleMinorTicks = (scale: LogScale, scaleTicks: number[], spacePerTick: number): number[] => {
    const minorTicks: number[] = [];

    scaleTicks.forEach((d: number, i: number) => {
        if (i < scaleTicks.length - 1) {
            const next = scaleTicks[i + 1];

            const rangeDiff = Math.abs(scale(d) - scale(next));

            if (rangeDiff > spacePerTick * 2) {
                const minorTickCount = Math.floor(rangeDiff / spacePerTick);
                const step = (next - d) / minorTickCount;

                for (let j = 1; j < minorTickCount; j++) {
                    minorTicks.push(d + j * step);
                }
            }
        }
    });

    return minorTicks;
};

const adjustLogScaleTickCount = ({
    scaleTicks,
    scale,
    viewportSize,
    estimatedTickCount,
}: {
    scaleTicks: number[];
    scale: LogScale;
    viewportSize: number;
    estimatedTickCount: number;
}): number[] => {
    const spacePerTick = Math.round(viewportSize / estimatedTickCount);

    // filter ticks that are too close one to another
    const reducedTickCount: number[] = [scaleTicks[0]];

    scaleTicks.slice(1).forEach((d: number) => {
        const prev = reducedTickCount[reducedTickCount.length - 1];
        const rangeDiff = Math.abs(scale(prev) - scale(d));

        if (rangeDiff > spacePerTick) {
            reducedTickCount.push(d);
        }
    });

    const minorTicks: number[] = getLogScaleMinorTicks(scale, scaleTicks, spacePerTick);

    return [...reducedTickCount, ...minorTicks].sort((a, b) => a - b);
};

const getLogScaleTicks = ({
    axisSettings,
    scale,
    scaleSize,
    viewportSize,
    scaleMeta,
}: {
    scale: LogScale;
    axisSettings: AxisSettings;
    scaleSize: number;
    viewportSize: number;
    scaleMeta;
}) => {
    // uniformly spaced tick count; for a `log10` scale it is set values `1, 10, 100, 1000, ....`
    const uniformTickCount = Math.round(Math.log10(scale.domain()[1]) - Math.log10(scale.domain()[0]));

    const count = getTickCount(
        scaleSize,
        defaultTickSize.y + axisSettings.tickFontSize * defaultTickSize.fontScaling,
        1
    );

    const scaleTicks = scale
        .ticks()
        .map(scale.tickFormat(uniformTickCount * count, 'f'))
        .filter((d) => d)
        .map(Number);

    const minorTicks: number[] = getLogScaleMinorTicks(scale, scaleTicks, Math.round(viewportSize / 2));

    const estimatedTickCount = typeof scaleMeta.ticks === 'number' ? scaleMeta.ticks : axisSettings.estimatedTickCount;

    return estimatedTickCount
        ? adjustLogScaleTickCount({
              scaleTicks,
              scale,
              viewportSize,
              estimatedTickCount,
          })
        : [...scaleTicks, ...minorTicks].sort((a, b) => a - b);
};

export function getXTicks(axisSettings: AxisSettings, scaleMeta, totalWidth: number, viewportWidth: number) {
    const scale = scaleMeta.scale;

    if (scaleMeta.ticks && Array.isArray(scaleMeta.ticks)) {
        return scaleMeta.ticks.map((d) => d.value);
    }
    if (!scale?.ticks) {
        return scale.domain();
    }
    if (scaleMeta.scaleType === 'log') {
        return getLogScaleTicks({
            axisSettings,
            scale,
            scaleSize: totalWidth,
            viewportSize: viewportWidth,
            scaleMeta,
        });
    }

    const estimatedTickCount = typeof scaleMeta.ticks === 'number' ? scaleMeta.ticks : axisSettings.estimatedTickCount;
    const spacePerTick = estimatedTickCount
        ? Math.round(viewportWidth / estimatedTickCount)
        : defaultTickSize.x + axisSettings.tickFontSize * defaultTickSize.fontScaling;

    return scale?.ticks(getTickCount(totalWidth, spacePerTick));
}

export function getYTicks(axisSettings: YAxisSettings, scaleMeta, totalHeight: number, viewportHeight: number) {
    const scale = scaleMeta.scale;

    if (scaleMeta.ticks && Array.isArray(scaleMeta.ticks)) {
        return scaleMeta.ticks.map((d) => d.value);
    }

    if (!scale?.ticks) {
        return scale.domain();
    }

    if (scaleMeta.scaleType === 'log') {
        return getLogScaleTicks({
            axisSettings,
            scale,
            scaleSize: totalHeight,
            viewportSize: viewportHeight,
            scaleMeta,
        });
    }

    const estimatedTickCount = typeof scaleMeta.ticks === 'number' ? scaleMeta.ticks : axisSettings.estimatedTickCount;
    const spacePerTick = estimatedTickCount
        ? Math.round(viewportHeight / estimatedTickCount)
        : defaultTickSize.y + axisSettings.tickFontSize * defaultTickSize.fontScaling;

    return scale?.ticks(getTickCount(totalHeight, spacePerTick));
}
