import { vec2 } from 'gl-matrix';
import { LineSegment } from './util';

const GAUSS_LEGENDRE_7: { t: number; weight: number }[] = [
    // eslint-disable-next-line no-loss-of-precision
    { t: 0.025446043828620737, weight: 0.06474248308443485 },
    // eslint-disable-next-line no-loss-of-precision
    { t: 0.12923440720030278, weight: 0.13985269574463833 },
    // eslint-disable-next-line no-loss-of-precision
    { t: 0.2970774243113014, weight: 0.19091502525255947 },
    { t: 0.5, weight: 0.20897959183673434 },
    // eslint-disable-next-line no-loss-of-precision
    { t: 0.7029225756886986, weight: 0.19091502525255947 },
    // eslint-disable-next-line no-loss-of-precision
    { t: 0.8707655927996972, weight: 0.13985269574463833 },
    { t: 0.9745539561713793, weight: 0.06474248308443485 },
];

export abstract class BezierBase2d<T extends BezierBase2d<T>> {
    get distance() {
        if (!this._distance) {
            this._distance = this._calculateDistance();
        }
        return this._distance;
    }

    get segments() {
        if (!this._segments) {
            this._segments = this._calculateSegments();
        }
        return this._segments;
    }

    abstract points: vec2[];

    protected get ratioPointLUT() {
        if (!this._ratioPointLUT) {
            const steps = this._calculateLookupSteps();
            this._ratioPointLUT = this._buildRatioPointLUT(steps);
        }
        return this._ratioPointLUT;
    }

    protected get ratioDistanceLUT() {
        if (!this._ratioDistanceLUT) {
            this._ratioDistanceLUT = this._buildRatioDistanceLUT();
        }
        return this._ratioDistanceLUT;
    }

    private readonly pFirst: vec2;
    private readonly pLast: vec2;

    private _distance?: number;
    private _segments?: LineSegment[];
    private _ratioPointLUT?: Float32Array;
    private _ratioDistanceLUT?: Float32Array;

    protected constructor(pFirst: vec2, pLast: vec2) {
        this.pFirst = pFirst;
        this.pLast = pLast;
    }

    tAtDistance = (dist: number) => {
        if (dist <= 0) {
            return 0;
        }
        if (dist >= this.distance) {
            return 1;
        }

        const distances = this.ratioDistanceLUT;

        let low = 0;
        let high = distances.length;
        do {
            const pos = (low + high) >> 1; // pos !== high && pos !== low, since high - low >= 2
            if (distances[pos] <= dist) {
                low = pos;
            } else {
                high = pos;
            }
        } while (high - low > 1);

        // state guarantees, high === low - 1, low > 0, high < distances.length
        const dLow = distances[low];
        const dHigh = distances[high];
        const r = (dist - dLow) / (dHigh - dLow);
        return (low + r) / (distances.length - 1);
    };

    abstract point(t: number): vec2;
    abstract split(t: number): { left: T; right: T };

    protected abstract derivative(t: number): vec2;

    private _calculateDistance = () => {
        return GAUSS_LEGENDRE_7.reduce((length, { t, weight }) => {
            const [dx, dy] = this.derivative(t);
            const integrand = Math.sqrt(dx * dx + dy * dy);
            return length + weight * integrand;
        }, 0);
    };

    private _buildRatioPointLUT = (steps: number): Float32Array => {
        const increment = 1 / steps;
        const points = [this.pFirst[0], this.pFirst[1]];
        for (let i = 1; i < steps; ++i) {
            const p = this.point(i * increment);
            points.push(p[0], p[1]);
        }
        points.push(this.pLast[0], this.pLast[1]); // steps + 1 points at even increments of t;
        return Float32Array.from(points);
    };

    private _buildRatioDistanceLUT = () => {
        const points = this.ratioPointLUT;
        const distances = [0];
        for (let i = 2; i < points.length - 2; i += 2) {
            const dx = points[i] - points[i - 2];
            const dy = points[i + 1] - points[i - 1];
            distances.push(distances[(i >> 1) - 1] + Math.sqrt(dx * dx + dy * dy));
        }
        distances.push(this.distance);
        return Float32Array.from(distances);
    };

    private _calculateSegments = () => {
        const points = this.ratioPointLUT;
        const segments: LineSegment[] = [
            {
                points: [vec2.fromValues(points[0], points[1]), vec2.fromValues(points[2], points[3])],
                length: Math.sqrt(
                    (points[2] - points[0]) * (points[2] - points[0]) +
                        (points[3] - points[1]) * (points[3] - points[1])
                ),
            },
        ];

        for (let i = 4; i < points.length - 1; i += 2) {
            const start = segments[segments.length - 1].points[1];
            const end = vec2.fromValues(points[i], points[i + 1]);
            segments.push({ points: [start, end], length: vec2.distance(start, end) });
        }

        return segments;
    };

    private _calculateLookupSteps = () => {
        // alternatively this could be user controlled. should probably not only depend on overall distance but curvature as well.
        return Math.max(Math.min(Math.ceil(this.distance / 2), 20), 1);
    };
}

export class QuadraticBezier2d extends BezierBase2d<QuadraticBezier2d> {
    get points() {
        return [this.p1, this.p2, this.p3];
    }

    private readonly p1: vec2;
    private readonly p2: vec2;
    private readonly p3: vec2;

    constructor(p1: vec2, p2: vec2, p3: vec2) {
        super(p1, p3);

        this.p1 = p1;
        this.p2 = p2;
        this.p3 = p3;
    }

    point = (t: number): vec2 => {
        const oneMinusT = 1 - t;
        const oneMinusT2 = oneMinusT * oneMinusT;
        const tOneMinusT = t * oneMinusT;
        const t2 = t * t;
        return vec2.fromValues(
            oneMinusT2 * this.p1[0] + 2 * tOneMinusT * this.p2[0] + t2 * this.p3[0],
            oneMinusT2 * this.p1[1] + 2 * tOneMinusT * this.p2[1] + t2 * this.p3[1]
        );
    };

    split(t: number) {
        const tMinusOne = t - 1;
        const tMinusOneT = tMinusOne * t;
        const tMinusOne2 = tMinusOne * tMinusOne;
        const t2 = t * t;
        const mid = vec2.fromValues(
            t2 * this.p3[0] - 2 * tMinusOneT * this.p2[0] + tMinusOne2 * this.p1[1],
            t2 * this.p3[1] - 2 * tMinusOneT * this.p2[1] + tMinusOne2 * this.p1[1]
        );
        const left = new QuadraticBezier2d(
            this.p1,
            vec2.fromValues(
                t * this.p2[0] - tMinusOne * this.p1[0], //
                t * this.p2[1] - tMinusOne * this.p1[1] //
            ),
            mid
        );
        const right = new QuadraticBezier2d(
            mid,
            vec2.fromValues(
                t * this.p3[0] - tMinusOne * this.p2[0], //
                t * this.p3[1] - tMinusOne * this.p2[1] //
            ),
            this.p3
        );
        return { left, right };
    }

    protected derivative(t: number): vec2 {
        return vec2.fromValues(
            this._derFn(t, this.p1[0], this.p2[0], this.p3[0]),
            this._derFn(t, this.p1[1], this.p2[1], this.p3[1])
        );
    }

    private _derFn = (t: number, w0: number, w1: number, w2: number) => {
        const oneMinusT = 1 - t;
        return 2 * oneMinusT * (w1 - w0) + 2 * t * (w2 - w1);
    };
}

export class CubicBezier2d extends BezierBase2d<CubicBezier2d> {
    get points() {
        return [this.p1, this.p2, this.p3, this.p4];
    }

    private readonly p1: vec2;
    private readonly p2: vec2;
    private readonly p3: vec2;
    private readonly p4: vec2;

    constructor(p1: vec2, p2: vec2, p3: vec2, p4: vec2) {
        super(p1, p4);

        this.p1 = p1;
        this.p2 = p2;
        this.p3 = p3;
        this.p4 = p4;
    }

    point = (t: number): vec2 => {
        const oneMinusT = 1 - t;
        const oneMinusT2 = oneMinusT * oneMinusT;
        const oneMinusT3 = oneMinusT * oneMinusT2;
        const tOneMinusT2 = t * oneMinusT2;
        const t2 = t * t;
        const t2OneMinusT = t2 * oneMinusT;
        const t3 = t * t2;
        return vec2.fromValues(
            oneMinusT3 * this.p1[0] + 3 * tOneMinusT2 * this.p2[0] + 3 * t2OneMinusT * this.p3[0] + t3 * this.p4[0],
            oneMinusT3 * this.p1[1] + 3 * tOneMinusT2 * this.p2[1] + 3 * t2OneMinusT * this.p3[1] + t3 * this.p4[1]
        );
    };

    split(t: number): { left: CubicBezier2d; right: CubicBezier2d } {
        const t2 = t * t;
        const t3 = t * t2;
        const tMinusOne = t - 1;
        const tMinusOne2 = tMinusOne * tMinusOne;
        const tMinusOne3 = tMinusOne * tMinusOne2;
        const tMinusOneT = tMinusOne * t;
        const tMinusOneT2 = tMinusOne * t2;
        const tMinusOne2T = tMinusOne2 * t;
        const mid = vec2.fromValues(
            t3 * this.p4[0] - 3 * tMinusOneT2 * this.p3[0] + 3 * tMinusOne2T * this.p2[0] - tMinusOne3 * this.p1[0],
            t3 * this.p4[1] - 3 * tMinusOneT2 * this.p3[1] + 3 * tMinusOne2T * this.p2[1] - tMinusOne3 * this.p1[1]
        );
        const left = new CubicBezier2d(
            this.p1,
            vec2.fromValues(
                t * this.p2[0] - tMinusOne * this.p1[0], //
                t * this.p2[1] - tMinusOne * this.p1[1] //
            ),
            vec2.fromValues(
                t2 * this.p3[0] - 2 * tMinusOneT * this.p2[0] + tMinusOne2 * this.p1[0],
                t2 * this.p3[1] - 2 * tMinusOneT * this.p2[1] + tMinusOne2 * this.p1[1]
            ),
            mid
        );
        const right = new CubicBezier2d(
            mid,
            vec2.fromValues(
                t2 * this.p4[0] - 2 * tMinusOneT * this.p3[0] + tMinusOne2 * this.p2[0],
                t2 * this.p4[1] - 2 * tMinusOneT * this.p3[1] + tMinusOne2 * this.p2[1]
            ),
            vec2.fromValues(
                t * this.p4[0] - tMinusOne * this.p3[0], //
                t * this.p4[1] - tMinusOne * this.p3[1] //
            ),
            this.p4
        );
        return { left, right };
    }

    protected derivative(t: number): vec2 {
        return vec2.fromValues(
            this._derFn(t, this.p1[0], this.p2[0], this.p3[0], this.p4[0]),
            this._derFn(t, this.p1[1], this.p2[1], this.p3[1], this.p4[1])
        );
    }

    private _derFn = (t: number, w0: number, w1: number, w2: number, w3: number) => {
        const oneMinusT = 1 - t;
        const oneMinusT2 = oneMinusT * oneMinusT;
        const tOneMinusT = t * oneMinusT;
        const t2 = t * t;
        return 3 * oneMinusT2 * (w1 - w0) + 6 * tOneMinusT * (w2 - w1) + 3 * t2 * (w3 - w2);
    };
}
