import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    property,
    styleMap,
    nothing,
} from '@umetrics/sartorius-ui-base';
import { namespacedTag } from '../tag';
import { portalStyles } from './portalStyles';
import { version } from '../metadata';
import { WithContext, WithContextProps } from '../data/plotContext';
import { LinearScale } from '../data';

export interface PortalProperties extends WithContextProps {
    dx?: number;
    dy?: number;
}

@defineElement({ tagName: namespacedTag('portal'), styles: portalStyles, version })
export class Portal extends WithContext(BaseElement) implements PortalProperties {
    @property()
    dx: number | undefined;

    @property()
    dy: number | undefined;

    render() {
        const [rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        if (!this.size || !this.getScales()) return nothing;
        const [scaleX, scaleY] = this.getScales() as [LinearScale, LinearScale];

        const _styleMap = {
            position: 'absolute',
            transform: `translate(${scaleX(this.dx as number)}px, ${scaleY(this.dy as number)}px)`,
        };

        return html` <slot class="${rootClasses} portal" style=${styleMap(_styleMap)}></slot> `;
    }
}
