import { PathCommandHandler } from './pathSegments';
import { BezierBase2d, CubicBezier2d, QuadraticBezier2d } from './bezier';
import { vec2 } from 'gl-matrix';

export class DashedPathHandler implements PathCommandHandler {
    private readonly _dashArray: number[];
    private _inner: PathCommandHandler;
    private _startX = 0;
    private _startY = 0;
    private _currentDashProgress = 0;
    private _currentDashIndex = 0;
    private _currentX = 0;
    private _currentY = 0;

    constructor(inner: PathCommandHandler, dashArray: number[]) {
        this._inner = inner;
        this._dashArray = dashArray;
    }

    beginPath(): void {
        this._inner.beginPath();
    }

    moveTo(x: number, y: number): void {
        this._currentX = x;
        this._currentY = y;
        this._currentDashIndex = 0;
        this._currentDashProgress = 0;
        this._startX = x;
        this._startY = y;
        this._inner.moveTo(x, y);
    }

    closePath(): void {
        this.lineTo(this._startX, this._startY); // for dashing
        this._inner.closePath(); // for correct cap and join handling
    }

    lineTo(x: number, y: number): void {
        const dx = x - this._currentX;
        const dy = y - this._currentY;
        const startX = this._currentX;
        const startY = this._currentY;
        const dist = Math.sqrt(dx * dx + dy * dy);
        let progress = 0;
        while (progress < dist) {
            const dashValue = this._dashArray[this._currentDashIndex % this._dashArray.length];
            const show = this._currentDashIndex % 2 === 0;
            const remainingDashProgress = 1 - this._currentDashProgress;
            const dashProgressIncrement = Math.min(remainingDashProgress, (dist - progress) / dashValue);
            const progressIncrement = dashProgressIncrement * dashValue;
            const px = startX + (dx * (progress + progressIncrement)) / dist;
            const py = startY + (dy * (progress + progressIncrement)) / dist;

            if (show) {
                this._inner.lineTo(px, py);
            } else {
                this._inner.moveTo(px, py);
            }

            this._currentX = px;
            this._currentY = py;
            progress += progressIncrement;
            this._currentDashProgress += dashProgressIncrement;
            if (this._currentDashProgress === 1) {
                this._currentDashProgress = 0;
                this._currentDashIndex++;
            }
        }
    }

    bezierCurveTo(cp1x: number, cp1y: number, cp2x: number, cp2y: number, x: number, y: number): void {
        const curve = new CubicBezier2d(
            vec2.fromValues(this._currentX, this._currentY),
            vec2.fromValues(cp1x, cp1y),
            vec2.fromValues(cp2x, cp2y),
            vec2.fromValues(x, y)
        );
        this._dashOverBezier(curve);
    }

    quadraticCurveTo(cpx: number, cpy: number, x: number, y: number): void {
        const curve = new QuadraticBezier2d(
            vec2.fromValues(this._currentX, this._currentY),
            vec2.fromValues(cpx, cpy),
            vec2.fromValues(x, y)
        );
        this._dashOverBezier(curve);
    }

    arcTo(
        rx: number,
        ry: number,
        xAxisRotation: number,
        largeArcFlag: number,
        sweepFlag: number,
        x: number,
        y: number
    ) {
        // no dash implementation yet
        this._currentX = x;
        this._currentY = y;
        this._inner.arcTo(rx, ry, xAxisRotation, largeArcFlag, sweepFlag, x, y);
    }

    private _dashOverBezier = <T extends BezierBase2d<T>>(curve: T): void => {
        const segments = curve.segments;
        segments.forEach((segment) => {
            let segmentProgress = 0;
            const endpointVec = vec2.create();
            while (segmentProgress < 1) {
                const dashValue = this._dashArray[this._currentDashIndex % this._dashArray.length];
                const remainingDashProgress = 1 - this._currentDashProgress;
                const remainingDashLength = remainingDashProgress * dashValue;
                const remainingSegmentProgress = 1 - segmentProgress;
                const remainingSegmentLength = remainingSegmentProgress * segment.length;
                const handleLength = Math.min(remainingDashLength, remainingSegmentLength);
                const progressIncrement = handleLength / segment.length;
                const dashProgressIncrement = handleLength / dashValue;
                const endpoint = vec2.lerp(
                    endpointVec,
                    segment.points[0],
                    segment.points[1],
                    segmentProgress + progressIncrement
                );
                const show = this._currentDashIndex % 2 === 0;
                if (show) {
                    this._inner.lineTo(endpoint[0], endpoint[1]);
                } else {
                    this._inner.moveTo(endpoint[0], endpoint[1]);
                }

                this._currentX = endpoint[0];
                this._currentY = endpoint[1];

                segmentProgress += progressIncrement;
                this._currentDashProgress += dashProgressIncrement;
                if (this._currentDashProgress === 1) {
                    this._currentDashProgress = 0;
                    this._currentDashIndex++;
                }
            }
        });
    };
}
