import { Point2D, isNumber } from '@umetrics/sartorius-ui-plot-utils';
import { curveMonotoneX, curveLinear, curveStepAfter, curveCardinal } from 'd3-shape';

export type Interpolation = 'straight' | 'last-good' | 'interpolate-monotone' | 'interpolate-curve';

export const interpolationLookup = {
    straight: curveLinear,
    'last-good': curveStepAfter,
    'interpolate-monotone': curveMonotoneX,
    'interpolate-curve': curveCardinal,
};

export const isSingleValueY = (data: Point2D[]) => data.length === 1 && !isNumber(data[0].x);
export const isSingleValueX = (data: Point2D[]) => data.length === 1 && !isNumber(data[0].y);
