import { css, cssSelector, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const nodesStyles: GenerateStyles = (tagName) => {
    const { root } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: flex;
                width: 100%;
                height: 100%;
                overflow: hidden;
            }

            ${root} {
                width: 100%;
                height: 100%;
            }

            ::slotted(*) {
                position: absolute;
                top: 0;
                left: 0;
                width: auto;
                height: auto;
            }
        `,
    ];
};
