import { css, cssSelector, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const lineInteractionStyles: GenerateStyles = (tagName) => {
    const { root } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
                pointer-events: none;
                transition:
                    fill 0.4s,
                    stroke-width 0.4s,
                    opacity 0.4s,
                    r 0.4s;
            }

            :host {
                display: block;
                width: 100%;
                height: 100%;
            }

            ${root} {
                width: 100%;
                height: 100%;
                overflow: hidden;
            }

            svg {
                display: block;
                width: 100%;
                height: 100%;
            }

            .hover-target {
                opacity: 0;
                pointer-events: initial;
                cursor: default;
            }
            .hover-target:hover {
                /*opacity: initial;*/
            }
        `,
    ];
};
