import { bisector } from 'd3-array';
import { Datum, Point2D } from '@umetrics/sartorius-ui-plot-utils';
import { BandScale, D3Scale, LinearScale } from './data';
import { PlottableInterface } from './plottable/plottableMixin';

export const joinData = (allData: Point2D[][]) => {
    const dataIndex = allData.reduce<number[]>((acc, d) => {
        const lastVal = acc.slice(-1)[0] ?? 0;
        return [...acc, lastVal + d.length];
    }, []);
    const flatData = allData.flat(1);
    return {
        dataIndex,
        flatData,
    };
};

function findClosestBandValue(xHovered: number, scale: BandScale): string | null {
    const domain = scale.domain();
    const bandwidth = scale.bandwidth();

    let closestValue: Datum | null = null;
    let minDistance = Number.POSITIVE_INFINITY;

    domain.forEach((value) => {
        const bandPosition = scale(value)! + bandwidth / 2; // Center of the band
        const distance = Math.abs(bandPosition - xHovered);

        if (distance < minDistance) {
            minDistance = distance;
            closestValue = value;
        }
    });
    return closestValue;
}

/**
 * Calculate the distance between two domain values.
 *   Continuous scales: numeric difference
 *   Band scales: exact match
 */
function calculateDomainDistance(a: any, b: any, scale: D3Scale): number {
    if ('invert' in scale) {
        return Math.abs(a - b);
    }
    return a === b ? 0 : Number.POSITIVE_INFINITY;
}

function findClosestContinous(points, targetValue, scale, acc: (point: Point2D<Datum>) => Datum) {
    let left = 0;
    let right = points.length - 1;
    let closest = null;
    let minDist = Infinity;

    while (left <= right) {
        const mid = Math.floor((left + right) / 2);
        const value = acc(points[mid]);
        const dist = calculateDomainDistance(value, targetValue, scale);
        if (dist === 0) {
            return points[mid]; // // Early return for exact match
        }
        if (dist < minDist) {
            minDist = dist;
            closest = points[mid];
        }
        if (value < targetValue) {
            left = mid + 1;
        } else {
            right = mid - 1;
        }
    }
    return closest;
}

function findClosestBand(points, targetValue, scale, acc: (point: Point2D<Datum>) => Datum) {
    // Linear search for band scales
    let closest = null;
    let minDist = Infinity;
    for (const point of points) {
        const value = acc(point);
        const dist = calculateDomainDistance(value, targetValue, scale);
        if (dist === 0) {
            return point;
        }
        if (dist < minDist) {
            minDist = dist;
            closest = point;
        }
    }
    return closest;
}

function filterRelevantData(
    data: PlottableInterface[],
    scaleName: string | undefined
): (PlottableInterface & { scaleX: string; scaleY: string })[] {
    return (data ?? []).filter(
        (el) =>
            (('scaleX' in el && el.scaleX === scaleName) || ('scaleY' in el && el.scaleY === scaleName)) &&
            el.plotData?.length > 0
    ) as (PlottableInterface & { scaleX: string; scaleY: string })[];
}

function determineAccessor(
    dataItem: PlottableInterface & { scaleX: string; scaleY: string },
    scaleName: string | undefined
): (point: Point2D<Datum>) => Datum {
    return dataItem.scaleY === scaleName ? (point) => point.y : (point) => point.x;
}

function prepareScale(scale: D3Scale, containerSize: number): D3Scale {
    const newScale = scale.copy();
    newScale.range([0, containerSize]);
    return newScale;
}

function calculateDomainValue(value: number, scale: D3Scale): any {
    return 'invert' in scale ? scale.invert(value) : findClosestBandValue(value, scale);
}

function findClosestPoint(
    data: (PlottableInterface & { scaleX: string; scaleY: string })[],
    domainValue: any,
    scale: D3Scale,
    accessor: (point: Point2D<Datum>) => Datum
): Point2D<number> | undefined {
    const finderFunc = 'invert' in scale ? findClosestContinous : findClosestBand;
    let closestPoint: Point2D<number> | undefined;
    let minDistance = Number.POSITIVE_INFINITY;

    for (const el of data) {
        const closestInEl = finderFunc(el.plotData, domainValue, scale, accessor);
        if (!closestInEl) continue;

        const dist = calculateDomainDistance(accessor(closestInEl), domainValue, scale);
        if (dist === 0) {
            return closestInEl; // Exact match
        } else if (dist < minDistance) {
            minDistance = dist;
            closestPoint = closestInEl; // Closest so far
        }
    }

    return closestPoint;
}

export function findDataPointByValue(
    value: number,
    data: PlottableInterface[],
    containerSize: number,
    scaleName: string | undefined,
    orgScale: D3Scale | undefined
): Point2D<number> | undefined {
    if (!orgScale || !data || data.length === 0) return undefined;

    const filteredData = filterRelevantData(data, scaleName);
    if (filteredData.length === 0) return undefined;

    const accessor = determineAccessor(filteredData[0], scaleName);
    const scale = prepareScale(orgScale, containerSize);
    const domainValue = calculateDomainValue(value, scale);

    return findClosestPoint(filteredData, domainValue, scale, accessor);
}

export function findInterpolatedDomainValue(
    value: number,
    data: PlottableInterface[],
    containerSize: number,
    scaleName: string | undefined,
    orgScale: D3Scale | undefined
): number | undefined {
    if (!orgScale || !data || data.length === 0) return undefined;
    const filteredData = filterRelevantData(data, scaleName);
    if (filteredData.length === 0) return undefined;
    const scale = prepareScale(orgScale, containerSize);
    return calculateDomainValue(value, scale);
}

type InterpolationFn<T = any> = (a: T, b: T) => (t: number) => T;

function getBoundaryPoints<TPlotData extends Point2D, T>(
    data: TPlotData[],
    mouseX: number,
    scales: [LinearScale, LinearScale],
    options?: PointSearchOptions<T>
) {
    const { pixelProximityThreshold = 0 } = options ?? {};
    const [scaleX] = scales;
    const xValues = data.map((point) => point.x);
    const max = Math.max(...xValues);
    const minX = scaleX(Math.min(...xValues));
    const maxX = scaleX(max);

    if (mouseX < minX - pixelProximityThreshold || mouseX > maxX + pixelProximityThreshold * 2) {
        return { x: null, y: null, scales };
    }

    if (scaleX.invert(mouseX) === max) {
        return { x: data[data.length - 1].x, y: data[data.length - 1].y, scales };
    }

    // no boundary condition met
    return null;
}

function findClosestDataPoint<TPlotData extends Point2D, T>(
    data: TPlotData[],
    invertedMouseX: number,
    scales: [LinearScale, LinearScale],
    options?: PointSearchOptions<T>
) {
    const { interpolateFn } = options ?? {};
    const bisect = bisector((d: TPlotData) => d.x).left;
    const index = bisect(data, invertedMouseX, 1);

    if (index === 0 || index >= data.length) {
        return { x: data[Math.min(index, data.length - 1)].x, y: data[Math.min(index, data.length - 1)].y, scales };
    }

    const prevPoint = data[index - 1];
    const nextPoint = data[index];

    if (!prevPoint || !nextPoint) return { x: null, y: null, scales };

    if (prevPoint.y === nextPoint.y && prevPoint.x === nextPoint.x) {
        return { x: prevPoint.x, y: prevPoint.y, scales };
    }

    if (interpolateFn) {
        const interpolateY = interpolateFn(prevPoint.y as unknown as T, nextPoint.y as unknown as T);
        const t = (invertedMouseX - prevPoint.x) / (nextPoint.x - prevPoint.x);
        const interpolatedY = interpolateY(t);
        return { x: invertedMouseX, y: interpolatedY as unknown as number, scales };
    }

    return { x: prevPoint.x, y: prevPoint.y, scales };
}

export function findClosestMiddleDataPoint<TPlotData extends { x: number; y: number }, T>(
    data: TPlotData[],
    invertedMouseX: number,
    scales: [LinearScale, LinearScale],
    options?: PointSearchOptions<T>
) {
    const bisect = bisector((d: TPlotData) => d.x).left;
    const index = bisect(data, invertedMouseX, 1);

    if (index === 0 || index >= data.length) {
        const point = data[Math.min(index, data.length - 1)];
        return { x: point.x, y: point.y, scales };
    }

    const prevPoint = data[index - 1];
    const nextPoint = data[index];

    if (!prevPoint || !nextPoint) {
        return { x: null, y: null, scales };
    }

    if (prevPoint.y === nextPoint.y && prevPoint.x === nextPoint.x) {
        return { x: prevPoint.x, y: prevPoint.y, scales };
    }

    if (options?.interpolateFn) {
        const t = (invertedMouseX - prevPoint.x) / (nextPoint.x - prevPoint.x);
        const interpolateY = options.interpolateFn(prevPoint.y as unknown as T, nextPoint.y as unknown as T);
        const interpolatedY = interpolateY(t) as unknown as number;
        return { x: invertedMouseX, y: interpolatedY, scales };
    }

    const midX = prevPoint.x + (nextPoint.x - prevPoint.x) / 2;
    return invertedMouseX < midX
        ? { x: prevPoint.x, y: prevPoint.y, scales }
        : { x: nextPoint.x, y: nextPoint.y, scales };
}

function processSearch({ data, element, mouseX, options, scaleX }) {
    if (data.length === 0) {
        return { x: null, y: null, scales: element.scales };
    }

    const boundaryPoint = getBoundaryPoints(data, mouseX, element.scales, options);
    if (boundaryPoint) return boundaryPoint;

    const invertedMouseX = scaleX.invert(mouseX);
    return options?.midpoint
        ? findClosestMiddleDataPoint(data, invertedMouseX, element.scales, options)
        : findClosestDataPoint(data, invertedMouseX, element.scales, options);
}

interface PointSearchOptions<T> {
    interpolateFn?: InterpolationFn<T>;
    pixelProximityThreshold?: number;
    midpoint?: boolean;
}

export function findDataAllPointsByX<TPlotData extends Point2D, T>(
    mouseX: number,
    allPlotData: {
        scales: [LinearScale, LinearScale];
        data: TPlotData[] | TPlotData[][];
    }[],
    options?: PointSearchOptions<T>
) {
    return allPlotData.map((element) => {
        const data = element.data;
        const [scaleX] = element.scales;
        if (data.every((x) => Array.isArray(x))) {
            return (
                element.data
                    .flatMap((d) =>
                        processSearch({
                            data: d,
                            mouseX,
                            element,
                            options,
                            scaleX,
                        })
                    )
                    .find((x) => x.y != null) ?? { x: null, y: null, scales: element.scales }
            );
        }

        return processSearch({
            data,
            mouseX,
            element,
            options,
            scaleX,
        });
    });
}

export enum GetYInRangeInvalidInputStrategy {
    RETURN_EMPTY = 'RETURN_EMPTY',
    USE_DEFAULT_RANGE = 'USE_DEFAULT_RANGE',
    THROW_ERROR = 'THROW_ERROR',
}

export function getYInRange<TPlotData extends Point2D>(
    allPlotData: TPlotData[][],
    currentXPos: number[],
    invalidInputStrategy: GetYInRangeInvalidInputStrategy = GetYInRangeInvalidInputStrategy.RETURN_EMPTY
): number[][] {
    switch (invalidInputStrategy) {
        case GetYInRangeInvalidInputStrategy.RETURN_EMPTY:
            if (currentXPos.length < 2) {
                return allPlotData.map(() => []);
            }
            break;

        case GetYInRangeInvalidInputStrategy.USE_DEFAULT_RANGE:
            if (currentXPos.length < 2) {
                currentXPos = [Number.NEGATIVE_INFINITY, Number.POSITIVE_INFINITY];
            }
            break;

        case GetYInRangeInvalidInputStrategy.THROW_ERROR:
            if (currentXPos.length < 2) {
                throw new Error('currentXPos must contain exactly two values to define a range.');
            }
            break;

        default:
            throw new Error('Unknown invalidInputStrategy.');
    }

    const [startX, endX] = [...currentXPos].sort((a, b) => a - b);

    const result: number[][] = [];

    for (let i = 0; i < allPlotData.length; i++) {
        const dataset = allPlotData[i];
        const yValues: number[] = [];

        for (let j = 0; j < dataset.length; j++) {
            const point = dataset[j];
            if (point.x >= startX && point.x <= endX) {
                yValues.push(point.y);
            }
        }

        result.push(yValues);
    }

    return result;
}

export const emptyBox: DOMRect = {
    left: 0,
    top: 0,
    width: 0,
    height: 0,
    x: 0,
    y: 0,
    bottom: 0,
    right: 0,
    toJSON: () => {},
};
