import { Point2D } from '@umetrics/sartorius-ui-plot-utils';
import { LinearScale } from '../data';
import { findDataAllPointsByX } from '../utils';
import { interpolateNumber } from 'd3-interpolate';

export type MetadataValue = number | string | Date | null;
export type MetadataDataPoint = Point2D<MetadataValue> & { highlighted?: boolean; scales: [LinearScale, LinearScale] };

export interface MetadataYSnapshot {
    interpolated: MetadataDataPoint[];
    exact: MetadataDataPoint[];
}

interface CalculateMetadataYSnapshotOptions {
    // x position in pixels, can recieve mouseX position
    xPosition: number;
    plotData: {
        scales: [LinearScale, LinearScale];
        data: Point2D<number>[];
        slot?: string;
        id?: string;
    }[];
    // threshold defines in pixels, setiing up gap when metadata will be read
    pixelProximityThreshold: number;
    midpoint?: boolean;
}

export interface MetaDataYSliceDefault<T = any> {
    data: T;
    positions: number;
}

export interface MetaDataYSliceInterpolated {
    positions: {
        x: number | null;
        y: number | null;
    }[];
    exact: MetadataDataPoint[];
    interpolated: MetadataDataPoint[];
}

export interface MetadataDetail<T = any, YSlice = any> {
    positionX: number;
    dataX: T;
    ySlice: YSlice;
}

export const calculateMetadataYSnapshot = ({
    xPosition,
    plotData,
    pixelProximityThreshold,
    midpoint,
}: CalculateMetadataYSnapshotOptions): MetadataYSnapshot => {
    const YSliceInterpolated = findDataAllPointsByX(xPosition, plotData, {
        interpolateFn: interpolateNumber,
        pixelProximityThreshold,
        midpoint,
    });
    const YSlice = findDataAllPointsByX(xPosition + pixelProximityThreshold, plotData, {
        pixelProximityThreshold,
        midpoint,
    });

    const ranges = plotData.map((dataSet) => {
        const [scaleX] = dataSet.scales;
        return dataSet.data.flat().map((point) => {
            const transformedX = scaleX(point.x);
            return [transformedX - pixelProximityThreshold, transformedX + pixelProximityThreshold];
        });
    });

    const interpolated = YSliceInterpolated.map((v, i) => {
        const [scaleX] = v.scales;
        const highlighted = ranges[i].some(([low, high]) => {
            const currentPxPosition = scaleX(v.x as number) as number;
            return currentPxPosition >= low && currentPxPosition <= high;
        });

        return { ...v, highlighted };
    });

    return {
        interpolated,
        exact: YSlice,
    };
};
