import { html, ifDefined, nothing, svg, TemplateResult } from '@umetrics/sartorius-ui-base';
import {
    AxisSettings,
    AxisStyle,
    Datum,
    defaultSettings,
    Settings,
    YAxisSettings,
} from '@umetrics/sartorius-ui-plot-utils';
import { DateScale, Orientation, ScaleWithMeta } from '../data';
import { Dimension } from '../data/plotContext';
import { getTimescaleXTicks, getXTicks, getYTicks } from '../scaleUtil';
import { customFontSize, TextMeasurer } from './textMeasurer';
import { Axis } from './axis';

const FONT_SCALING = 4;
const MIN_LABEL_SPACING = 5;
const textMeasurer = new TextMeasurer();

interface Tick {
    value: any;
    pos: number;
    original?: any;
}

interface OrientationSettings {
    dimensions: Dimension;
    translate: string;
    axisPath: TemplateResult;
    tickPathTemplate: (tick: Tick) => TemplateResult;
}

interface SVGOrientationSettings {
    tickFontSize: number;
    titleFontSize: number;
    tickAccessor: () => Tick[];
    tickPathTemplate: ({ pos }: Tick) => string;
    tickLabelTemplate: ({ value, pos }: Tick) => TemplateResult;
    axisLinePath: string;
    titleTransform: string;
    titleBaseLine: 'ideographic' | 'hanging' | 'text-before-edge' | 'text-after-edge';
}

type TickTransformFn = (tick: number, idx: number, _: number[]) => Tick;

// Space that each tick will want to occupy on the axis (number + whitespace)
export const defaultTickSize = {
    x: defaultSettings.axis.x.tickFontSize * FONT_SCALING,
    y: defaultSettings.axis.y.tickFontSize * FONT_SCALING,
    fontScaling: FONT_SCALING,
};

function _verticalAxisTickTransformFn(scaleYMeta: ScaleWithMeta, extent: number, settings: AxisSettings) {
    const scale = scaleYMeta.scale;
    const scaleTicks = Array.isArray(scaleYMeta.ticks) ? scaleYMeta.ticks : undefined;
    const len = scaleTicks?.length || -1;
    const count = extent / 2 / settings.tickFontSize;
    return scaleTicks
        ? (tick: number, idx: number, _: number[]) =>
              ({
                  value:
                      len > count && idx !== 0 && idx !== len - 1 && idx !== Math.floor(len / 2)
                          ? ''
                          : scaleTicks[idx].label,
                  pos: scale(tick),
              }) as Tick
        : (tick: number, _: number, __: number[]) => ({ value: tick, pos: scale(tick) }) as Tick;
}

function _horizontalAxisTickTransformFn(scaleXMeta: ScaleWithMeta) {
    const scale = scaleXMeta.scale;
    const scaleTicks = Array.isArray(scaleXMeta.ticks) ? scaleXMeta.ticks : undefined;
    const len = scaleTicks?.length || -1;
    return scaleTicks
        ? (tick: Datum, idx: number, ticks: Datum[]) =>
              ({
                  value: ticks.length > len ? '' : scaleTicks[idx].label,
                  pos: scale(tick as number),
              }) as Tick
        : (tick: Datum, _: number, __: Datum[]) => ({ value: tick, pos: scale(tick as number) }) as Tick;
}

export const getTicksWithLabels = (
    orientation: Orientation,
    dimension: Dimension,
    scalesWMeta: ScaleWithMeta,
    settings: Settings
): Tick[] => {
    const extent = orientation === 'vertical' ? dimension[1] : dimension[0];

    let transformFn: TickTransformFn;
    let sourceTicks: number[];

    if (orientation === 'vertical') {
        const scale = scalesWMeta.scale;
        const range = scale.range();
        const h = Math.abs(range[0] - range[1]);

        transformFn = _verticalAxisTickTransformFn(scalesWMeta, extent, settings.axis.y);
        sourceTicks = getYTicks(settings.axis.y, scalesWMeta, h, extent);
    } else {
        const scale = scalesWMeta.scale;
        const range = scale.range();
        const w = Math.abs(range[1] - range[0]);

        transformFn = _horizontalAxisTickTransformFn(scalesWMeta);
        sourceTicks = getXTicks(settings.axis.x, scalesWMeta, w, extent);
    }

    return sourceTicks.map(transformFn).filter(({ pos }) => pos >= 0 && pos <= extent);
};

/**
 * For SVG export
 */
function horizontalSettings(
    settings: AxisSettings,
    scaleMeta: ScaleWithMeta,
    ticksExtent: number,
    width: number,
    height: number,
    flip: boolean,
    format: (v: any) => string,
    axisStyle?: AxisStyle
): SVGOrientationSettings {
    const tickFontSize = settings.tickFontSize;
    const titleFontSize = settings.titleFontSize;
    const tickTransformFn = _horizontalAxisTickTransformFn(scaleMeta);
    let tickAccessor = () =>
        getXTicks(settings, scaleMeta, ticksExtent, width)
            .map(tickTransformFn)
            .filter(({ pos }) => pos >= 0 && pos <= width);
    if (scaleMeta.scaleType === 'timescale') {
        tickAccessor = () =>
            getTimescaleXTicks(settings, scaleMeta.scale as DateScale, width)
                .map((d) => d as Datum)
                .map(tickTransformFn)
                .filter(({ pos }) => pos >= 0 && pos <= width);
    }

    const tickPathTemplate = ({ pos }) => `M${pos} 0 v${flip ? -6 : 6}`;
    const flipText = flip ? -6 : 10;
    const baseline = flip ? 'ideographic' : 'hanging';
    const tickLabelTemplate = ({ value, pos }) => {
        const transform =
            axisStyle === 'tilt45'
                ? `translate(${pos + 10} ${flipText}) rotate(${45})`
                : `translate(${pos} ${flipText})`;
        const textAnchor = axisStyle !== 'tilt45' ? 'middle' : undefined;
        return svg`<g transform="${transform}"><text class="number" text-anchor="${ifDefined(textAnchor)}" dominant-baseline="${baseline}">${format(
            value
        )}</text></g>`;
    };

    const axisLinePath = `M0,${flip ? -6 : 6} 0,0 H${width}v${flip ? -6 : 6}`;
    const titleTransform = `translate(${width / 2}, ${flip ? -height : height})`;

    return {
        tickFontSize,
        titleFontSize,
        tickAccessor,
        tickPathTemplate,
        tickLabelTemplate,
        axisLinePath,
        titleTransform,
        titleBaseLine: flip ? 'text-before-edge' : 'ideographic',
    };
}

/**
 * For SVG export
 */
function verticalSettings(
    settings: AxisSettings,
    scaleMeta: ScaleWithMeta,
    ticksExtent: number,
    width: number,
    height: number,
    flip: boolean,
    format: (v: any) => string
): SVGOrientationSettings {
    const tickFontSize = settings.tickFontSize;
    const titleFontSize = settings.titleFontSize;
    const tickTransformFn = _verticalAxisTickTransformFn(scaleMeta, ticksExtent, settings);
    const tickAccessor = () =>
        getYTicks(settings, scaleMeta, ticksExtent, height)
            .map(tickTransformFn)
            .filter(({ pos }) => pos >= 0 && pos <= height);

    const tickPathTemplate = ({ pos }) => `M0 ${pos} ${flip ? 6 : -6} ${pos}`;
    const verticalTicks = (settings as YAxisSettings).labelOrientation === 'vertical';
    const flipAnchor = flip ? 'start' : 'end';
    const anchor = verticalTicks ? 'middle' : flipAnchor;
    const flipText = flip ? 10 : -10;
    const rot = verticalTicks ? -90 : 0;
    const tickLabelTemplate = ({ value, pos }) =>
        svg`<text class="number" transform="translate(${flipText}, ${pos}) rotate(${rot})" text-anchor="${anchor}" dominant-baseline="middle">${format(value)}</text>`;
    const axisLinePath = `M${flip ? 6 : -6}, ${height} H0V0H${flip ? 6 : -6}`;
    const titlePosX = flip ? width : -width;
    const titleTransform = `translate(${titlePosX}, ${height / 2}) rotate(-90)`;

    return {
        tickFontSize,
        titleFontSize,
        tickAccessor,
        tickPathTemplate,
        tickLabelTemplate,
        axisLinePath,
        titleTransform,
        titleBaseLine: flip ? 'text-after-edge' : 'text-before-edge',
    };
}

/**
 * For SVG export
 */
const calculateOrientationSettings = (
    [width, height]: [number, number],
    scaleMeta: ScaleWithMeta,
    settings: Settings,
    orientation: Orientation,
    flip: boolean,
    formatter?: (v: any) => string
): SVGOrientationSettings => {
    const range = scaleMeta.scale.range();
    const ticksExtent = Math.abs(range[1] - range[0]);
    const format = formatter || ((s: any) => s as string);

    return orientation === 'horizontal'
        ? horizontalSettings(settings.axis.x, scaleMeta, ticksExtent, width, height, flip, format, settings.axisStyle)
        : verticalSettings(settings.axis.y, scaleMeta, ticksExtent, width, height, flip, format);
};

/**
 * SVG export
 */
export function renderAxisSVG(
    size: [number, number],
    scaleMeta: ScaleWithMeta,
    settings: Settings,
    colorSettings: { axis: string; tick: string; label: string },
    axis: { title: string; orientation: Orientation; flip: boolean },
    formatter?: (value: any) => string
): TemplateResult {
    const { title, orientation, flip } = axis;
    const orientationSettings = calculateOrientationSettings(size, scaleMeta, settings, orientation, flip, formatter);

    const ticksInView = orientationSettings.tickAccessor();
    const tickPath = ticksInView.map(orientationSettings.tickPathTemplate).join(' ');

    return svg`
        <g class="axis-labels" stroke="none" fill=${colorSettings.label} font-size=${orientationSettings.tickFontSize}>
        ${ticksInView.map(orientationSettings.tickLabelTemplate)}
        </g>
        <g class="axis-ticks" stroke=${colorSettings.tick} fill="none">
            <path d="${orientationSettings.axisLinePath}" fill="none" stroke="${colorSettings.axis}" />
            <path d="${tickPath}" />
        </g>
        <g class="axis-title ${orientation}"
            fill=${colorSettings.label}
            transform="${orientationSettings.titleTransform}">
            <text font-size='${
                orientationSettings.titleFontSize
            }' text-anchor='middle' dominant-baseline='${orientationSettings.titleBaseLine}'>${title}</text>
        </g>
    `;
}

const _renderTemplateVerticalSettings = (
    extent: number,
    flip: boolean,
    includeEdgePseudoTicks: boolean
): OrientationSettings => {
    const bracePathDef = `M${flip ? 6 : -6},0 H0 V${extent} H${flip ? 6 : -6}`;
    const linePathDef = `M0,0 V${extent}`;

    return {
        dimensions: [extent, 6],
        translate: `translate(${flip ? 0 : 6},0)`,
        axisPath: svg`<path d="${
            includeEdgePseudoTicks ? bracePathDef : linePathDef
        }" stroke="currentColor" stroke-linecap="square" />`,
        tickPathTemplate: ({ pos }) => svg`<path d="M0,${pos} H${flip ? 6 : -6}" />`,
    };
};

const _renderTemplateHorizontalSettings = (
    extent: number,
    flip: boolean,
    includeEdgePseudoTicks: boolean
): OrientationSettings => {
    const bracePathDef = `M0,${flip ? -6 : 6} V0 H${extent} V${flip ? -6 : 6}`;
    const linePathDef = `M0,0 H${extent}`;

    return {
        dimensions: [6, extent],
        translate: `translate(0,${flip ? 6 : 0})`,
        axisPath: svg`<path d="${
            includeEdgePseudoTicks ? bracePathDef : linePathDef
        }" stroke="currentColor" stroke-linecap="square" />`,
        tickPathTemplate: ({ pos }) => svg`<path d="M${pos},0 V${flip ? -6 : 6}" />`,
    };
};

const _renderTemplate = (
    orientation: Orientation,
    extent: number,
    flip: boolean,
    ticks: Tick[],
    includeEdgePseudoTicks: boolean
): TemplateResult => {
    const settings =
        orientation === 'vertical'
            ? _renderTemplateVerticalSettings(extent, flip, includeEdgePseudoTicks)
            : _renderTemplateHorizontalSettings(extent, flip, includeEdgePseudoTicks);
    return html`
        <svg height="${settings.dimensions[0]}" width="${settings.dimensions[1]}" class="ticks">
            <g fill="none" class="axis ticks ${orientation}-axis" transform="${settings.translate}">
                ${settings.axisPath}
                <g stroke="currentColor">${ticks.map(settings.tickPathTemplate)}</g>
            </g>
        </svg>
    `;
};

export function renderTicks(
    orientation: Orientation,
    extent: number,
    flip: boolean,
    ticks: Tick[],
    includeEdgePseudoTicks = true
): TemplateResult {
    const filteredTicks = ticks.filter(({ pos }) => pos > 0 && pos <= extent);
    return _renderTemplate(orientation, extent, flip, filteredTicks, includeEdgePseudoTicks);
}

// calculate approximate label width based on provided settings
// the main idea is to take into account possible styles that might be applicable to labels, for example
// `new line` symbol
const getLabelWidth = (label: string, settings: AxisSettings): number => {
    const font = customFontSize(settings.tickFontSize ?? 12);

    if (settings.labelWidth === 'auto') {
        const labelChunks = settings.breakNewLine ? label.split(/\r\n|\r|\n/) : [label];

        const chunkSizeList = labelChunks.map((labelChunk) => textMeasurer.measureTextLength(labelChunk, font));

        return Math.max(...chunkSizeList);
    } else {
        return settings.labelWidth ?? 0;
    }
};

const adjustLabel = ({
    labelWidth,
    pos,
    labelGapMin,
    containerWidth,
}: {
    labelWidth: number;
    pos: number;
    labelGapMin: number;
    containerWidth: number;
}) => {
    const end = pos + labelWidth / 2;
    const start = pos - labelWidth / 2;

    let adjusted = pos;

    if (end > containerWidth) {
        adjusted = pos - (end - containerWidth);
    }

    if (start < 0) {
        adjusted = pos + Math.abs(start);
    }

    const halfLabelWidth = labelWidth / 2 + labelGapMin / 2;

    return {
        pos: adjusted,
        left: adjusted - halfLabelWidth,
        right: adjusted + halfLabelWidth,
    };
};

const _handleLabelClick = (axisElem: Axis, value: string, original?: string) => {
    axisElem.dispatchEvent(
        new CustomEvent('label-clicked', {
            detail: {
                label: original ?? value,
                scale: axisElem.orientation === 'horizontal' ? axisElem.scaleX : axisElem.scaleY,
            },
            bubbles: true,
        })
    );
};

function _verticalAxisLabelsTemplate(
    containerWidth: number,
    settings: AxisSettings | YAxisSettings,
    labelGapMin: number,
    axisElem: Axis
) {
    if (settings?.['labelOrientation'] !== 'vertical') {
        return (labels: Tick[]) =>
            labels.map(
                ({ value, pos, original }) =>
                    html` <div
                        class="number"
                        style="transform: translateY(${pos}px)"
                        @click="${() => _handleLabelClick(axisElem, value, original)}"
                    >
                        ${value}
                    </div>`
            );
    }

    return (labels: Tick[]) => {
        let prevLabelTop: number | undefined;

        return labels.map(({ value, pos }) => {
            const labelWidth = getLabelWidth(value, settings);

            const {
                pos: adjusted,
                left: top,
                right: bottom,
            } = adjustLabel({
                labelWidth,
                pos,
                labelGapMin,
                containerWidth,
            });

            // hide current label since it overlaps with a previous one
            if (prevLabelTop && prevLabelTop <= bottom) {
                return nothing;
            }

            prevLabelTop = top;

            return html` <div
                class="number"
                style="transform: ${`translateY(calc(${adjusted}px - 50%)) rotate(180deg)`}"
            >
                ${value}
            </div>`;
        });
    };
}

function _horizontalAxisLabelsTemplate(
    containerWidth: number,
    settings: AxisSettings | YAxisSettings,
    labelGapMin: number,
    axisElem: Axis
) {
    return (labels: Tick[]) => {
        let prevLabelRight: number | undefined;

        return labels.map(({ value, pos, original }) => {
            const labelWidth = getLabelWidth(value, settings);

            const {
                pos: adjusted,
                left,
                right,
            } = adjustLabel({
                labelWidth,
                pos,
                labelGapMin,
                containerWidth,
            });

            // hide current label since it overlaps with a previous one
            if (prevLabelRight && prevLabelRight >= left) {
                return nothing;
            }

            prevLabelRight = right;

            return html`
                <div class="number-wrapper" style="left: ${adjusted}px">
                    <div class="number" @click="${() => _handleLabelClick(axisElem, value, original)}">${value}</div>
                </div>
            `;
        });
    };
}

const _labelsTemplate = (
    isVertical: boolean,
    settings: AxisSettings | YAxisSettings,
    containerWidth: number,
    axisElem: Axis
): ((labels: Tick[]) => (TemplateResult | typeof nothing)[]) => {
    const labelGapMin = settings.labelGapMin ?? MIN_LABEL_SPACING;
    if (isVertical) {
        return _verticalAxisLabelsTemplate(containerWidth, settings, labelGapMin, axisElem);
    }

    return _horizontalAxisLabelsTemplate(containerWidth, settings, labelGapMin, axisElem);
};

interface RenderAxisOptions {
    axisElem: Axis;
    orientation: Orientation;
    dimension: Dimension;
    labels: Tick[];
    flip: boolean;
    settings: AxisSettings | YAxisSettings;
    formatter?: (d: Datum) => string;
}

export function renderAxis({ axisElem, orientation, dimension, labels, flip, settings }: RenderAxisOptions) {
    const isVertical = orientation === 'vertical';
    const extent = isVertical ? dimension[1] : dimension[0];

    const labelsTemplate = _labelsTemplate(isVertical, settings, extent, axisElem);

    const horizontalTemplate = html`
        ${renderTicks('horizontal', extent, flip, labels, settings.includeEdgePseudoTicks)}
        <div class="axis-numbers horizontal">${labelsTemplate(labels)}</div>
    `;
    const verticalTemplate = html`
        <div class="axis-numbers vertical">${labelsTemplate(labels)}</div>
        <div class="tick-wrapper">
            ${renderTicks('vertical', extent, flip, labels, settings.includeEdgePseudoTicks)}
        </div>
    `;

    return isVertical ? verticalTemplate : horizontalTemplate;
}
