import { RenderingEngine, RenderingEngineOptions } from './renderingEngine';
import {
    CachedOptions,
    CircleOptions,
    FillOptions,
    PathOptions,
    RectangleOptions,
    ScaleOptions,
    SceneGroup,
    SceneTexture,
    SceneTextureSourceOptions,
    Shape,
    ShapeKind,
    ShapeOptions,
    SharedRefOptions,
    StrokeOptions,
    SupportedEventType,
    SVGOptions,
    TransformOptions,
    TransformTypeOptions,
    TranslateOptions,
} from '../types';
import {
    AutoDetectOptions,
    autoDetectRenderer,
    Container,
    ContainerChild,
    FillStyle,
    Graphics,
    GraphicsContext,
    Matrix,
    Rectangle,
    Renderer,
    Sprite,
    StrokeInput,
    Texture,
    Ticker,
} from 'pixi.js';
import { segmentsFromSVGPath } from '../../paths/pathSegments';

const eventTypeMapping: Record<SupportedEventType, string> = {
    [SupportedEventType.CLICK]: 'click',
    [SupportedEventType.GLOBAL_MOUSE_MOVE]: 'globalmousemove',
    [SupportedEventType.GLOBAL_POINTER_MOVE]: 'globalpointermove',
    [SupportedEventType.GLOBAL_TOUCH_MOVE]: 'globaltouchmove',
    [SupportedEventType.MOUSE_DOWN]: 'mousedown',
    [SupportedEventType.MOUSE_ENTER]: 'mouseenter',
    [SupportedEventType.MOUSE_LEAVE]: 'mouseleave',
    [SupportedEventType.MOUSE_MOVE]: 'mousemove',
    [SupportedEventType.MOUSE_OUT]: 'mouseout',
    [SupportedEventType.MOUSE_OVER]: 'mouseover',
    [SupportedEventType.MOUSE_UP]: 'mouseup',
    [SupportedEventType.MOUSE_UP_OUTSIDE]: 'mouseupoutside',
    [SupportedEventType.POINTER_CANCEL]: 'pointercancel',
    [SupportedEventType.POINTER_DOWN]: 'pointerdown',
    [SupportedEventType.POINTER_ENTER]: 'pointerenter',
    [SupportedEventType.POINTER_LEAVE]: 'pointerleave',
    [SupportedEventType.POINTER_MOVE]: 'pointermove',
    [SupportedEventType.POINTER_OUT]: 'pointerout',
    [SupportedEventType.POINTER_OVER]: 'pointerover',
    [SupportedEventType.POINTER_TAP]: 'pointertap',
    [SupportedEventType.POINTER_UP]: 'pointerup',
    [SupportedEventType.POINTER_UP_OUTSIDE]: 'pointerupoutside',
    [SupportedEventType.RIGHT_CLICK]: 'rightclick',
    [SupportedEventType.RIGHT_DOWN]: 'rightdown',
    [SupportedEventType.RIGHT_UP]: 'rightup',
    [SupportedEventType.RIGHT_UP_OUTSIDE]: 'rightupoutside',
    [SupportedEventType.TAP]: 'tap',
    [SupportedEventType.TOUCH_CANCEL]: 'touchcancel',
    [SupportedEventType.TOUCH_END]: 'touchend',
    [SupportedEventType.TOUCH_END_OUTSIDE]: 'touchendoutside',
    [SupportedEventType.TOUCH_MOVE]: 'touchmove',
    [SupportedEventType.TOUCH_START]: 'touchstart',
    [SupportedEventType.WHEEL]: 'wheel',
};

const _defaultShapeProperties: ShapeOptions = {
    stroke: {
        color: 'black',
        opacity: 1,
        width: 1,
    },
};

class CachedOptionsImpl implements CachedOptions {
    cachedTransformOptions?: TransformOptions;
    cachedFillOptions?: FillOptions;
    cachedStrokeOptions?: StrokeOptions;
    cachedCircleOptions?: CircleOptions;
    cachedPathOptions?: PathOptions;
    cachedRectangleOptions?: RectangleOptions;
    cachedSharedRefOptions?: SharedRefOptions;
    cachedSVGOptions?: SVGOptions;
    cachedZIndex?: number;

    clearCache() {
        delete this.cachedTransformOptions;
        delete this.cachedFillOptions;
        delete this.cachedStrokeOptions;
        delete this.cachedCircleOptions;
        delete this.cachedPathOptions;
        delete this.cachedRectangleOptions;
        delete this.cachedSharedRefOptions;
        delete this.cachedSVGOptions;
        delete this.cachedZIndex;
    }
}

interface WithCachedOptions {
    cachedOptions: CachedOptions;
}

class ExtendedGraphicsContext extends GraphicsContext implements WithCachedOptions {
    cachedOptions = new CachedOptionsImpl();
    label?: string;
}

class ExtendedGroup extends Container {
    shared: ExtendedGraphicsContext[] = [];
    textures: (Texture & { cachedTextureSourceOptions: SceneTextureSourceOptions })[] = [];
}

class ExtendedGraphics extends Graphics implements WithCachedOptions {
    cachedOptions = new CachedOptionsImpl();
    hasListeners = false;
}

class ExtendedSprite extends Sprite implements WithCachedOptions {
    cachedOptions = new CachedOptionsImpl();
    hasListeners = false;
}

export class PixiEngine extends RenderingEngine {
    protected stage: Container;

    private _renderer?: Renderer;
    protected get renderer() {
        if (!this._renderer) {
            throw new Error('No renderer initialized');
        }
        return this._renderer;
    }
    protected set renderer(value: Renderer) {
        this._renderer = value;
    }

    private readonly ticker: Ticker;

    private _attachedTo?: HTMLElement;

    private _hasPending = false;

    constructor(_?: RenderingEngineOptions) {
        super();
        this.stage = new Container();
        this.ticker = new Ticker();
    }

    async init(elem: HTMLElement) {
        this.renderer = await autoDetectRenderer({
            preference: 'webgl',
            backgroundAlpha: 0,
            resolution: 2,
            antialias: true,
            autoDensity: true,
            powerPreference: 'high-performance',
            eventMode: 'passive',
        } as AutoDetectOptions);

        elem.appendChild(this.renderer.view.canvas as HTMLCanvasElement);
        this._attachedTo = elem;

        this.resize();

        this.ticker.add(() => {
            if (this._hasPending) {
                this._setUpStageHitBox();
                this.renderer.render(this.stage);
                this._hasPending = false;
            }
        });
        this.ticker.start();
    }

    eventTypeMap(): Record<SupportedEventType, string> {
        return eventTypeMapping;
    }

    resize() {
        if (!this._attachedTo) {
            return;
        }
        const { width, height } = this._attachedTo.getBoundingClientRect();
        this.renderer.resize(width, height);
        this._hasPending = true;
    }

    async loadTextures(groups: SceneGroup[]) {
        this._ensureStageGroups(groups);
        const newTextures: { stageGroup: ExtendedGroup; textures: SceneTexture[] }[] = groups
            .map((group) => {
                const stageGroup = this.stage.getChildByLabel(group.id) as ExtendedGroup;
                const added =
                    group.textures?.filter((texture) => !stageGroup.textures.some((t) => t.label === texture.id)) || [];
                return { stageGroup, textures: added };
            })
            .filter(({ textures }) => textures.length);
        await Promise.all(
            newTextures.map(async ({ stageGroup, textures }) => {
                const added = (await Promise.all(textures.map(async (texture) => this._createTexture(texture)) || []))
                    .filter(Boolean)
                    .map((texture) => texture!);
                stageGroup.textures = [...stageGroup.textures, ...added];
            })
        );
    }

    async update(groups: SceneGroup[]) {
        this._hasPending =
            groups.reduce((changed, group) => {
                if (!group.id) {
                    // No scene group id, will not render
                    return changed;
                }
                const stageGroup: ExtendedGroup = this.stage.getChildByLabel(group.id) as ExtendedGroup;
                if (!stageGroup) {
                    // build and add a new render group
                    this.stage.addChild(this._createGroup(group, true, group.zIndex));
                    return true;
                }
                return this._updateGroup(stageGroup, group) || changed;
            }, false) || this._hasPending;
        this._hasPending = this.stage.children?.reduce((changed, container) => {
            const group = groups.find((g) => g.id === container.label);
            if (!group) {
                return changed;
            }
            return this._destroyRemoved(container, group) || changed;
        }, this._hasPending);
    }

    removeGroups(groupIDs: string[]) {
        const remove = groupIDs
            .map((id) => this.stage.getChildByLabel(id))
            .filter(Boolean)
            .map((group) => group as ExtendedGroup);
        remove.forEach((group) => {
            this._destroyTextures(group);
            this._destroyChildren(group);
            group.destroy();
        });
        this.stage.removeChild(...remove);
        this._hasPending = !!remove.length;
    }

    tearDown() {
        this._renderer?.destroy();
        this.stage.destroy();
        if (this.ticker.started) {
            this.ticker.stop();
            this.ticker.destroy();
        }
        this._renderer = undefined;
        this._attachedTo = undefined;
    }

    private _ensureStageGroups(groups: SceneGroup[]) {
        groups.forEach((group, idx) => {
            if (!group.id) {
                return;
            }
            let stageGroup: ExtendedGroup = this.stage.getChildByLabel(group.id) as ExtendedGroup;
            if (!stageGroup) {
                stageGroup = new ExtendedGroup();
                stageGroup.label = group.id;
                this.stage.addChildAt(stageGroup, idx);
            }
        });
    }

    private _setUpStageHitBox() {
        const currentHitBox = this.stage.getChildByLabel(this._hitBoxId());
        let update = false;
        if (!currentHitBox) {
            update = true;
        } else {
            const rect = (currentHitBox as unknown as ExtendedGraphics).cachedOptions.cachedRectangleOptions;
            if (!(rect?.width === this.renderer.width && rect?.height === this.renderer.height)) {
                update = true;
            }
        }

        if (update) {
            if (currentHitBox) {
                currentHitBox.destroy();
                this.stage.removeChild(currentHitBox);
            }
            const hitBox = new ExtendedGraphics()
                .rect(0, 0, this.renderer.width, this.renderer.height)
                .fill({ color: 'black', alpha: 0 });
            hitBox.label = this._hitBoxId();
            hitBox.cachedOptions.cachedRectangleOptions = {
                x: 0,
                y: 0,
                width: this.renderer.width,
                height: this.renderer.height,
            };
            const evtMap = this.eventTypeMap();
            [SupportedEventType.MOUSE_OVER, SupportedEventType.MOUSE_LEAVE, SupportedEventType.MOUSE_DOWN]
                .map((et) => evtMap[et])
                .forEach((evt) =>
                    hitBox.on(evt, (e) => {
                        this._attachedTo?.dispatchEvent(
                            new CustomEvent('scene-canvas-event-dispatch', {
                                bubbles: true,
                                detail: { native: e.nativeEvent },
                            })
                        );
                    })
                );
            hitBox.hitArea = new Rectangle(0, 0, this.renderer.width, this.renderer.height);
            hitBox.eventMode = 'static';
            this.stage.addChildAt(hitBox, 0);
        }
    }

    private _hitBoxId = () => `stage-hit-box-backdrop`;

    private _destroyRemoved = (container: Container, group: SceneGroup) => {
        const groupChildrenByLabel =
            group.children?.reduce((labels: Record<string, Shape | SceneGroup>, child) => {
                labels[child.id] = child;
                return labels;
            }, {}) || {};
        const removed = container.children?.filter((c) => !groupChildrenByLabel[c.label]);
        removed.forEach((r) => {
            this._destroyChildren(r);
            r.destroy();
        });
        container.removeChild(...removed);
        const removedInSub = container.children
            ?.filter((c) => c.children?.length)
            .reduce((changed, subcontainer) => {
                const subGroup = groupChildrenByLabel[subcontainer.label];
                return this._destroyRemoved(subcontainer, subGroup) || changed;
            }, false);
        return !!removed.length || removedInSub;
    };

    private _destroyTextures = (group: ExtendedGroup) => {
        group.textures.forEach((texture) => texture.destroy(true));
    };

    private _destroyChildren = (group: Container) => {
        if (!group.children?.length) {
            return;
        }
        group.children.forEach((child) => {
            this._destroyChildren(child);
            child.destroy();
        });
    };

    private _createGroup = (group: SceneGroup, isRenderGroup = false, zIndex = 1) => {
        const stageGroup = new ExtendedGroup({ isRenderGroup });
        stageGroup.zIndex = zIndex;
        stageGroup.label = group.id;
        group.children
            ?.map((child) => {
                if ((child as Shape).kind !== undefined) {
                    return this._createGraphics(child as Shape, stageGroup);
                }
                const container = this._createGroup(child, false, child.zIndex);
                container.eventMode = 'passive';
                return container;
            })
            .forEach((c) => stageGroup.addChild(c));
        return stageGroup;
    };

    private _createGraphics = (shape: Shape, group: ExtendedGroup) => {
        let graphics: ExtendedGraphics | ExtendedSprite;
        if (shape.kind === ShapeKind.SPRITE) {
            graphics = new ExtendedSprite();
        } else {
            graphics = new ExtendedGraphics();
        }
        graphics.label = shape.id;
        this._setUpInteractions(shape, graphics, group);
        this._transferNodeOptions(graphics, shape, group, _defaultShapeProperties);
        return graphics;
    };

    private async _updateGroup(stageGroup: ExtendedGroup, group: SceneGroup) {
        let updated = false;
        updated = (await this._updateChildren(group, stageGroup)) || updated;
        return updated;
    }

    private async _updateChildren(group: SceneGroup, stageGroup: ExtendedGroup) {
        return group.children?.reduce((changed, child: SceneGroup | Shape) => {
            return this._updateChild(stageGroup, child) || changed;
        }, false);
    }

    private _createTexture = async (
        sceneTexture: SceneTexture
    ): Promise<void | (Texture & { cachedTextureSourceOptions: SceneTextureSourceOptions })> => {
        if (sceneTexture.options.svg) {
            const blob = new Blob([sceneTexture.options.svg], { type: 'image/svg+xml' });
            const sourceURL = URL.createObjectURL(blob);
            const imageResource = new Image();
            imageResource.src = sourceURL;
            return new Promise((resolve) => {
                imageResource.onload = () => {
                    const texture: any = Texture.from(imageResource);
                    texture.label = sceneTexture.id;
                    texture.cachedTextureSourceOptions = sceneTexture.options;
                    resolve(texture);
                };
            });
        }
    };

    private _updateChild(stageGroup: ExtendedGroup, child: SceneGroup | Shape) {
        const c = stageGroup.getChildByLabel(child.id);
        if (c?.children?.length) {
            return this._updateChildGroup(child, c, stageGroup);
        }
        if ((child as SceneGroup).children) {
            return this._updateSceneGroup(c, child);
        }

        if (c) {
            return this._updateExistingChild(c, child, stageGroup);
        }

        stageGroup.addChild(this._createGraphics(child as Shape, stageGroup));
        return true;
    }

    private _updateExistingChild(c: Container<ContainerChild>, child: SceneGroup | Shape, stageGroup: ExtendedGroup) {
        const nodeOptionsUpdated = this._transferNodeOptions(
            c as ExtendedGraphics | ExtendedSprite,
            child as Shape,
            stageGroup,
            _defaultShapeProperties
        );
        if (nodeOptionsUpdated) {
            this._setUpInteractions(child as Shape, c as ExtendedGraphics, stageGroup);
        }
        return nodeOptionsUpdated;
    }

    private _updateSceneGroup(c: Container<ContainerChild> | null, child: SceneGroup | Shape) {
        c?.destroy();
        this._createGroup(child as SceneGroup, false, child.zIndex);
        return true;
    }

    private _updateChildGroup(child: SceneGroup | Shape, c: Container<ContainerChild>, stageGroup: ExtendedGroup) {
        if ((child as SceneGroup).children) {
            return this._updateGroup(c as ExtendedGroup, child as SceneGroup);
        }
        if ((child as Shape).kind) {
            this._destroyChildren(c);
            c.destroy();
            stageGroup.addChild(this._createGraphics(child as Shape, stageGroup));
            return true;
        }

        return false;
    }

    private _setUpInteractions(shape: Shape, graphics: ExtendedGraphics | ExtendedSprite, group: ExtendedGroup) {
        const eventMap = this.eventTypeMap();
        if (graphics.hasListeners) {
            Object.entries(eventMap).forEach(([, evt]) => graphics.removeAllListeners(evt));
        }
        if (shape.on) {
            const on = shape.on;
            if (Object.keys(on).length) {
                Object.keys(on).forEach((evt) => {
                    const handler = on[evt];
                    graphics.on(eventMap[evt], (e) => {
                        handler({
                            event: e.nativeEvent,
                            eventType: evt,
                            target: shape,
                            updateShapes: (...shapes: Shape[]) => this._updateShapes(group.label, shapes),
                        });
                        return true;
                    });
                });
            }
            graphics.hasListeners = true;
        }
        graphics.eventMode = 'static';
    }

    private _updateShapes = (groupLabel: string, shapes: Shape[]) => {
        const group = this.stage.getChildByLabel(groupLabel, true) as ExtendedGroup;
        if (!group) {
            return;
        }
        this._hasPending = shapes.reduce((changed, shape) => {
            const graphics = group.getChildByLabel(shape.id);
            if (!graphics) {
                return changed;
            }
            const nodeChanged = this._transferNodeOptions(
                graphics as ExtendedGraphics | ExtendedSprite, // this is not sound
                shape,
                group,
                _defaultShapeProperties
            );
            return nodeChanged || changed;
        }, this._hasPending);
    };

    private _transferNodeOptions = (
        graphics: ExtendedGraphics | ExtendedSprite,
        shape: Shape,
        group: ExtendedGroup,
        fallbackShapeProperties?: ShapeOptions
    ) => {
        const dirty = this._hasUpdates(graphics.cachedOptions, shape, fallbackShapeProperties);
        if (dirty) {
            if (graphics instanceof ExtendedGraphics) {
                graphics.clear();
                graphics.cachedOptions.clearCache();
            }
            // specific shape
            this._transferShapeOptions(graphics, shape, group, fallbackShapeProperties);
            // general options
            this._transferZIndex(graphics, shape.zIndex);
            this._transferTransformOptions(graphics, shape.transform);
            if (graphics instanceof Graphics) {
                this._transferFillOptions(graphics, shape.fill, fallbackShapeProperties?.fill);
                this._transferStrokeOptions(graphics, shape, fallbackShapeProperties?.stroke);
            }
        }
        return dirty;
    };

    private _transferShapeOptions = (
        graphics: WithCachedOptions,
        shape: Shape,
        group: ExtendedGroup,
        fallbackShapeProperties?: ShapeOptions
    ) => {
        if (graphics instanceof ExtendedGraphics) {
            if (shape.kind === ShapeKind.PATH) {
                this._transferPathOptions(graphics, shape.path, shape.stroke);
            }
            if (shape.kind === ShapeKind.CIRCLE) {
                this._transferCircleOptions(graphics, shape.circle, fallbackShapeProperties?.circle);
            }
            if (shape.kind === ShapeKind.RECTANGLE) {
                this._transferRectangleOptions(graphics, shape.rectangle, fallbackShapeProperties?.rectangle);
            }
            if (shape.kind === ShapeKind.SVG) {
                this._transferSVGOptions(graphics, shape.svg);
            }
        }
        if (graphics instanceof ExtendedSprite) {
            if (shape.kind === ShapeKind.SPRITE) {
                this._transferSpriteOptions(graphics, group, shape.ref);
            }
        }
    };

    private _transferPathOptions = (graphics: ExtendedGraphics, path?: PathOptions, strokeOptions?: StrokeOptions) => {
        if (!path) {
            graphics.cachedOptions.cachedPathOptions = undefined;
            return;
        }

        const svgPath = path.svgPath;
        const dashArray = strokeOptions?.dashArray;
        segmentsFromSVGPath(svgPath, graphics, dashArray);
        graphics.localBlendMode = 'max';
        graphics.cachedOptions.cachedPathOptions = path;
    };

    private _transferCircleOptions = (
        graphics: ExtendedGraphics,
        circle?: CircleOptions,
        fallbackCircleOptions?: CircleOptions
    ) => {
        const x = circle?.x === undefined ? fallbackCircleOptions?.x : circle.x;
        const y = circle?.y === undefined ? fallbackCircleOptions?.y : circle.y;
        const radius = circle?.radius === undefined ? fallbackCircleOptions?.radius : circle.radius;
        if (x !== undefined && y !== undefined && radius !== undefined) {
            graphics.cachedOptions.cachedCircleOptions = { x, y, radius };
            graphics.circle(x, y, radius);
        }
    };

    private _transferRectangleOptions = (
        graphics: ExtendedGraphics,
        rectangle?: RectangleOptions,
        fallbackRectangleOptions?: RectangleOptions
    ) => {
        const x = rectangle?.x === undefined ? fallbackRectangleOptions?.x : rectangle.x;
        const y = rectangle?.y === undefined ? fallbackRectangleOptions?.y : rectangle.y;
        const width = rectangle?.width === undefined ? fallbackRectangleOptions?.width : rectangle.width;
        const height = rectangle?.height === undefined ? fallbackRectangleOptions?.height : rectangle.height;
        if (x !== undefined && y !== undefined && width !== undefined && height !== undefined) {
            graphics.cachedOptions.cachedRectangleOptions = { x, y, width, height };
            graphics.rect(x, y, width, height);
        }
    };

    private _transferSVGOptions = (graphics: ExtendedGraphics, svg?: SVGOptions) => {
        if (!svg?.svg) {
            return;
        }

        graphics.cachedOptions.cachedSVGOptions = { svg: svg.svg };
        graphics.svg(svg.svg);
    };

    private _transferSpriteOptions = (graphics: ExtendedSprite, group: ExtendedGroup, ref?: SharedRefOptions) => {
        if (!ref) {
            throw new Error('Sprite defined without resource');
        }
        const texture = group.textures.find((t) => t.label === ref.sharedId);
        if (!texture) {
            throw new Error('Unable to find referenced texture: ' + ref.sharedId);
        }
        graphics.texture = texture;
        graphics.cachedOptions.cachedSharedRefOptions = ref;
    };

    private _transferTransformOptions = (graphics: ExtendedGraphics | ExtendedSprite, transform?: TransformOptions) => {
        if (!transform?.length) {
            return;
        }

        const cachedTransformOptions: TransformTypeOptions[] = [];
        const transformMatrix = new Matrix();
        transformMatrix.identity();
        transform.forEach((trans) => {
            if (trans.type === 'scale') {
                this._transferScaleOptions(trans as ScaleOptions, transformMatrix);
            }
            if (trans.type === 'translate') {
                this._transferTranslateOptions(trans as TranslateOptions, transformMatrix);
            }
            cachedTransformOptions.push(trans);
        });
        graphics.cachedOptions.cachedTransformOptions = cachedTransformOptions;
        graphics.setFromMatrix(transformMatrix);
    };

    private _transferScaleOptions(scale: ScaleOptions, transformMatrix: Matrix) {
        transformMatrix.scale(scale.scaleX, scale.scaleY);
    }

    private _transferTranslateOptions(scale: TranslateOptions, transformMatrix: Matrix) {
        transformMatrix.translate(scale.x, scale.y);
    }

    private _transferFillOptions = (
        graphics: ExtendedGraphics,
        fill?: FillOptions,
        fallbackFillOptions?: FillOptions
    ) => {
        if (!fill && !fallbackFillOptions) {
            return;
        }
        const color = fill?.color === undefined ? fallbackFillOptions?.color : fill.color;
        const opacity = fill?.opacity === undefined ? fallbackFillOptions?.opacity : fill.opacity;
        graphics.cachedOptions.cachedFillOptions = {
            color,
            opacity,
        } as FillOptions;
        graphics.fill({
            color,
            alpha: opacity,
        } as FillStyle);
    };

    private _transferStrokeOptions = (
        graphics: ExtendedGraphics,
        shape: Shape,
        fallbackStrokeOptions?: StrokeOptions
    ) => {
        if (!shape.stroke && !fallbackStrokeOptions) {
            return;
        }
        const color = shape.stroke?.color === undefined ? fallbackStrokeOptions?.color : shape.stroke.color;
        const opacity = shape.stroke?.opacity === undefined ? fallbackStrokeOptions?.opacity : shape.stroke.opacity;
        const width = (shape.stroke?.width === undefined ? fallbackStrokeOptions?.width : shape.stroke.width) || 1;
        const dashArray =
            shape.stroke?.dashArray === undefined ? fallbackStrokeOptions?.dashArray : shape.stroke.dashArray;
        graphics.cachedOptions.cachedStrokeOptions = {
            color,
            width,
            opacity,
            dashArray,
        } as StrokeOptions;
        graphics.stroke({
            color,
            alpha: opacity === undefined ? 1 : opacity,
            width,
            alignment: 0.5,
        } as StrokeInput);
    };

    private _transferZIndex(graphics: ExtendedGraphics | ExtendedSprite, zIndex: number | undefined) {
        const z = zIndex || 1;
        graphics.zIndex = z;
        graphics.cachedOptions.cachedZIndex = z;
    }
}
