export interface DataClipRange {
    accessor: (d: any, idx: number, ds: any[]) => number;
    min: number;
    max: number;
}

const computeRegions = (ranges: DataClipRange[], values: number[]): number => {
    const len = ranges.length;
    return ranges.reduce((state, r, idx) => {
        const v = values[idx];
        if (v < r.min) {
            return state | (1 << idx);
        }
        if (v > r.max) {
            return state | (1 << (idx + len));
        }
        return state;
    }, 0);
};

const firstSignificantBit = (n: number) => 31 - Math.clz32(n);

const interpolate = (x1: number, x2: number, v1: number, v2: number, vNew: number) =>
    v1 + ((v2 - v1) * (vNew - x1)) / (x2 - x1);

const interpolateValues = (
    low: boolean,
    ranges: DataClipRange[],
    outsideRangeIdx: number,
    vals: number[],
    d1Vals: number[],
    d2Vals: number[]
) => {
    const vNew = low ? ranges[outsideRangeIdx].min : ranges[outsideRangeIdx].max;
    for (let i = 0; i < ranges.length; ++i) {
        if (i !== outsideRangeIdx) {
            vals[i] = interpolate(d1Vals[outsideRangeIdx], d2Vals[outsideRangeIdx], d1Vals[i], d2Vals[i], vNew);
        }
    }
    vals[outsideRangeIdx] = vNew;
};

const populateInitialValues = (
    ranges: DataClipRange[],
    d1Vals: number[],
    ds: any[],
    d1Idx: number,
    d2Vals: number[],
    d2Idx: number
) => {
    for (let i = 0; i < ranges.length; ++i) {
        d1Vals[i] = ranges[i].accessor(ds[d1Idx], d1Idx, ds);
        d2Vals[i] = ranges[i].accessor(ds[d2Idx], d2Idx, ds);
    }
};

/**
 * Adaption of Cohen-Sutherland clipping for arbitrary number of dimensions to check if a line segment intersects the clipping volume.
 *
 * @param ranges clip ranges for the separate dimensions
 * @param ds the data points
 * @param d1Idx the index of the first data point of the line to check
 * @param d2Idx the index of the second data point of the line to check
 */
const inClipVolume = (ranges: DataClipRange[], ds: any[], d1Idx: number, d2Idx: number) => {
    const len = ranges.length;

    const d1Vals: number[] = new Array(ranges.length);
    const d2Vals: number[] = new Array(ranges.length);
    populateInitialValues(ranges, d1Vals, ds, d1Idx, d2Vals, d2Idx);

    let regions1 = computeRegions(ranges, d1Vals);
    let regions2 = computeRegions(ranges, d2Vals);

    while (true) {
        if (regions1 === 0 && regions2 === 0) {
            return true;
        }
        if (regions1 & regions2) {
            break;
        }
        const regions = regions1 !== 0 ? regions1 : regions2;
        const vals = regions1 !== 0 ? d1Vals : d2Vals;

        const outsideIdx = firstSignificantBit(regions);
        const outsideRangeIdx = outsideIdx % len;
        if (d1Vals[outsideRangeIdx] === d2Vals[outsideRangeIdx]) {
            break;
        }
        interpolateValues(outsideIdx < len, ranges, outsideRangeIdx, vals, d1Vals, d2Vals);
        if (regions1 !== 0) {
            regions1 = computeRegions(ranges, d1Vals);
        } else {
            regions2 = computeRegions(ranges, d2Vals);
        }
    }
    return false;
};

const isGrowing = (data: any[], range: DataClipRange) => {
    if (data.length < 2) {
        return true;
    }
    let last = range.accessor(data[0], 0, data);
    const growing = range.accessor(data[data.length - 1], data.length - 1, data) >= last;
    for (let i = 1; i < data.length; ++i) {
        const val = range.accessor(data[i], i, data);
        if (growing ? val < last : val > last) {
            return false;
        }
        last = val;
    }
    return true;
};

const findMonotone = (data: any[], ranges: DataClipRange[]) => {
    return ranges.find((range) => isGrowing(data, range));
};

function clipToRanges(data: any[], rs: DataClipRange[]) {
    return data.reduce(
        (result, d, idx, ds) => {
            if (idx === 0) {
                return result;
            }
            const inVolume = inClipVolume(rs, ds, idx, idx - 1);
            if (inVolume) {
                if (result.lastInVolume) {
                    result.paths[result.paths.length - 1].push(d);
                } else {
                    result.paths.push([ds[idx - 1], d]);
                }
                result.lastInVolume = true;
            } else {
                result.lastInVolume = false;
            }
            return result;
        },
        { lastInVolume: false, paths: [] }
    ).paths;
}

const anyPointInside = (flip: boolean, monotoneRange: DataClipRange, last: number, first: number) => {
    return flip
        ? monotoneRange.min > last || monotoneRange.max < first
        : monotoneRange.min > first || monotoneRange.max < last;
};

const firstInside = (descending: boolean, interval: { max: number; min: number }, v: number) => {
    return descending ? interval.max >= v : interval.min <= v;
};

const lastInside = (descending: boolean, interval: { max: number; min: number }, v: number) => {
    return descending ? interval.min > v : interval.max < v;
};

const clipDataToSingleRange = (monotoneRange: DataClipRange, data: any[]) => {
    const first = monotoneRange.accessor(data[0], 0, data);
    const last = monotoneRange.accessor(data[data.length - 1], data.length - 1, data);
    const descending = last < first;
    if (anyPointInside(descending, monotoneRange, last, first)) {
        let firstIndex = -1;
        let lastIndex = -1;

        for (let i = 0; i < data.length; ++i) {
            const v = monotoneRange.accessor(data[i], i, data);
            if (firstIndex === -1 && firstInside(descending, monotoneRange, v)) {
                firstIndex = Math.max(0, i - 1);
            }
            if (lastIndex === -1 && lastInside(descending, monotoneRange, v)) {
                lastIndex = i + 1;
                break;
            }
        }

        if (firstIndex === -1) {
            // everything is outside
            return [];
        }

        return [data.slice(firstIndex, lastIndex === -1 ? data.length : lastIndex)];
    }
    return [data];
};

export const clipDataTo = <T>(data: T[], ranges: DataClipRange[]): T[][] => {
    if (!data?.length) {
        return [];
    }
    if (!ranges?.length) {
        return [data];
    }

    let rough = [data];
    if (data.length >= 2) {
        const monotoneRange = findMonotone(data, ranges);
        if (monotoneRange) {
            rough = clipDataToSingleRange(monotoneRange, data);
        }
    }

    return rough.flatMap((path) => clipToRanges(path, ranges));
};
