import { cssVariables, html, render, svg, type TemplateResult } from '@umetrics/sartorius-ui-base';
import { Dimension } from './data/plotContext';
import { mapKeys } from './genericUtils';

//TODO: Move this to styles-package
const cssKeyToVar = (v: string) => `--${v.replace(/_/g, '-')}`;
const cssVarMap = mapKeys(cssVariables, cssKeyToVar);
const cssVarString = Object.entries(cssVarMap)
    .map(([k, v]) => `${k}:${v}`)
    .join(';');

const addCssVars = (contents) => svg`
  <style>
    :root{
      ${cssVarString}
    }
  </style>
  ${contents}
`;

export function renderSVGToElement(target, contents, dimensions) {
    const [width, height] = dimensions;
    const contentWithWrapper = html` <svg viewBox="0 0 ${width} ${height}">${addCssVars(contents)}</svg> `;
    render(contentWithWrapper, target);
}

export function getSVGBlob(contents: TemplateResult, dimensions: Dimension) {
    const svgTarget = document.createElement('svg');
    //TODO: Evaluate if these attributes are desirable, they will cause a long legend to be cropped when set.
    const [width, height] = dimensions;
    svgTarget.setAttribute('width', String(width));
    svgTarget.setAttribute('height', String(height));
    svgTarget.setAttribute('viewBox', `0 0 ${width} ${height}`);
    svgTarget.setAttribute('font-family', 'Roboto, Arial, Open Sans, Helvetica, sans-serif');
    svgTarget.setAttribute('xmlns', 'http://www.w3.org/2000/svg');

    render(contents, svgTarget);

    const preface = '<?xml version="1.0" standalone="no"?>\n';
    return new Blob([preface, svgTarget.outerHTML.replace(/<!--.*?-->/g, '')], { type: 'image/svg+xml;charset=utf-8' });
}

const svgToPng = (svgDataurl, [width, height]: Dimension): Promise<any> =>
    new Promise((resolve, _reject) => {
        let canvas: HTMLCanvasElement;
        let ctx: CanvasRenderingContext2D;
        let img;
        const outputScaling = 2;
        const outputWidth = width * outputScaling;
        const outputHeight = height * outputScaling;

        img = new Image();
        img.src = svgDataurl;
        img.width = width;
        img.height = height;

        img.onload = () => {
            canvas = document.createElement('canvas');
            canvas.width = outputWidth;
            canvas.height = outputHeight;
            ctx = canvas.getContext('2d')!;
            ctx.fillStyle = 'white';
            ctx.fillRect(0, 0, outputWidth, outputHeight);
            ctx.drawImage(img, 0, 0, img.width, img.height, 0, 0, outputWidth, outputHeight);

            img = new Image();
            img.src = canvas.toDataURL('image/png');
            img.onload = () => {
                canvas = document.createElement('canvas');
                canvas.width = outputWidth;
                canvas.height = outputHeight;
                ctx = canvas.getContext('2d')!;
                ctx.drawImage(img, 0, 0);
                canvas.toBlob(resolve);
            };
        };
    });

export async function copySVGFileToClipboard(contents: TemplateResult, dimensions: Dimension) {
    const svgBlob = getSVGBlob(contents, dimensions);
    const blob = await svgToPng(URL.createObjectURL(svgBlob), dimensions);

    await navigator.clipboard.write([
        new ClipboardItem({
            [blob.type]: blob,
        }),
    ]);
}

export function renderSVGToSVGImg(target, contents, dimensions) {
    const svgBlob = getSVGBlob(contents, dimensions);
    const svgUrl = URL.createObjectURL(svgBlob);
    const [width, height] = dimensions;

    target.src = svgUrl;
    target.width = width;
    target.height = height;
}

function downloadURLBlob(blob, filename) {
    const url = URL.createObjectURL(blob);
    const downloadLink = document.createElement('a');
    downloadLink.href = url;
    downloadLink.download = filename;
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
    URL.revokeObjectURL(url);
}

export async function downloadPNGFile(filename: string, contents: TemplateResult, dimensions: Dimension) {
    const svgBlob = getSVGBlob(contents, dimensions);
    const pngBlob = await svgToPng(URL.createObjectURL(svgBlob), dimensions);
    downloadURLBlob(pngBlob, filename);
}

export function downloadSVGFile(filename: string, contents: TemplateResult, dimensions: Dimension) {
    const svgBlob = getSVGBlob(contents, dimensions);
    downloadURLBlob(svgBlob, filename);
}

export const exportElement = (opts?) => (el) => {
    if (!el.exportSVG) {
        console.warn(el.tagName, 'does not support SVG export');
        return '';
    }
    if (opts?.useElementSize) {
        const { width, height } = el.getBoundingClientRect();
        return el.exportSVG([width, height]);
    }
    return el.exportSVG();
};
