import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    PropertyValues,
    TemplateResult,
} from '@umetrics/sartorius-ui-base';
import { namespacedTag } from '../tag';
import { version } from '../metadata';
import { sceneRendererStyle } from './sceneRendererStyle';
import { WithContext } from '../data/plotContext';
import { WithDataContext } from '../data/dataContext';
import { createRenderingEngine, PixiEngine, RenderingEngine } from './engines';
import { WithRenderUpdateTrigger } from './renderUpdateTrigger';
import { SVGShape } from '../plottable/svg/svgShape';
import { PlottableInterface } from '../plottable/plottableMixin';

const RESIZE_ON = ['size'];
const SOURCES_ON = ['allDataElements'];

const shouldTrigger = (_changedProperties: PropertyValues, watching: string[]) =>
    watching.some((w) => _changedProperties.has(w));

@defineElement({ tagName: namespacedTag('scene-renderer'), styles: sceneRendererStyle, version })
export class SceneRenderer extends WithRenderUpdateTrigger(WithDataContext(WithContext(BaseElement))) {
    private _engine: RenderingEngine = createRenderingEngine(PixiEngine);
    private _renderSources: string[] = [];

    disconnectedCallback() {
        super.disconnectedCallback();
        this._engine.tearDown();
    }

    async renderUpdate(toUpdate: string[]) {
        if (!this.allDataElements?.length) {
            // nothing to update
            return [];
        }
        const isTarget = (plottable: PlottableInterface) =>
            this._renderSources.includes(plottable.plotUniqueId) && toUpdate.includes(plottable.plotUniqueId);
        const rerender = this.allDataElements.filter(isTarget).map((p, idx) => ({ shape: p as SVGShape, z: idx + 1 }));

        const groups = await Promise.all(
            rerender.map(async ({ shape, z }) => {
                const group = await shape.sceneGroup();
                if (group) {
                    group.zIndex = z + 1;
                }
                return group;
            })
        );
        const defined = groups.filter((group) => !!group).map((group) => group!);

        await this._engine.loadTextures(defined);
        this._engine.update(defined);
        return rerender.map(({ shape }) => shape.plotUniqueId);
    }

    protected async firstUpdated(_changedProperties: PropertyValues) {
        super.firstUpdated(_changedProperties);
        await this._initApplication();
    }

    protected async updated(_changedProperties: PropertyValues) {
        super.updated(_changedProperties);
        if (shouldTrigger(_changedProperties, RESIZE_ON)) {
            await this._resize();
        }
        if (shouldTrigger(_changedProperties, SOURCES_ON)) {
            await this._updateTargets();
        }
    }

    protected render(): TemplateResult {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});
        // create a root for the rendering stage
        return html` <div class=${rootClasses}></div> `;
    }

    private _initApplication = async () => {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});
        const root: HTMLDivElement | undefined = this.shadowRoot!.querySelector(`.${rootClasses}`) as HTMLDivElement;
        if (!root) {
            return;
        }

        await this._engine.init(root);
    };

    private async _resize() {
        this._engine.resize();
    }

    private async _updateTargets() {
        const previousSources = this._renderSources.slice();
        const currentSources =
            this.allDataElements
                ?.filter((dataElement) => dataElement instanceof SVGShape)
                .filter((dataElement) => dataElement.slot === this.slot)
                .map((dataElement) => dataElement as SVGShape)
                .filter((shape) => shape.mode === 'canvas' || shape.mode === 'webgl')
                .map((shape) => shape.plotUniqueId) || [];
        const removed = previousSources.filter((previousSource) => !currentSources.includes(previousSource));
        this._engine.removeGroups(removed);
        this._renderSources = currentSources;
    }
}
