import { PlottableInterface } from '../../plottable/plottableMixin';
import { SVGBar, SVGLine, SVGMultiPolygon, SVGScatter } from '../../plottable';
import { LabelAlignment, LabelAlignmentOptions, LabelItem, LabelSettings, PlacementOption } from './labels';
import { BarLabelGenerator } from './barLabelGenerator';
import { Point2D, QuadTree } from '@umetrics/sartorius-ui-plot-utils';
import {
    AABB,
    isCompletelyInside,
    RectangleBounds,
    RectangleCorners,
} from '@umetrics/sartorius-ui-plot-utils/dist/geometrics';
import { SVGShape } from '../../plottable/svg/svgShape';
import { customFontSize, TextMeasurer } from '../../axis/textMeasurer';
import { MultiPolygonLabelGenerator } from './multiPolygonLabelGenerator';
import { ScatterLabelGenerator } from './scatterLabelGenerator';
import { LineLabelGenerator } from './lineLabelGenerator';

export enum GeneratorType {
    BAR,
    LINE,
    MULTI_POLY,
    SCATTER,
}

export interface RawLabelPoint {
    point: Point2D;
    id: string;
    value: any;
    userData?: any;
    pointIdx: number;
    seriesIdx: number;
    forceAlignment?: LabelAlignment;
    type?: string;
    angle?: number;
}

export interface PlotLabelData {
    items: RawLabelPoint[];
    plotSurface: RectangleCorners[];
}

export interface LabelGenerator<T extends PlottableInterface, D> {
    generatorType: GeneratorType;
    calculateLabels: (
        plottable: T,
        labelSettings: LabelSettings,
        accumumlated: QuadTree,
        transform?: (data: D) => D
    ) => PlotLabelData;
    extremes: (data: D) => D;
}

const getGenerator = (plottable: PlottableInterface): LabelGenerator<any, any> | undefined => {
    if (plottable instanceof SVGBar) {
        return new BarLabelGenerator();
    }
    if (plottable instanceof SVGLine) {
        return new LineLabelGenerator();
    }
    if (plottable instanceof SVGScatter) {
        return new ScatterLabelGenerator();
    }
    if (plottable instanceof SVGMultiPolygon) {
        return new MultiPolygonLabelGenerator();
    }

    return undefined;
};

const _textMeasurer = new TextMeasurer();

const generate = (
    plottable: PlottableInterface,
    generator: LabelGenerator<any, any> | undefined,
    transform: ((data: any) => any) | undefined,
    labelSettings: LabelSettings,
    accumulated: QuadTree
) => {
    return (
        generator?.calculateLabels(plottable, labelSettings, accumulated, transform) || { items: [], plotSurface: [] }
    );
};

const identity = (data: any[]) => data;

const _getCenterPointRadius = (type: GeneratorType, shape: SVGShape, labelData: RawLabelPoint) => {
    return type === GeneratorType.SCATTER
        ? (shape as SVGScatter).pointSize(labelData.point, labelData.pointIdx, labelData.seriesIdx)
        : 0;
};

const calculateLabelAlignment = (alignment: LabelAlignment, textX: number, textY: number, centerExtent = 0) => {
    switch (alignment) {
        case 'top':
            return { x: 0, y: -textY / 2 - 4 - centerExtent / 2 };
        case 'bottom':
            return { x: 0, y: textY / 2 + 4 + centerExtent / 2 };
        case 'left':
            return { x: -textX / 2 - 4 - centerExtent / 2, y: 0 };
        case 'right':
            return { x: textX / 2 + 4 + centerExtent / 2, y: 0 };
        default:
            return { x: 0, y: 0 };
    }
};

const rotatedCoordinates = (point: Point2D, theta: number, origin: Point2D): Point2D => {
    const transX = point.x - origin.x;
    const transY = point.y - origin.y;
    return {
        x: transX * Math.cos(theta) - transY * Math.sin(theta) + origin.x,
        y: transY * Math.cos(theta) + transX * Math.sin(theta) + origin.y,
    };
};

export const calculateCoords = (offset: Point2D, screen: Point2D, extentX: number, extentY: number) => {
    return {
        topLeft: {
            x: screen.x + offset.x - extentX / 2,
            y: screen.y + offset.y - extentY / 2,
        },
        topRight: {
            x: screen.x + offset.x + extentX / 2,
            y: screen.y + offset.y - extentY / 2,
        },
        bottomLeft: {
            x: screen.x + offset.x - extentX / 2,
            y: screen.y + offset.y + extentY / 2,
        },
        bottomRight: {
            x: screen.x + offset.x + extentX / 2,
            y: screen.y + offset.y + extentY / 2,
        },
    };
};

export const calculateRotatedCoords = (
    origin: Point2D,
    angleRad: number,
    offsetX: number,
    offsetY: number
): RectangleCorners => {
    if (angleRad === 0) {
        return calculateCoords({ x: 0, y: 0 }, origin, offsetX * 2, offsetY * 2);
    }
    return {
        topLeft: rotatedCoordinates({ x: origin.x - offsetX, y: origin.y - offsetY }, angleRad, origin),
        topRight: rotatedCoordinates({ x: origin.x + offsetX, y: origin.y - offsetY }, angleRad, origin),
        bottomRight: rotatedCoordinates({ x: origin.x + offsetX, y: origin.y + offsetY }, angleRad, origin),
        bottomLeft: rotatedCoordinates({ x: origin.x - offsetX, y: origin.y + offsetY }, angleRad, origin),
    };
};

const aabb = (plottables: PlottableInterface[]) => {
    const { w, h } = plottables.reduce((bounds: { w: number; h: number } | undefined, wD) => {
        const { width, height } = wD.getBoundingClientRect();
        if (!bounds) {
            return { w: width, h: height };
        }
        return { w: Math.max(width, bounds.w), h: Math.max(height, bounds.h) };
    }, undefined)!;

    return {
        minX: 0,
        minY: 0,
        maxX: w,
        maxY: h,
    } as AABB;
};

const finalizeLabelItems = (
    generated: { plot: PlottableInterface; plotLabelData: PlotLabelData; type?: GeneratorType }[],
    defaultLabelSettings: LabelSettings,
    occupied: QuadTree
): { id: string; labels: LabelItem[] }[] => {
    const withData = generated.filter((gen) => !!gen.plotLabelData.items);
    if (!withData?.length) {
        return [];
    }
    generated.forEach((gen) => {
        const plotSurface = gen.plotLabelData.plotSurface;
        if (plotSurface?.length) {
            plotSurface.forEach((rect) => {
                occupied.insert({
                    minX: Math.min(rect.topLeft.x, rect.topRight.x),
                    maxX: Math.max(rect.topLeft.x, rect.topRight.x),
                    minY: Math.min(rect.topLeft.y, rect.bottomLeft.y),
                    maxY: Math.max(rect.topLeft.y, rect.bottomLeft.y),
                });
            });
        }
    });
    // process plots in reverse so that the one drawn on top has more options for labels.
    const reversed = generated.slice().reverse();
    return reversed.map((unprocessedPlotLabels) => {
        const type = unprocessedPlotLabels.type;
        if (type === undefined || !unprocessedPlotLabels.plotLabelData.items.length) {
            return { id: unprocessedPlotLabels.plot.id, labels: [] };
        }
        const shape = unprocessedPlotLabels.plot as SVGShape;
        const verticalBar = shape instanceof SVGBar && (shape as SVGBar).vertical;
        const { width, height } = shape.getBoundingClientRect();
        const screenBounds: RectangleBounds = { topLeft: { x: 0, y: 0 }, bottomRight: { x: width, y: height } };
        const { id, metadata } = shape as SVGShape;
        const [scaleX, scaleY] = shape.getScales();
        const labels = unprocessedPlotLabels.plotLabelData.items
            .reduce((result: LabelItem[], labelData: RawLabelPoint) => {
                const settings = unprocessedPlotLabels.plot.getLabelSettings(
                    {
                        index: labelData.seriesIdx,
                        pointIndex: labelData.pointIdx,
                        point: labelData.point,
                        plotId: id,
                        metadata,
                    },
                    defaultLabelSettings
                );
                const label = settings.labelFormatter!(labelData.value, labelData.userData);
                const screen = { x: scaleX(labelData.point.x) as number, y: scaleY(labelData.point.y) as number };
                const angle = (!settings.noAngle && labelData.angle) || 0;
                const offsetHorizontal = settings.offsetHorizontal!;
                const offsetVertical = settings.offsetVertical!;
                const textX = _textMeasurer.measureTextLength(label, customFontSize(12)) + 4;
                const textY = 12 + 4;
                const originOffsetX = textX / 2;
                const originOffsetY = textY / 2;
                const centerPointRadius = _getCenterPointRadius(type, shape, labelData);
                let alignmentOffset: { x: number; y: number } | undefined;
                let coords: RectangleCorners | undefined;
                if (shape instanceof SVGMultiPolygon) {
                    alignmentOffset = { x: 0, y: 0 };
                    if (!angle) {
                        coords = calculateCoords({ x: offsetHorizontal, y: offsetHorizontal }, screen, textX, textY);
                    } else {
                        coords = calculateRotatedCoords(screen, angle, textX / 2, textY / 2);
                    }
                } else if (!settings.collisionAvoidance && !settings.hideCollisions) {
                    // charts with forced alignment and where we don't care about collisions
                    const align = labelData.forceAlignment || settings.alignment!;
                    const adjustedX = labelData.angle && labelData.angle !== 0 ? textY : textX;
                    const adjustedY = labelData.angle && labelData.angle !== 0 ? textX : textY;
                    alignmentOffset = calculateLabelAlignment(align, adjustedX, adjustedY, centerPointRadius);
                    coords = calculateCoords(
                        { x: alignmentOffset.x + offsetHorizontal, y: alignmentOffset.y + offsetVertical },
                        screen,
                        adjustedX,
                        adjustedY
                    );
                } else {
                    // collision avoidance in line or scatter plot
                    const alignmentOptions = [
                        labelData.forceAlignment || settings.alignment!,
                        ...(!labelData.forceAlignment && settings.collisionAvoidance
                            ? LabelAlignmentOptions.filter((a) => a !== settings.alignment)
                            : []),
                    ].filter((a) => {
                        if (labelData.type === 'lineLimitX') {
                            return ['left', 'right', 'center'].includes(a);
                        }
                        if (labelData.type === 'lineLimitY') {
                            return ['top', 'bottom', 'center'].includes(a);
                        }

                        return true;
                    });

                    const adjustedX = labelData.type === 'lineLimitX' || verticalBar ? textY : textX;
                    const adjustedY = labelData.type === 'lineLimitX' || verticalBar ? textX : textY;

                    const offsetCoords = alignmentOptions
                        .map((alignment) => ({
                            offset: calculateLabelAlignment(alignment, adjustedX, adjustedY, centerPointRadius),
                        }))
                        .map(({ offset }) => ({
                            offset,
                            coordinates: calculateCoords(
                                { x: offset.x + offsetHorizontal, y: offset.y + offsetVertical },
                                screen,
                                adjustedX,
                                adjustedY
                            ),
                        }));
                    const onScreen = offsetCoords.filter(({ coordinates }) =>
                        isCompletelyInside(coordinates, screenBounds)
                    );
                    const notOverlapping = onScreen.filter(({ coordinates }) => {
                        const box = {
                            minX: Math.min(coordinates.topLeft.x, coordinates.topRight.x),
                            maxX: Math.max(coordinates.topLeft.x, coordinates.topRight.x),
                            minY: Math.min(coordinates.topLeft.y, coordinates.bottomLeft.y),
                            maxY: Math.max(coordinates.topLeft.y, coordinates.bottomLeft.y),
                        };
                        return !occupied.hasIntersectingBoxesWith(box);
                    });
                    const match = notOverlapping.find(Boolean);
                    alignmentOffset =
                        match?.offset ||
                        (!settings.hideCollisions &&
                            ((onScreen.length && onScreen[0].offset) || offsetCoords[0].offset)) ||
                        undefined;
                    coords =
                        match?.coordinates ||
                        (!settings.hideCollisions &&
                            ((onScreen.length && onScreen[0].coordinates) || offsetCoords[0].coordinates)) ||
                        undefined;
                }

                if (!alignmentOffset) {
                    return result;
                }

                occupied.insert({
                    minX: Math.min(coords!.topLeft.x, coords!.topRight.x),
                    maxX: Math.max(coords!.topLeft.x, coords!.topRight.x),
                    minY: Math.min(coords!.topLeft.y, coords!.bottomLeft.y),
                    maxY: Math.max(coords!.topLeft.y, coords!.bottomLeft.y),
                });

                result = [
                    ...result,
                    {
                        ...labelData,
                        label,
                        textOpacity: settings.textOpacity,
                        textColor: settings.textColor,
                        backgroundColor: settings.backgroundColor,
                        hoverBackgroundColor: settings.hoverBackgroundColor,
                        angleInRadians: angle,
                        originOffsetX,
                        originOffsetY,
                        offsetHorizontal: offsetHorizontal + alignmentOffset.x,
                        offsetVertical: offsetVertical + alignmentOffset.y,
                        editable: !!settings.editable,
                    },
                ];
                return result;
            }, [])
            .map((lc) => {
                const li: any = { ...lc };
                delete li.value;
                delete li.userData;
                delete li.pointIdx;
                delete li.seriesIdx;
                delete li.forceAlignment;
                delete li.type;
                delete li.angle;
                return li as LabelItem;
            });

        return { id: shape.id, labels };
    });
};

export const generateExtremes = (
    plottable: PlottableInterface[],
    labelSettings: LabelSettings
): { id: string; labels: LabelItem[] }[] => {
    if (!plottable?.length) {
        return [];
    }
    const accumulated = new QuadTree(aabb(plottable));
    const generated = plottable.map((plot) => {
        const generator = getGenerator(plot);
        const plotLabelData = generate(plot, generator, generator?.extremes, labelSettings, accumulated);
        return { plot, plotLabelData, type: generator?.generatorType };
    });
    return finalizeLabelItems(generated, labelSettings, accumulated);
};

export const generateAll = (
    plottable: PlottableInterface[],
    labelSettings: LabelSettings
): { id: string; labels: LabelItem[] }[] => {
    if (!plottable?.length) {
        return [];
    }
    const accumulated = new QuadTree(aabb(plottable));
    const generated = plottable.map((plot) => {
        const generator = getGenerator(plot);
        const plotLabelData = generate(plot, generator, identity, labelSettings, accumulated);
        return { plot, plotLabelData, type: generator?.generatorType };
    });
    return finalizeLabelItems(generated, labelSettings, accumulated);
};

export const calculatePlacement = (placement: PlacementOption, extent: number, textExtent: number) => {
    const availableExtent = extent - textExtent;
    let inAvailable: number;
    if (placement.type === 'pixel') {
        if (placement.amount >= 0) {
            inAvailable = Math.min(availableExtent, placement.amount);
        } else {
            inAvailable = Math.max(0, availableExtent + placement.amount);
        }
    } else {
        // percent
        const part = Math.min(1, Math.max(0, placement.amount / 100));
        inAvailable = availableExtent * part;
    }

    return (inAvailable + textExtent / 2) / extent;
};
