import {
    booleanConverter,
    createClassName,
    defineElement,
    html,
    ifDefined,
    property,
    state,
    svg,
    nothing,
} from '@umetrics/sartorius-ui-base';
import { version } from '../../metadata';
import { namespacedTag } from '../../tag';
import { FilterProperties, WithFilter } from '../filterMixin';
import {
    Datum,
    isFullySpecified,
    Point2D,
    roundToPrecision2,
    roundToSignificantDigits,
} from '@umetrics/sartorius-ui-plot-utils';
import { DEFAULT_SHAPE_SIZE, PlotShape, rawShape, renderShape } from '../../shapes';
import { WithColor, WithColorProperties } from '../colorMixin';
import { ShapeStyle, SVGShape, SVGShapeProperties } from './svgShape';
import { svgScatterStyles } from './svgScatterStyles';
import {
    EventHandlingFunction,
    SceneGroup,
    SceneTexture,
    Shape,
    ShapeKind,
    SupportedEventType,
    TranslateOptions,
} from '../../rendering/types';

export interface SVGScatterEvent {
    evt: MouseEvent;
    point: Point2D<Datum>;
    index: number;
    metadata: any;
}

export type SVGScatterEventHandler = (evt: MouseEvent, point: Point2D<Datum>, index: number, metadata: any) => void;

export interface SVGScatterProperties extends SVGShapeProperties, FilterProperties, WithColorProperties {
    lineWidth?: number /* Deprecated, use defaultStyle func */;
    shape?: PlotShape | string;
    customShape?: { d: string; transform?: string };

    targetRadius?: number | ((index) => number);
    targetColor?: string | ((index) => string);
    targetOpacity?: number | ((index) => number);

    pointSize?: (p, i, seriesIndex?: number) => number;
    pointStyle?: (p, i, seriesIndex?: number) => string;
    outline?: boolean /* Deprecated, use defaultStyle func */;

    overflow?: boolean;

    // Experimental, skip event handling and pass handlers as props directly
    handleScatterClick?: SVGScatterEventHandler;
    handleScatterHover?: SVGScatterEventHandler;
    handleScatterHoverEnd?: SVGScatterEventHandler;

    onScatterClick?: CustomEvent<SVGScatterEvent>;
    onScatterHover?: CustomEvent<SVGScatterEvent>;
    onScatterHoverEnd?: CustomEvent<SVGScatterEvent>;
}

@defineElement({ tagName: namespacedTag('svg-scatter'), styles: svgScatterStyles, version })
export class SVGScatter extends WithColor(WithFilter(SVGShape)) implements SVGScatterProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    @property({ type: String, reflect: false })
    shape = PlotShape.circle;
    @property({ type: Object, reflect: false, attribute: false })
    customShape?: { d: string; transform?: string };

    @property({ type: Object, reflect: false, attribute: false })
    targetRadius: number | ((index) => number) = 9;
    @property({ type: Object, reflect: false, attribute: false })
    targetColor: string | ((index) => string) = 'black';
    @property({ type: Object, reflect: false, attribute: false })
    targetOpacity: number | ((index) => number) = 0.2;

    @property({ type: Boolean, reflect: false, converter: booleanConverter })
    overflow = false;
    /* Deprecated */
    @property({ type: Boolean, reflect: false, converter: booleanConverter })
    outline = false;

    @property({ type: Function, reflect: false, attribute: false })
    handleScatterClick?: (evt: MouseEvent, point: Point2D<Datum>, index: number, metadata: any) => void;
    @property({ type: Function, reflect: false, attribute: false })
    handleScatterHover?: (evt: MouseEvent, point: Point2D<Datum>, index: number, metadata: any) => void;
    @property({ type: Function, reflect: false, attribute: false })
    handleScatterHoverEnd?: (evt: MouseEvent, point: Point2D<Datum>, index: number, metadata: any) => void;

    private _elementFill: (Path2D | null)[] = [];
    @state()
    private _hoverIndex = -1;

    @property({ type: Function, reflect: false, attribute: false })
    pointSize = (_p, _i, _seriesIndex = 0) => 5;
    @property({ type: Function, reflect: false, attribute: false })
    pointStyle = (_p, _i, _seriesIndex = 0) => '';

    sceneGroup(): SceneGroup | Promise<SceneGroup> | undefined {
        const scales = this.getScales();
        if (!scales) {
            return undefined;
        }
        const [scaleX, scaleY] = scales;
        const pointSeries = (this.data?.length && (Array.isArray(this.data[0]) ? this.data : [this.data])) || [];
        const template = rawShape(this.shape);
        const fillColor = this.outline ? 'transparent' : this.color;
        const strokeColor = this.outline ? this.color : 'transparent';
        const points: { series: Shape[]; texture: SceneTexture }[] = pointSeries.map((series, seriesIdx) => {
            const style = this._getDefaultStyle(
                { index: seriesIdx },
                {
                    fillColor,
                    fillOpacity: this.opacity,
                    strokeColor,
                    strokeWidth: 1,
                    strokeOpacity: 1,
                    strokeDashArray: 'initial',
                }
            );

            const textureId = `scatter-${this.plotUniqueId}-texture-${seriesIdx}`;
            const size = this.pointSize({ x: 0, y: 0 }, 0, seriesIdx);
            const scaleFactor = size / DEFAULT_SHAPE_SIZE;
            const texture = {
                id: textureId,
                options: {
                    svg: `<svg 
                        xmlns="http://www.w3.org/2000/svg"
                        stroke="${style.strokeColor}" 
                        stroke-width="${style.strokeWidth}" 
                        stroke-opacity="${style.strokeOpacity}" 
                        fill="${style.fillColor}"
                        fill-opacity="${style.fillOpacity}"
                        viewBox="${-6 * scaleFactor} ${-6 * scaleFactor} ${12 * scaleFactor} ${12 * scaleFactor}"
                        width="${12 * scaleFactor}"
                        height="${12 * scaleFactor}">${template}</svg>`,
                },
            } as SceneTexture;

            return {
                series: series
                    .filter((point) => isFullySpecified(point))
                    .map((point, pointIdx) => {
                        return {
                            id: `scatter-${this.plotUniqueId}-point-${seriesIdx}-${pointIdx}`,
                            kind: ShapeKind.SPRITE,
                            ref: { sharedId: textureId },
                            transform: [
                                {
                                    type: 'translate',
                                    x: scaleX(point.x),
                                    y: scaleY(point.y),
                                } as TranslateOptions,
                            ],
                        } as Shape;
                    }),
                texture,
            };
        });

        const interactionPoints: Shape[] = pointSeries.flatMap((series, seriesIdx) => {
            if (!this._isInteractable(seriesIdx)) {
                return [] as Shape[];
            }
            const mouseOverHandler = (({ event, updateShapes, target }) => {
                updateShapes({ ...target, fill: { ...target.fill, opacity: this._targetOpacity(seriesIdx) } });
                const t: Shape & { point: Point2D } = target as any as Shape & { point: Point2D };
                this._handleSvgCircleMouseOver(event, t.point, seriesIdx);
            }) as EventHandlingFunction<MouseEvent>;
            const mouseLeaveHandler = (({ event, updateShapes, target }) => {
                updateShapes({ ...target, fill: { ...target.fill, opacity: 0 } });
                const t: Shape & { point: Point2D } = target as any as Shape & { point: Point2D };
                this._handleSvgCircleMouseLeave(event, t.point, seriesIdx);
            }) as EventHandlingFunction<MouseEvent>;
            const mouseDownHandler = (({ event, target }) => {
                const t: Shape & { point: Point2D } = target as any as Shape & { point: Point2D };
                this._handleSvgCircleClick(event, t.point, seriesIdx);
            }) as EventHandlingFunction<MouseEvent>;
            return series
                .filter((point) => isFullySpecified(point))
                .map((point, pointIdx) => {
                    return {
                        id: `scatter-${this.plotUniqueId}-point-target-${seriesIdx}-${pointIdx}`,
                        on: {
                            [SupportedEventType.POINTER_OVER]: mouseOverHandler,
                            [SupportedEventType.POINTER_LEAVE]: mouseLeaveHandler,
                            [SupportedEventType.POINTER_DOWN]: mouseDownHandler,
                        },
                        point,
                        kind: ShapeKind.CIRCLE,
                        circle: {
                            x: scaleX(point.x),
                            y: scaleY(point.y),
                            radius: this._targetRadius(seriesIdx),
                        },
                        fill: {
                            color: this._targetColor(seriesIdx),
                            opacity: 0,
                        },
                        stroke: {
                            opacity: 0,
                        },
                    } as Shape;
                });
        });
        return {
            id: `scatter-${this.plotUniqueId}`,
            shared: [],
            textures: points.map((p) => p.texture),
            children: [...points.flatMap((p) => p.series), ...interactionPoints],
        };
    }

    public exportSVG() {
        if (!this.size || !this.getScales()) return '';
        const [scaleX, scaleY] = this.getScales();
        return this.renderPoints('scatter', scaleX, scaleY, false);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            overflow: this.overflow,
            canvas: this.mode !== 'svg',
            hover: this._hoverIndex > -1, // Canvas hover
        });
        if (!this.size || !this.getScales()) return;
        const [width, height] = this.size;
        const [scaleX, scaleY] = this.getScales();

        if (this.mode !== 'svg') {
            return html``;
        }

        return html`
            <svg width=${width} height=${height} class="${rootClasses}">
                ${this.renderPoints(baseClassName, scaleX, scaleY, true)}
            </svg>
        `;
    }

    protected _invalidateCanvas(ctx: CanvasRenderingContext2D) {
        const [scaleX, scaleY] = this.getScales();

        this._elementFill.splice(0, this._elementFill.length);
        // issue with typings https://github.com/microsoft/TypeScript/issues/55162
        (ctx as CanvasRenderingContext2D & { reset: () => void }).reset();
        ctx.scale(this._dpi, this._dpi);

        this.plotData.forEach((p, index) => {
            if (this.interactable) {
                this._renderCanvasHoverTarget(index, ctx, scaleX, p, scaleY);
            }
            this._renderCanvasTarget(p, index, ctx, scaleX, scaleY);
        });
    }

    protected _invalidateGl(gl: WebGLRenderingContext) {
        throw new Error('not implemented' + gl);
    }

    private renderPoints(baseClassName, scaleX, scaleY, includeHoverTarget) {
        const fillColor = this.outline ? 'none' : this.color;
        const strokeColor = this.outline ? this.color : 'none';

        return this.data
            .filter((p) => isFullySpecified(p) || p.length > 0)
            .map((p, i) => {
                const style = this._getDefaultStyle(
                    { index: i },
                    {
                        fillColor,
                        fillOpacity: this.opacity,
                        strokeColor,
                        strokeWidth: 1,
                        strokeOpacity: 1,
                        strokeDashArray: 'initial',
                    }
                );

                const interactableStyle = this.pointStyle ? (this.pointStyle(p, i) ?? '') : '';
                const hidden = this._isVisibilityHidden(i);
                const interactable = this._isInteractable(i);
                const generatedStyle = this._generateStyle({ index: i }, style as any, interactableStyle);
                const shape = renderShape(this.shape, this.customShape, this.pointSize(p, i));

                if (Array.isArray(p)) {
                    return p
                        .filter((q) => isFullySpecified(q))
                        .map((q) =>
                            this._renderPoint(baseClassName, scaleX, scaleY, q, i, {
                                style,
                                generatedStyle,
                                shape,
                                hidden,

                                includeHoverTarget,
                                interactable,
                            })
                        );
                }
                return this._renderPoint(baseClassName, scaleX, scaleY, p, i, {
                    style,
                    generatedStyle,
                    shape,
                    hidden,
                    includeHoverTarget,
                    interactable,
                });
            });
    }

    private _renderPoint(
        baseClassName,
        scaleX,
        scaleY,
        p,
        i,
        { style, generatedStyle, shape, hidden, includeHoverTarget, interactable }
    ) {
        const cx = roundToPrecision2(scaleX(p.x) as number);
        const cy = roundToPrecision2(scaleY(p.y) as number);
        const transform = `translate(${cx} ${cy})`;
        return isNaN(cx) || isNaN(cy)
            ? nothing
            : svg`

                <g 
                class='${baseClassName}-target'
                data-testid=${this._dataTestId(p)}
                    fill="${style.fillColor}" 
                    fill-opacity="${style.fillOpacity}"  
                    stroke="${style.strokeColor}"
                    stroke-width="${style.strokeWidth}"
                    stroke-opacity="${style.strokeOpacity}"
                    stroke-dashArray=${style.strokeDashArray}
                    visibility=${ifDefined(hidden ? 'hidden' : undefined)}
                    transform=${transform}
                    style=${generatedStyle}
                >
                     ${shape}
                </g>
                ${
                    includeHoverTarget && interactable
                        ? this._renderHoverTarget(baseClassName, cx, cy, p, i, hidden)
                        : null
                }
            `;
    }

    private _renderHoverTarget(baseClassName, cx, cy, p, i, hidden) {
        const hightlighted = this._isHighlight(i);
        return svg`
            <circle class='${baseClassName}-hover-target${hightlighted ? ' highlight' : ''}${hidden ? ' hidden' : ''}' 
                @click=${(e) => this._handleSvgCircleClick(e, p, i)} 
                @mouseenter=${(e) => this._handleSvgCircleMouseOver(e, p, i)}
                @mouseleave=${(e) => this._handleSvgCircleMouseLeave(e, p, i)}
                cx=${cx} 
                cy=${cy} 
                
                r=${this._targetRadius(i) ?? 9} 
                fill="${this._targetColor(i) ?? 'black'}" 
                fill-opacity="${this._targetOpacity(i) ?? 0.2}"
            />
        `;
    }

    private _dataTestId = (point: Point2D<any>) => {
        if (!point) {
            return undefined;
        }
        const values = [
            { lab: 'x', val: point.x },
            { lab: 'y', val: point.y },
        ]
            .filter((d) => d.val !== undefined && d.val !== null)
            .map(({ lab, val }) => ({ lab, val: val! }));

        const content = values
            .map(({ lab, val }) => ({ lab, val: typeof val === 'number' ? roundToSignificantDigits(val, 5) : val }))
            .map((d) => `${d.lab}: ${d.val}`)
            .join(',');

        if (!content) {
            return undefined;
        }

        return `(${content})`;
    };

    private _targetRadius = (index) =>
        typeof this.targetRadius === 'function' ? this.targetRadius(index) : this.targetRadius;
    private _targetColor = (index) =>
        typeof this.targetColor === 'function' ? this.targetColor(index) : this.targetColor;
    private _targetOpacity = (index) =>
        typeof this.targetOpacity === 'function' ? this.targetOpacity(index) : this.targetOpacity;

    private _handleSvgCircleClick(evt, point, index) {
        if (this.handleScatterClick) {
            this.handleScatterClick(evt, point, index, this.metadata);
        } else {
            this._dispatchClick(evt, index);
        }
    }

    private _handleSvgCircleMouseOver(evt: MouseEvent, point, index: number) {
        if (this.handleScatterHover) {
            this.handleScatterHover(evt, point, index, this.metadata);
        } else {
            this._dispatchHover(evt, index);
        }
    }

    private _handleSvgCircleMouseLeave(evt: MouseEvent, point, index: number) {
        if (this.handleScatterHoverEnd) {
            this.handleScatterHoverEnd(evt, point, index, this.metadata);
        } else {
            this._dispatchHoverEnd(evt, index);
        }
    }

    private _dispatchClick = (evt, index) =>
        this.dispatchEvent(
            new CustomEvent('scatter-click', {
                bubbles: true,
                detail: {
                    evt,
                    metadata: this.metadata,
                    point: this.data[index],
                    index,
                },
            })
        );

    private _dispatchHover = (evt, index) =>
        this.dispatchEvent(
            new CustomEvent('scatter-hover', {
                bubbles: true,
                detail: {
                    evt,
                    metadata: this.metadata,
                    point: this.data[index],
                    index,
                },
            })
        );

    private _dispatchHoverEnd = (evt, index) =>
        this.dispatchEvent(
            new CustomEvent('scatter-hover-end', {
                bubbles: true,
                detail: {
                    evt,
                    metadata: this.metadata,
                    point: this.data[index],
                    index,
                },
            })
        );

    private _mergedStyle = (index, def): ShapeStyle => {
        let style = this._getDefaultStyle({ index }, def);

        if (this._hoverIndex === index) {
            style = { ...style, ...this._getHoverStyle({ index }) };
        }
        return style;
    };

    private _renderCanvasTarget(p: Point2D<number>, index: number, ctx: CanvasRenderingContext2D, scaleX, scaleY) {
        const style = this._mergedStyle(index, { fillColor: this.color ?? 'black', fillOpacity: this.opacity ?? 1 });

        ctx.beginPath();
        ctx.fillStyle = style.fillColor!;
        ctx.globalAlpha = style.fillOpacity!;
        ctx.arc(scaleX(p.x)!, scaleY(p.y)!, this.pointSize ? this.pointSize(p, index) : 5, 0, 2 * Math.PI);
        ctx.fill();
    }

    private _renderCanvasHoverTarget(index: number, ctx: CanvasRenderingContext2D, scaleX, p: Point2D<number>, scaleY) {
        const fill = new Path2D();
        if (this._hoverIndex === index) {
            ctx.fillStyle = this._targetColor(index) ?? 'black';
            ctx.globalAlpha = this._targetOpacity(index) ?? '0.2';
        } else {
            ctx.fillStyle = 'transparent';
            ctx.globalAlpha = 1;
        }
        fill.arc(scaleX(p.x)!, scaleY(p.y)!, this._targetRadius(index) ?? 9, 0, 2 * Math.PI);
        ctx.fill(fill);
        this._elementFill.push(fill);
    }
}
