import {
    CachedOptions,
    CircleOptions,
    FillOptions,
    PathOptions,
    RectangleOptions,
    ScaleOptions,
    SceneGroup,
    Shape,
    ShapeKind,
    ShapeOptions,
    SharedRefOptions,
    StrokeOptions,
    SupportedEventType,
    SVGOptions,
    TransformOptions,
    TranslateOptions,
} from '../types';

export interface RenderingEngineOptions {
    debugInfo?: boolean;
}

export type RenderingEngineType<T extends RenderingEngine> = new (options?: RenderingEngineOptions) => T;

export abstract class RenderingEngine<E = string> {
    abstract init(parent?: HTMLElement): void | Promise<void>;
    abstract eventTypeMap(): Record<SupportedEventType, E>;
    abstract resize(): void;
    abstract loadTextures(groups: SceneGroup[]): Promise<void>;
    abstract update(groups: SceneGroup[]): void;
    abstract removeGroups(groupIDs: string[]): void;
    abstract tearDown(): void;

    protected _hasUpdates = (cached: CachedOptions, shape: Shape, fallbackShapeProperties?: ShapeOptions) => {
        let dirty = this._hasShapeUpdates(cached, shape, fallbackShapeProperties);
        dirty = dirty || this._hasTransformUpdates(cached, shape.transform);
        dirty = dirty || this._hasFillUpdates(cached, shape.fill, fallbackShapeProperties?.fill);
        dirty = dirty || this._hasStrokeUpdates(cached, shape.stroke, fallbackShapeProperties?.stroke);
        return dirty;
    };

    protected _hasShapeUpdates = (cached: CachedOptions, shape: Shape, fallbackShapeProperties?: ShapeOptions) => {
        if (shape.kind === ShapeKind.PATH) {
            return this._hasPathUpdates(cached, shape.path);
        }
        if (shape.kind === ShapeKind.CIRCLE) {
            return this._hasCircleUpdates(cached, shape.circle, fallbackShapeProperties?.circle);
        }
        if (shape.kind === ShapeKind.RECTANGLE) {
            return this._hasRectangleUpdates(cached, shape.rectangle, fallbackShapeProperties?.rectangle);
        }
        if (shape.kind === ShapeKind.SVG) {
            return this._hasSVGUpdates(cached, shape.svg);
        }
        if (shape.kind === ShapeKind.SPRITE) {
            return this._hasSpriteUpdates(cached, shape.ref);
        }
        return false;
    };

    protected _hasPathUpdates = (cached: CachedOptions, path?: PathOptions) => {
        return path?.svgPath !== cached.cachedPathOptions?.svgPath;
    };

    protected _hasCircleUpdates = (
        cached: CachedOptions,
        circle?: CircleOptions,
        fallbackCircleOptions?: CircleOptions
    ) => {
        const x = circle?.x === undefined ? fallbackCircleOptions?.x : circle.x;
        const y = circle?.y === undefined ? fallbackCircleOptions?.y : circle.y;
        const radius = circle?.radius === undefined ? fallbackCircleOptions?.radius : circle.radius;
        const previous = cached.cachedCircleOptions;
        return !(x === previous?.x && y === previous?.y && radius === previous?.radius);
    };

    protected _hasRectangleUpdates = (
        cached: CachedOptions,
        rectangle?: RectangleOptions,
        fallbackRectangleOptions?: RectangleOptions
    ) => {
        const x = rectangle?.x === undefined ? fallbackRectangleOptions?.x : rectangle.x;
        const y = rectangle?.y === undefined ? fallbackRectangleOptions?.y : rectangle.y;
        const width = rectangle?.width === undefined ? fallbackRectangleOptions?.width : rectangle.width;
        const height = rectangle?.height === undefined ? fallbackRectangleOptions?.height : rectangle.height;
        const previous = cached.cachedRectangleOptions;
        return !(x === previous?.x && y === previous?.y && width === previous?.width && height === previous?.height);
    };

    protected _hasSVGUpdates = (cached: CachedOptions, svg?: SVGOptions) => {
        if (!svg) {
            return !!cached.cachedSVGOptions;
        }

        const previous = cached.cachedSVGOptions;
        return svg.svg === previous?.svg;
    };

    protected _hasRefUpdates = (cached: CachedOptions, ref?: SharedRefOptions) => {
        if (!ref) {
            return !!cached.cachedSharedRefOptions;
        }

        const { sharedId } = ref;
        const previous = cached?.cachedSharedRefOptions;
        return sharedId !== previous?.sharedId;
    };

    protected _hasTransformUpdates = (cached: CachedOptions, transform?: TransformOptions) => {
        const previous = cached.cachedTransformOptions;
        if (previous?.length !== transform?.length) {
            return true;
        }

        return (
            previous?.some((prevTrans, idx) => {
                const trans = transform![idx];
                if (prevTrans.type !== trans.type) {
                    return true;
                }
                if (prevTrans.type === 'scale') {
                    return this._hasScaleUpdates(prevTrans as ScaleOptions, trans as ScaleOptions);
                }
                if (prevTrans.type === 'translate') {
                    return this._hasTranslateUpdates(prevTrans as TranslateOptions, trans as TranslateOptions);
                }
                throw new Error('Unknown transform type');
            }) || false
        );
    };

    protected _hasSpriteUpdates = (cached: CachedOptions, ref?: SharedRefOptions) => {
        return cached.cachedSharedRefOptions?.sharedId !== ref?.sharedId;
    };

    protected _hasFillUpdates = (cached: CachedOptions, fill?: FillOptions, fallbackFillOptions?: FillOptions) => {
        const color = fill?.color === undefined ? fallbackFillOptions?.color : fill.color;
        const opacity = fill?.opacity === undefined ? fallbackFillOptions?.opacity : fill.opacity;
        const previous = cached.cachedFillOptions;
        return !(color === previous?.color && opacity === previous?.opacity);
    };

    protected _hasStrokeUpdates = (
        cached: CachedOptions,
        stroke?: StrokeOptions,
        fallbackStrokeOptions?: StrokeOptions
    ) => {
        const color = stroke?.color === undefined ? fallbackStrokeOptions?.color : stroke.color;
        const opacity = stroke?.opacity === undefined ? fallbackStrokeOptions?.opacity : stroke.opacity;
        const width = stroke?.width === undefined ? fallbackStrokeOptions?.width : stroke.width;
        const previous = cached.cachedStrokeOptions;
        return !(color === previous?.color && opacity === previous?.opacity && width === previous?.width);
    };

    private _hasTranslateUpdates = (cachedTranslateOptions: TranslateOptions, translate: TranslateOptions) => {
        return !(translate.x === cachedTranslateOptions.x && translate?.y === cachedTranslateOptions.y);
    };

    private _hasScaleUpdates = (cachedScaleOptions: ScaleOptions, scale: ScaleOptions) => {
        return !(scale.scaleX === cachedScaleOptions.scaleX && scale.scaleY === cachedScaleOptions.scaleY);
    };
}

export const createRenderingEngine = <T extends RenderingEngine>(
    type: RenderingEngineType<T>,
    options?: RenderingEngineOptions
) => {
    return new type(options);
};
