import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    type PropertyValueMap,
    type PropertyValues,
    svg,
} from '@umetrics/sartorius-ui-base';
import { version } from '../metadata';
import { WithContext } from '../data/plotContext';
import { WithDataContext } from '../data/dataContext';
import { namespacedTag } from '../tag';
import { WithInteractionContext } from './interactionContext';
import { dispatchLineClick, dispatchLineHover, dispatchLineHoverEnd } from '../plottable/svg/lineUtils';
import { lineInteractionStyles } from './lineInteractionStyles';

export interface LineInteractionProperties {
    onLineClick?;
}

@defineElement({ tagName: namespacedTag('line-interaction'), styles: lineInteractionStyles, version })
export class LineInteraction
    extends WithInteractionContext(WithDataContext(WithContext(BaseElement)))
    implements LineInteractionProperties
{
    // Static members

    // Private members

    // Protected members

    // Public members
    onLineClick?;

    _lineElements;

    constructor() {
        super();
    }

    disconnectedCallback(): void {
        super.disconnectedCallback();
        // this.interactionTarget?.removeEventListener('mousemove', this._onMouseMove);
        // this.interactionTarget?.removeEventListener('mouseleave', this._onMouseLeave);
    }

    protected firstUpdated(_changedProperties: PropertyValueMap<any> | Map<PropertyKey, unknown>): void {
        // this.interactionTarget?.addEventListener('mousemove', this._onMouseMove);
        // this.interactionTarget?.addEventListener('mouseleave', this._onMouseLeave);
    }

    protected willUpdate(_changedProperties: PropertyValues<this>): void {
        super.willUpdate(_changedProperties);
        if (_changedProperties.has('allDataElements')) {
            if (this.allDataElements) {
                this._lineElements = this.allDataElements
                    .filter((el) => el.includeInDomain && el.slot === this.interactionTargetSlot)
                    .filter((el) => el.tagName.endsWith('SVG-LINE'));
            }
        }
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});
        if (!this.size || !this.scaleMap) return;
        const [width, height] = this.size;

        return html`<svg width=${width} height=${height} class="${rootClasses}">${this._renderLineTargets()}</svg> `;
    }

    private _renderLineTargets() {
        if (!this._lineElements) return null;

        return this._lineElements
            .filter((line) => !line.interactable) // handled by line itself
            .map((line) => {
                const pathStr = line.getPath();
                return svg`
        <path d=${pathStr} class="hover-target" fill="none" @mousedown="${(e) => e.stopPropagation()}" @click=${(e) =>
            this._dispatchClick(e, line)} @mouseenter=${(e) => this._dispatchHover(e, line)} @mouseleave=${
            this._dispatchHoverEnd
        } stroke=${line.color} stroke-width="8" />
        `;
            });
    }

    private _dispatchClick(evt, line) {
        dispatchLineClick(this, line.getScalesWithMeta(), evt, line);
    }
    private _dispatchHover(evt, line) {
        dispatchLineHover(this, line.getScalesWithMeta(), evt, line);
    }
    private _dispatchHoverEnd(e) {
        dispatchLineHoverEnd(this, e);
    }
}
